/**
 * External dependencies
 */
import deepmerge from 'deepmerge';

/**
 * WordPress dependencies
 */
import {
	useCallback,
	useContext,
	useEffect,
	useMemo,
} from '@wordpress/element';
import {
	privateApis as blockEditorPrivateApis,
	store as blockEditorStore,
} from '@wordpress/block-editor';
import { store as coreStore } from '@wordpress/core-data';
import { useDispatch, useRegistry, useSelect } from '@wordpress/data';

/**
 * Internal dependencies
 */

import { unlock } from '@utils/lock-unlock';
import useBlocks from '@hooks/use-blocks';
import useSiteMetadata from '@hooks/use-site-metadata';
import useVariations from '@hooks/use-variations';
import { store as aiStore } from '@store';
import getVariations from '@utils/custom-color-variations';
import { BIG_SKY_CUSTOM_THEME_PREFIX } from '@utils/custom-color-variations/constants';
const { GlobalStylesContext } = unlock( blockEditorPrivateApis );
const { useGlobalSetting, useGlobalStyle, useGlobalStylesReset } = unlock(
	blockEditorPrivateApis
);

function resolveColor( color, variation ) {
	const normalized = color?.replace( /\s/g, '' );
	if ( 'var(--wp--preset--color--' !== normalized?.slice( 0, 25 ) ) {
		return color;
	}

	const slug = normalized.slice( 25, -1 );

	return variation.settings.color.palette.theme.find(
		( c ) => c.slug === slug
	).color;
}

export default function useStyles() {
	const { getTemplatePartClientID } = useBlocks();
	const { currentColor, currentFont, isOnboarded } =
		useSiteMetadata().getSiteMetadata();
	const {
		setInitializedCustomScheme,
		pushGeneratedVariations,
		setCurrentColor: setCurrentColorState,
		setCurrentFont: setCurrentFontState,
		setUserStyleWithSettings,
		setCurrentPaletteColors,
	} = useDispatch( aiStore );
	const [ canReset, reset ] = useGlobalStylesReset();
	const { getBlocks, getBlockAttributes, getBlocksByName } =
		useSelect( blockEditorStore );
	const { updateBlockAttributes } = useDispatch( blockEditorStore );
	const { editEntityRecord, saveEditedEntityRecord } =
		useDispatch( coreStore );
	const { setCurrentColor, setCurrentFont } = useSiteMetadata();
	const { setUserConfig } = useContext( GlobalStylesContext );
	const { globalStylesId, getEntityRecord, getEditedEntityRecord } =
		useSelect( ( select ) => ( {
			globalStylesId:
				select( coreStore ).__experimentalGetCurrentGlobalStylesId(),
			getEntityRecord: select( coreStore ).getEntityRecord,
			getEditedEntityRecord: select( coreStore ).getEditedEntityRecord,
		} ) );
	const registry = useRegistry();
	const { getColorVariations, getFontVariations, getColorVariationByTitle } =
		useVariations();
	const variations = useSelect( ( select ) => {
		return select(
			coreStore
		).__experimentalGetCurrentThemeGlobalStylesVariations();
	}, [] );
	const {
		getCurrentColor,
		getCurrentFont,
		getInitializedCustomScheme,
		getUserStyleWithSettings,
		getCustomVariationTemplates,
		isEditorIframeLoaded,
	} = useSelect( ( select ) => {
		const ai = select( aiStore );
		return {
			currentPaletteColors: ai.getCurrentPaletteColors(),
			getCurrentColor: ai.getCurrentColor,
			getCurrentFont: ai.getCurrentFont,
			getInitializedCustomScheme: ai.getInitializedCustomScheme,
			getUserStyleWithSettings: ai.getUserStyleWithSettings,
			getCustomVariationTemplates: ai.getCustomVariationTemplates,
			isEditorIframeLoaded: ai.getIsEditorIframeLoaded(),
		};
	} );
	const [ coreColors ] = useGlobalSetting( 'color.palette.core' );
	const [ themeColors ] = useGlobalSetting( 'color.palette.theme' );
	const [ customColors ] = useGlobalSetting( 'color.palette.custom' );

	const paletteColors = useMemo(
		() => [
			...( customColors || [] ),
			...( themeColors || [] ),
			...( coreColors || [] ),
		],
		[ customColors, themeColors, coreColors ]
	);

	const [ userStyle ] = useGlobalStyle( '', undefined, 'user', {
		shouldDecodeEncode: false,
	} );
	const [ customCss, setCustomCss ] = useGlobalStyle( '', undefined, 'all', {
		shouldDecodeEncode: false,
	} );

	useEffect( () => {
		// check if userStyle has settings
		if (
			[ 'blocks', 'color', 'elements', 'typography' ].every( ( key ) =>
				userStyle.hasOwnProperty( key )
			)
		) {
			setUserStyleWithSettings( userStyle );
		}
	}, [ userStyle, setUserStyleWithSettings ] );

	const useCustomCss = useCallback(
		() => [
			customCss.css,
			( css ) =>
				setCustomCss( {
					...getUserStyleWithSettings(),
					css,
				} ),
		],
		[ customCss.css, getUserStyleWithSettings, setCustomCss ]
	);

	const resetStyles = () => {
		if ( canReset ) {
			reset();
			editEntityRecord( 'root', 'globalStyles', globalStylesId, {
				id: globalStylesId,
				settings: {},
				styles: {},
			} );
			saveEditedEntityRecord( 'root', 'globalStyles', globalStylesId );
		}
	};

	const currentTheme = useMemo( () => {
		const globalStyles = getEditedEntityRecord(
			'root',
			'globalStyles',
			globalStylesId
		);
		return globalStyles;
	}, [ getEditedEntityRecord, globalStylesId ] );

	const getCurrentTheme = useCallback( () => {
		const globalStyles = getEditedEntityRecord(
			'root',
			'globalStyles',
			globalStylesId
		);
		return globalStyles;
	}, [ getEditedEntityRecord, globalStylesId ] );

	const getVariationSectionValues = useCallback(
		( colorTitle ) => {
			const variation = getColorVariationByTitle( colorTitle );
			const sectionColors = Object.entries(
				variation?.styles?.blocks?.[ 'core/group' ]?.variations ?? {}
			).map( ( [ name, sectionColor ] ) => ( {
				name,
				background: sectionColor?.color?.background,
				text: sectionColor?.color?.text,
				elements: sectionColor?.elements,
			} ) );

			if (
				! sectionColors.find(
					( sectionColor ) => sectionColor.name === 'default'
				)
			) {
				sectionColors.unshift( {
					name: 'section-default',
					background: variation?.styles?.color?.background,
					text: variation?.styles?.color?.text,
					elements: variation?.styles?.elements,
				} );
			}

			return sectionColors;
		},
		[ getColorVariationByTitle ]
	);

	const updateRootBlocksStyles = useCallback(
		( {
			rootBlocks,
			background_color,
			text_color,
			button_color,
			button_text_color,
			section_to_update = '',
		} ) => {
			// if colors is an object, get the slug
			// if colors are in the format var(--wp--preset--color--theme-n)
			// extract the "theme-n"
			background_color =
				background_color?.slug ||
				background_color?.match(
					/var\(--wp--preset--color--(theme-\d+)\)/
				)?.[ 1 ] ||
				background_color;
			text_color =
				text_color?.slug ||
				text_color?.match(
					/var\(--wp--preset--color--(theme-\d+)\)/
				)?.[ 1 ] ||
				text_color;
			button_color =
				button_color?.slug ||
				button_color?.match(
					/var\(--wp--preset--color--(theme-\d+)\)/
				)?.[ 1 ] ||
				button_color;
			button_text_color =
				button_text_color?.slug ||
				button_text_color?.match(
					/var\(--wp--preset--color--(theme-\d+)\)/
				)?.[ 1 ] ||
				button_text_color;

			// Collect all updates first
			const allUpdates = [];

			// Helper to collect button updates recursively
			const collectChildUpdates = ( block ) => {
				const updates = [];
				const attributes = getBlockAttributes( block.clientId );
				const updatedAttributes = { ...attributes };

				switch ( block.name ) {
					case 'core/button':
					case 'jetpack/button':
						if ( button_text_color ) {
							updatedAttributes.textColor = button_text_color;
						}
						if ( button_color ) {
							updatedAttributes.backgroundColor = button_color;
						}

						if ( button_color || button_text_color ) {
							updates.push( {
								clientId: block.clientId,
								attributes: updatedAttributes,
							} );
						}
						break;
					case 'core/heading':
					case 'core/paragraph':
					case 'core/site-title':
					case 'core/post-title':
						if ( text_color ) {
							updatedAttributes.textColor = text_color;
							updates.push( {
								clientId: block.clientId,
								attributes: updatedAttributes,
							} );
						}
						break;
					case 'jetpack/subscriptions':
						if ( button_text_color ) {
							updatedAttributes.textColor = button_text_color;
						}
						if ( button_color ) {
							updatedAttributes.buttonBackgroundColor =
								button_color;
						}

						if ( button_color || button_text_color ) {
							updates.push( {
								clientId: block.clientId,
								attributes: updatedAttributes,
							} );
						}
						break;
					default:
						break;
				}

				block.innerBlocks.forEach( ( innerBlock ) => {
					updates.push( ...collectChildUpdates( innerBlock ) );
				} );

				return updates;
			};

			// Collect root block updates and their button updates
			rootBlocks.forEach( ( rootBlock ) => {
				const attributes = getBlockAttributes( rootBlock.clientId );

				// if we're updating a section, check if the block has a className with is-style-
				if ( section_to_update ) {
					const matches =
						attributes.className?.match( /is-style-(\S+)\b/ );
					if ( matches ) {
						const sectionName = matches[ 1 ];
						if ( sectionName !== section_to_update ) {
							return;
						}
					}
				}

				const updatedAttributes = { ...attributes };

				if ( background_color ) {
					updatedAttributes.backgroundColor = background_color;
				}
				if ( text_color ) {
					updatedAttributes.textColor = text_color;
				}

				if ( background_color || text_color ) {
					allUpdates.push( {
						clientId: rootBlock.clientId,
						attributes: updatedAttributes,
					} );
				}

				// Collect button updates for this root block
				allUpdates.push( ...collectChildUpdates( rootBlock ) );
			} );

			// Apply all updates
			allUpdates.forEach( ( update ) => {
				updateBlockAttributes( update.clientId, update.attributes );
			} );
		},
		[ getBlockAttributes, updateBlockAttributes ]
	);

	const getRootBlocks = useCallback( () => {
		const postContentBlockId = getBlocksByName( 'core/post-content' )[ 0 ];
		const rootContentBlocks = getBlocks( postContentBlockId );
		const header = getBlocks( getTemplatePartClientID( 'header' ) );
		const footer = getBlocks( getTemplatePartClientID( 'footer' ) );
		return [ ...rootContentBlocks, ...header, ...footer ];
	}, [ getBlocksByName, getBlocks, getTemplatePartClientID ] );

	const setStyles = useCallback(
		( parameters ) => {
			const {
				color,
				font,
				undoIgnore = false,
				updateBlocks = true,
			} = parameters;

			let selectedColorTitle = color || getCurrentColor() || currentColor;
			let selectedFontTitle = font || getCurrentFont() || currentFont;

			const colors = getColorVariations();
			// Check to make sure both the font and color are valid
			// If either are not valid, set them to a random color or font title
			if ( ! colors?.find( ( c ) => c.title === selectedColorTitle ) ) {
				selectedColorTitle =
					// eslint-disable-next-line
					colors[ Math.floor( Math.random() * colors.length ) ]
						?.title;
			}

			// I would love to get rid of the `variations` here, but if we do that, we
			// have few renders where colors/fonts is undefined at the start of the app.
			// This causes the reducer down in `getVariationKeywords` to fail horribly.
			// This is probably something to streamline.
			const colorVariation = ( colors ?? variations )?.find(
				( v ) => v.title === selectedColorTitle
			);
			if ( color ) {
				setCurrentColor( selectedColorTitle );
				setCurrentColorState( selectedColorTitle );

				const colorPalette =
					colorVariation?.settings?.color?.palette?.theme;
				setCurrentPaletteColors( colorPalette );

				// set block attributes styles to match the current variation
				if ( updateBlocks ) {
					registry.batch( () => {
						const sectionValues =
							getVariationSectionValues( selectedColorTitle );
						sectionValues.forEach( ( sectionValue ) => {
							updateRootBlocksStyles( {
								rootBlocks: getRootBlocks(),
								background_color: sectionValue.background,
								text_color: sectionValue.text,
								button_color:
									sectionValue.elements?.button?.color
										?.background,
								button_text_color:
									sectionValue.elements?.button?.color?.text,
								section_to_update: sectionValue.name,
							} );
						} );
					} );
				}
			}

			const fonts = getFontVariations();
			if ( ! fonts?.find( ( f ) => f.title === selectedFontTitle ) ) {
				selectedFontTitle =
					// eslint-disable-next-line
					fonts[ Math.floor( Math.random() * fonts.length ) ]?.title;
			}
			const fontVariation = ( fonts ?? variations )?.find(
				( v ) => v.title === selectedFontTitle
			);
			if ( font ) {
				setCurrentFont( selectedFontTitle );
				setCurrentFontState( selectedFontTitle );
			}

			setUserConfig(
				( currentConfig ) => {
					// Custom merge options to handle arrays properly
					const mergeOptions = {
						arrayMerge: ( destinationArray, sourceArray ) =>
							sourceArray,
					};

					// Custom merge function to ensure only all keys from the target are included, except for typography
					const customMerge = ( target = {}, source = {} ) => {
						const result = { ...target };

						for ( const key in source ) {
							if ( source.hasOwnProperty( key ) ) {
								if ( key === 'typography' ) {
									// Only include typography from source
									result[ key ] = source[ key ];
								} else if (
									typeof source[ key ] === 'object' &&
									! Array.isArray( source[ key ] )
								) {
									result[ key ] = customMerge(
										target[ key ] || {},
										source[ key ]
									);
								} else {
									result[ key ] = source[ key ];
								}
							}
						}

						return result;
					};

					const newConfig = {
						settings: [
							colorVariation?.settings,
							fontVariation?.settings,
						]
							.filter( Boolean )
							.reduce(
								( acc, curr ) =>
									deepmerge( acc, curr, mergeOptions ),
								{}
							),
						styles: [
							colorVariation?.styles,
							fontVariation?.styles,
						]
							.filter( Boolean )
							.reduce(
								( acc, curr ) =>
									deepmerge( acc, curr, mergeOptions ),
								{}
							),
					};

					return customMerge( currentConfig, newConfig );
				},
				{ undoIgnore }
			);

			// Update the title of the global styles entity
			editEntityRecord(
				'root',
				'globalStyles',
				globalStylesId,
				{
					title: selectedColorTitle,
				},
				{ undoIgnore: true }
			);

			return {
				selectedColorVariation: selectedColorTitle,
				selectedFontVariation: selectedFontTitle,
			};
		},
		[
			currentColor,
			currentFont,
			editEntityRecord,
			getColorVariations,
			getCurrentColor,
			getCurrentFont,
			getFontVariations,
			getRootBlocks,
			getVariationSectionValues,
			globalStylesId,
			registry,
			setCurrentColor,
			setCurrentColorState,
			setCurrentFont,
			setCurrentFontState,
			setCurrentPaletteColors,
			setUserConfig,
			updateRootBlocksStyles,
			variations,
		]
	);

	const saveStylesEntityRecord = async () => {
		await saveEditedEntityRecord( 'root', 'globalStyles', globalStylesId );
	};

	const getCustomVariationTemplateCopy = () => {
		return JSON.parse(
			JSON.stringify( getCustomVariationTemplates()[ 0 ] )
		);
	};

	const getThemeColors = () => {
		return themeColors;
	};

	const hexToRgb = ( hex ) => {
		let r = 0,
			g = 0,
			b = 0;

		// 3 digits
		if ( hex.length === 4 ) {
			r = '0x' + hex[ 1 ] + hex[ 1 ];
			g = '0x' + hex[ 2 ] + hex[ 2 ];
			b = '0x' + hex[ 3 ] + hex[ 3 ];

			// 6 digits
		} else if ( hex.length === 7 ) {
			r = '0x' + hex[ 1 ] + hex[ 2 ];
			g = '0x' + hex[ 3 ] + hex[ 4 ];
			b = '0x' + hex[ 5 ] + hex[ 6 ];
		}

		return [ +r, +g, +b ].join( ',' );
	};

	const getVariationBackgroundColor = useCallback(
		( colorTitle ) => {
			const variation = getColorVariationByTitle( colorTitle );
			return variation?.styles?.color?.background;
		},
		[ getColorVariationByTitle ]
	);

	const getVariationTextColor = useCallback(
		( colorTitle ) => {
			const variation = getColorVariationByTitle( colorTitle );
			return variation?.styles?.color?.text;
		},
		[ getColorVariationByTitle ]
	);

	const getVariationSectionColors = useCallback(
		( colorTitle ) => {
			const variation = getColorVariationByTitle( colorTitle );
			const sectionColors = Object.entries(
				variation?.styles?.blocks?.[ 'core/group' ]?.variations ?? {}
			).map( ( [ name, sectionColor ] ) => ( {
				name,
				label: name,
				background: resolveColor(
					sectionColor?.color?.background,
					variation
				),
				text: resolveColor( sectionColor?.color?.text, variation ),
			} ) );

			if (
				! sectionColors.find(
					( sectionColor ) => sectionColor.name === 'default'
				)
			) {
				sectionColors.unshift( {
					name: 'default',
					label: 'Default',
					background: resolveColor(
						variation?.styles?.color?.background,
						variation
					),
					text: resolveColor(
						variation?.styles?.color?.text,
						variation
					),
				} );
			}

			return sectionColors;
		},
		[ getColorVariationByTitle ]
	);

	const resetBlockTextColor = ( block ) => {
		updateBlockAttributes( block.clientId, {
			style: {
				...block.attributes.style,
				color: {
					text: undefined,
				},
			},
		} );
	};

	/**
	 * Combines custom colors with predefined templates to create color scheme variations.
	 *
	 * This approach uses static templates rather than AI generation for several reasons:
	 * 1. Templates ensure consistent array length since AI generation cannot guarantee exact element counts
	 * 2. Templates allow precise element descriptions and metadata that would be difficult to maintain with AI
	 * 3. Templates preserve critical slug mappings needed for Gutenberg block integration
	 * 4. Templates enable flexible color reordering while maintaining the same color set
	 *
	 * @param {Object} paletteColorsObject - Object containing color values {color1: '#000000', color2: '#000000', color3: '#000000', color4: '#000000'}
	 *
	 * @return {Array} Array of color scheme variations
	 *
	 * @see createCustomSchemeVariations
	 * @see customSchemeTemplate1
	 * @see customSchemeTemplate2
	 */
	const createCustomSchemeVariations = useCallback(
		( { custom_colors } ) => {
			const customVariationTemplates = getCustomVariationTemplates();
			const generatedVariations = [];
			const customColorVariations = getVariations();
			for ( const customColorVariation of customColorVariations ) {
				// make deep copies of the customVariationTemplate
				const templateTheme = JSON.parse(
					JSON.stringify( customVariationTemplates[ 0 ] )
				);
				generatedVariations.push(
					customColorVariation( {
						templateTheme,
						colors: custom_colors,
					} )
				);
			}
			// filter out any null values from the generatedVariations array
			const filteredVariations = generatedVariations.filter(
				( variation ) => variation !== null
			);

			pushGeneratedVariations( filteredVariations );
			return filteredVariations;
		},
		[ getCustomVariationTemplates, pushGeneratedVariations ]
	);

	// Push the existing palette colors to the generated variations on initialization
	// This ensures we always have a Custom Scheme in the palette
	useEffect( () => {
		if (
			! getCustomVariationTemplates().length ||
			getInitializedCustomScheme() ||
			! isOnboarded ||
			! isEditorIframeLoaded
		) {
			return;
		}

		// always create a variation for a custom theme
		const currentThemeCopy = JSON.parse( JSON.stringify( currentTheme ) );
		currentThemeCopy.title = currentColor?.startsWith(
			BIG_SKY_CUSTOM_THEME_PREFIX
		)
			? currentColor
			: BIG_SKY_CUSTOM_THEME_PREFIX;
		pushGeneratedVariations( [ currentThemeCopy ] );
		setCurrentPaletteColors( paletteColors );
		setInitializedCustomScheme( true );
	}, [
		currentColor,
		paletteColors,
		getCustomVariationTemplates,
		getEntityRecord,
		getInitializedCustomScheme,
		globalStylesId,
		isOnboarded,
		pushGeneratedVariations,
		setInitializedCustomScheme,
		setCurrentPaletteColors,
		currentTheme,
		setStyles,
		isEditorIframeLoaded,
		getRootBlocks,
	] );

	return {
		currentTheme,
		getCurrentTheme,
		resetStyles,
		setStyles,
		saveStylesEntityRecord,
		themeColors,
		paletteColors,
		hexToRgb,
		getVariationBackgroundColor,
		getVariationTextColor,
		getVariationSectionColors,
		useCustomCss,
		getThemeColors,
		resetBlockTextColor,
		createCustomSchemeVariations,
		getCustomVariationTemplateCopy,
		updateRootBlocksStyles,
	};
}
