/**
 * WordPress dependencies
 */
import { useDispatch, useSelect } from '@wordpress/data';
import { store as editorStore } from '@wordpress/editor';
import { useCallback } from '@wordpress/element';
import { store as coreStore } from '@wordpress/core-data';
import { privateApis as routerPrivateApis } from '@wordpress/router';
import { serialize } from '@wordpress/blocks';
import { __ } from '@wordpress/i18n';

/**
 * Internal dependencies
 */
import useCanvas from '@hooks/use-canvas';
import usePatterns from '@hooks/use-patterns';
import { getCanvasIframeElements } from '@utils/canvas-iframe-elements';
import { unlock } from '@utils/lock-unlock';
import setDelay from '@utils/set-delay';
import log from '@utils/log-debug.js';
import { store as aiStore } from '@store';

const { useHistory } = unlock( routerPrivateApis );
const emptyArray = [];
export default function usePages() {
	const { pages, homePageId, getEntityRecords } = useSelect(
		( select ) => ( {
			pages:
				select( coreStore ).getEntityRecords( 'postType', 'page', {
					per_page: -1,
					status: 'publish,future,draft,pending,private',
				} ) || emptyArray,
			homePageId: select( coreStore ).getEntityRecord( 'root', 'site' )
				?.page_on_front,
			getEntityRecords: select( coreStore ).getEntityRecords,
		} ),
		[]
	);

	const { saveEntityRecord, deleteEntityRecord } = useDispatch( coreStore );

	const deleteAllPagesExceptHome = useCallback( async () => {
		const allPages = getEntityRecords( 'postType', 'page', {
			per_page: -1,
			status: 'publish,future,draft,pending,private',
		} );

		if ( ! allPages ) return;

		const deletePromises = allPages
			.filter( ( page ) => page.id !== homePageId ) // Exclude home page
			.map( ( page ) =>
				deleteEntityRecord( 'postType', 'page', page.id, {
					force: true,
				} )
			);

		await Promise.all( deletePromises );
	}, [ homePageId, deleteEntityRecord, getEntityRecords ] );

	const { currentPostId, getCurrentPostId } = useSelect( ( select ) => ( {
		currentPostId: select( editorStore ).getCurrentPostId(),
		getCurrentPostId: select( editorStore ).getCurrentPostId,
	} ) );

	const { isComingSoon, isBlogPrivate } = useSelect( ( select ) => ( {
		isComingSoon: select( aiStore ).getIsComingSoon(),
		isBlogPrivate: select( aiStore ).getIsBlogPrivate(),
	} ) );

	const isSitePublished = ! isComingSoon && ! isBlogPrivate;

	const history = useHistory();
	const { addPatternsToCanvas } = useCanvas();
	const { composePatterns, syncSiteMetadataPatterns } = usePatterns();

	const { savePost } = useDispatch( editorStore );

	const goToPage = ( { postId, postType } = {} ) => {
		if ( ! postId ) {
			// redirect to homepage
			if ( history.navigate ) {
				history.navigate( `/wp-admin/site-editor.php?p=/&canvas=edit` );
			} else {
				history.push( {
					canvas: 'edit',
				} );
			}
			return;
		}

		if ( history.navigate ) {
			history.navigate(
				`/wp-admin/site-editor.php?p=/page/${ postId }&canvas=edit`
			);
		} else {
			history.push( {
				postId,
				postType,
				canvas: 'edit',
			} );
		}
	};
	const isPagePublished = () => {
		// check if this is a development environment
		if ( process.env.NODE_ENV === 'development' ) {
			return 'publish';
		}
		return isSitePublished ? 'draft' : 'publish';
	};

	const createPage = async (
		title,
		categories,
		topic,
		siteDescription,
		siteLocation,
		loadInEditor = false,
		menuOrder = 1, // increment this for each new page, 0 is the homepage by default
		existingPageId = null, // New parameter to handle updating existing pages
		pageDescription = null,
		slug = null
	) => {
		try {
			if ( loadInEditor ) {
				const { canvasiFrameBody } = getCanvasIframeElements();
				canvasiFrameBody.classList.add( 'big-sky__is-creating-page' );

				const newPage = await saveEntityRecord(
					'postType',
					'page',
					{
						status: isPagePublished(),
						title,
						slug: slug || title || __( 'No title', 'big-sky' ),
					},
					{ throwOnError: true }
				);

				goToPage( { postId: newPage.id, postType: newPage.type } );
				await setDelay( 3000 );

				const concepts = composePatterns( {
					title,
					categories,
					topic,
					siteDescription,
					siteLocation,
					numberOfConcepts: 1,
					newPage: true,
					noCache: true,
					pageSlug: slug,
				} );

				const conceptPatternsAndResolvers = concepts[ 0 ].patterns.map(
					( pattern, i ) => ( {
						pattern,
						resolver: concepts[ 0 ].resolvers[ i ].resolver,
					} )
				);
				canvasiFrameBody.classList.remove(
					'big-sky__is-creating-page'
				);
				await addPatternsToCanvas( conceptPatternsAndResolvers, false );
				return newPage;
			}

			const concepts = composePatterns( {
				title,
				categories,
				topic,
				siteDescription,
				siteLocation,
				numberOfConcepts: 1,
				newPage: true,
				noCache: true,
				pageDescription,
				pageSlug: slug,
			} );
			const resolvers = concepts[ 0 ].resolvers
				.map( ( resolver ) => resolver.resolver )
				.flat();

			await Promise.all( resolvers );
			const rootBlocks = concepts[ 0 ].patterns
				.map( ( pattern ) => pattern.blocks )
				.flat();
			const pageContent = serialize( rootBlocks );

			const pageData = {
				status: isPagePublished(),
				title,
				content: pageContent,
				slug: slug || title || __( 'No title', 'big-sky' ),
				menu_order: menuOrder,
			};

			// If we have an existing page ID, update it instead of creating a new one
			const newPage = await saveEntityRecord(
				'postType',
				'page',
				existingPageId ? { ...pageData, id: existingPageId } : pageData,
				{ throwOnError: true }
			);
			return { newPage, rootBlocks };
		} catch ( error ) {
			log.error( error );
		}
	};

	const saveAndGoToPage = async ( pageId, goHome ) => {
		syncSiteMetadataPatterns( {
			ids: [ getCurrentPostId() ],
			persist: true,
		} );
		await savePost( getCurrentPostId() );
		if ( goHome ) {
			goToPage( { postId: null, postType: null } );
			return;
		}

		goToPage( { postId: pageId, postType: 'page' } );
	};

	const getPages = useCallback( () => {
		return ( pages || [] )
			.filter( ( page ) => page.title.rendered.trim() !== '' )
			.map( ( page ) => ( {
				id: page.id,
				slug: page.slug,
				title: page.title.rendered,
				status: page.status,
				isHomePage: page.id === homePageId,
				isCurrentPage: page.id === currentPostId,
			} ) );
	}, [ pages, homePageId, currentPostId ] );

	const deletePage = useCallback(
		async ( pageId ) => {
			try {
				await deleteEntityRecord( 'postType', 'page', pageId, {
					force: true,
				} );
				return true;
			} catch ( error ) {
				log.error( error );
				return false;
			}
		},
		[ deleteEntityRecord ]
	);
	// go back to the previous page
	const goBackPage = useCallback( () => {
		history.back();
	}, [ history ] );

	return {
		createPage,
		deletePage,
		deleteAllPagesExceptHome,
		getPages,
		goToPage,
		saveAndGoToPage,
		goBackPage,
	};
}
