export default function getStockImageTool() {
	return {
		name: 'get_stock_image',
		description: `Get a new stock image.`,
		parameters: {
			type: 'object',
			properties: {
				clientId: {
					type: 'string',
					description: 'The clientId of the current selected block.',
				},
				description: {
					type: 'string',
					description:
						'The description of the new image. Do not include any text about replacing the image or something similar, only what the content of the image should be. For example, if the user request is "replace this image with a dog", the description should be "dog".',
				},
			},
			required: [ 'clientId', 'description' ],
		},
		strict: true,
	};
}
