/**
 * WordPress dependencies
 */
import {
	edit,
	Icon,
	mapMarker,
	termDescription,
	title,
} from '@wordpress/icons';
import { Tooltip } from '@wordpress/components';
import { __, sprintf } from '@wordpress/i18n';
import { useEffect, useMemo, useState } from '@wordpress/element';
import { useDispatch } from '@wordpress/data';
import { useChat, withToolCall } from '@automattic/big-sky-agents';

/**
 * Internal dependencies
 */
import useSiteMetadata from '@hooks/use-site-metadata';
import { store as aiStore } from '@store';
import ChatMessage from '@components/chat/message';
import { ChatSuggestions } from '@ai/assistants/shared/make-suggestions';
import { messageRoles } from '@utils/constants';
import { recordTracksEvent } from '@utils/tracks';
import useJetpackToken from '@hooks/use-jetpack-token';
import './style.scss';

function SiteSpecsList( { args, respond, onLoad } ) {
	const { withJetpackTokenRequest } = useJetpackToken();
	const { userSay, running } = useChat();
	const { setSiteMetadata, getSiteMetadata } = useSiteMetadata();
	const { setCurrentUserMessage, setChatComponents } = useDispatch( aiStore );
	const currentSiteMetadata = getSiteMetadata();
	const { isOnboarded } = currentSiteMetadata;
	const hoveringOffState = {
		title: false,
		description: false,
		location: false,
	};

	const [ isHovering, setIsHovering ] = useState( hoveringOffState );
	const {
		siteTitle,
		siteType,
		topic,
		siteDescription,
		siteLocationName,
		defaultImageStyle,
		actions,
	} = {
		...currentSiteMetadata,
		siteLocationName: currentSiteMetadata.siteLocation?.name,
		...Object.fromEntries(
			Object.entries( args || {} ).filter( ( [ , value ] ) => !! value )
		), // only include args that have a value, fallback to currentSiteMetadata
	};

	const siteMeta = useMemo(
		() => ( {
			siteType,
			topic,
			siteTitle,
			siteDescription,
			defaultImageStyle,
			siteLocation: {
				name: siteLocationName,
			},
		} ),
		[
			siteType,
			topic,
			siteTitle,
			siteDescription,
			siteLocationName,
			defaultImageStyle,
		]
	);

	const hasSpecs = siteTitle || siteDescription || siteLocationName;

	useEffect( () => {
		if ( onLoad ) {
			onLoad();
		}
	}, [ onLoad ] );

	useEffect( () => {
		if ( hasSpecs && ! isOnboarded ) {
			const specActions = actions || [
				{
					actionName: __( 'Looks good', 'big-sky' ),
					description: sprintf(
						/* Translators: placeholder is the site title */
						__( 'Build %s', 'big-sky' ),
						siteTitle
					),
				},
			];
			setChatComponents( [
				<ChatSuggestions
					key="suggestions"
					args={ {
						siteTitle,
						siteType,
						topic,
						siteDescription,
						siteLocationName,
						actions: specActions,
					} }
					respond={ () => {
						setSiteMetadata( siteMeta, false, true );
						if ( args ) {
							respond( specActions[ 0 ]?.description );
							return;
						}
						const message = specActions[ 0 ]?.description;
						setCurrentUserMessage( message );
						withJetpackTokenRequest( () => userSay( message ) );
					} }
				/>,
			] );
		}
	}, [
		setChatComponents,
		setSiteMetadata,
		respond,
		siteMeta,
		hasSpecs,
		siteTitle,
		siteType,
		topic,
		siteDescription,
		siteLocationName,
		actions,
		isOnboarded,
		args,
		setCurrentUserMessage,
		withJetpackTokenRequest,
		userSay,
	] );

	if ( ( ! args && ! currentSiteMetadata?.siteTitle ) || isOnboarded ) {
		return null;
	}

	const upperCaseFirstLetter = ( specContent ) =>
		specContent
			? `${ specContent.charAt( 0 ).toUpperCase() }${ specContent.slice(
					1
			  ) }`
			: '';

	const handleTableRowClick = ( specItem ) => {
		recordTracksEvent( 'site_specs_list_click', {
			spec_item: specItem,
		} );
		setCurrentUserMessage( `Change my ${ specItem } to ` );
		document.querySelector( '.big-sky__ai-input__input textarea' )?.focus();
	};

	if ( ! hasSpecs || running ) {
		return null;
	}

	return (
		<ChatMessage
			message={ {
				role: messageRoles.COMPONENT,
				content: (
					<>
						<table className="big-sky__spec-list">
							<tbody>
								<tr
									onClick={ () =>
										handleTableRowClick( 'site title' )
									}
									onMouseEnter={ () =>
										setIsHovering( {
											title: true,
											description: false,
											location: false,
										} )
									}
									onMouseLeave={ () =>
										setIsHovering( hoveringOffState )
									}
								>
									<td className="big-sky__spec-icon">
										<Tooltip
											text={ __(
												'Click to update site title',
												'big-sky'
											) }
										>
											<>
												{ ! isHovering.title && (
													<Icon icon={ title } />
												) }

												{ isHovering.title && (
													<Icon icon={ edit } />
												) }
											</>
										</Tooltip>
									</td>
									<td>
										<Tooltip
											text={ __(
												'Click to update site title',
												'big-sky'
											) }
										>
											<span>{ siteTitle }</span>
										</Tooltip>
									</td>
								</tr>
								<tr
									onClick={ () =>
										handleTableRowClick(
											'site description'
										)
									}
									onMouseEnter={ () =>
										setIsHovering( {
											title: false,
											description: true,
											location: false,
										} )
									}
									onMouseLeave={ () =>
										setIsHovering( hoveringOffState )
									}
								>
									<td className="big-sky__spec-icon">
										<Tooltip
											text={ __(
												'Click to update description',
												'big-sky'
											) }
										>
											<>
												{ ! isHovering.description && (
													<Icon
														icon={ termDescription }
													/>
												) }
												{ isHovering.description && (
													<Icon icon={ edit } />
												) }
											</>
										</Tooltip>
									</td>
									<td>
										<Tooltip
											text={ __(
												'Click to update description',
												'big-sky'
											) }
										>
											<span>
												{ upperCaseFirstLetter(
													siteDescription
												) }
											</span>
										</Tooltip>
									</td>
								</tr>
								{ siteLocationName && (
									<tr
										onClick={ () =>
											handleTableRowClick( 'location' )
										}
										onMouseEnter={ () =>
											setIsHovering( {
												title: false,
												description: false,
												location: true,
											} )
										}
										onMouseLeave={ () =>
											setIsHovering( hoveringOffState )
										}
									>
										<td className="big-sky__spec-icon">
											<Tooltip
												text={ __(
													'Click to update location',
													'big-sky'
												) }
											>
												<>
													{ ! isHovering.location && (
														<Icon
															icon={ mapMarker }
														/>
													) }
													{ isHovering.location && (
														<Icon icon={ edit } />
													) }
												</>
											</Tooltip>
										</td>
										<td>
											<Tooltip
												text={ __(
													'Click to update location',
													'big-sky'
												) }
											>
												<span>
													{ siteLocationName }
												</span>
											</Tooltip>
										</td>
									</tr>
								) }
							</tbody>
						</table>

						{ args && (
							<div className="big-sky__site-specs-list-confirm">
								{ __(
									'Do the above details look correct?',
									'big-sky'
								) }
							</div>
						) }
					</>
				),
			} }
		/>
	);
}
export default withToolCall( 'confirm_site_specs', SiteSpecsList );
