/**
 * WordPress dependencies
 */
import { store as blockEditorStore } from '@wordpress/block-editor';
import { subscribe, useDispatch, useSelect } from '@wordpress/data';
import { store as editorStore } from '@wordpress/editor';
import { store as preferencesStore } from '@wordpress/preferences';
import { useCallback, useEffect, useRef, useState } from '@wordpress/element';

/**
 * Internal dependencies
 */
import useSaveExternalImages from '@hooks/use-save-external-images';
import useSiteMetadata from '@hooks/use-site-metadata';
import useBlocks from '@hooks/use-blocks';
import { store as aiStore } from '@store';
import setDelay from '@utils/set-delay';
import useCanvas from '@hooks/use-canvas.js';
import usePatterns from '@hooks/use-patterns.js';
import { getCanvasIframeElements } from '@utils/canvas-iframe-elements';
import useColorizedLogo from '@hooks/use-colorized-logo';

export default function EditorSetup() {
	useCanvasSetup( { awaitEditor: true } );
	const { importExternalImagesOnSave } = useSaveExternalImages();
	useEffect( () => {
		importExternalImagesOnSave();
	}, [ importExternalImagesOnSave ] );

	return null;
}

// This hook is deliberately in the same file as the component that uses it as the
// useEffects run should only be added once so we don't want this hook accidently used elsewhere.
function useCanvasSetup( { awaitEditor } ) {
	const {
		canvasMode,
		clearTemplateParts,
		clearBlocks,
		insertDummySections,
		setIframeScale,
		zoomIn,
		zoomOut,
	} = useCanvas();
	const { setDefaultEditMode } = usePatterns();
	const [ awaitingEditor, setAwaitingEditor ] = useState( false );
	const [ awaitingTemplatePartContent, setAwaitingTemplatePartContent ] =
		useState( false );
	const { getBlocks, getBlockCount } = useSelect( ( select ) => ( {
		getBlocks: select( blockEditorStore ).getBlocks,
		getBlockCount: select( blockEditorStore ).getBlockCount,
	} ) );

	const { setRenderingMode } = useDispatch( editorStore );

	const { isOnboarded } = useSiteMetadata().getSiteMetadata();
	const {
		chosenCategories,
		isBuildingSite,
		isEditorReady,
		isEditorIframeLoaded,
		isHandoff,
		isTemplatePartContentReady,
	} = useSelect( ( select ) => ( {
		chosenCategories: select( aiStore ).getChosenCategories(),
		isBuildingSite: select( aiStore ).getIsBuildingSite(),
		isEditorReady: select( aiStore ).getIsEditorReady(),
		isEditorIframeLoaded: select( aiStore ).getIsEditorIframeLoaded(),
		isHandoff: select( aiStore ).getIsHandoff(),
		isTemplatePartContentReady:
			select( aiStore ).getIsTemplatePartContentReady(),
	} ) );

	const { getContentBlockClientID, getTemplatePartClientID } = useBlocks();

	const { setDefaults: setPreferencesDefaults } =
		useDispatch( preferencesStore );
	const {
		setIsEditorReady,
		setIsEditorIframeLoaded,
		setIsTemplatePartContentReady,
	} = useDispatch( aiStore );

	const { setShowWelcomeGuide } =
		useDispatch( 'automattic/wpcom-welcome-guide' ) ?? {}; // eslint-disable-line @wordpress/data-no-store-string-literals

	useEffect( () => {
		const whenEditorIsReady = () => {
			return new Promise( ( resolve ) => {
				const unsubscribe = subscribe( () => {
					// This will trigger after the initial render blocking, before the window load event
					// This seems currently more reliable than using __unstableIsEditorReady
					if ( getBlockCount( getContentBlockClientID() ) >= 0 ) {
						unsubscribe();
						resolve();
					}
				} );
			} );
		};

		const waitForEditorReady = async () => {
			await whenEditorIsReady();
			setIsEditorReady( true );
		};

		if ( awaitEditor && ! awaitingEditor ) {
			setAwaitingEditor( true );
			waitForEditorReady();
		}
	}, [
		getBlockCount,
		setIsEditorReady,
		awaitEditor,
		awaitingEditor,
		getContentBlockClientID,
	] );

	const getCanvasHtml = useCallback( () => {
		const { canvasiFrameDocument } = getCanvasIframeElements();
		return canvasiFrameDocument?.querySelector( 'html' );
	}, [] );

	useEffect( () => {
		setPreferencesDefaults( 'core', {
			distractionFree: true,
			fixedToolbar: true,
			isComplementaryAreaVisible: false,
			showBlockBreadcrumbs: false,
		} );

		setPreferencesDefaults( 'core/edit-site', {
			isComplementaryAreaVisible: false,
			welcomeGuide: false,
			welcomeGuidePage: false,
			welcomeGuideStyles: false,
			welcomeGuideTemplate: false,
		} );
	}, [ setPreferencesDefaults ] );

	useEffect( () => {
		if ( setShowWelcomeGuide ) setShowWelcomeGuide( false );
	}, [ setShowWelcomeGuide ] );

	useEffect( () => {
		const checkIframeLoaded = async () => {
			const canvasHtml = getCanvasHtml();
			if (
				! canvasHtml?.classList.contains( 'block-editor-iframe__html' )
			) {
				await setDelay( 500 );
				checkIframeLoaded();
				return;
			}

			setIsEditorIframeLoaded( true );
		};
		if ( isEditorReady && awaitEditor && ! isEditorIframeLoaded ) {
			checkIframeLoaded();
		}
	}, [
		awaitEditor,
		getCanvasHtml,
		isEditorIframeLoaded,
		isEditorReady,
		setIsEditorIframeLoaded,
	] );

	useColorizedLogo();

	const observer = useRef( null );

	useEffect( () => {
		const { canvasiFrame } = getCanvasIframeElements();

		if ( isEditorIframeLoaded ) {
			// set iframe visibility
			canvasiFrame.style.visibility = 'visible';

			if ( ! observer.current ) {
				observer.current = new window.MutationObserver(
					( mutations ) => {
						for ( const mutation of mutations ) {
							for ( const removedNode of mutation.removedNodes ) {
								if ( removedNode.contains( canvasiFrame ) ) {
									setIsEditorIframeLoaded( false );
								}
							}
						}
					}
				);
				observer.current.observe(
					document.getElementById( 'site-editor' ),
					{ subtree: true, childList: true }
				);
			}
		} else if ( observer.current ) {
			observer.current.disconnect();
			observer.current = null;
		}

		return () => {
			if ( observer.current ) {
				observer.current.disconnect();
				observer.current = null;
			}
		};
	}, [ isEditorIframeLoaded, setIsEditorIframeLoaded ] );

	useEffect( () => {
		const adjustCanvasOpacity = async () => {
			const { canvasiFrame } = getCanvasIframeElements();
			if ( isEditorReady && ! isEditorIframeLoaded && canvasiFrame ) {
				canvasiFrame.style.opacity = 0;
			} else if (
				isEditorReady &&
				isEditorIframeLoaded &&
				canvasiFrame
			) {
				await setDelay( 300 );
				canvasiFrame.style.opacity = 1;
			}
		};
		adjustCanvasOpacity();
	}, [ isEditorReady, isEditorIframeLoaded ] );

	useEffect( () => {
		const whenTemplatePartContentIsReady = () => {
			return new Promise( ( resolve ) => {
				const unsubscribe = subscribe( () => {
					const headerContentBlocks = getBlocks(
						getTemplatePartClientID( 'header' )
					);
					const footerContentBlocks = getBlocks(
						getTemplatePartClientID( 'footer' )
					);
					if (
						headerContentBlocks.length > 0 &&
						footerContentBlocks.length > 0
					) {
						unsubscribe();
						resolve();
					}
				} );
			} );
		};

		const waitForTemplatePartContentReady = async () => {
			await whenTemplatePartContentIsReady();
			setIsTemplatePartContentReady( true );
		};

		if ( awaitEditor && ! awaitingTemplatePartContent ) {
			setAwaitingTemplatePartContent( true );
			waitForTemplatePartContentReady();
		}
	}, [
		getTemplatePartClientID,
		getBlocks,
		setIsTemplatePartContentReady,
		awaitEditor,
		awaitingTemplatePartContent,
	] );

	useEffect( () => {
		if ( ! isTemplatePartContentReady ) {
			return;
		}

		const headerBlocks = getBlocks( getTemplatePartClientID( 'header' ) );
		const footerBlocks = getBlocks( getTemplatePartClientID( 'footer' ) );

		setDefaultEditMode( [ ...headerBlocks, ...footerBlocks ] );
	}, [
		isTemplatePartContentReady,
		getBlocks,
		getTemplatePartClientID,
		setDefaultEditMode,
	] );

	useEffect( () => {
		const adjustCanvasMode = async () => {
			const { canvasiFrame, canvasiFrameRoot } =
				getCanvasIframeElements();

			if ( isBuildingSite ) {
				canvasiFrame.classList.add( 'big-sky__is-building-site' );
				canvasiFrameRoot.classList.add( 'big-sky__is-building-site' );
				canvasiFrameRoot.classList.add(
					'big-sky__is-building-site-overlay'
				);

				zoomOut();
				setRenderingMode( 'template-locked' );

				if ( ! isHandoff ) {
					await setDelay( 500 );
					await Promise.all( [
						clearTemplateParts(),
						clearBlocks(),
					] );
					if ( chosenCategories.length ) {
						await insertDummySections( {
							count: chosenCategories.length + 2,
							replaceTemplateParts: true,
						} );
						canvasiFrameRoot.classList.remove(
							'big-sky__is-building-site-overlay'
						);
					}
				}
				return;
			}

			if ( isOnboarded ) {
				setRenderingMode( 'template-locked' );
				zoomIn();
			}

			canvasiFrame.classList.remove( 'big-sky__is-building-site' );
			canvasiFrameRoot.classList.remove( 'big-sky__is-building-site' );
		};

		if ( ! isEditorIframeLoaded ) return;

		adjustCanvasMode();
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [
		chosenCategories,
		isBuildingSite,
		isEditorIframeLoaded,
		isOnboarded,
	] );

	useEffect( () => {
		const handleCanvasModeChange = async () => {
			const { canvasiFrameRoot } = getCanvasIframeElements();
			const messageList = document.querySelector(
				'.big-sky__ai-messages'
			);
			if ( canvasMode === 'view' ) {
				document
					.querySelector( 'body' )
					.classList.add( 'big-sky__canvas-mode-view' );
				canvasiFrameRoot?.classList?.add( 'big-sky__canvas-mode-view' );
				if ( messageList ) messageList.style.visibility = 'hidden';
			} else {
				document
					.querySelector( 'body' )
					.classList.remove( 'big-sky__canvas-mode-view' );
				canvasiFrameRoot?.classList?.remove(
					'big-sky__canvas-mode-view'
				);

				await setDelay( 1500 );
				if ( messageList ) messageList.style.visibility = 'visible';
				setIframeScale();
			}
		};

		handleCanvasModeChange();
	}, [ canvasMode, isEditorIframeLoaded, setIframeScale ] );
}
