/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { store as blockEditorStore } from '@wordpress/block-editor';
import { useDispatch, useSelect } from '@wordpress/data';

/**
 * Internal dependencies
 */
import useCanvas from '@hooks/use-canvas';
import usePatternToolValidation from '@ai/assistants/pattern/use-pattern-tool-validation.js';
import { store as aiStore } from '@store';
import { dockViews } from '@utils/constants';

export default function useRemoveBlockTool() {
	const { zoomIn } = useCanvas();
	const { setIsToolContainerVisible } = useDispatch( aiStore );
	const { validateRemoveBlockArgs } = usePatternToolValidation();
	const { setDockView, setLastSelectedBlock } = useDispatch( aiStore );

	const { removeBlock: removeBlockFromEditor } =
		useDispatch( blockEditorStore );

	const { getBlock, getSelectedBlock, getBlockRootClientId } = useSelect(
		( select ) => ( {
			getBlock: select( blockEditorStore ).getBlock,
			getSelectedBlock: select( blockEditorStore ).getSelectedBlock,
			getBlockRootClientId:
				select( blockEditorStore ).getBlockRootClientId,
		} )
	);

	const { lastSelectedBlock } = useSelect( ( select ) => {
		const ai = select( aiStore );

		return {
			lastSelectedBlock: ai.getLastSelectedBlock(),
		};
	} );

	const removeBlock = async ( { clientId } ) => {
		try {
			let __clientId = clientId;
			if ( ! clientId ) {
				// Use the selected block if no clientId is provided
				__clientId = getSelectedBlock()?.clientId;
			}

			if ( ! __clientId ) {
				// If there's no selected block, use the last selected block
				__clientId = lastSelectedBlock?.clientId;
			}

			await validateRemoveBlockArgs( { clientId: __clientId } );

			// If the block is a core/cover block, remove the parent core/group block instead
			const _clientId =
				getBlock( __clientId )?.name === 'core/cover'
					? getBlockRootClientId( __clientId )
					: __clientId;

			if ( getBlock( _clientId ) ) {
				removeBlockFromEditor( _clientId );
			}

			setLastSelectedBlock( null );
			setDockView( dockViews.MENUS );
			setIsToolContainerVisible( false );
			zoomIn();

			return __( 'I have removed the block for you.', 'big-sky' );
		} catch ( error ) {
			throw error;
		}
	};

	return {
		removeBlock,
	};
}
