/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { Button } from '@wordpress/components';
import { chevronDown, Icon, image } from '@wordpress/icons';

export default function CoverImageDockMenuItem( {
	onAssistantMenuClick,
	imageUrl,
} ) {
	const assistantMenuTitle = __( 'Background Image', 'big-sky' );
	const assistantMenuSlug = 'background-image';

	return (
		<>
			<Button
				onClick={ () =>
					onAssistantMenuClick(
						assistantMenuTitle,
						assistantMenuSlug
					)
				}
				icon={ ! imageUrl && image }
				className="has-color-picker has-chevron"
				label={ __( 'Background image', 'big-sky' ) }
			>
				{ imageUrl && (
					<img
						src={ imageUrl }
						alt={ __( 'Cover image', 'big-sky' ) }
						className="big-sky__assistant-ai-actions__color-picker__button is-small"
					/>
				) }
				<Icon icon={ chevronDown } />
			</Button>
		</>
	);
}
