/**
 * WordPress dependencies
 */
import { store as blockEditorStore } from '@wordpress/block-editor';
import { useDispatch, useSelect } from '@wordpress/data';
import { __ } from '@wordpress/i18n';
import { applyFormat, create, toHTMLString } from '@wordpress/rich-text';

/**
 * Internal dependencies
 */
import { store as aiStore } from '@store';
import log from '@utils/log-debug.js';

function useFormatContentToolFunctions() {
	const { getSelectedBlock, getSelectionStart, getSelectionEnd } = useSelect(
		( select ) => select( blockEditorStore )
	);
	const { updateBlockAttributes } = useDispatch( blockEditorStore );
	const { setBlockBeforeChanges } = useDispatch( aiStore );

	const validateFormatContentArgs = ( args ) => {
		const selectedBlock = getSelectedBlock();

		if (
			! selectedBlock.attributes?.content?.originalHTML ||
			selectedBlock.attributes?.content?.originalHTML !== args?.text
		) {
			throw new Error(
				__(
					'Please select a single block that contains the text you want to format.',
					'big-sky'
				)
			);
		}
		return true;
	};

	const formatContent = async ( { text, substring, formats } ) => {
		try {
			const selectedBlock = getSelectedBlock();

			let start = 0;
			let end = text?.length;

			if ( substring ) {
				const substringStart = text?.indexOf( substring );
				if ( substringStart !== -1 ) {
					start = substringStart;
					end = start + substring.length;
				}
			} else {
				const selectionStart = getSelectionStart()?.offset;
				const selectionEnd = getSelectionEnd()?.offset;

				if ( selectionStart !== selectionEnd ) {
					start = selectionStart ?? 0;
					end = selectionEnd ?? text?.length;
				}
			}

			setBlockBeforeChanges( selectedBlock );

			const richText = create( {
				html: selectedBlock.attributes.content || '',
			} );
			let newRichText = richText;

			formats.forEach( ( format ) => {
				newRichText = applyFormat(
					newRichText,
					{ type: `core/${ format }` },
					start,
					end
				);
			} );
			updateBlockAttributes( selectedBlock.clientId, {
				content: toHTMLString( { value: newRichText } ),
			} );

			return __( 'Content has been formatted successfully.', 'big-sky' );
		} catch ( error ) {
			log.error( 'Error formatting content:', error );
			return __( 'Failed to format the content.', 'big-sky' );
		}
	};

	return {
		formatContent,
		validateFormatContentArgs,
	};
}

export default useFormatContentToolFunctions;
