/**
 * External dependencies
 */
import { useState } from '@wordpress/element';

/**
 * Internal dependencies
 */
import './style.scss';
import ItemPicker from '@components/item-picker';

export default function LayoutPicker( {
	concepts: propConcepts,
	selectedConcept,
	onGenerateNewConcept,
} ) {
	const [ concepts, setConcepts ] = useState( propConcepts );

	const handleGenerateNewConcept = async () => {
		const updatedConcepts = await onGenerateNewConcept( concepts );
		setConcepts( updatedConcepts );
	};

	return (
		<div className="big-sky__dock__menu__layout-picker">
			<div className="components-grid">
				<ItemPicker
					key="site-layout-picker"
					items={ concepts }
					selectedItem={ selectedConcept }
					generateNewItem={ handleGenerateNewConcept }
				/>
			</div>
		</div>
	);
}
