/**
 * WordPress dependencies
 */
import { Button } from '@wordpress/components';
import {
	useCallback,
	useEffect,
	useLayoutEffect,
	useState,
} from '@wordpress/element';
import { chevronLeft, chevronRight } from '@wordpress/icons';

export default function useScrollButtons(
	ref,
	scrollableSelector,
	pixelsToScrollBy = 260
) {
	const [ canScrollLeft, setCanScrollLeft ] = useState( false );
	const [ canScrollRight, setCanScrollRight ] = useState( false );

	const ScrollButton = ( { direction, onClick, disabled, icon } ) => (
		<Button
			icon={ icon }
			className={ `big-sky__dock__menu__arrow-${ direction }` }
			onClick={ onClick }
			disabled={ disabled }
		/>
	);

	const checkScrollButtons = useCallback( () => {
		const element = ref.current;
		if ( ! element ) return;

		const grid = element.querySelector( scrollableSelector );
		if ( ! grid ) return;

		const { scrollLeft, scrollWidth, clientWidth } = grid;

		// Increase the buffer to account for potential rounding errors
		const buffer = 10;
		setCanScrollLeft( scrollLeft > buffer );
		setCanScrollRight( scrollLeft < scrollWidth - clientWidth - buffer );
	}, [ ref, scrollableSelector ] );

	useLayoutEffect( () => {
		const initialCheckTimeout = setTimeout( () => {
			checkScrollButtons();
		}, 200 );

		return () => clearTimeout( initialCheckTimeout );
	}, [ checkScrollButtons ] );

	useEffect( () => {
		const element = ref.current;
		const grid = element?.querySelector( scrollableSelector );
		if ( grid ) {
			checkScrollButtons();
			grid.addEventListener( 'scroll', checkScrollButtons );
			return () => {
				grid.removeEventListener( 'scroll', checkScrollButtons );
			};
		}
	}, [ ref, checkScrollButtons, scrollableSelector ] );

	const scrollElement = ( amount ) => {
		const element = ref.current;
		if ( ! element ) return;

		const grid = element.querySelector( scrollableSelector );
		if ( ! grid ) return;

		const newScrollLeft = grid.scrollLeft + amount;
		grid.scrollTo( {
			left: newScrollLeft,
			behavior: 'smooth',
		} );

		// Update scroll buttons after scrolling
		setTimeout( checkScrollButtons, 300 );
	};

	const leftScrollButton = (
		<ScrollButton
			direction="left"
			icon={ chevronLeft }
			onClick={ () => scrollElement( -pixelsToScrollBy ) }
			disabled={ ! canScrollLeft }
		/>
	);

	const rightScrollButton = (
		<ScrollButton
			direction="right"
			icon={ chevronRight }
			onClick={ () => scrollElement( pixelsToScrollBy ) }
			disabled={ ! canScrollRight }
		/>
	);

	return { leftScrollButton, rightScrollButton, checkScrollButtons };
}
