import { colord } from 'colord';
import { BIG_SKY_CUSTOM_COLOR_PREFIX } from './custom-color-variations/constants.js';

/**
 * Gets the lightest color from an array of colors
 * @param {Array} colors The array of colors to compare
 * @return {Object} The lightest color
 */
export const getLightestColor = ( colors ) => {
	return Object.values( colors ).reduce( ( lightest, current ) =>
		colord( current.color ).brightness() >
		colord( lightest.color ).brightness()
			? current
			: lightest
	);
};

/**
 * Gets the darkest color from an array of colors
 * @param {Array} colors The array of colors to compare
 * @return {Object} The darkest color
 */
export const getDarkestColor = ( colors ) => {
	return Object.values( colors ).reduce( ( darkest, current ) =>
		colord( current.color ).brightness() <
		colord( darkest.color ).brightness()
			? current
			: darkest
	);
};

/**
 * Gets the best contrasting color for a given color from the theme colors
 * @param {string} color       The color to contrast
 * @param {Array}  themeColors The available theme colors
 * @return {Object} The contrasting color
 */
export const getContrastingThemeColor = ( color, themeColors ) => {
	const isLightColor = colord( color ).isLight();
	let contrastingColor;
	if ( isLightColor ) {
		// get the darkest theme color
		contrastingColor = getDarkestColor( themeColors );
	} else {
		// get the lightest theme color
		contrastingColor = getLightestColor( themeColors );
	}

	return contrastingColor;
};

/**
 * Creates a hash from custom colors object for identification
 * @param {Object} colors Object containing color values
 * @return {string} Hash string representing the color combination
 */
export const createColorHash = ( colors ) => {
	if ( ! colors ) return '';

	// Sort the colors to ensure consistent hashing regardless of object key order
	const sortedColors = Object.values( colors ).sort();
	// Join colors and create a simple hash
	return sortedColors.join( '-' ).replace( /#/g, '' ).toUpperCase();
};

/*
theme.settings.color.palette.theme has a schema like this:
[
    {
        "color": "#333333",
        "name": "Custom Color 1",
        "slug": "theme-1"
    },
    {
        "color": "#FFFFFF",
        "name": "Custom Color 2",
        "slug": "theme-2"
    },
    {
        "color": "#FF5722",
        "name": "Custom Color 3",
        "slug": "theme-3"
    },
    {
        "color": "#F5F5F5",
        "name": "Custom Color 4",
        "slug": "theme-4"
    }
]
*/
export const addColorToTheme = ( { theme, color } ) => {
	if ( ! theme?.settings?.color ) theme.settings.color = {};
	if ( ! theme?.settings?.color?.palette ) theme.settings.color.palette = {};
	if ( ! theme?.settings?.color?.palette?.theme )
		theme.settings.color.palette.theme = [];

	// check if the color already exists in the theme
	const existingColor = theme.settings.color.palette.theme.find(
		( c ) => c.color === color
	);
	if ( existingColor ) {
		return existingColor;
	}

	// get the next available slug by checking the highest slug and adding 1
	const nextIndex =
		Math.max(
			...theme.settings.color.palette.theme.map( ( c ) =>
				parseInt( c.slug.split( '-' )[ 1 ] )
			)
		) + 1;
	const newColor = {
		color,
		name: `${ BIG_SKY_CUSTOM_COLOR_PREFIX } ${ nextIndex }`,
		slug: `theme-${ nextIndex }`,
	};
	theme.settings.color.palette.theme.push( newColor );

	return newColor;
};

/* eslint-disable jsdoc/require-param, jsdoc/check-param-names */
/**
 * Applies theme adjustments to ensure all sections are legible
 * @param {Object} styles                        The theme styles
 * @param {Array}  palette                       The palette of colors
 * @param {Array}  path                          The path of the theme object
 * @param {string} currentVariationBackgroundVar The current variation background variable
 * @param {string} currentVariationTextVar       The current variation text variable
 */
/* eslint-enable jsdoc/require-param, jsdoc/check-param-names */
const applyThemeAdjustments = ( {
	styles,
	palette,
	path = [],
	currentVariationBackgroundVar = '',
	currentVariationTextVar = '',
} ) => {
	if ( path.includes( 'elements' ) && path.includes( 'button' ) ) {
		// ensure the button background contrasts with the variation background
		if ( styles?.color?.background ) {
			const backgroundColor = palette.find( ( color ) =>
				styles.color.background.includes( color.slug )
			).color;
			const currentVariationBackgroundColor = palette.find( ( color ) =>
				currentVariationBackgroundVar.includes( color.slug )
			).color;
			if (
				colord( backgroundColor ).contrast(
					colord( currentVariationBackgroundColor )
				) < 4.5
			) {
				// try to pick a contrasting color from one of the other palette colors
				const contrastingColor = getContrastingThemeColor(
					currentVariationBackgroundColor,
					palette
				);
				styles.color.background = `var(--wp--preset--color--${ contrastingColor.slug })`;
				if ( styles?.[ ':hover' ]?.color?.background ) {
					// check if the contrasting color is light or dark and adjust the hover color accordingly
					const isLightContrastingColor = colord(
						contrastingColor.color
					).isLight();
					if ( isLightContrastingColor ) {
						styles[
							':hover'
						].color.background = `color-mix(in srgb, var(--wp--preset--color--${ contrastingColor.slug }) 85%, #000)`;
					} else {
						styles[
							':hover'
						].color.background = `color-mix(in srgb, var(--wp--preset--color--${ contrastingColor.slug }) 90%, transparent)`;
					}
				}
			}
		}

		// ensure the button text contrasts with the button background
		if ( styles?.color?.text ) {
			const backgroundColor = palette.find( ( color ) =>
				styles.color.background.includes( color.slug )
			).color;
			const textColor = palette.find( ( color ) =>
				styles.color.text.includes( color.slug )
			).color;
			if (
				colord( backgroundColor ).contrast( colord( textColor ) ) < 4.5
			) {
				// try to pick a contrasting color from one of the other palette colors
				const contrastingColor = getContrastingThemeColor(
					backgroundColor,
					palette
				);
				styles.color.text = `var(--wp--preset--color--${ contrastingColor.slug })`;
				if ( styles?.[ ':hover' ]?.color?.text ) {
					styles[
						':hover'
					].color.text = `var(--wp--preset--color--${ contrastingColor.slug })`;
				}
			}
		}

		return;
	}

	// ensure the variation background and text contrast with each other
	if ( styles?.color?.background && styles?.color?.text ) {
		currentVariationBackgroundVar = styles.color.background;
		currentVariationTextVar = styles.color.text;
		// get hex value of themeStyles.color.background and themeStyles.color.text from the palette
		const backgroundColor = palette.find( ( color ) =>
			styles.color.background.includes( color.slug )
		).color;
		const textColor = palette.find( ( color ) =>
			styles.color.text.includes( color.slug )
		).color;
		if ( colord( backgroundColor ).contrast( colord( textColor ) ) < 4.5 ) {
			// try to pick a contrasting color from one of the other palette colors
			const contrastingColor = getContrastingThemeColor(
				backgroundColor,
				palette
			);
			styles.color.text = `var(--wp--preset--color--${ contrastingColor.slug })`;
		}
	}

	// Recursively check all object properties
	if ( typeof styles === 'object' && styles !== null ) {
		for ( const [ key, value ] of Object.entries( styles ) ) {
			applyThemeAdjustments( {
				styles: value,
				palette,
				path: [ ...path, key ],
				currentVariationBackgroundVar,
				currentVariationTextVar,
			} );
		}
	}
};

/* eslint-disable jsdoc/require-param, jsdoc/check-param-names */
/**
 * Updates the theme palette and makes sure the variations are legible
 * @param {Object} theme  The theme
 * @param {Object} colors The colors in this format: { color1: '#000000', color2: '#000000', color3: '#000000', color4: '#000000', color5: '#000000' }
 */
/* eslint-enable jsdoc/require-param, jsdoc/check-param-names */
export const updateThemePalette = ( { theme, colors } ) => {
	theme.settings.color.palette.theme.forEach( ( color, index ) => {
		if ( colors[ `color${ index + 1 }` ] ) {
			color.color = colors[ `color${ index + 1 }` ];
			color.name = `${ BIG_SKY_CUSTOM_COLOR_PREFIX } ${ index + 1 }`;
		}
	} );

	// apply any theme adjustments here to ensure the palette and variations are legible
	applyThemeAdjustments( {
		styles: theme.styles,
		palette: theme?.settings?.color?.palette?.theme,
	} );
};

// Helper to ensure a color meets saturation and hue rules
const adjustToNeutralBase = ( color ) => {
	const { s, l } = colord( color ).toHsl();
	return colord( { h: 0, s: Math.min( s, 20 ), l } ).toHex();
};

// Helper to ensure contrast ratio between two colors
const adjustContrast = ( color1, targetContrast, referenceColor ) => {
	const refColor = colord( referenceColor );
	const isRefLight = refColor.isLight(); // Determine if reference color is light
	let attempts = 0;
	const maxAttempts = 50;
	let adjustedColor = color1;

	while (
		attempts < maxAttempts &&
		colord( adjustedColor ).contrast( refColor ) < targetContrast
	) {
		const { h, s, l } = colord( adjustedColor ).toHsl();
		const adjustment = isRefLight ? -5 : 5; // Darken if reference is light, lighten if dark
		const newL = Math.min( Math.max( l + adjustment, 0 ), 100 ); // Clamp lightness to [0, 100]
		adjustedColor = colord( { h, s, l: newL } ).toHex();
		attempts++;
	}

	return adjustedColor;
};

// Generate a tonal variation of a base color
const generateTonalVariation = ( baseColor ) => {
	const { h, s, l } = colord( baseColor ).toHsl();
	/* eslint-disable no-restricted-syntax */
	const newL = Math.max( 0, Math.min( 100, l + ( Math.random() * 10 - 5 ) ) ); // 5%
	const newS = Math.max( 0, Math.min( 100, s + ( Math.random() * 10 - 5 ) ) ); // 5%
	/* eslint-enable no-restricted-syntax */
	return colord( { h, s: newS, l: newL } ).toHex();
};

// Adjust tonal variation to meet rules
const adjustTonalVariation = ( color, baseColor ) => {
	const { h, s, l } = colord( baseColor ).toHsl();
	const { s: cS, l: cL } = colord( color ).toHsl();

	const adjustedS = Math.max( 0, Math.min( 100, s + ( cS - s ) ) ); // 5% saturation
	const adjustedL = Math.max( 0, Math.min( 100, l + ( cL - l ) ) ); // 5% lightness

	return colord( { h, s: adjustedS, l: adjustedL } ).toHex();
};

// Generate or adjust palette
export const generateHslPalette = ( colors ) => {
	let [ color1, color2, color3, color4 ] = colors;

	// Step 1: Adjust or generate color1
	if ( ! color1 ) {
		color1 = adjustToNeutralBase( '#000000' ); // Default to black as base
	} else {
		color1 = colord( color1 ).toHex();
	}

	// Step 2: Adjust or generate color2
	if ( ! color2 ) {
		color2 = adjustContrast( '#808080', 4.5, color1 ); // Start with gray
	} else {
		color2 = colord( color2 ).toHex();
	}

	// Step 3: Adjust or generate color3
	if ( ! color3 ) {
		color3 = generateTonalVariation( color1 ); // Generate a tonal variation
		if ( ! colord( color3 ).contrast( colord( color1 ) ) <= 2 ) {
			color3 = adjustTonalVariation( color3, color1 ); // Adjust tonal variation
		}
	} else {
		color3 = colord( color3 ).toHex();
	}

	// Step 4: Adjust or generate color4
	if ( ! color4 ) {
		color4 = adjustContrast( '#c0c0c0', 4.5, color1 ); // Start with light gray
	} else {
		color4 = colord( color4 ).toHex();
	}

	return [ color1, color2, color3, color4 ];
};
