/**
 * Internal dependencies
 */
import generateImageTool from './tools/generate-image/generate-image-tool.js';
import getStockImageTool from './tools/get-stock-image/get-stock-image-tool.js';
import generateSiteLogoTool from './tools/generate-site-logo/generate-site-logo-tool.js';
import uploadSiteLogoTool from './tools/upload-site-logo/upload-site-logo-tool.js';
import changeAssistantTool from '@ai/assistants/shared/change-assistant/change-assistant-tool.js';
import { ASSISTANTS } from '@ai/assistants/constants.js';
import { isForwardedByRouter } from '@ai/assistants/shared/prompts.js';

const imageToolkit = {
	name: 'image',
	tools: ( context ) => {
		const tools = [
			generateImageTool( {
				styles: context.images?.styles,
				defaultStyle: context.images?.defaultStyle,
			} ),
			getStockImageTool(),
			generateSiteLogoTool(),
			uploadSiteLogoTool(),
			...( ! isForwardedByRouter( context )
				? [ changeAssistantTool( ASSISTANTS.IMAGE ) ]
				: [] ),
		];

		return tools;
	},
};

export default imageToolkit;
