/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
import { __unstableMotion as motion } from '@wordpress/components';
import { useRef } from '@wordpress/element';

/**
 * Internal dependencies
 */
import { recordTracksEvent } from '@utils/tracks';
import './style.scss';

const ActionCarousel = ( { actions, animationVariants = {} } ) => {
	const sliderRef = useRef();
	let isDown = false;
	let startX;
	let scrollLeft;

	const isConfirmationActions = actions.some(
		( action ) =>
			typeof action?.type === 'string' &&
			action.type?.startsWith( 'confirmation' )
	);

	const handlePointerDown = ( e ) => {
		isDown = true;
		sliderRef.current.classList.add( 'active' );
		startX = e.pageX - sliderRef.current.offsetLeft;
		scrollLeft = sliderRef.current.scrollLeft;
	};

	const handlePointerLeave = () => {
		isDown = false;
		sliderRef.current.classList.remove( 'active' );
	};

	const handlePointerUp = () => {
		isDown = false;
		sliderRef.current.classList.remove( 'active' );
	};

	const handlePointerMove = ( e ) => {
		if ( ! isDown ) return;
		e.preventDefault();
		const x = e.pageX - sliderRef.current.offsetLeft;
		const walk = ( x - startX ) * 2;
		sliderRef.current.scrollLeft = scrollLeft - walk;
	};

	return (
		<div
			className={ classnames( 'big-sky__chat-actions', {
				'has-confirmation-actions': isConfirmationActions,
			} ) }
		>
			<div
				className={ classnames( 'big-sky__chat-actions__carousel', {
					draggable: actions.length > 1,
				} ) }
				ref={ sliderRef }
				role="region"
				tabIndex={ 0 }
				{ ...( actions.length > 1 && {
					// Conditionally apply event handlers
					onPointerDown: handlePointerDown,
					onPointerLeave: handlePointerLeave,
					onPointerUp: handlePointerUp,
					onPointerMove: handlePointerMove,
				} ) }
			>
				{ actions.map( ( action, index ) => (
					<motion.button
						initial={ animationVariants.initial || {} }
						animate={ animationVariants.animate || {} }
						exit={ animationVariants.exit || {} }
						transition={ {
							...( animationVariants.transition || {} ),
							delay:
								index *
								( animationVariants.transition?.delay || 0 ),
						} }
						key={ action.key }
						onClick={ () => {
							if ( action.action_type === 'suggested' ) {
								recordTracksEvent( 'chat_suggestion_click', {
									action_key: action.key,
								} );
							} else {
								recordTracksEvent( 'chat_action_click', {
									action_key: action.key,
								} );
							}
							action.onClick();
						} }
						className={ classnames(
							'big-sky__chat-actions__carousel__item',
							{
								'confirmation-action':
									action.type === 'confirmation',
								'confirmation-action-destructive':
									action.type === 'confirmationDestructive',
							}
						) }
						title={ action.description }
					>
						<h5>{ action.actionName }</h5>
						{ action.description && <p>{ action.description }</p> }
					</motion.button>
				) ) }
			</div>
		</div>
	);
};

export default ActionCarousel;
