import createShowFontPickerTool from './tools/show-font-picker.js';
import changeAssistantTool from '@ai/assistants/shared/change-assistant/change-assistant-tool';
import { ASSISTANTS } from '@ai/assistants/constants.js';
import { isForwardedByRouter } from '@ai/assistants/shared/prompts.js';

const fontToolkit = {
	name: 'font',
	tools: ( context ) => {
		const tools = [ createShowFontPickerTool( context?.keywords ) ];

		if (
			context.currentHandoffArea !== 'fonts' &&
			! isForwardedByRouter( context )
		) {
			tools.push( changeAssistantTool( ASSISTANTS.FONT ) );
		}

		return tools;
	},
};

export default fontToolkit;
