/**
 * WordPress dependencies
 */
import { useDispatch, useSelect } from '@wordpress/data';

/**
 * Internal dependencies
 */
import { store as aiStore } from '@store';
import { recordTracksEvent } from '@utils/tracks';
import useVariations from '@hooks/use-variations';
import log from '@utils/log-debug.js';
import getSystemPrompt from './system-prompt.js';
import createGetBuildDesignsTool from './tool.js';
import { dockViews } from '@utils/constants.js';
import createColorPaletteGeneratorTool from '../../assistants/color/tools/color-palette-generator.js';

export default function useGetBuildDesigns() {
	const { runChatCompletion } = useDispatch( aiStore );

	const { chatModel } = useSelect( ( select ) => ( {
		chatModel: select( aiStore ).getAdvancedChatModel(),
	} ) );

	const {
		setIsBuildingSite,
		resetCachedContent,
		setChosenColors,
		setChosenCustomColors,
		setChosenFonts,
		setDockView,
		setShowThread,
	} = useDispatch( aiStore );

	const { getColorVariations, getFontVariations } = useVariations();

	const getBuildDesigns = async ( siteMeta, isQuickOnboarding ) => {
		setIsBuildingSite( true );
		setDockView( dockViews.MENUS );
		setShowThread( false );

		recordTracksEvent( 'designing_build_started' );

		if ( process.env.NODE_ENV === 'development' ) {
			// eslint-disable-next-line no-undef
			const buildDesigns = localStorage.getItem( 'buildDesigns' );
			if ( buildDesigns ) {
				const { colors: chosenColors, fonts: chosenFonts } =
					JSON.parse( buildDesigns );
				setChosenColors( chosenColors );
				setChosenFonts( chosenFonts );

				return {
					...siteMeta,
					color: chosenColors[ 0 ],
					font: chosenFonts[ 0 ],
				};
			}
		}

		// reset cached content
		resetCachedContent();

		// generate color string from variations
		const colorVariations = getColorVariations();
		const colorVariationData = colorVariations.map( ( variation ) => {
			return {
				name: variation.title,
				keywords: variation[ 'x-big-sky-meta' ]?.keywords || [],
			};
		} );

		// generate font string from variations
		const fontVariations = getFontVariations();
		const fontVariationData = fontVariations.map( ( variation ) => {
			return {
				name: variation.name,
				keywords: variation[ 'x-big-sky-meta' ]?.keywords || [],
			};
		} );

		const messages = [
			{
				role: 'system',
				content: getSystemPrompt(
					siteMeta,
					JSON.stringify( colorVariationData ),
					JSON.stringify( fontVariationData )
				),
			},
		];

		const colorVariationNames = colorVariations.map(
			( variation ) => variation.title
		);
		const fontVariationNames = fontVariations.map(
			( variation ) => variation.title
		);

		const additionalProperties = {};
		if ( siteMeta.colorRequest && siteMeta.colorRequest.length > 0 ) {
			// If and only if user has requested custom colors during handoff, we inject the custom_colors property to the the build designs tool.
			additionalProperties.custom_colors =
				createColorPaletteGeneratorTool(
					[]
				).parameters.properties.custom_colors;
		}

		const buildSiteTools = [
			{
				type: 'function',
				function: createGetBuildDesignsTool(
					colorVariationNames,
					fontVariationNames,
					additionalProperties
				),
			},
		];

		const response = await runChatCompletion(
			chatModel,
			messages,
			buildSiteTools
		);

		const toolCalls = response?.choices?.[ 0 ]?.message?.tool_calls;
		if (
			! toolCalls ||
			toolCalls.length === 0 ||
			toolCalls[ 0 ].function.name !== 'getBuildDesigns'
		) {
			log.error( 'getBuildDesigns tool call missing in model response.' );
			return;
		}

		const args = toolCalls[ 0 ].function.arguments;

		try {
			const parsedArgs = JSON.parse( args );
			// Choose the first (best) color palette from parsedArgs.colors(an array).
			// Save the rest of the chosen colors in the ai store for later use.

			const {
				categories,
				colors: chosenColors,
				fonts: chosenFonts,
				custom_colors: chosenCustomColors,
			} = parsedArgs;
			setChosenColors( chosenColors );
			setChosenFonts( chosenFonts );
			if ( chosenCustomColors ) {
				setChosenCustomColors( chosenCustomColors );
			}

			if ( isQuickOnboarding ) {
				// eslint-disable-next-line no-undef
				localStorage.setItem(
					'buildDesigns',
					JSON.stringify( {
						categories,
						colors: chosenColors,
						fonts: chosenFonts,
						custom_colors: chosenCustomColors,
					} )
				);
			}

			return {
				...siteMeta,
				color: chosenColors[ 0 ],
				font: chosenFonts[ 0 ],
				categories,
			};
		} catch ( e ) {
			log.error(
				'getBuildDesigns JSON parsing error, some content may be missing'
			);
		}
	};

	return {
		getBuildDesigns,
	};
}
