import { __ } from '@wordpress/i18n';
import { dockViews } from '@utils/constants';
const defaultDimensions = {
	menus: { width: 'auto', height: 48 },
	chat: { width: 340, height: 400 },
	pending: { width: 'auto', height: 48 },
	completed: { width: 'auto', height: 48 },
	handoff: { width: 368, height: 'auto' },
};

const maximizedDimensions = {
	menus: { width: 'auto', height: 48 },
	chat: { width: 600, height: 600 },
	pending: { width: 'auto', height: 48 },
	completed: { width: 'auto', height: 48 },
	handoff: { width: 600, height: 'auto' },
};

export const getDockWidth = ( dockView, isMaximized ) => {
	if ( isMaximized ) {
		return maximizedDimensions[ dockView ]?.width || 'auto';
	}
	return defaultDimensions[ dockView ]?.width || 'auto';
};

export const getDockHeight = ( dockView, isMaximized ) => {
	if ( isMaximized ) {
		return maximizedDimensions[ dockView ]?.height || 'auto';
	}
	return defaultDimensions[ dockView ]?.height || 48;
};

const getContentReplacementMessage = ( selectedBlock ) => {
	if ( ! selectedBlock ) return __( 'Replacing content', 'big-sky' );

	switch ( selectedBlock.name ) {
		case 'core/paragraph':
			return __( 'Regenerating text', 'big-sky' );
		case 'core/image':
			return __( 'Regenerating image', 'big-sky' );
		default:
			return __( 'Replacing content', 'big-sky' );
	}
};

export const getPendingActionMessage = ( currentToolCall, selectedBlock ) => {
	if ( currentToolCall ) {
		switch ( currentToolCall ) {
			case 'modify_contact_form':
				return __( 'Generating contact form', 'big-sky' );
			case 'remove_block':
				if (
					selectedBlock &&
					( selectedBlock?.name === 'core/pattern' ||
						selectedBlock?.name === 'core/group' )
				) {
					return __( 'Removing pattern', 'big-sky' );
				}
				return __( 'Removing block', 'big-sky' );
			case 'add_pattern':
				return __( 'Adding pattern', 'big-sky' );
			case 'replace_pattern':
				return __( 'Replacing pattern', 'big-sky' );
			case 'generate_pattern':
				return __( 'Generating pattern', 'big-sky' );
			case 'generate_image':
				return __( 'Generating image', 'big-sky' );
			case 'get_stock_image':
				return __( 'Getting stock image', 'big-sky' );
			case 'replace_content':
				return getContentReplacementMessage( selectedBlock );
			case 'upload_site_logo':
				return __( 'Updating site logo', 'big-sky' );
			case 'generate_site_logo':
				return __( 'Generating site logo', 'big-sky' );
			default:
				return __( 'Working on your request', 'big-sky' );
		}
	}
};

export const getDefaultChatSettings = ( isOnboarded = false ) => {
	return isOnboarded
		? {
				title: __( 'How can I help you?', 'big-sky' ),
				prompt: __( "Type what you'd like to do", 'big-sky' ),
		  }
		: {
				title: __( 'What sort of website do you need?', 'big-sky' ),
				prompt: __( 'Describe your site', 'big-sky' ),
		  };
};

export function getNewDockView( {
	isOnboarded,
	isHandoff,
	dockView,
	showThread,
	isToolContainerVisible,
	pendingToolCallbacks,
	selectedBlockHasChanged,
	currentToolCall,
} ) {
	if ( ! isOnboarded ) {
		return dockViews.CHAT;
	}

	if ( isHandoff ) {
		return dockViews.HANDOFF;
	}

	if ( pendingToolCallbacks > 0 ) {
		return dockViews.PENDING;
	}

	if ( dockView === dockViews.PENDING && ! pendingToolCallbacks ) {
		return dockViews.COMPLETED;
	}

	// If the selected block changes you usually want to show a new menu for that block, unless we just removed a block.
	// In that case, we still want to leave the view at COMPLETED so the user can undo the removal.
	if (
		( dockView === dockViews.CHAT || dockView === dockViews.COMPLETED ) &&
		! showThread &&
		selectedBlockHasChanged &&
		! isToolContainerVisible &&
		currentToolCall !== 'remove_block' &&
		currentToolCall !== 'modify_contact_form'
	) {
		return dockViews.MENUS;
	}

	return dockView;
}

// The message thread shows the latest agent and user messages. When it is hidden the quick actions display instead.
export function shouldHideMessageThread( {
	showThread,
	newView,
	useBlockContext,
	selectedBlockHasChanged,
} ) {
	if ( ! showThread ) {
		return false;
	}

	if ( newView === dockViews.PENDING || newView === dockViews.COMPLETED ) {
		return true;
	}

	if (
		newView === dockViews.CHAT &&
		useBlockContext &&
		selectedBlockHasChanged
	) {
		return true;
	}
	return false;
}

/**
 * Determines if a block must be undone using Gutenberg's undo functionality.
 *
 * Some blocks, like the Site Logo block, don't have attribute changes that can be
 * directly undone. For these blocks, we need to use Gutenberg's built-in undo
 * functionality instead of relying on updateBlockAttributes().
 *
 * @param {Object} selectedBlock - The currently selected block object.
 * @return {boolean} Returns true if the block needs to be undone using Gutenberg,
 *                    false otherwise.
 */

export const mustUndoBlockWithGutenberg = ( currentToolCall ) => {
	return [ 'generate_site_logo', 'remove_block' ].includes( currentToolCall );
};
