/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { Button } from '@wordpress/components';
import { useDispatch, useSelect } from '@wordpress/data';
import { store as editorStore } from '@wordpress/editor';

function NavigationItemDockMenus( { selectedBlock } ) {
	const { currentPostId } = useSelect( ( select ) => ( {
		currentPostId: select( editorStore ).getCurrentPostId(),
	} ) );
	const { savePost } = useDispatch( editorStore );

	// Don't show the menu if the menu item is the current page.
	if ( selectedBlock?.attributes?.id === currentPostId ) {
		return null;
	}

	const assistantMenuTitle = __( 'Edit page', 'big-sky' );
	const navigateToPage = async () => {
		await savePost();
		window.location.href = `/wp-admin/site-editor.php?postId=${ selectedBlock?.attributes?.id }&postType=${ selectedBlock?.attributes?.type }&canvas=edit`;
	};

	return (
		<>
			<Button icon="edit" onClick={ navigateToPage }>
				{ assistantMenuTitle }
			</Button>
		</>
	);
}

export default NavigationItemDockMenus;
