export default function generateImageTool( {
	styles = [],
	defaultStyle = 'photographic',
} ) {
	return {
		name: 'generate_image',
		description: `Generate a new image using AI.`,
		parameters: {
			type: 'object',
			properties: {
				clientId: {
					type: 'string',
					description: 'The clientId of the current selected block.',
				},
				description: {
					type: 'string',
					description:
						'The description of the new image. Do not include any text about replacing the image or something similar, only what the content of the image should be. For example, if the user request is "replace this image with a dog", the description should be "dog".',
				},
				style: {
					type: 'string',
					description: `The style of an AI image. If the user does not specify a style for the AI image, default to ${ defaultStyle }`,
					enum: styles,
				},
			},
			required: [ 'clientId', 'description', 'style' ],
		},
		strict: true,
	};
}
