/**
 * WordPress dependencies
 */
import { useEffect } from '@wordpress/element';
import { store as commandsStore } from '@wordpress/commands';
import { useDispatch, useSelect } from '@wordpress/data';

/**
 * Internal dependencies
 */
import { store as aiStore } from '@store';
import log from '@utils/log-debug.js';

/**
 * This file is FUNKY!
 * So we are basically doing a lot React magic to retrieve command pallette commands from the command pallette API.
 * The complexity stems from the fact that command pallette loaders contain custom React hooks that we have to run.
 * We have iterate over arbitrary hooks and then render them without breaking the rules of hooks.
 */

// This is copied from original command pallette work https://github.com/WordPress/gutenberg/blob/8f8c4d7b44427c49d0e231b3a895da2a3c448ec9/packages/commands/src/components/command-menu.js#L79
function CommandMenuLoaderWrapper( { name, hook, search } ) {
	const { commands } = useSelect( ( select ) => ( {
		commands: select( aiStore ).getCommands(),
	} ) );

	const { addCommand } = useDispatch( aiStore );
	const hookResult = hook( { search } ) ?? {};
	useEffect( () => {
		if (
			! commands[ name ] ||
			commands[ name ].length !== hookResult.commands.length
		) {
			addCommand( { [ name ]: hookResult.commands } );
		}
	}, [ addCommand, commands, hook, hookResult.commands, name ] );

	if ( ! name || ! hook ) {
		log.warn( 'invalid loader', { name, hook } );
		return null;
	}
	return null;
}

export default function RetrieveCommands() {
	const { commandLoadersNonContextual, commandLoadersContextual } = useSelect(
		( select ) => {
			return {
				commandLoadersNonContextual:
					select( commandsStore ).getCommandLoaders( false ),
				commandLoadersContextual:
					select( commandsStore ).getCommandLoaders( true ),
			};
		}
	);

	// Filter the non-ai specific commnad palette actions. These are not designed to work with AI, so we limit this to
	// only generic command pallette actions that make sense in the AI context.
	const nonAIActions = [
		'core/block-editor/blockQuickActions',
		'core/block-editor/blockTransforms',
	];

	return (
		<>
			{ commandLoadersContextual
				.concat( commandLoadersNonContextual )
				.filter(
					( loader ) =>
						loader?.name &&
						( nonAIActions.includes( loader.name ) ||
							loader.name.startsWith( 'ai/' ) )
				)
				.map( ( loader ) => {
					// We need to retrieve both contextual and NON-contextual loaders because they are always meant to show all.
					if ( ! loader || ! loader.name ) {
						log.warn( 'invalid loader', loader );
						return null;
					}
					return (
						<CommandMenuLoaderWrapper
							key={ loader.name }
							name={ loader.name }
							hook={ loader.hook }
							search=""
						/>
					);
				} ) }
		</>
	);
}
