/**
 * WordPress dependencies
 */
import apiFetch from '@wordpress/api-fetch';

/**
 * Internal dependencies
 */
import log from '@utils/log-debug.js';

export async function getDummyPosts( tagId ) {
	try {
		return await apiFetch( {
			path: `/wp/v2/posts?tags=${ tagId }&status=private`,
			method: 'GET',
		} );
	} catch ( error ) {
		log.error( 'Error fetching dummy posts: ', error );
	}
}

export async function createDummyPosts( posts ) {
	if ( ! posts ) {
		return;
	}

	const data = {
		posts,
	};

	try {
		return await apiFetch( {
			path: '/wpcom/v2/big-sky/v1/dummy-posts',
			method: 'POST',
			data,
		} );
	} catch ( error ) {
		log.error( 'Error creating dummy posts: ', error );
		return false;
	}
}

export async function deleteDummyPosts() {
	try {
		return await apiFetch( {
			path: '/wpcom/v2/big-sky/v1/dummy-posts',
			method: 'DELETE',
		} );
	} catch ( error ) {
		log.error( 'Error deleting dummy posts: ', error );
		return false;
	}
}

export async function getDummyPostTag() {
	try {
		return await apiFetch( {
			path: '/wp/v2/tags?search=dummy-post',
			method: 'GET',
		} );
	} catch ( error ) {
		log.error( 'Error fetching dummy post tag: ', error );
	}
}

export async function createDummyPostTag() {
	try {
		return await apiFetch( {
			path: '/wp/v2/tags?name=dummy-post',
			method: 'POST',
		} );
	} catch ( error ) {
		log.error( 'Error creating dummy post tag: ', error );
	}
}
