/**
 * Internal dependencies
 */
import { getBaseTemplate } from '@data/site-structure-templates.js';

const createGetSiteStructureTool = ( siteType, siteGoals ) => {
	// Get the business template as default to determine available sections and pages
	const baseTemplate = getBaseTemplate( siteType, siteGoals );

	// Build the parameters object
	const parameters = {
		type: 'object',
		properties: {
			sections: {
				type: 'array',
				description:
					'Array of sections to include in the homepage, with reasons for their inclusion',
				items: {
					type: 'object',
					properties: {
						slug: {
							type: 'string',
							description: 'The slug of the section',
						},
						reason: {
							type: 'string',
							description:
								'The reason for including this section based on the site specifications',
						},
					},
					required: [ 'slug', 'reason' ],
					additionalProperties: false,
				},
				minItems: baseTemplate.homepageSections.required.length,
			},
			pages: {
				type: 'array',
				description:
					'Array of pages to include in the site, with customized names',
				items: {
					type: 'object',
					properties: {
						slug: {
							type: 'string',
							description: 'The slug of the page',
						},
						name: {
							type: 'string',
							description: 'The customized name for the page',
						},
						description: {
							type: 'string',
							description:
								'briefly describe the sort of content that will be on this page, no more than 30 words',
						},
					},
					required: [ 'slug', 'name' ],
					additionalProperties: false,
				},
				minItems: baseTemplate.subPages.required.length,
			},
		},
		required: [ 'sections', 'pages' ],
		additionalProperties: false,
	};

	return {
		name: 'getSiteStructure',
		description:
			'Determines which sections to include on the homepage (must include all required sections) and which pages to include with their customized names',
		parameters,
	};
};

export default createGetSiteStructureTool;
