/**
 * WordPress dependencies
 */
import apiFetch from '@wordpress/api-fetch';

/**
 * Internal dependencies
 */
import log from '@utils/log-debug.js';
import { getIsDevMode } from '@utils/tracks.js';

export default async function logAction(
	session_id,
	content,
	message_role = '',
	message_id = '',
	parent_message_id = '',
	attempt = 0
) {
	if ( ! session_id ) {
		return;
	}

	const data = {
		content,
		message_type: message_role,
		message_id,
		parent_message_id,
		is_test: getIsDevMode(),
	};

	try {
		return await apiFetch( {
			path:
				'/wpcom/v2/big-sky/v1/session/' +
				encodeURIComponent( session_id ) +
				'/action',
			method: 'POST',
			data,
		} );
	} catch ( error ) {
		log.errorBreakLoop( attempt, 'Error logging action: ', error );
	}
}
