/**
 * Internal dependencies
 */
import uuidv4 from '@utils/uuid';
import patternCategories from '@data/pattern-categories.js';
import { customSectionNames } from '@ai/tasks/replace-content/section-types';
import { defaultSiteMetadata, dockViews } from '@utils/constants';

const OPENAI_PRICING = {
	'gpt-3.5-turbo-0125': {
		inputCost: 0.0005, // per 1000/tok
		outputCost: 0.0015, // per 1000/tok
	},
	'gpt-4o': {
		inputCost: 0.005, // per 1000/tok
		outputCost: 0.015, // per 1000/tok
	},
	'gpt-4-turbo': {
		inputCost: 0.01, // per 1000/tok
		outputCost: 0.03, // per 1000/tok
	},
	'gpt-4o-mini': {
		inputCost: 0.00015, // per 1000/tok
		outputCost: 0.0006, // per 1000/tok
	},
	'dall-e-3': {
		imageCost: 0.04, // standard quality, 1024x1024
	},
};

const ANTHROPIC_PRICING = {
	'claude-3-5-sonnet-20241022': {
		inputCost: 0.003, // $3.00 per million tokens
		outputCost: 0.015, // $15.00 per million tokens
	},
};

const getPatternCategories = () => {
	if ( ! patternCategories ) {
		return [];
	}

	const categories = {};
	patternCategories.forEach( ( category ) => {
		if ( ! categories[ category.slug ] ) {
			categories[ category.slug ] = {
				description: category.description,
				title: category.title,
			};
		}
	} );

	return categories;
};

export const initialState = {
	bigSkyInitialState: window?.bigSkyInitialState, // Initial state passed from localized_script

	// chat completion
	apiKey: '',
	currentUserMessage: '',
	pendingToolCallbacks: 0,
	currentToolCall: '',
	hasRetriedChatRequest: false,
	assistant: '',

	// chat model
	advancedChatModel: 'gpt-4o',
	basicChatModel: 'gpt-4o-mini',
	imageGenModel: 'dall-e-3',
	openAIUsage: {},
	anthropicUsage: {},

	// command palette
	commands: {},

	// content
	stock_photos: {},
	selectedSectionContent: {},
	cachedContent: {},
	patternCategories: getPatternCategories(),
	remotePatterns: [],
	generatedPatterns: [],
	currentPageMetadata: {},
	newPageId: false, // the new page id we are creating, otherwise 0.

	// delay settings
	delaySettings: {
		onboarding: {
			afterBlockProcessed: 1000,
			afterScrollToBlock: 1500,
		},
	},

	// dock state
	isAssistantButtonVisible: false, // used to override the visibility of the assistant button in handoff dock view
	isToolContainerVisible: false, // used to determine if the tool container (sub menu) is visible in the dock
	toolComponent: null,
	showThread: false,
	chatComponents: [],
	dockView: dockViews.MENUS,
	onSelectedBlockAction: {},
	inputtedFiles: [],
	onInputtedFilesAction: {},
	showChatContinueButton: false,
	chatContinueButtonOnClick: () => {},
	showEditorWelcome: false,

	// editor state
	isEditorReady: false, // used to determine if the editor is loaded and contains blocks
	isEditorIframeLoaded: false, // used to determine if the editor iframe DOM elements are loaded
	isTemplatePartContentReady: false, // used to determine if the template part content is loaded
	lastSelectedBlock: null,
	lastCancelableAction: {},
	blockBeforeChanges: null, // used to cleanly undo a change to a block

	// handoff
	handoffStep: 0,

	// session logging
	logSessions: true,
	sessionId: uuidv4(),

	// site design
	// TODO: update the defaults
	chosenCategories: [],
	chosenColors: [],
	chosenCustomColors: null,
	generatedVariations: [],
	chosenFonts: [],
	isBuildingSite: false,
	currentHandoffArea: 'welcome',
	userStyleWithSettings: {},
	currentColor: '',
	currentFont: '',
	imageStyle: '',
	isTyping: false,
	siteMetadata:
		window?.bigSkyInitialState?.siteMetadata ?? defaultSiteMetadata,
	isMaximized: false,
	initializedCustomScheme: false,
	hideInput: false,
	customVariationTemplates: [],
	currentPaletteColors: [],

	// Generated Pattern State
	generatedPatternChunksAsJSON: [],
	generatedPatternAsBlocks: [],
	generatedPatternAsString: '',
	generatedPatternDummySectionClientId: null,
	generatedRootPatternClientId: null,
};

export function getOpenAICost( model, prompt, completion ) {
	if ( OPENAI_PRICING[ model ] ) {
		const { inputCost, outputCost } = OPENAI_PRICING[ model ];
		const inputTokensCost = ( prompt / 1000 ) * inputCost;
		const outputTokensCost = ( completion / 1000 ) * outputCost;
		return inputTokensCost + outputTokensCost;
	}
	return ( ( prompt + completion ) / 1000 ) * 0.03;
}

export function getAnthropicCost( model, input, output ) {
	if ( ANTHROPIC_PRICING[ model ] ) {
		const { inputCost, outputCost } = ANTHROPIC_PRICING[ model ];
		const inputTokensCost = ( input / 1000 ) * inputCost;
		const outputTokensCost = ( output / 1000 ) * outputCost;
		return inputTokensCost + outputTokensCost;
	}
	return 0;
}

export function reducer( state = initialState, action ) {
	const cachedContentKey = `bigSkyCachedContent${ window.Jetpack_Editor_Initial_State?.wpcomBlogId }`;
	switch ( action.type ) {
		case 'SET_LAST_SELECTED_BLOCK':
			return {
				...state,
				lastSelectedBlock: action.payload,
			};
		case 'SET_IS_ASSISTANT_BUTTON_VISIBLE':
			return {
				...state,
				isAssistantButtonVisible: action.payload,
			};
		case 'SET_IS_TOOL_CONTAINER_VISIBLE':
			return {
				...state,
				isToolContainerVisible: action.payload,
			};
		case 'SET_TOOL_COMPONENT':
			return {
				...state,
				toolComponent: action.payload,
			};
		case 'SET_CURRENT_USER_MESSAGE':
			return {
				...state,
				currentUserMessage: action.payload,
			};
		case 'LOG_OPENAI_USAGE':
			const { model, usage = {} } = action.payload;
			const newUsage = {
				completionTokens: 0,
				promptTokens: 0,
				totalTokens: 0,
				totalRequests: 0,
				estimatedCost: 0,
				...state.openAIUsage[ model ],
			};

			newUsage.completionTokens += usage.completion_tokens;
			newUsage.promptTokens += usage.prompt_tokens;
			newUsage.totalTokens += usage.total_tokens;
			newUsage.totalRequests += 1;

			const estimatedCost = getOpenAICost(
				model,
				usage.prompt_tokens,
				usage.completion_tokens
			);

			newUsage.estimatedCost += estimatedCost;

			return {
				...state,
				openAIUsage: {
					...state.openAIUsage,
					[ model ]: newUsage,
				},
			};
		case 'LOG_ANTHROPIC_USAGE':
			const { model: anthropicModel, usage: anthropicUsage = {} } =
				action.payload;
			const newAnthropicUsage = {
				inputTokens: 0,
				outputTokens: 0,
				totalTokens: 0,
				totalRequests: 0,
				estimatedCost: 0,
				...state.anthropicUsage[ anthropicModel ],
			};

			newAnthropicUsage.inputTokens += anthropicUsage.input_tokens || 0;
			newAnthropicUsage.outputTokens += anthropicUsage.output_tokens || 0;
			newAnthropicUsage.totalTokens =
				newAnthropicUsage.inputTokens + newAnthropicUsage.outputTokens;
			newAnthropicUsage.totalRequests += 1;

			const estimatedAnthropicCost = getAnthropicCost(
				anthropicModel,
				anthropicUsage.input_tokens || 0,
				anthropicUsage.output_tokens || 0
			);

			newAnthropicUsage.estimatedCost += estimatedAnthropicCost;

			return {
				...state,
				anthropicUsage: {
					...state.anthropicUsage,
					[ anthropicModel ]: newAnthropicUsage,
				},
			};
		case 'SET_LOG_SESSIONS':
			return {
				...state,
				logSessions: action.payload,
			};
		case 'SET_SESSION_ID':
			return {
				...state,
				sessionId: action.payload,
			};
		case 'SET_SESSION_LOGGED':
			return {
				...state,
				sessionLogged: action.payload,
			};
		case 'SET_STOCK_PHOTOS':
			return {
				...state,
				stock_photos: {
					...state.stock_photos,
					[ action.topic ]: action.payload,
				},
			};
		case 'SET_PATTERN_SHUFFLING_CONTENT':
			return {
				...state,
				selectedSectionContent: {
					...state.selectedSectionContent,
					...action.payload,
				},
			};
		case 'CLEAR_PATTERN_SHUFFLING_CONTENT':
			return {
				...state,
				selectedSectionContent: {},
			};
		case 'SET_IS_EDITOR_READY':
			return {
				...state,
				isEditorReady: action.payload,
			};
		case 'SET_IS_EDITOR_IFRAME_LOADED':
			return {
				...state,
				isEditorIframeLoaded: action.payload,
			};
		case 'ADD_CACHED_CONTENT':
			const blockTypes = {
				p: 'paragraph',
				h2: 'heading',
				b: 'button',
			};
			const blockType = blockTypes[ action.payload.type ];
			const category = action.payload.category;
			const append = !! action.payload.append;
			let cachedContent;
			if ( customSectionNames.includes( category ) ) {
				const content = Array.isArray( action.payload.content )
					? action.payload.content
					: [ action.payload.content ];
				cachedContent = {
					...state.cachedContent,
				};

				if ( ! cachedContent[ category ] || action.payload.clear ) {
					cachedContent[ category ] = [];
				}

				const nameKey = category === 'pricing' ? 'title' : 'name';
				content.forEach( ( item ) => {
					const existingItemIndex = cachedContent[
						category
					].findIndex(
						( cachedItem ) =>
							cachedItem[ nameKey ] === item[ nameKey ]
					);

					if ( existingItemIndex === -1 ) {
						cachedContent[ category ].push( item );
					} else if ( append ) {
						if ( category === 'menu' || category === 'pricing' ) {
							const subKey =
								category === 'menu' ? 'items' : 'tiers';
							cachedContent[ category ][ existingItemIndex ][
								subKey
							].push( ...item[ subKey ] );
						} else {
							cachedContent[ category ].push( item );
						}
					} else {
						cachedContent[ category ][ existingItemIndex ] = item;
					}
				} );
			} else {
				const cachekey = `${ category }-${ blockType }`;
				cachedContent = {
					...state.cachedContent,
					[ cachekey ]: [
						...( state.cachedContent[ cachekey ]
							? state.cachedContent[ cachekey ]
							: [] ),
						action.payload.content,
					],
				};
			}
			// eslint-disable-next-line no-undef
			localStorage.setItem(
				cachedContentKey,
				JSON.stringify( { ...cachedContent, lastUpdated: Date.now() } )
			);
			return {
				...state,
				cachedContent,
			};
		case 'SET_CACHED_CONTENT':
			// eslint-disable-next-line no-undef
			localStorage.setItem(
				cachedContentKey,
				JSON.stringify( { ...action.payload, lastUpdated: Date.now() } )
			);
			return {
				...state,
				cachedContent: action.payload,
			};
		case 'ADD_CACHED_IMAGE':
			return {
				...state,
				cachedContent: {
					...state.cachedContent,
					images: [
						...( state.cachedContent.images
							? state.cachedContent.images
							: [] ),
						action.payload,
					],
				},
			};
		case 'ADD_COMMAND':
			return {
				...state,
				commands: { ...state.commands, ...action.payload },
			};
		case 'SET_REMOTE_PATTERNS':
			const [ remotePatterns, locale ] = action.payload;
			return {
				...state,
				remotePatterns: {
					...state.remotePatterns,
					[ locale ]: remotePatterns,
				},
			};
		case 'SET_GENERATED_PATTERNS':
			return {
				...state,
				generatedPatterns: action.payload,
			};
		case 'SET_IS_EDITING_PAGE_LAYOUT':
			return {
				...state,
				isEditingPageLayout: action.payload,
			};
		case 'SET_CURRENT_PAGE_METADATA':
			return {
				...state,
				currentPageMetadata: action.payload,
			};
		case 'SET_IS_BUILDING_SITE':
			return {
				...state,
				isBuildingSite: action.payload,
			};
		case 'SET_IS_HANDOFF':
			return {
				...state,
				isHandoff: action.payload,
			};
		case 'SET_CURRENT_HANDOFF_AREA':
			return {
				...state,
				currentHandoffArea: action.payload,
			};
		case 'SET_CHAT_COMPONENTS':
			return {
				...state,
				chatComponents: action.payload,
			};
		case 'SET_SHOW_THREAD':
			return {
				...state,
				showThread: action.payload,
			};
		case 'SET_CURRENT_TOOL_CALL':
			return {
				...state,
				currentToolCall: action.payload,
			};
		case 'INCREMENT_PENDING_TOOL_CALLBACKS':
			return {
				...state,
				pendingToolCallbacks: state.pendingToolCallbacks + 1,
			};
		case 'DECREMENT_PENDING_TOOL_CALLBACKS':
			return {
				...state,
				pendingToolCallbacks: state.pendingToolCallbacks - 1,
			};
		case 'SET_DOCK_VIEW':
			return {
				...state,
				dockView: action.payload,
			};
		case 'SET_LAST_CANCELABLE_ACTION':
			if ( action.payload === null ) {
				return {
					...state,
					lastCancelableAction: {},
				};
			}

			return {
				...state,
				lastCancelableAction: {
					...state.lastCancelableAction,
					...action.payload,
				},
			};
		case 'SET_CHOSEN_CATEGORIES':
			return {
				...state,
				chosenCategories: action.payload,
			};
		case 'SET_CHOSEN_COLORS':
			return {
				...state,
				chosenColors: action.payload,
			};
		case 'SET_CHOSEN_CUSTOM_COLORS':
			return {
				...state,
				chosenCustomColors: action.payload,
			};
		case 'SET_INITIALIZED_CUSTOM_SCHEME':
			return {
				...state,
				initializedCustomScheme: action.payload,
			};
		case 'PUSH_GENERATED_VARIATIONS':
			return {
				...state,
				generatedVariations: [
					...action.payload,
					...state.generatedVariations,
				],
			};
		case 'SET_CHOSEN_FONTS':
			return {
				...state,
				chosenFonts: action.payload,
			};
		case 'SET_IMAGE_STYLE':
			return {
				...state,
				imageStyle: action.payload,
			};
		case 'SET_BLOCK_BEFORE_CHANGES':
			return {
				...state,
				blockBeforeChanges: action.payload,
			};
		case 'SET_ON_SELECTED_BLOCK_ACTION':
			return {
				...state,
				onSelectedBlockAction: action.payload,
			};
		case 'SET_IS_TEMPLATE_PART_CONTENT_READY':
			return {
				...state,
				isTemplatePartContentReady: action.payload,
			};
		case 'SET_USER_STYLE_WITH_SETTINGS':
			return {
				...state,
				userStyleWithSettings: action.payload,
			};
		case 'SET_INPUTTED_FILES':
			const current = state.inputtedFiles ?? [];
			const next = action.payload ?? [];

			for ( const currentFile of current ) {
				if (
					next.some(
						( nextFile ) => nextFile.url === currentFile.url
					)
				) {
					continue;
				}

				URL.revokeObjectURL( currentFile.url );
			}
			return {
				...state,
				inputtedFiles: next,
			};
		case 'SET_ON_INPUTTED_FILES_ACTION':
			return {
				...state,
				onInputtedFilesAction: action.payload,
			};
		case 'SET_SHOW_CHAT_CONTINUE_BUTTON':
			return {
				...state,
				showChatContinueButton: action.payload,
			};
		case 'SET_CHAT_CONTINUE_BUTTON_ON_CLICK':
			return {
				...state,
				chatContinueButtonOnClick: action.payload,
			};
		case 'SET_CURRENT_COLOR':
			return {
				...state,
				currentColor: action.payload,
			};
		case 'SET_CURRENT_FONT':
			return {
				...state,
				currentFont: action.payload,
			};
		case 'SET_IS_TYPING':
			return {
				...state,
				isTyping: action.payload,
			};
		case 'SET_API_KEY':
			return {
				...state,
				apiKey: action.payload,
			};
		case 'SET_HAS_RETRIED_CHAT_REQUEST':
			return {
				...state,
				hasRetriedChatRequest: action.payload,
			};
		case 'SET_SITE_METADATA':
			return {
				...state,
				siteMetadata: {
					...state.siteMetadata,
					...action.payload,
				},
			};
		case 'SET_HANDOFF_STEP':
			return {
				...state,
				handoffStep: action.payload,
			};
		case 'RESET_STORE':
			// this gets called in the error boundary
			return {
				...initialState,
				// we need to keep some values from the current state
				apiKey: state.apiKey,
				stock_photos: state.stock_photos,
				cachedContent: state.cachedContent,
				patternCategories: state.patternCategories,
				remotePatterns: state.remotePatterns,
				currentPageMetadata: state.currentPageMetadata,
				siteMetadata: state.siteMetadata,
			};
		case 'SET_IS_MAXIMIZED':
			return {
				...state,
				isMaximized: action.payload,
			};
		case 'SET_HIDE_INPUT':
			return {
				...state,
				hideInput: action.payload,
			};
		case 'SET_SHOW_EDITOR_WELCOME':
			return {
				...state,
				showEditorWelcome: action.payload,
			};
		case 'SET_ASSISTANT':
			return {
				...state,
				assistant: action.payload,
			};
		case 'SET_CUSTOM_VARIATION_TEMPLATES':
			return {
				...state,
				customVariationTemplates: action.payload,
			};
		case 'SET_GENERATED_PATTERN_CHUNKS_AS_JSON':
			if (
				Array.isArray( action.payload ) &&
				action.payload.length === 0
			) {
				return {
					...state,
					generatedPatternChunksAsJSON: [],
				};
			}
			return {
				...state,
				generatedPatternChunksAsJSON: [
					...state.generatedPatternChunksAsJSON,
					...( Array.isArray( action.payload )
						? action.payload
						: [ action.payload ] ),
				],
			};
		case 'SET_GENERATED_PATTERN_AS_BLOCKS':
			return {
				...state,
				generatedPatternAsBlocks: action.payload,
			};
		case 'SET_GENERATED_PATTERN_AS_STRING':
			return {
				...state,
				generatedPatternAsString: action.payload,
			};
		case 'SET_GENERATED_PATTERN_DUMMY_SECTION_CLIENT_ID':
			return {
				...state,
				generatedPatternDummySectionClientId: action.payload,
			};
		case 'SET_GENERATED_ROOT_PATTERN_CLIENT_ID':
			return {
				...state,
				generatedRootPatternClientId: action.payload,
			};
		case 'SET_CURRENT_PALETTE_COLORS':
			return {
				...state,
				currentPaletteColors: action.payload,
			};
		case 'SET_NEW_PAGE_ID':
			return {
				...state,
				newPageId: action.payload,
			};
		default:
			return state;
	}
}
