/**
 * External dependencies
 */
import { systemPromptTemplate } from '@ai/system-prompt-template.js';
import { getBaseTemplate } from '@data/site-structure-templates.js';

const getModelPageData = ( siteTemplate ) => {
	const { required = [], optional = [] } = siteTemplate.subPages;

	return {
		required: required.map( ( page ) => ( {
			slug: page.slug,
			name: page.pageName,
		} ) ),
		optional: optional.map( ( page ) => ( {
			slug: page.slug,
			name: page.pageName,
		} ) ),
	};
};

export default function getSiteStructureSystemPrompt( siteMeta, siteGoals ) {
	const { siteType, topic, siteDescription, siteTitle, siteLocation } =
		siteMeta;

	const baseTemplate = getBaseTemplate( siteType, siteGoals );
	const requiredSections = baseTemplate.homepageSections.required.map(
		( s ) => ( {
			slug: s.slug,
			title: s.title,
			description: s.description,
		} )
	);
	const optionalSections = baseTemplate.homepageSections.optional.map(
		( s ) => ( {
			slug: s.slug,
			title: s.title,
			description: s.description,
		} )
	);
	const pageData = getModelPageData( baseTemplate );

	return systemPromptTemplate.format( {
		role: 'You are a website architecture expert who specializes in determining which website sections and pages would benefit specific site types and crafting compelling reasons for section inclusion based on the site context.',

		context: `Current site specifications:
${ JSON.stringify( {
	siteType,
	topic,
	siteDescription,
	siteTitle,
	siteLocation,
} ) }

Required homepage sections (must be included in your response):
${ JSON.stringify( requiredSections, null, 2 ) }

Available optional sections (include only those that would benefit this site):
${ JSON.stringify( optionalSections, null, 2 ) }

Pages information:
${ JSON.stringify( pageData, null, 2 ) }`,

		do: [
			'Return an array of sections that includes ALL required sections plus any beneficial optional sections.',
			'For each section, provide a specific reason for including it that relates to the site specifications.',
			'Think about how sections work together to create a cohesive user experience.',
			'Consider industry standards and user expectations.',
			'Return an array of pages that includes ALL required pages with potentially customized names, and a description of the page content.',
			'Include any optional pages that would benefit this site also with potentially customized names, and a description of the page content.',
			'Customize a page name if you think there is a more relevant name in this sites context that still maintains the core purpose indicated by the original page name.',
			'Keep any customized page names concise and under 15 characters.',
			'Always call getSiteStructure, and leave your message as null.',
		],

		dont: [
			'Skip any required sections or pages.',
			'Include sections not listed in required or optional lists.',
			'Include pages not listed in required or optional lists.',
			'Make assumptions about site specifications not provided.',
			'Ask questions or request more information.',
			'Add any additional fields or explanations to the response.',
		],
	} );
}
