export default function navigateToPageTool( pages = [] ) {
	const homepage = pages.find( ( page ) => page.isHomePage );
	const currentPage = pages.find( ( page ) => page.isCurrentPage );

	return {
		name: 'navigate_to_page',
		description: `Navigate to and edit a page based on user input.
		Home page: "${ homepage?.title }" (ID: ${ homepage?.id })
		Current page: "${ currentPage?.title }" (ID: ${ currentPage?.id })
		If already on requested page, respond: "You're already on the page."
		For unclear requests or non-existent pages, ask for clarification.
		If page doesn't exist, offer to create a new one.
		Do not return any content in your response.`,
		parameters: {
			type: 'object',
			properties: {
				page: {
					type: 'string',
					description: 'The title:postId of the page.',
					enum: [
						...pages.map(
							( page ) => `${ page.title }:${ page.id }`
						),
					],
				},
				goHome: {
					type: 'boolean',
					description:
						'Whether the user wants to navigate to the homepage in order to edit it.',
				},
			},
			required: [ 'page', 'goHome' ],
		},
		additionalProperties: false,
		strict: true,
	};
}
