/**
 * WordPress dependencies
 */
import apiFetch from '@wordpress/api-fetch';
import { useDispatch, useSelect } from '@wordpress/data';
import { store as blockEditorStore } from '@wordpress/block-editor';

/**
 * Internal dependencies
 */
import useUserPrompt from '@ai/assistants/pattern/tools/generate-pattern/prompts/use-user-prompt';
import log from '@utils/log-debug.js';
import useStreamParser from '@ai/assistants/pattern/tools/generate-pattern/hooks/use-stream-parser';
import useCanvas from '@hooks/use-canvas';
import useBlocks from '@hooks/use-blocks';
import useSiteMetadata from '@hooks/use-site-metadata';
import { store as aiStore } from '@store/index.js';
import useCreateToolCallback from '@ai/assistants/shared/use-create-tool-callback';

/**
 * Hook that provides pattern generation functionality.
 *
 * Handles the full lifecycle of pattern generation including:
 * - Setting up a temporary canvas area
 * - Making the API request to generate the pattern
 * - Parsing the streaming response
 * - Inserting the generated pattern into the page
 * - Cleaning up temporary elements
 *
 * @return {Object} Pattern generation functions object containing:
 *                  - generatePattern: Function to generate a new pattern based on user prompt.
 */
export default function useGeneratePatternTool() {
	const { getUserPrompt } = useUserPrompt();
	const { extractPhase, parseSSEChunk } = useStreamParser();
	const { zoomOut, createDummySection } = useCanvas();
	const { insertBlock, replaceBlock, clearSelectedBlock } =
		useDispatch( blockEditorStore );
	const { setCurrentToolCall, setLastCancelableAction } =
		useDispatch( aiStore );

	const { withPendingToolCallback } = useCreateToolCallback();
	const { getSiteMetadata, setSiteMetadata } = useSiteMetadata();
	const { logAnthropicUsage } = useDispatch( aiStore );

	const {
		getContentBlockClientID,
		scrollToBlock,
		getParentRootPatternBlock,
	} = useBlocks();

	const {
		setGeneratedPatternAsString,
		setGeneratedPatternAsBlocks,
		setGeneratedPatternChunksAsJSON,
		setGeneratedPatternDummySectionClientId,
		setGeneratedRootPatternClientId,
	} = useDispatch( aiStore );

	const {
		getBlock,
		getBlockIndex,
		getBlockParentsByBlockName,
		getBlocksByName,
		getBlocks,
	} = useSelect( ( select ) => ( {
		getBlock: select( blockEditorStore ).getBlock,
		getBlockIndex: select( blockEditorStore ).getBlockIndex,
		getBlockParentsByBlockName:
			select( blockEditorStore ).getBlockParentsByBlockName,
		getBlocksByName: select( blockEditorStore ).getBlocksByName,
		getBlocks: select( blockEditorStore ).getBlocks,
	} ) );

	/**
	 * Generates a new pattern based on user input.
	 *
	 * @param {Object} options            Pattern generation options.
	 * @param {string} options.userPrompt The user's prompt for pattern generation.
	 * @param {string} options.clientId   The client ID of the block to insert the pattern near.
	 * @param {string} options.direction  The direction to insert the pattern ('before' or 'after').
	 * @return {Promise<string>} The generated pattern stream buffer.
	 */
	const generatePattern = async ( { userPrompt, clientId, direction } ) => {
		const dummySectionClientId = prepareCanvas( clientId, direction );

		// Set the current tool call
		setCurrentToolCall( 'generate_pattern' );

		// Store the user prompt for pattern registration
		const storedUserPrompt = userPrompt;

		// Deselect the current block for better visual generation without outlines
		clearSelectedBlock();

		// Create the pattern generation function
		const generatePatternAction = async () => {
			try {
				const _userPrompt = await getUserPrompt( storedUserPrompt );
				const response = await makePatternRequest( _userPrompt );
				const streamBuffer = await processPatternStream(
					response.body.getReader()
				);
				cleanupCanvas( dummySectionClientId, storedUserPrompt );
				return streamBuffer;
			} catch ( error ) {
				log.error( 'Error generating pattern:', error );
				throw error;
			}
		};

		// Wrap the action with the pending callback handler
		const result = await withPendingToolCallback( generatePatternAction )();
		return result;
	};

	/**
	 * Makes the API request to generate a pattern using the Anthropic API.
	 *
	 * @param {string} userPrompt The prompt to send to the API.
	 * @return {Promise<Response>} The API response.
	 */
	const makePatternRequest = async ( userPrompt ) => {
		const response = await fetch(
			'/wp-json/wpcom/v2/big-sky/v1/anthropic/messages',
			{
				method: 'POST',
				headers: {
					'Content-Type': 'application/json',
					'X-WP-Nonce': apiFetch.nonceMiddleware.nonce,
				},
				body: JSON.stringify( {
					model: 'claude-3-5-sonnet-20241022',
					max_tokens: 8000,
					temperature: 0.2,
					messages: [ { role: 'user', content: userPrompt } ],
					stream: true,
				} ),
			}
		);

		if ( ! response.ok ) {
			throw new Error( `HTTP error! status: ${ response.status }` );
		}

		return response;
	};

	/**
	 * Process phases from the stream buffer.
	 *
	 * @param {string} streamBuffer    The current stream buffer.
	 * @param {Set}    completedPhases Set of completed phases.
	 * @return {string} The updated stream buffer.
	 */
	const processPhases = ( streamBuffer, completedPhases ) => {
		let currentPhase = 1;
		while ( true ) {
			if ( completedPhases.has( currentPhase ) ) {
				currentPhase++;
				continue;
			}

			const phase = extractPhase( streamBuffer, currentPhase );
			if ( phase === null ) break;

			completedPhases.add( currentPhase );
			currentPhase++;
		}
		return streamBuffer;
	};

	/**
	 * Processes a stream of SSE events, extracting and managing phases.
	 *
	 * @param {ReadableStreamDefaultReader} reader The stream reader.
	 * @return {Promise<string>} The complete stream buffer.
	 */
	const processPatternStream = async ( reader ) => {
		const decoder = new TextDecoder();
		let streamBuffer = '';
		const completedPhases = new Set();
		let pendingChunk = '';
		let totalInputTokens = 0;
		let totalOutputTokens = 0;

		try {
			while ( true ) {
				const { done, value } = await reader.read();
				if ( done ) break;

				const chunk = decoder.decode( value, { stream: true } );
				pendingChunk += chunk;

				const events = pendingChunk.split( '\n\n' );
				pendingChunk = events.pop() || '';

				for ( const event of events ) {
					if ( event.trim() ) {
						const text = parseSSEChunk( event );
						if ( text ) {
							streamBuffer += text;
							streamBuffer = processPhases(
								streamBuffer,
								completedPhases
							);
						}

						// Extract token counts from specific events
						try {
							const [ , eventData ] = event
								.split( '\n' )
								.map( ( line ) =>
									line.replace( /^[^:]+: /, '' )
								);

							if ( eventData ) {
								const data = JSON.parse( eventData );

								// Get input tokens from message_start event
								if (
									data.type === 'message_start' &&
									data.message?.usage?.input_tokens
								) {
									totalInputTokens =
										data.message.usage.input_tokens;
								}

								// Get output tokens from final message_delta event
								if (
									data.type === 'message_delta' &&
									data.delta?.stop_reason === 'end_turn'
								) {
									totalOutputTokens =
										data.usage?.output_tokens || 0;
								}
							}
						} catch ( e ) {
							// Ignore parsing errors for non-usage events
						}
					}
				}
			}

			// Log the token usage after stream completion
			if ( totalInputTokens > 0 || totalOutputTokens > 0 ) {
				logAnthropicUsage( 'claude-3-5-sonnet-20241022', {
					input_tokens: totalInputTokens,
					output_tokens: totalOutputTokens,
				} );
			}

			return streamBuffer;
		} finally {
			reader.releaseLock();
		}
	};

	/**
	 * Register a generated pattern in the site metadata.
	 *
	 * @param {Object} block      The block to register as a pattern.
	 * @param {string} userPrompt The user's prompt that generated the pattern.
	 */
	const registerGeneratedPattern = ( block, userPrompt ) => {
		const metadata = getSiteMetadata();

		// Create a title from the user prompt, limiting it to a reasonable length
		const title =
			userPrompt.length > 40
				? userPrompt.substring( 0, 40 ) + '...'
				: userPrompt;

		// Generate a unique pattern ID starting with 999
		const timestamp = Date.now().toString().slice( -5 ); // Get last 5 digits of timestamp
		const patternId = parseInt( '999' + timestamp, 10 );

		// Add metadata to the block's attributes
		block.attributes = {
			...block.attributes,
			metadata: {
				remotePatternId: patternId.toString(),
				patternCategory: 'generated',
			},
		};

		// Create the pattern metadata
		const pattern = {
			category: 'generated',
			title,
			content: block,
		};

		// Initialize patterns array for this pattern ID if needed
		if ( ! metadata.patterns[ patternId ] ) {
			metadata.patterns[ patternId ] = [];
		}

		// Add the pattern to the patterns array
		metadata.patterns[ patternId ].push( pattern );

		// Update the site metadata
		setSiteMetadata( metadata );
	};

	/**
	 * Resets all generated pattern state to initial values.
	 */
	const resetGeneratedPattern = () => {
		setGeneratedPatternAsString( '' );
		setGeneratedPatternAsBlocks( [] );
		setGeneratedPatternChunksAsJSON( [] );
		setGeneratedPatternDummySectionClientId( null );
		setGeneratedRootPatternClientId( null );
	};

	/**
	 * Prepares the canvas for pattern generation by inserting a dummy section.
	 *
	 * @param {string} clientId  The client ID of the block to insert near.
	 * @param {string} direction The direction to insert ('before' or 'after').
	 * @return {string} The client ID of the inserted dummy section.
	 */
	const prepareCanvas = ( clientId, direction ) => {
		const zoomedOut = zoomOut();

		const insertionId = getBlocksByName( 'core/post-content' );
		const insertionBlockId = insertionId[ 0 ];

		// get the parent template-part/post-content block of the last selected block
		const parentBlock = getBlock(
			getBlockParentsByBlockName( clientId, [
				'core/template-part',
				'core/post-content',
			] )[ 0 ]
		);

		// if the parent block is a template-part, determine if it's a header or footer
		// and set the insertion block id accordingly
		let blockIndex;
		if ( parentBlock?.name === 'core/template-part' ) {
			if ( parentBlock.attributes.slug === 'header' ) {
				blockIndex = 0;
			} else if ( parentBlock.attributes.slug === 'footer' ) {
				blockIndex = getBlocks( getContentBlockClientID() ).length;
			}
		} else {
			// get the root pattern block of the selected block
			const offset = direction === 'before' ? 0 : 1;
			const rootPatternBlock = getParentRootPatternBlock( clientId );
			if ( ! rootPatternBlock ) {
				blockIndex = getBlockIndex( clientId ) + offset;
			} else {
				blockIndex =
					getBlockIndex( rootPatternBlock.clientId ) + offset;
			}
		}

		// Insert a dummy loading section while the patterns are resolving
		const dummySection = createDummySection( {
			isEmpty: false,
			className: 'big-sky__is-loading-dummy__generated-pattern',
		} );

		insertBlock( dummySection, blockIndex, insertionBlockId );

		let scrolled = false;
		zoomedOut.then( () => {
			if ( scrolled ) {
				return;
			}

			scrolled = true;
			// Select the in-progress new (dummy) section.
			scrollToBlock( dummySection.clientId, 'center' );
		} );

		setGeneratedPatternDummySectionClientId( dummySection.clientId );

		return dummySection.clientId;
	};

	/**
	 * Cleans up the canvas after pattern generation by replacing the dummy section.
	 *
	 * @param {string} dummySectionClientId The client ID of the dummy section.
	 * @param {string} userPrompt           The user's prompt that generated the pattern.
	 */
	const cleanupCanvas = ( dummySectionClientId, userPrompt ) => {
		// Validate the dummy section ID
		if ( ! dummySectionClientId ) {
			return;
		}

		// Get the dummy section and its first inner block
		const dummySection = getBlock( dummySectionClientId );
		const innerBlock = getBlock(
			dummySection?.innerBlocks?.[ 1 ]?.clientId // dummy patterns have a p at [0]
		);

		if ( ! innerBlock ) {
			return;
		}

		// Register the pattern in site metadata
		registerGeneratedPattern( innerBlock, userPrompt );

		// Replace the dummy section with the inner blocks
		replaceBlock( dummySection.clientId, innerBlock );

		// After the pattern is generated and rendered, set it as a cancelable action
		setLastCancelableAction( {
			action: 'add_pattern',
			clientId: innerBlock.clientId,
		} );

		resetGeneratedPattern();
	};

	return {
		generatePattern,
	};
}
