import routerAssistantPrompt from './prompt.js';
import routerToolkit from './toolkit.js';
import { ASSISTANTS } from '../constants.js';
import useRouterToolkit from './use-router-toolkit.js';

const routerAssistant = {
	id: ASSISTANTS.ROUTER,
	description:
		'An assistant that analyzes user requests and routes them to the most appropriate specialized assistant.',
	instructions: ( context ) => {
		return routerAssistantPrompt( context );
	},
	tags: [ ASSISTANTS.ROUTER ],
	toolkits: [ routerToolkit ],
	toolkitHook: useRouterToolkit,
	onStart: ( invoke, clearMessages = () => {}, agentMessage = null ) => {
		if ( agentMessage ) {
			clearMessages();
			invoke.agentSay( agentMessage );
		}
	},
};

export default routerAssistant;
