/**
 * WordPress dependencies
 */
import { store as blockEditorStore } from '@wordpress/block-editor';
import { useSelect } from '@wordpress/data';

export default function useImageToolValidation() {
	const { getBlock } = useSelect( blockEditorStore );

	const validateArguments = ( args ) => {
		if ( ! args?.clientId ) {
			throw new Error( 'Please select an image block and try again.' );
		}

		// check if the block is an image
		const block = getBlock( args.clientId );
		if ( block?.name !== 'core/image' ) {
			throw new Error(
				'The selected block is not an image. Please select an image and try again.'
			);
		}
		return true;
	};

	return {
		validateArguments,
	};
}
