/**
 * WordPress dependencies
 */
import { useDispatch, useSelect } from '@wordpress/data';
import { store as editorStore } from '@wordpress/editor';
import { useEffect, useRef, useState } from '@wordpress/element';
import { store as blockEditorStore } from '@wordpress/block-editor';
import { AgentsProvider, useChat } from '@automattic/big-sky-agents';
import { __ } from '@wordpress/i18n';

/**
 * Internal dependencies
 */
import routerAssistant from '@ai/assistants/router';
import specAssistant from '@ai/assistants/spec';
import { assistants } from '@ai/assistants';
import AIBuildMode from '@components/ai-build-mode';
import AssemblerDebug from '@components/assembler-debug';
import Dock from '@components/dock';
import useAfterSave from '@hooks/use-after-save';
import useLog from '@hooks/use-log';
import { messageRoles } from '@utils/constants';
import { store as aiStore } from '@store';
import EditorSetup from '@components/editor-setup';
import useCanvas from '@hooks/use-canvas';
import ToolkitsProvider from '@ai/assistants/toolkits-provider';
import Onboarding from '@components/onboarding';
import { getCanvasIframeElements } from '@utils/canvas-iframe-elements';
import { useRouting } from '@hooks/use-routing';
import { dockViews } from '@utils/constants.js';
import ConfirmationDialog from '@components/onboarding/confirmation-dialog';
import useResetSite from '@hooks/use-reset-site.js';
import useSiteMetadata from '@hooks/use-site-metadata';

/**
 * This component wraps the Assembler component so that:
 * 1. It only renders on page post types
 * 2. It only renders after the canvas has loaded
 * 3. It logs the save action
 */
function BigSky() {
	const {
		isBuildingSite,
		isEditorIframeLoaded,
		isDevMode,
		siteMetadata,
		currentHandoffArea,
	} = useSelect( ( select ) => ( {
		isBuildingSite: select( aiStore ).getIsBuildingSite(),
		isEditorIframeLoaded: select( aiStore ).getIsEditorIframeLoaded(),
		isDevMode: select( aiStore ).getIsDevMode(),
		siteMetadata: select( aiStore ).getSiteMetadata(),
		currentHandoffArea: select( aiStore ).getCurrentHandoffArea(),
	} ) );
	const isAfterSave = useAfterSave();
	const { logAction, logToolCall, logAssistantMessage, logError } = useLog();

	const { canvasMode } = useCanvas();

	const { setLastSelectedBlock, setDockView } = useDispatch( aiStore );
	const { lastSelectedBlock } = useSelect( ( select ) => {
		const ai = select( aiStore );

		return {
			lastSelectedBlock: ai.getLastSelectedBlock(),
		};
	} );
	const { selectedBlock } = useSelect( ( select ) => {
		const { getSelectedBlock } = select( blockEditorStore );
		return {
			selectedBlock: getSelectedBlock(),
		};
	} );

	const { postType } = useSelect( ( select ) => {
		return {
			postType: select( editorStore ).getCurrentPostType(),
		};
	} );

	const [ isPagePostType, setIsPagePostType ] = useState( false );
	const [ hasCanvasLoaded, setHasCanvasLoaded ] = useState( false );
	const previousSelectedBlockRef = useRef( null );
	const { messages, error } = useChat();
	const lastLoggedMessageRef = useRef( null );
	const lastLoggedErrorRef = useRef( null );

	const { buildNewSite } = useResetSite();
	const { setSiteMetadata } = useSiteMetadata();
	const isOnboarded = siteMetadata?.isOnboarded;

	const { setAiStep, showOnboardingDialog, setShowOnboardingDialog } =
		useRouting( {
			runUrlChangeHandling: true,
		} );

	const { canvasiFrameBody } = getCanvasIframeElements();

	// Log the last assistant message or tool call.
	useEffect( () => {
		// Get the last message.
		const lastMessage = messages?.[ messages.length - 1 ];

		// Check if this message has already been logged
		if ( lastMessage && lastMessage !== lastLoggedMessageRef.current ) {
			const role = lastMessage?.role;
			const content =
				lastMessage?.content?.[ 0 ]?.text || lastMessage?.content || '';
			const lastToolCall = lastMessage?.tool_calls?.[ 0 ];

			if ( role === messageRoles.ASSISTANT ) {
				const toolCall = lastToolCall?.function;
				if ( toolCall ) {
					logToolCall( toolCall, lastMessage.id );
				}
				if ( content ) {
					logAssistantMessage( content, lastMessage.id );
				}
			}

			// Update the ref with the last logged message
			lastLoggedMessageRef.current = lastMessage;
		}
	}, [ messages, logToolCall, logAssistantMessage ] );

	// Log errors that occur in the chat history
	useEffect( () => {
		if ( error ) {
			if ( error !== lastLoggedErrorRef.current ) {
				logError( error );
				lastLoggedErrorRef.current = error;
			}
		} else {
			lastLoggedErrorRef.current = null;
		}
	}, [ error, logError ] );

	useEffect( () => {
		if ( isAfterSave ) {
			logAction( 'Save Page', messageRoles.SAVE_PAGE );
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [ isAfterSave ] );

	useEffect( () => {
		if (
			selectedBlock &&
			selectedBlock.clientId !==
				previousSelectedBlockRef.current?.clientId
		) {
			setLastSelectedBlock( previousSelectedBlockRef.current );
			previousSelectedBlockRef.current = selectedBlock;
		}
	}, [ selectedBlock, setLastSelectedBlock, lastSelectedBlock ] );

	useEffect( () => {
		const allowablePostTypes = [ 'page' ];
		if ( postType && allowablePostTypes.includes( postType ) ) {
			setIsPagePostType( true );
		} else {
			setIsPagePostType( false );
		}
	}, [ postType ] );

	useEffect( () => {
		if ( ! canvasiFrameBody ) {
			setHasCanvasLoaded( false );
			return;
		}
		if ( isEditorIframeLoaded && ! hasCanvasLoaded ) {
			setHasCanvasLoaded( true );
		}
	}, [
		isEditorIframeLoaded,
		hasCanvasLoaded,
		setHasCanvasLoaded,
		canvasiFrameBody,
	] );

	const agents = isOnboarded ? [ ...assistants ] : [ specAssistant ];

	const colorAssistant = assistants.find(
		( assistant ) => assistant.id === 'big-sky-color'
	);
	const fontAssistant = assistants.find(
		( assistant ) => assistant.id === 'big-sky-font'
	);
	const getActiveAssistantId = () => {
		if ( currentHandoffArea === 'colors' ) {
			return colorAssistant.id;
		}
		if ( currentHandoffArea === 'fonts' ) {
			return fontAssistant.id;
		}

		return routerAssistant.id;
	};

	return (
		<>
			{ canvasMode === 'edit' && isPagePostType && (
				<>
					<AgentsProvider
						activeAgentId={
							isOnboarded
								? getActiveAssistantId()
								: specAssistant.id
						}
						agents={ agents }
						started={ true }
					>
						{ hasCanvasLoaded && isOnboarded && <Dock /> }
						{ ! isOnboarded && <Onboarding /> }
						<ToolkitsProvider />
					</AgentsProvider>

					{ isBuildingSite && <AIBuildMode /> }
					{ isDevMode ? <AssemblerDebug /> : null }
				</>
			) }
			<ConfirmationDialog
				isOpen={ showOnboardingDialog }
				onClose={ () => setShowOnboardingDialog( false ) }
				onCancel={ () => {
					setShowOnboardingDialog( false );
					setAiStep( 'handoff', true );
					setDockView( dockViews.MENUS );
				} }
				onConfirm={ () => {
					setShowOnboardingDialog( false );
					setSiteMetadata( { isOnboarded: false }, false, true );
					buildNewSite();
				} }
				message={ __(
					'If you go back, your current progress may be lost. From there, you can start fresh and create a new site.',
					'big-sky'
				) }
				cancelText={ __( 'Continue editing', 'big-sky' ) }
				confirmText={ __( 'Build a new site', 'big-sky' ) }
				title={ __( 'Start Over?', 'big-sky' ) }
			/>
			<EditorSetup />
		</>
	);
}

export default BigSky;
