import layoutAssistantPrompt from './prompt.js';
import layoutToolkit from './toolkit.js';
import { ASSISTANTS } from '../constants.js';
import useLayoutToolkit from './use-layout-toolkit.js';

const layoutAssistant = {
	id: ASSISTANTS.LAYOUT,
	description:
		'Helps with all requests related to switching the page layout.',
	instructions: ( context ) => {
		return layoutAssistantPrompt( context );
	},
	tags: [ ASSISTANTS.LAYOUT ],
	toolkits: [ layoutToolkit ],
	toolkitHook: useLayoutToolkit,
	onStart: ( invoke, clearMessages = () => {}, agentMessage = null ) => {
		if ( agentMessage ) {
			clearMessages();
			invoke.agentSay( agentMessage );
		}
	},
};

export default layoutAssistant;
