/**
 * WordPress dependencies
 */
import { store as blocksStore } from '@wordpress/blocks';
import { useSelect } from '@wordpress/data';
import { useCallback } from '@wordpress/element';
import { store as coreStore } from '@wordpress/core-data';

/**
 * External dependencies
 */
import { extend } from 'colord';
import a11yPlugin from 'colord/plugins/a11y';
import { store as aiStore } from '@store';
extend( [ a11yPlugin ] );

export default function useVariations() {
	const MAX_VARIATIONS_SHOWN = 10;

	const { getBlockVariations } = useSelect( blocksStore );

	const { __experimentalGetCurrentThemeGlobalStylesVariations } =
		useSelect( coreStore );

	const { getGeneratedVariations } = useSelect( aiStore );

	const getColorVariations = useCallback( () => {
		const variations =
			__experimentalGetCurrentThemeGlobalStylesVariations();

		const colorVariations = variations?.filter( ( v ) =>
			isColorVariation( v )
		);

		// Inject custom-generated variation so that it's available to the color picker.
		if ( getGeneratedVariations && getGeneratedVariations()?.length > 0 ) {
			return [ ...getGeneratedVariations(), ...colorVariations ];
		}
		return colorVariations;
	}, [
		__experimentalGetCurrentThemeGlobalStylesVariations,
		getGeneratedVariations,
	] );

	const getFontVariations = useCallback( () => {
		const variations =
			__experimentalGetCurrentThemeGlobalStylesVariations();

		const fontVariations = variations?.filter( ( v ) =>
			isFontVariation( v )
		);

		return fontVariations;
	}, [ __experimentalGetCurrentThemeGlobalStylesVariations ] );

	const getColorVariationByTitle = useCallback(
		( title ) => {
			const colors = getColorVariations();
			return colors?.find( ( v ) => v.title === title );
		},
		[ getColorVariations ]
	);

	const getFontVariationByTitle = ( title ) => {
		const fonts = getFontVariations();
		return fonts?.find( ( v ) => v.title === title );
	};

	const getHexColorFromThemeVar = useCallback(
		( colorTitle, themeVar ) => {
			// themeVar can now be a hex color, if so just return it
			if ( themeVar.startsWith( '#' ) ) {
				return themeVar;
			}

			const variation = getColorVariationByTitle( colorTitle );
			const varIndex = themeVar?.match( /\d+/ )[ 0 ];
			return variation?.settings?.color?.palette?.theme?.find(
				( color ) => {
					return varIndex === color.slug.match( /\d+/ )[ 0 ];
				}
			)?.color;
		},
		[ getColorVariationByTitle ]
	);

	const getSectionVariationBackgroundColor = useCallback(
		( sectionVariationName, colorTitle ) => {
			const variation = getColorVariationByTitle( colorTitle );

			let themeVar = false;
			if ( sectionVariationName.includes( 'default' ) ) {
				themeVar = variation?.styles?.color?.background || false;
			} else {
				themeVar =
					variation?.styles?.blocks?.[ 'core/group' ]?.variations?.[
						sectionVariationName
					]?.color?.background || false;
			}

			if ( ! themeVar ) return false;

			const hex = getHexColorFromThemeVar( colorTitle, themeVar );

			if ( ! hex ) return false;

			return {
				themeVar,
				hex,
			};
		},
		[ getColorVariationByTitle, getHexColorFromThemeVar ]
	);

	const getSectionVariationTextColor = useCallback(
		( sectionVariationName, colorTitle ) => {
			const variation = getColorVariationByTitle( colorTitle );

			let themeVar = false;
			if ( sectionVariationName.includes( 'default' ) ) {
				themeVar = variation?.styles?.color?.text || false;
			} else {
				themeVar =
					variation?.styles?.blocks?.[ 'core/group' ]?.variations?.[
						sectionVariationName
					]?.color?.text || false;
			}

			if ( ! themeVar ) return false;

			const hex = getHexColorFromThemeVar( colorTitle, themeVar );

			if ( ! hex ) return false;

			return {
				themeVar,
				hex,
			};
		},
		[ getColorVariationByTitle, getHexColorFromThemeVar ]
	);

	const isColorVariation = ( variation ) => {
		return variation?.settings?.color && ! variation?.settings?.typography;
	};

	const isFontVariation = ( variation ) => {
		return variation?.settings?.typography && ! variation?.settings?.color;
	};

	const getVariationKeywords = ( type ) => {
		let variations;
		switch ( type ) {
			case 'color':
				variations = getColorVariations();
				break;
			case 'font':
				variations = getFontVariations();
				break;
			default:
				throw new Error( 'Invalid variation type' );
		}

		const allKeywords = variations.reduce( ( acc, variation ) => {
			const keywords = variation?.[ 'x-big-sky-meta' ]?.keywords;
			if ( keywords ) {
				keywords.forEach( ( category ) => {
					if ( ! acc.includes( category ) ) {
						acc.push( category );
					}
				} );
			}
			return acc;
		}, [] );
		return allKeywords;
	};

	const getSocialLinkVariations = useCallback( () => {
		const variations = getBlockVariations( 'core/social-link' );
		return variations;
	}, [ getBlockVariations ] );

	return {
		MAX_VARIATIONS_SHOWN,
		getColorVariations,
		getFontVariations,
		getColorVariationByTitle,
		getFontVariationByTitle,
		getHexColorFromThemeVar,
		getSectionVariationBackgroundColor,
		getSectionVariationTextColor,
		isColorVariation,
		isFontVariation,
		getVariationKeywords,
		getSocialLinkVariations,
	};
}
