import { useCallback } from '@wordpress/element';
import log from '@utils/log-debug';
import apiFetch from '@wordpress/api-fetch';

const cachedCoordinates = {};
export default function useGeocoder() {
	const getCoordinates = useCallback( async ( locationName ) => {
		if ( ! cachedCoordinates[ locationName ] ) {
			let data;
			try {
				data = await apiFetch( {
					path:
						'/wpcom/v2/big-sky/v1/geo-coordinates?address=' +
						encodeURIComponent( locationName ),
					method: 'GET',
				} );
				log.info( 'Geocode Response', data );
			} catch ( e ) {
				log.error( 'Geocode Error', e );
				return null;
			}

			const coordinates = data?.features?.[ 0 ]?.geometry?.coordinates;
			// Mapbox returns coordinates in [longitude, latitude] format
			// but we need it in [latitude, longitude] format
			cachedCoordinates[ locationName ] = coordinates
				? coordinates.reverse()
				: coordinates;
		}

		return cachedCoordinates[ locationName ];
	}, [] );

	return {
		getCoordinates,
	};
}
