import VariationPicker from '@components/variation-picker';
import useSiteMetadata from '@hooks/use-site-metadata';

const ColorVariationPicker = ( {
	colorVariations,
	maxColorsToShow = 12,
	numSuggestions = 0,
	currentVariationIsSuggested = false,
} ) => {
	const { currentColor } = useSiteMetadata().getSiteMetadata();

	return (
		<VariationPicker
			variations={ colorVariations }
			currentVariation={ currentColor }
			maxToShow={ maxColorsToShow }
			numSuggestions={ numSuggestions }
			currentVariationIsSuggested={ currentVariationIsSuggested }
			type="color"
		/>
	);
};

export default ColorVariationPicker;
