/**
 * Colorizes the black-and-transparent logo to match the current color.
 *
 * There are three ways of colorizing the logo:
 * 1. SVG Filter: https://developer.mozilla.org/en-US/docs/Web/SVG/Element/feColorMatrix
 * 2. CSS Mask: https://developer.mozilla.org/en-US/docs/Web/CSS/CSS_masking
 * 3. Canvas Compositing: https://developer.mozilla.org/en-US/docs/Web/API/CanvasRenderingContext2D/globalCompositeOperation
 *
 * # SVG Filter #
 * The filter can be applied to any element. It's defined as a matrix which takes as input RGBA values and outputs
 * transformed R'G'B'A' values.
 *
 * Pros:
 * * Quick. The effect is generated by the browser using CSS.
 * * Applicable to any element and is "live" on that element.
 *
 * Cons:
 * * Because of some odd colorspace issues, the resulting color isn't always precisely the same as what we'd
 *   expect. (It's always close.)
 * * Requires DOM manipulation. We need an SVG element in the canvas iframe.
 *   In theory, it would be possible to include the SVG element in the CSS file instead via an inline data URL.
 *   Mobile Safari does not support fragments in data URLs, though (i.e., data:image/svg,...#fragment),
 *   and fragments are required for referencing the SVG filter in CSS. So we're stuck with direct DOM manipulation.
 * * Requires CSS/HTML changes on the frontend.
 *
 * # CSS Masking #
 * Sets the background color of an element to the desired color, then applies the logo as the CSS mask for that element.
 *
 * Pros:
 * * Quick. The effect is generated by the browser using CSS.
 * * No DOM manipulation required.
 * * No colorspace issues.
 *
 * Cons:
 * * Can't be used on the logo when it's being edited (zoomed, rotated, etc.) without going through many hoops.
 * * Requires CSS changes on the frontend.
 *
 * # Canvas Compositing #
 * Creates a new image (and Blob URL) based on the site logo but colorized with the color we want.
 *
 * Pros:
 * * No DOM manipulation required.
 * * No colorspace issue.
 * * Generates a real image we can later store and use on the front end without requiring any CSS/JS. This is helpful
 *   since we want the frontend to still function if Big Sky is later disabled.
 *
 * Cons:
 * * (Comparatively) slow. The effect is generated via JS. Requires some careful garbage collection.
 * * Difficult to integrate with the editor when the logo is being actively modified.
 * * We can make as many different colorized PNGs as we want, but it's hard to trick the site-logo block to load
 *   the correct colorized file for each differently colored section.
 *
 * Taking the various pros and cons of each technique into account, we do the following:
 * 1. Use an SVG Filter when editing the image (cropping, zooming, rotating, etc.) in Gutenberg.
 *    One <svg> element per section style variation is injected into the canvas
 *    iframe and updated via DOM manipulation (rather than React). We need the SVG filter
 *    here to make the interactive logo modification UI (zooming, rotating, etc.) work nicely.
 * 2. Elsewhere in Big Sky and Gutenberg, and on the front end, we use CSS Masking. Passing around
 *    various custom CSS props is simpler than injecting SVG elements everywhere.
 *    The front end CSS masking CSS is added to the Global Style's custom CSS with a highly specific
 *    selector:
 *    .custom-logo[src="{the logo's URL}"]
 *    That way, if the Big Sky plugin is disabled, the custom CSS will stick around. If the logo is
 *    subsequently changed outside of Big Sky, the masking CSS won't apply and so won't mess up the
 *    new logo.
 * We no longer generate real colorized PNGs via Canvas Compositing.
 */

/**
 * WordPress dependencies
 */
import { store as coreStore, useEntityRecord } from '@wordpress/core-data';
import { useSelect } from '@wordpress/data';
import { useEffect, useRef } from '@wordpress/element';

/**
 * Internal dependencies
 */
import useSiteMetadata from '@hooks/use-site-metadata';
import useStyles from '@hooks/use-styles';
import useVariations from '@hooks/use-variations';
import { store as aiStore } from '@store';
import { getMediaById } from '@utils/media.js';
import { removeCustomLogoCss, setCustomLogoCss } from '@utils/images.js';
import useCanvas from '@hooks/use-canvas.js';
import { getCanvasIframeElements } from '@utils/canvas-iframe-elements';

function setSvgColors( svg, style, sectionColors ) {
	svg.replaceChildren();

	const styleRules = [];
	for ( const sectionColor of sectionColors ) {
		// `is-stlye-{sectionColor.name}-{unpredictable-uuid}` is getting the color set, but not
		// `is-stlye-{sectionColor.name}`, so we need to set it here. Minimize impact by just setting it for `feFlood`.
		styleRules.push(
			`.is-style-${ sectionColor.name } feFlood { color: ${ sectionColor.text }; }`
		);
		// Set the custom CSS property for the section class.
		styleRules.push(
			`.is-style-${ sectionColor.name } { --site-logo-filter: url(#svg-logo-color-${ sectionColor.name }); }`
		);

		const filter = svg.ownerDocument.createElementNS(
			'http://www.w3.org/2000/svg',
			'filter'
		);

		filter.setAttribute( 'id', `svg-logo-color-${ sectionColor.name }` );
		filter.setAttribute( 'class', `is-style-${ sectionColor.name }` );
		filter.setAttribute( 'color-interpolation-filters', 'sRGB' );
		const feFlood = svg.ownerDocument.createElementNS(
			'http://www.w3.org/2000/svg',
			'feFlood'
		);
		feFlood.setAttribute( 'flood-color', 'currentcolor' ); // `currentcolor` uses the current color CSS prop.
		feFlood.setAttribute( 'result', 'flood' );

		const feComposite = svg.ownerDocument.createElementNS(
			'http://www.w3.org/2000/svg',
			'feComposite'
		);
		feComposite.setAttribute( 'operator', 'in' );
		feComposite.setAttribute( 'in', 'flood' );
		feComposite.setAttribute( 'in2', 'SourceGraphic' );

		filter.appendChild( feFlood );
		filter.appendChild( feComposite );

		svg.appendChild( filter );
	}

	style.textContent = styleRules.join( '\n' );
}

export default function useColorizedLogo() {
	// Holds the <svg> element in the canvas iframe.
	const svgRef = useRef();
	// Holds the <style> element in the canvas iframe.
	const styleRef = useRef();

	const { getMedia } = useSelect( ( select ) => {
		const { getMedia: _getMedia } = select( coreStore );
		return {
			getMedia: _getMedia,
		};
	}, [] );

	const {
		record: siteData,
		editedRecord: editedSiteData,
		isResolving: isResolvingSiteData,
	} = useEntityRecord( 'root', 'site' );

	const { useCustomCss } = useStyles();

	const [ customCss, setCustomCss ] = useCustomCss();

	const { currentColor } = useSiteMetadata().getSiteMetadata();

	const { setStyleProperties } = useCanvas();

	const { getColorVariationByTitle } = useVariations();

	const { isEditorReady, isEditorIframeLoaded } = useSelect( ( select ) => ( {
		isEditorReady: select( aiStore ).getIsEditorReady(),
		isEditorIframeLoaded: select( aiStore ).getIsEditorIframeLoaded(),
	} ) );

	const { canvasiFrameDocument } = getCanvasIframeElements();

	// Set up the <svg> element when the iframe is ready.
	useEffect( () => {
		if (
			! isEditorReady ||
			! isEditorIframeLoaded ||
			! canvasiFrameDocument
		) {
			return;
		}

		const svgAlready =
			canvasiFrameDocument.getElementById( 'svg-logo-colors' );
		if ( svgAlready ) {
			svgRef.current = svgAlready;
		} else {
			const svg = canvasiFrameDocument.createElementNS(
				'http://www.w3.org/2000/svg',
				'svg'
			);
			svg.setAttribute( 'id', 'svg-logo-colors' );
			svg.setAttribute( 'width', '0px' );
			svg.setAttribute( 'height', '0px' );
			svg.setAttribute( 'class', 'logo-svg' );

			canvasiFrameDocument.body.appendChild( svg );

			svgRef.current = svg;
		}

		const styleAlready =
			canvasiFrameDocument.getElementById( 'style-logo-colors' );
		if ( styleAlready ) {
			styleRef.current = styleAlready;
		} else {
			const style = canvasiFrameDocument.createElement( 'style' );
			style.setAttribute( 'id', 'style-logo-colors' );
			canvasiFrameDocument.body.appendChild( style );

			styleRef.current = style;
		}
	}, [ canvasiFrameDocument, isEditorIframeLoaded, isEditorReady ] );

	useEffect( () => {
		if ( ! isEditorReady || ! isEditorIframeLoaded ) {
			return;
		}

		if ( isResolvingSiteData ) {
			return;
		}

		const siteLogo = editedSiteData?.site_logo ?? siteData?.site_logo;

		if ( ! siteLogo ) {
			return;
		}

		const setSiteLogoColors = async () => {
			let media = getMedia( siteLogo );
			if ( ! media && siteLogo ) {
				media = await getMediaById( siteLogo );
			}

			const isBigSkyLogo = !! media?.meta?.big_sky_generated_logo;

			const variation = getColorVariationByTitle( currentColor );

			// If it's a Big Sky logo, set up the per-section-color-variation SVG filters.
			// If it's not, remove any SVG filters already present.
			// The SVG filters are only used when the logo is being edited in Gutenberg (zoomed, rotated, etc.).
			const sectionColors = isBigSkyLogo
				? Object.entries(
						getColorVariationByTitle( currentColor )?.styles
							?.blocks?.[ 'core/group' ]?.variations ?? {}
				  ).map( ( [ name, sectionVariation ] ) => ( {
						name,
						text:
							sectionVariation?.color?.text ??
							variation?.styles?.color?.text ??
							'#000',
				  } ) )
				: [];
			if (
				isBigSkyLogo &&
				! sectionColors.includes( 'section-default' )
			) {
				sectionColors.unshift( {
					name: 'section-default',
					text: variation?.styles?.color?.text ?? '#000',
				} );
			}

			// Set the SVG filters to the new colors.
			if ( svgRef.current && styleRef.current ) {
				setSvgColors( svgRef.current, styleRef.current, sectionColors );
			}

			// Set the CSS Masking properties.
			// These are used by the page/section layout preview iframes.
			setStyleProperties(
				isBigSkyLogo
					? {
							'--site-logo-url': `url(${ media.source_url })`,
							'--site-logo-object-position': '-1024px',
					  }
					: {
							'--site-logo-url': 'none',
							'--site-logo-object-position': '50%',
					  }
			);

			if ( isBigSkyLogo ) {
				setCustomLogoCss( customCss, setCustomCss, media.source_url );
			} else {
				removeCustomLogoCss( customCss, setCustomCss );
			}
		};
		setSiteLogoColors();
	}, [
		currentColor,
		getColorVariationByTitle,
		getMedia,
		isEditorIframeLoaded,
		isEditorReady,
		isResolvingSiteData,
		setStyleProperties,
		customCss,
		setCustomCss,
		editedSiteData?.site_logo,
		siteData?.site_logo,
	] );
}
