/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { useCallback, useEffect, useState } from '@wordpress/element';
import {
	__unstableAnimatePresence as AnimatePresence,
	Button,
	__unstableMotion as motion,
} from '@wordpress/components';
import { reusableBlock } from '@wordpress/icons';
import { useDispatch, useSelect } from '@wordpress/data';
import { useChat } from '@automattic/big-sky-agents';
/**
 * Internal dependencies
 */
import ChatActions from '@components/chat/actions';
import uuidv4 from '@utils/uuid';
import { store as aiStore } from '@store';
import setDelay from '@utils/set-delay';
import useLog from '@hooks/use-log';
import useJetpackToken from '@hooks/use-jetpack-token';
import { recordTracksEvent } from '@utils/tracks';

const ALL_ACTIONS = [
	{
		actionName: __( 'Build a website', 'big-sky' ),
		description: __( 'for my bakery', 'big-sky' ),
	},
	{
		actionName: __( 'Design a portfolio', 'big-sky' ),
		description: __( 'for showcasing my work', 'big-sky' ),
	},
	{
		actionName: __( 'Make a landing page', 'big-sky' ),
		description: __( 'for my new book', 'big-sky' ),
	},
	{
		actionName: __( 'Create a portfolio', 'big-sky' ),
		description: __( 'for my handmade jewelry', 'big-sky' ),
	},
	{
		actionName: __( 'Build a blog', 'big-sky' ),
		description: __( 'for my travel adventures', 'big-sky' ),
	},
	{
		actionName: __( 'Design a website', 'big-sky' ),
		description: __( 'for my restaurant', 'big-sky' ),
	},
	{
		actionName: __( 'Design a landing page', 'big-sky' ),
		description: __( 'for my photography', 'big-sky' ),
	},
	{
		actionName: __( 'Make a website', 'big-sky' ),
		description: __( 'for my yoga studio', 'big-sky' ),
	},
	{
		actionName: __( 'Build an online presence', 'big-sky' ),
		description: __( 'for my business', 'big-sky' ),
	},
	{
		actionName: __( 'Create a site', 'big-sky' ),
		description: __( 'for my podcast', 'big-sky' ),
	},
	{
		actionName: __( 'Design a website', 'big-sky' ),
		description: __( 'for my fitness coaching', 'big-sky' ),
	},
	{
		actionName: __( 'Build a website', 'big-sky' ),
		description: __( 'for my coffee shop', 'big-sky' ),
	},
];

export default function OnboardingSiteSuggestions( {
	onClickSuggestion,
	animationVariants,
} ) {
	const { setChatComponents, setCurrentUserMessage } = useDispatch( aiStore );
	const { userSay } = useChat();
	const { logUserChoice } = useLog();
	const { withJetpackTokenRequest } = useJetpackToken();
	const [ isLoading, setIsLoading ] = useState( false );

	const { currentUserMessage } = useSelect( ( select ) => {
		const ai = select( aiStore );
		return {
			currentUserMessage: ai.currentUserMessage(),
		};
	} );

	const respond = useCallback(
		async ( message ) => {
			recordTracksEvent( 'onboarding_suggestion_click' );
			onClickSuggestion();
			setCurrentUserMessage( message );

			// Wait for animations to finish before sending message
			await setDelay( 1500 );

			logUserChoice( message );
			await withJetpackTokenRequest( () => userSay( message ) );
			setChatComponents( [] );
			setCurrentUserMessage( '' );
		},
		[
			logUserChoice,
			onClickSuggestion,
			setChatComponents,
			setCurrentUserMessage,
			userSay,
			withJetpackTokenRequest,
		]
	);

	const getRandomActions = useCallback(
		( count = 3, previousActions = [] ) => {
			const previousActionStrings = previousActions.map(
				( action ) => `${ action.actionName } ${ action.description }`
			);

			const availableActions = ALL_ACTIONS.filter(
				( action ) =>
					! previousActionStrings.includes(
						`${ action.actionName } ${ action.description }`
					)
			);

			const shuffled = [ ...availableActions ].sort(
				// eslint-disable-next-line no-restricted-syntax
				() => 0.5 - Math.random()
			);
			return shuffled.slice( 0, count ).map( ( action ) => ( {
				...action,
				key: uuidv4(),
				onClick: () => {
					respond( `${ action.actionName } ${ action.description }` );
				},
			} ) );
		},
		[ respond ]
	);

	const [ actions, setActions ] = useState( getRandomActions() );

	useEffect( () => {
		if ( currentUserMessage?.length > 0 && actions?.length > 0 ) {
			setActions( [] );
		}
		if ( ! currentUserMessage?.length > 0 && ! actions?.length > 0 ) {
			setActions( getRandomActions() );
		}
	}, [ currentUserMessage, actions, getRandomActions ] );

	const refreshActions = async () => {
		recordTracksEvent( 'onboarding_suggestion_refresh_click' );
		setIsLoading( true );

		// Wait for 1 second
		await setDelay( 500 );

		setActions( getRandomActions( 3, actions ) );
		setIsLoading( false );
	};

	const suggestionItemAnimationProps = {
		initial: { opacity: 0, y: 10 },
		animate: { opacity: 1, y: 0 },
		exit: { opacity: 0, y: 10 },
		transition: {
			ease: [ 0.42, 0.24, 0.02, 1 ],
			duration: 0.2,
			delay: 0.05,
		},
	};

	return (
		<motion.div { ...animationVariants }>
			<AnimatePresence
				mode="wait"
				onExitComplete={ () => setIsLoading( false ) }
			>
				<div
					className={ classnames( 'big-sky-onboarding__actions', {
						'big-sky-onboarding__actions__is-loading': isLoading,
					} ) }
				>
					<ChatActions
						actions={ actions }
						key={ actions[ 0 ]?.key }
						animationVariants={ suggestionItemAnimationProps }
					/>
					{ actions.length > 0 && (
						<Button
							icon={ reusableBlock }
							onClick={ refreshActions }
							className="big-sky-onboarding__refresh-actions"
							label={ __( 'Refresh', 'big-sky' ) }
							disabled={ isLoading }
						/>
					) }
				</div>
			</AnimatePresence>
		</motion.div>
	);
}
