import showLayoutPickerTool from './tools/show-layout-picker/show-layout-picker-tool.js';
import changeAssistantTool from '@ai/assistants/shared/change-assistant/change-assistant-tool';
import { ASSISTANTS } from '@ai/assistants/constants.js';
import { isForwardedByRouter } from '@ai/assistants/shared/prompts.js';

const layoutToolkit = {
	name: 'layout',
	tools: ( context ) => {
		const tools = [ showLayoutPickerTool() ];

		if (
			context.currentHandoffArea !== 'layouts' &&
			! isForwardedByRouter( context )
		) {
			tools.push( changeAssistantTool( ASSISTANTS.LAYOUT ) );
		}

		return tools;
	},
};

export default layoutToolkit;
