/**
 * External dependencies
 */
import classNames from 'classnames';

/**
 * WordPress dependencies
 */
import { __unstableMotion as motion } from '@wordpress/components';
import {
	store as blockEditorStore,
	BlockPreview,
} from '@wordpress/block-editor';
import { select, useDispatch, useSelect } from '@wordpress/data';
import { store as viewportStore } from '@wordpress/viewport';
import { memo, useCallback, useEffect, useState } from '@wordpress/element';

/**
 * Internal dependencies
 */
import './style.scss';
import usePatterns from '@hooks/use-patterns';
import { animationTiming } from '@utils/constants';
import setDelay from '@utils/set-delay';
import ThinkingMessage from '../thinking-message/index.js';
import { store as aiStore } from '@store';
import { getCanvasIframeElements } from '@utils/canvas-iframe-elements';
import { recordTracksEvent } from '@utils/tracks';

const PatternsPreview = ( {
	item,
	index,
	additionalStyles,
	viewportWidthMax = 1440,
	clientId = null,
	onRendered,
} ) => {
	const MAX_SHOWN_PATTERNS = 4;
	const [ isResolved, setIsResolved ] = useState( false );
	const { setLastCancelableAction } = useDispatch( aiStore );
	const { getSelectedBlockClientId } = useSelect( blockEditorStore );
	const { selectBlock } = useDispatch( blockEditorStore );

	const { getRootBlocksOfPostContent, insertPatterns, replaceTemplatePart } =
		usePatterns();

	const fullLayoutItemSelect = useCallback( async () => {
		// This is a full page layout picker, so we need to update the selected item with all patterns on canvas.
		insertPatterns( item.patterns, null, 'replace', false );

		const { canvasiFrameDocument } = getCanvasIframeElements();
		const el = canvasiFrameDocument.querySelector(
			`.wp-block:first-child`
		);

		await setDelay( animationTiming.scrollWaitTime );
		el.scrollIntoView( {
			behavior: 'instant',
			block: 'start',
		} );
	}, [ insertPatterns, item.patterns ] );

	const singleSectionItemSelect = useCallback( async () => {
		const replaceClientId = clientId ?? getSelectedBlockClientId();

		// This is a single section picker, so we only need to update the pattern in specified block.
		if ( ! replaceClientId ) {
			return;
		}

		// Ensure item and patterns exist and are valid
		if (
			! item?.patterns ||
			! Array.isArray( item.patterns ) ||
			item.patterns.length === 0
		) {
			return;
		}

		const firstPattern = item.patterns[ 0 ];
		if (
			! firstPattern?.blocks ||
			! Array.isArray( firstPattern.blocks ) ||
			firstPattern.blocks.length === 0
		) {
			return;
		}

		// Make sure the pattern doesn't already exist on the page before inserting
		const rootBlocks = getRootBlocksOfPostContent() || [];
		const rootBlockIds = Array.isArray( rootBlocks )
			? rootBlocks.map( ( block ) => block?.clientId ).filter( Boolean )
			: [];

		const rootClientId = firstPattern.blocks[ 0 ]?.clientId;
		if ( ! rootClientId || rootBlockIds.includes( rootClientId ) ) {
			return;
		}

		if (
			firstPattern.category &&
			[ 'header', 'footer' ].includes( firstPattern.category )
		) {
			await replaceTemplatePart(
				firstPattern.category,
				firstPattern.blocks
			);
		} else {
			insertPatterns( item.patterns, replaceClientId, 'replace' );

			// Update the last cancelable action client ID to allow the user to undo the pattern insertion/replacement
			setLastCancelableAction( {
				clientId: rootClientId,
			} );
		}
		selectBlock( rootClientId );
	}, [
		clientId,
		getRootBlocksOfPostContent,
		getSelectedBlockClientId,
		insertPatterns,
		item?.patterns,
		replaceTemplatePart,
		selectBlock,
		setLastCancelableAction,
	] );

	useEffect( () => {
		async function checkIsResolved() {
			if ( ! item.resolvers ) {
				setIsResolved( true );
				onRendered();
				return;
			}

			await Promise.all(
				item.resolvers.map( async ( resolver ) => resolver.resolver )
			);
			setIsResolved( true );
			onRendered();
		}
		if ( ! isResolved ) {
			checkIsResolved();
		}
	}, [ item, isResolved, onRendered ] );
	if ( ! isResolved ) {
		return (
			<div className="big-sky__item-loading big-sky__item-preview">
				<ThinkingMessage />
			</div>
		);
	}

	// Get the first three patterns from the item, with defensive checks
	const shownPatterns = Array.isArray( item?.patterns )
		? item.patterns
				.filter(
					( pattern ) =>
						pattern &&
						Array.isArray( pattern.blocks ) &&
						pattern.blocks.length > 0
				)
				.map( ( pattern ) => pattern.blocks[ 0 ] )
				.slice( 0, MAX_SHOWN_PATTERNS )
		: [];

	const { isViewportMatch } = select( viewportStore );
	let viewportWidth = viewportWidthMax;
	if ( isViewportMatch( '< small' ) ) {
		viewportWidth = 480;
	} else if ( isViewportMatch( '< medium' ) ) {
		viewportWidth = 600;
	} else if ( isViewportMatch( '< large' ) ) {
		viewportWidth = 782;
	} else if ( isViewportMatch( '< wide' ) ) {
		viewportWidth = 960;
	} else if ( isViewportMatch( '< huge' ) ) {
		viewportWidth = 1280;
	}

	const onItemSelect = ( element ) => {
		recordTracksEvent( 'patterns_preview_click', {
			type: item.patterns.length > 1 ? 'full-layout' : 'single-pattern',
		} );
		// remove selected class from all elements
		const elements = document.querySelectorAll( '.big-sky__item-preview' );
		elements.forEach( ( el ) => {
			el.classList.remove( 'selected' );
		} );
		// add selected class to the element
		element.currentTarget.classList.add( 'selected' );

		if ( item.patterns.length > 1 ) {
			fullLayoutItemSelect();
		} else {
			singleSectionItemSelect();
		}
	};

	const className = classNames( 'big-sky__item-preview', {
		selected: index === 0,
		[ 'big-sky__item-preview__id-' + item.id ]: true,
	} );

	return (
		<motion.button
			initial={ {
				opacity: 0.8,
			} }
			whileHover={ {
				opacity: 1,
				scale: 1.05,
				boxShadow: '0px 2px 4px rgba(0,0,0,.05)',
			} }
			className={ className }
			onClick={ onItemSelect }
			data-pattern-id={ item?.patterns?.[ 0 ]?.ID || '' }
		>
			<BlockPreview
				blocks={ shownPatterns }
				additionalStyles={ additionalStyles }
				viewportWidth={ viewportWidth }
			/>
		</motion.button>
	);
};

export default memo( PatternsPreview );
