/**
 * WordPress dependencies
 */
import { registerPlugin } from '@wordpress/plugins';
import { more } from '@wordpress/icons';
import { unlock } from '@utils/lock-unlock';
import { privateApis as blockEditorPrivateApis } from '@wordpress/editor';

const { GlobalStylesProvider } = unlock( blockEditorPrivateApis );

/**
 * Internal dependencies
 */
import './style.scss';
import BigSky from './components/index.js';
import ErrorBoundary from './components/error-boundary.js';
import RetrieveCommands from '@ai/commands/retrieve-commands.js';
import EditorSubscriptions from '@components/editor-subscriptions';
import useHandleReset from './hooks/use-handle-reset.js';

export const AssemblerPlugin = () => {
	const handleReset = useHandleReset();

	return (
		<ErrorBoundary onReset={ handleReset }>
			<GlobalStylesProvider>
				<BigSky />
				<RetrieveCommands />
				<EditorSubscriptions />
			</GlobalStylesProvider>
		</ErrorBoundary>
	);
};

registerPlugin( 'big-sky-assembler', {
	icon: more,
	render: AssemblerPlugin,
} );
