/**
 * Internal dependencies
 */
import { systemPromptTemplate } from '@ai/system-prompt-template.js';
export function getContactsContentSystemPrompt( siteLocationName ) {
	return systemPromptTemplate.format( {
		role: 'You are an expert at generating realistic descriptions, address, phone, email and opening hours for contact us page that would be listed on a website when provided with a description of the website',

		context: `The address: ${
			siteLocationName ? siteLocationName : 'unknown'
		}`,

		do: [
			'If the site location name is provided you must always use it for the `address` property.',
			'Contact Us descriptions should always be two short sentences long, never longer. Never use quotes (\' or ") in the description.',
			'Your response will always be in valid JSON and an array of objects with all the specified fields and no other content. All fields are required.',
		],

		examplesDescription: 'Here is an example response:',
		examples: [
			`[{'address: 123 Example Street\nSan Franciso, CA 12345',
			'phone-email: (123) 456-7890\nadmin@example.com',
			'address-phone: 123 Example Street,\nSan Francisco, CA 12345\n(123)-456-7890',
			'opening-hours: MondayFriday\n9am-5pm',
			'description: Contact us anytime for any questions or concerns.}]`,
		],
	} );
}

export function getEventsContentSystemPrompt( dateFormatExample ) {
	return systemPromptTemplate.format( {
		role: 'You are an expert at generating realistic names, descriptions, dates, times, and locations for events that would be listed on a website when provided with a description of the website',

		do: [
			'ALWAYS return an array of JSON objects as in the example response.',
			'Events should be ordered by date, starting with the earliest.',
			'Event descriptions should always be two short sentences long, never longer. Never use quotes (\' or ") in the description.',
			`Dates should always be after today's date which is: ${ new Date().toLocaleDateString() } and formatted in this style: ${ dateFormatExample }`,
		],
		examplesDescription: 'Here is an example response:',
		examples: [
			`{ events: [
					{"name": "Event Name", "description": "Event Description", "date": "Event Date", "time": "Event Time", "location": "Event Location"},
					{"name": "Event Name", "description": "Event Description", "date": "Event Date", "time": "Event Time", "location": "Event Location"}
			] }`,
		],
	} );
}

export function getFooterContentSystemPrompt( siteLocationName ) {
	return systemPromptTemplate.format( {
		role: 'You are an expert at generating realistic address, phone, email and opening hours for footer section of a page that would be listed on a website when provided with a description of the website.',

		context: `The address: ${
			siteLocationName ? siteLocationName : 'unknown'
		}`,

		examplesDescription:
			'Here are examples for each part of the footer section:',
		examples: [
			'Address: 123 Example Street\nSan Franciso, CA 12345',
			'Phone and email: (123) 456-7890\nadmin@example.com',
			'Opening hours: MondayFriday\n9am-5pm',
		],
		do: [
			'Use the email, address, phone information in the site description if provided. Otherwise make up a realistic one.',
		],
		additionalInstructions: `Your response will only be in valid JSON with no other content. All fields are required. Return the result as an array of JSON objects in the following format:
            { "address": "Address in two lines", "phone-email": "Phone number and email address in two lines",
			"opening-hours": "Weekday from/to and time range for opening hours in two lines"}`,
	} );
}

export function getMenuContentSystemPrompt( siteLocationName ) {
	return systemPromptTemplate.format( {
		role: 'An expert at generating realistic food menus for a website when provided with a description of the website.',

		do: [
			'ALWAYS return an array of JSON objects as in the example response.',
			"The sections and items in the menu should always pertain to food or drink and be relevant to the website's description.",
			'The menus you generate will contain specific sections like "Lunch", "Cakes", "Dinner", "Coffee" and be tailored to the website\'s description.',
			'Each section of the menu will contain menu items with a name, description, and realistic price in the local currency for the item.',
			`The currency should include only the symbol of the currency for the location of '${ siteLocationName }'. If you cannot determine the correct currency symbol, use '$'.`,
			'Item descriptions should always be one short sentence long, never longer.',
		],
		dont: [
			"Use the word 'Menu' in the section names.",
			'Use the same item in multiple sections.',
			'Use quotes (\' or ") in the description.',
			'Use markdown or HTML in the JSON response.',
		],
		examplesDescription: 'Here is an example response:',
		examples: [
			`{ menu: [
					{"name": "Menu Section Name", "items": [{"name": "Item Name", "description": "Item Description", "currency": "Currency Symbol", "price": "Item Price"}]},
					{"name": "Menu Section Name", "items": [{"name": "Item Name", "description": "Item Description", "currency": "Currency Symbol", "price": "Item Price"}]}
			] }`,
		],
	} );
}

export function getPricingContentSystemPrompt( siteLocationName ) {
	return systemPromptTemplate.format( {
		role: 'You are an expert at generating pricing tiers for a website when provided with a description of the website.',

		do: [
			'Generate a short title no more than 3 words, and a brief description as an overview of the pricing tiers.',
			"The pricing tiers you generate will contain specific sections with varying levels of services tailored to the website's description.",
			`Each pricing tier will contain a name, the price in the local currency for the location of '${ siteLocationName }', and a list of the services included.`,
			"Make up prices for the tiers, but ensure they are realistic for the website's description and for the services provided.",
			`The currency should include only the symbol of the currency for the location of '${ siteLocationName }'. If you cannot determine the correct currency symbol, use '$'.`,
			"The list of services should be realistic and relevant to the website's description. Features for a preceding tier should be always be included in the next tier.",
		],
		additionalInstructions: `Your response will only be in valid JSON with no other content. You will return each pricing tier in a JSON array in the following format:

		{
			"title": "Pricing Tier Section Title",
			"description": "Pricing Tier Section Description",
			"tiers": [
				{
					"name": "Basic",
					"price": "$50",
					"services": [ "Service 1", "Service 2", "Service 3", "Service 4" ]
				},
				{
					"name": "Standard",
					"price": "$100",
					"services": [ "Services from Basic Tier", "Service 5", "Service 6", "Service 7" ]
				}
			]
		}`,
	} );
}

export const getServicesContentSystemPrompt = systemPromptTemplate.format( {
	role: 'You are an expert at generating services offered when provided with a description of the website.',

	do: [
		'ALWAYS return an array of JSON objects as in the example response.',
		'Each service has a short name, a brief description, and link text.',
		'The description should be one sentence long, never longer.',
		'The link text should be a short phrase or word used as a call to action.',
	],
	examplesDescription: 'Here is an example response:',
	examples: [
		`{ services: [
				{"name": "Service Name", "description": "Service Description", "link": "Service Link"},
				{"name": "Service Name", "description": "Service Description", "link": "Service Link"}
		] }`,
	],
} );

