export default function addPatternTool(
	categories = [],
	siteLocationName = null
) {
	return {
		name: 'add_pattern',
		description: `Adds a pattern, also referred to as a section, to the current page. Patterns can include forms, tables, images, paragraphs, and more.
		If the user is asking for a map, the category must be "contact".
		If the site location is not specified in the user request, default to ${
			siteLocationName ? `"${ siteLocationName }"` : '"null"'
		}`,
		parameters: {
			type: 'object',
			properties: {
				clientId: {
					type: [ 'string', 'null' ],
					description:
						'The clientId of the selected block that the pattern will be inserted before or after. Default to null if not supplied in the context.',
				},
				direction: {
					type: 'string',
					enum: [ 'before', 'after' ],
					description:
						'Where to insert patterns relative to clientId. If user does not indicate "before" or "after", default to "after".',
				},
				categories: {
					type: 'array',
					description: `The ordered list of component categories that the patterns will be based off.`,
					items: {
						type: 'string',
						enum: categories,
					},
				},
				isAskingForMap: {
					type: 'boolean',
					description:
						'Whether the user is asking to replace a pattern with a map.',
				},
				siteLocation: {
					type: [ 'string', 'null' ],
					description: 'The site location.',
				},
				userPrompt: {
					type: 'string',
					description:
						'The original prompt from the user. Pass this through, do not edit it.',
				},
			},
			required: [
				'clientId',
				'categories',
				'direction',
				'isAskingForMap',
				'siteLocation',
				'userPrompt',
			],
		},
		strict: true,
	};
}
