export const createToolShowFontPicker = ( keywords = [] ) => {
	return {
		name: 'show_font_picker',
		description:
			'Call this if the user asks for anything to do with site fonts. If font keywords are provided, use it as is. If a user requests all fonts, do not return keywords or fonts.',
		parameters: {
			type: 'object',
			properties: {
				fonts: {
					type: [ 'array', 'null' ],
					description:
						'The requested font(s). Default to null if not supplied in the user request.',
					items: {
						type: 'string',
						description: 'A font name.',
					},
				},
				keywords: {
					type: [ 'array', 'null' ],
					description:
						'The requested keywords of the font. Default to null if not supplied in the user request. The more keywords, the more specific the font. If none of the keyword match user request, try to pick a combination that represents the intent.',
					items: {
						type: 'string',
						enum: keywords,
					},
				},
			},
			required: [ 'fonts', 'keywords' ],
		},
		strict: true,
	};
};

export default createToolShowFontPicker;
