/**
 * External dependencies
 */
import classNames from 'classnames';

/**
 * WordPress dependencies
 */
import {
	__unstableAnimatePresence as AnimatePresence,
	__unstableMotion as motion,
	Popover,
} from '@wordpress/components';

import { useDispatch, useSelect } from '@wordpress/data';
import { store as blockEditorStore } from '@wordpress/block-editor';
import { useCallback, useEffect, useRef, useState } from '@wordpress/element';
import { store as editorStore } from '@wordpress/editor';

/**
 * Internal dependencies
 */
import { dockViews } from '@utils/constants';
import { recordTracksEvent } from '@utils/tracks';
import './style.scss';

import Chat from '@components/chat';
import { getCanvasIframeElements } from '@utils/canvas-iframe-elements';
import useCanvas from '@hooks/use-canvas.js';
import { store as aiStore } from '@store';
import useSiteMetadata from '@hooks/use-site-metadata';

import {
	getDefaultChatSettings,
	getDockHeight,
	getDockWidth,
	getNewDockView,
	shouldHideMessageThread,
} from '@components/dock/utils';
import ToolContainer from './tool-container/index.js';
import ButtonBar from './button-bar/index.js';
import { useChat } from '@automattic/big-sky-agents';
import useShowLayoutPickerToolFunctions from '@ai/assistants/layout/tools/show-layout-picker/use-show-layout-picker-tool.js';

export default function Dock() {
	// If there is a selected block, but the user clicks the assistant button instead
	// the block menu then the block context is ignored and site actions are shown.
	const [ useBlockContext, setUseBlockContext ] = useState( true );

	const [ cssClasses, setCssClasses ] = useState( 'big-sky__dock' );
	const [ previousSelectedBlockClientId, setPreviousSelectedBlockClientId ] =
		useState( null );
	const { isOnboarded } = useSiteMetadata().getSiteMetadata();
	const { zoomIn, zoomOut } = useCanvas();
	const [ chatSettings, setChatSettings ] = useState(
		getDefaultChatSettings( isOnboarded )
	);

	const { messages, clearMessages, agentSay } = useChat();
	const { showSiteLayouts } = useShowLayoutPickerToolFunctions();

	const {
		setChatComponents,
		setShowThread,
		setDockView,
		setOnSelectedBlockAction,
		setInputtedFiles,
		setOnInputtedFilesAction,
		setShowEditorWelcome,
		setToolComponent,
		setIsToolContainerVisible,
	} = useDispatch( aiStore );
	const { selectedBlock, editorMode, currentPostId } = useSelect(
		( select ) => {
			const { getSelectedBlock, __unstableGetEditorMode } =
				select( blockEditorStore );
			const { getCurrentPostId } = select( editorStore );
			return {
				selectedBlock: getSelectedBlock(),
				editorMode: __unstableGetEditorMode(),
				currentPostId: getCurrentPostId(),
			};
		}
	);
	const {
		isHandoff,
		isBuildingSite,
		isToolContainerVisible,
		isMaximized,
		showThread,
		pendingToolCallbacks,
		dockView,
		currentToolCall,
		onSelectedBlockAction,
		isEditorReady,
		newPageId,
	} = useSelect( ( select ) => {
		const ai = select( aiStore );

		return {
			isHandoff: ai.getIsHandoff(),
			isBuildingSite: ai.getIsBuildingSite(),
			isToolContainerVisible: ai.isToolContainerVisible(),
			currentToolCall: ai.getCurrentToolCall(),
			showThread: ai.getShowThread(),
			pendingToolCallbacks: select( aiStore ).getPendingToolCallbacks(),
			dockView: ai.getDockView(),
			isMaximized: ai.getIsMaximized(),
			onSelectedBlockAction: ai.getOnSelectedBlockAction(),
			isEditorReady: ai.getIsEditorReady(),
			newPageId: ai.getNewPageId(),
		};
	} );

	const dockContentRef = useRef();

	const resetDockState = useCallback( () => {
		setShowThread( false );
		setChatComponents( [] );
		setOnSelectedBlockAction( {} );
		setInputtedFiles( [] );
		setOnInputtedFilesAction( {} );
		clearMessages();
		setShowEditorWelcome( false );
	}, [
		setShowThread,
		setChatComponents,
		setOnSelectedBlockAction,
		setInputtedFiles,
		setOnInputtedFilesAction,
		clearMessages,
		setShowEditorWelcome,
	] );

	// Set the correct CSS classes on the top level dock container
	// depending on the state of the dock.
	useEffect( () => {
		setCssClasses(
			classNames( 'big-sky__dock', {
				'big-sky__dock__is-menus': dockView === dockViews.MENUS,
				'big-sky__dock__is-chat': dockView === dockViews.CHAT,
			} )
		);

		const { canvasiFrameBody } = getCanvasIframeElements();

		// add class to the iframe body representing the current dock view
		if ( canvasiFrameBody?.classList ) {
			canvasiFrameBody.classList.remove( 'big-sky__dock__is-menus' );
			canvasiFrameBody.classList.remove( 'big-sky__dock__is-chat' );
			canvasiFrameBody.classList.add( `big-sky__dock__is-${ dockView }` );
		}
	}, [ dockView, isOnboarded ] );

	const selectedBlockHasChanged =
		selectedBlock?.clientId &&
		previousSelectedBlockClientId !== selectedBlock?.clientId;

	useEffect( () => {
		if ( selectedBlockHasChanged ) {
			resetDockState();
			setPreviousSelectedBlockClientId( selectedBlock.clientId );
		}
	}, [
		onSelectedBlockAction?.action,
		resetDockState,
		selectedBlock,
		selectedBlockHasChanged,
		editorMode,
	] );

	useEffect( () => {
		const newView = getNewDockView( {
			isOnboarded,
			isHandoff,
			dockView,
			showThread,
			isToolContainerVisible,
			pendingToolCallbacks,
			selectedBlockHasChanged,
			currentToolCall,
		} );

		if ( newView !== dockView ) {
			setDockView( newView );
		}

		if (
			shouldHideMessageThread( {
				showThread,
				newView,
				useBlockContext,
				selectedBlockHasChanged,
			} )
		) {
			setShowThread( false );
		}
	}, [
		isOnboarded,
		isHandoff,
		dockView,
		showThread,
		selectedBlock,
		previousSelectedBlockClientId,
		isToolContainerVisible,
		pendingToolCallbacks,
		setShowThread,
		selectedBlockHasChanged,
		useBlockContext,
		setDockView,
		currentToolCall,
	] );

	const handleOnClickBack = useCallback( () => {
		recordTracksEvent( 'dock_back_button_click' );
		zoomIn();
		setDockView( dockViews.MENUS );
		resetDockState();
	}, [ zoomIn, setDockView, resetDockState ] );

	// Close Dock on Escape ESC
	useEffect( () => {
		if ( ! isOnboarded || isBuildingSite ) {
			return;
		}
		const handleKeyDown = ( event ) => {
			if ( event.key === 'Escape' ) {
				handleOnClickBack();
			}
		};
		document.addEventListener( 'keydown', handleKeyDown );
		return () => {
			document.removeEventListener( 'keydown', handleKeyDown );
		};
	}, [ handleOnClickBack, isOnboarded, isBuildingSite ] );

	// When creating a new page, show the layout picker after navigation
	useEffect( () => {
		if ( ! isEditorReady || newPageId !== currentPostId ) {
			return;
		}
		agentSay(
			showSiteLayouts( {
				callback: ( component ) => {
					zoomOut();
					setToolComponent( component );
					setIsToolContainerVisible( true );
					setDockView( dockViews.COMPLETED );
				},
			} )
		);
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [ currentPostId, isEditorReady, newPageId ] );

	return (
		<>
			<ToolContainer />
			<Popover
				resize={ isMaximized }
				className={ classNames(
					'big-sky__dock__wrapper',
					'big-sky__assistant-ai-actions__popover-content',
					{
						'big-sky__dock__is-onboarding': ! isOnboarded,
						'big-sky__dock__is-maximized': isMaximized,
					}
				) }
			>
				<div className={ cssClasses }>
					<AnimatePresence mode="wait">
						<div className="big-sky__dock__container">
							<motion.div
								layoutRoot
								className="big-sky__dock__animator"
								key={ `${ selectedBlock?.name }-content` }
								ref={ dockContentRef }
								initial={ {
									width: dockContentRef.current?.offsetWidth,
									height: dockContentRef.current
										?.offsetHeight,
									opacity: 0,
								} }
								animate={ {
									opacity: 1,
									width: getDockWidth(
										dockView,
										isMaximized
									),
									height: getDockHeight(
										dockView,
										isMaximized
									),
									transition: {
										type: 'spring',
										mass: 1.1,
										stiffness: 280,
										damping: 28,
									},
								} }
							>
								{ dockView === dockViews.CHAT &&
									! selectedBlockHasChanged && (
										<Chat
											showNavigation={
												messages.length > 0
											}
											navigationTitle={
												chatSettings.title
											}
											onClickBackButton={ () =>
												handleOnClickBack()
											}
											useBlockContext={ useBlockContext }
											defaultPrompt={
												chatSettings.prompt
											}
											isOnboarded={ isOnboarded }
										/>
									) }
								<ButtonBar
									setChatSettings={ setChatSettings }
									setUseBlockContext={ setUseBlockContext }
								/>
							</motion.div>
						</div>
					</AnimatePresence>
				</div>
			</Popover>
		</>
	);
}
