/**
 * WordPress dependencies
 */
import { useSelect } from '@wordpress/data';
import { __ } from '@wordpress/i18n';

/**
 * Internal dependencies
 */
import log from '@utils/log-debug.js';
import { requestJetpackToken } from '@utils/request-jetpack-token.ts';
import { store as aiStore } from '@store';
let lastOdieChat = 0;

const odieSource = 'big-sky';
export default function useAskOdieToolFunctions() {
	const { isDevMode, sessionId } = useSelect( ( select ) => ( {
		isDevMode: select( aiStore ).getIsDevMode(),
		sessionId: select( aiStore ).getSessionId(),
	} ) );

	const ask_odie = async ( parameters, retry = false ) => {
		let output = __(
			"I don't know the answer to that question.",
			'big-sky'
		);

		const jetpackToken = await requestJetpackToken( ! retry );
		if ( jetpackToken ) {
			const odieUrl =
				'https://public-api.wordpress.com/wpcom/v2/odie/chat/wpcom-chat-bigsky' +
				( lastOdieChat ? `/${ lastOdieChat }` : '' );

			try {
				const response = await fetch( odieUrl, {
					method: 'POST',
					headers: {
						Authorization: `Bearer ${ jetpackToken.token }`,
						'Content-Type': 'application/json',
					},
					body: JSON.stringify( {
						// context: ..., @todo add context parameters.
						message: parameters.question,
						source: odieSource,
						source_user: sessionId,
						test: isDevMode,
					} ),
				} );

				const body = await response.json();

				const message = body?.messages?.[ 0 ];
				if ( message?.content ) {
					lastOdieChat = body.chat_id;
					output = message.content;

					if ( message?.context?.sources ) {
						// Dedupe sources based on URL
						const uniqueSources = Object.values(
							message.context.sources.reduce( ( acc, source ) => {
								if ( ! acc[ source.url ] ) {
									acc[ source.url ] = source;
								}
								return acc;
							}, {} )
						);

						output += uniqueSources
							.map(
								( source ) =>
									`\n\n- [${ source.title }](${ source.url })`
							)
							.join( '' );
					}
				}
			} catch ( error ) {
				// retry once with a new token
				if ( ! retry ) {
					return ask_odie( parameters, true );
				}

				log.error( 'Error calling WordPress Expert API: ', error );
			}
		}

		return output;
	};
	return {
		ask_odie,
	};
}
