import { store as editorStore } from '@wordpress/editor';
import { store as coreStore } from '@wordpress/core-data';
import { useEffect, useRef, useState } from '@wordpress/element';
import { useSelect } from '@wordpress/data';
import { recordTracksEvent } from '@utils/tracks';
/**
 * Returns `true` if the post is done saving, `false` otherwise.
 *
 * @return {boolean} Whether the post is done saving.
 */
export default () => {
	const [ isPostSaved, setIsPostSaved ] = useState( false );
	const isPostSavingInProgress = useRef( false );
	const { postType, postId } = useSelect( ( select ) => ( {
		postType: select( editorStore ).getCurrentPostType(),
		postId: select( editorStore ).getCurrentPostId(),
	} ) );
	const { isSavingPost } = useSelect( ( select ) => {
		return {
			isSavingPost: select( coreStore ).isSavingEntityRecord(
				'postType',
				postType,
				postId
			),
		};
	} );

	useEffect( () => {
		if ( isSavingPost && ! isPostSavingInProgress.current ) {
			setIsPostSaved( false );
			isPostSavingInProgress.current = true;
		}
		if ( ! isSavingPost && isPostSavingInProgress.current ) {
			// Code to run after post is done saving.
			setIsPostSaved( true );
			recordTracksEvent( 'post_saved' );
			isPostSavingInProgress.current = false;
		}
	}, [ isSavingPost ] );

	return isPostSaved;
};
