/**
 * WordPress dependencies
 */
import { useCallback, useEffect, useState } from '@wordpress/element';
import { useDispatch } from '@wordpress/data';

/**
 * Internal dependencies
 */
import useSiteMetadata from '@hooks/use-site-metadata';
import { store as aiStore } from '@store';
import { dockViews } from '@utils/constants.js';
import useCanvas from '@hooks/use-canvas';
import routerAssistant from '@ai/assistants/router/index.js';

export function useRouting( { runUrlChangeHandling } = {} ) {
	const { siteTitle, isOnboarded } = useSiteMetadata().getSiteMetadata();
	const [ userNavigating, setUserNavigating ] = useState( false );
	const [ showOnboardingDialog, setShowOnboardingDialog ] = useState( false );
	const [ { currentStep, previousStep }, setSteps ] = useState( () => ( {
		currentStep: new URL( window.location.href ).searchParams.get(
			'ai-step'
		),
		previousStep: null,
	} ) );

	const {
		setAssistant,
		setDockView,
		setHandoffStep,
		setChatComponents,
		setCurrentUserMessage,
		setIsHandoff,
	} = useDispatch( aiStore );
	const { setSiteMetadata } = useSiteMetadata();
	const { zoomOut } = useCanvas();

	const getAiStep = () => {
		return currentStep;
	};

	const setAiStep = useCallback(
		( newStep, forceToHistory = false ) => {
			if ( currentStep !== newStep || forceToHistory ) {
				setSteps( ( state ) => ( {
					previousStep: state.currentStep,
					currentStep: newStep,
				} ) );
				const url = new URL( window.location.href );
				url.searchParams.set( 'ai-step', newStep );
				window.history.pushState( { aiStep: newStep }, '', url );
			}
		},
		[ currentStep, setSteps ]
	);

	const getPreviousAiStep = () => {
		return previousStep || currentStep;
	};

	const handleAiStepChange = useCallback(
		( aiStep ) => {
			if ( aiStep === 'handoff' ) {
				zoomOut();
				setIsHandoff( true );
				setHandoffStep( 0 );
				setDockView( dockViews.HANDOFF );
				if ( ! isOnboarded ) {
					setSiteMetadata( { isOnboarded: true }, true );
				}
			}
			if ( aiStep === 'edit' ) {
				setAssistant( routerAssistant.id );
				setCurrentUserMessage( '' );
				if ( userNavigating ) {
					setChatComponents( [] );
					setDockView( dockViews.MENUS );
				}

				if ( ! isOnboarded ) {
					setSiteMetadata( { isOnboarded: true }, true );
				}
			}
			if (
				aiStep === 'onboarding' &&
				previousStep !== 'onboarding' &&
				isOnboarded
			) {
				setShowOnboardingDialog( true );
			}
		},
		[
			previousStep,
			isOnboarded,
			zoomOut,
			setIsHandoff,
			setHandoffStep,
			setDockView,
			setSiteMetadata,
			setAssistant,
			setCurrentUserMessage,
			userNavigating,
			setChatComponents,
		]
	);

	const handleUrlChange = useCallback( () => {
		const aiStep = new URL( window.location.href ).searchParams.get(
			'ai-step'
		);

		// We need to track if user starts navigating with back button as we handle returning to edit mode
		// differently from this point.
		if ( aiStep === 'handoff' && previousStep === 'edit' ) {
			setUserNavigating( true );
		}

		// If the return toonboarding dialog is open, we don't want to change the step
		if ( aiStep === 'handoff' && showOnboardingDialog ) {
			setAiStep( 'onboarding', true );
			return;
		}

		if ( ( aiStep === 'handoff' || aiStep === 'edit' ) && ! siteTitle ) {
			setAiStep( 'onboarding', true );
			return;
		}
		if ( ! ( previousStep === currentStep && currentStep === aiStep ) ) {
			setSteps( ( state ) => ( {
				previousStep: state.currentStep,
				currentStep: aiStep,
			} ) );
			handleAiStepChange( aiStep, previousStep );
		}
	}, [
		previousStep,
		showOnboardingDialog,
		siteTitle,
		currentStep,
		setAiStep,
		setUserNavigating,
		setSteps,
		handleAiStepChange,
	] );

	useEffect( () => {
		if ( ! runUrlChangeHandling ) {
			return;
		}

		// Initial check
		handleUrlChange();

		// Listen for history changes
		window.addEventListener( 'popstate', handleUrlChange );

		return () => {
			window.removeEventListener( 'popstate', handleUrlChange );
		};
	}, [ runUrlChangeHandling, handleUrlChange ] );

	return {
		setAiStep,
		getAiStep,
		getPreviousAiStep,
		showOnboardingDialog,
		setShowOnboardingDialog,
	};
}
