/**
 * WordPress dependencies
 */
import { useDispatch, useSelect } from '@wordpress/data';
import { useEffect, useRef, useState } from '@wordpress/element';
import { Popover, ToggleControl } from '@wordpress/components';
import { store as blockEditorStore } from '@wordpress/block-editor';
import { store as coreStore } from '@wordpress/core-data';
import { store as editorStore } from '@wordpress/editor';

/**
 * Internal dependencies
 */
import './style.scss';
import { store as aiStore } from '@store';
import imageStyles from '@data/image-styles.json' assert { type: 'json' };
import usePatterns from '@hooks/use-patterns';
import useVariations from '@hooks/use-variations.js';
import useSiteMetadata from '@hooks/use-site-metadata';
import { useChat } from '@automattic/big-sky-agents';
import useResetSite from '@hooks/use-reset-site';
import useBuildSiteTool from '@ai/assistants/spec/tools/build-site/use-build-site-tool';

const ResetSiteMetadataButton = () => {
	const [ isResetting, setIsResetting ] = useState( false );
	const { resetSite } = useResetSite();

	return (
		<button
			className="button button-primary"
			disabled={ isResetting }
			onClick={ async () => {
				setIsResetting( true );
				resetSite();
			} }
		>
			{ isResetting ? `Resetting...` : `Reset remote site metadata` }
		</button>
	);
};

const StoreConversationToggle = () => {
	const { isStoringConversation, setStoreConversation } = useChat();

	return (
		<ToggleControl
			label={ 'Store conversation in OpenAI Evals' }
			checked={ isStoringConversation }
			onChange={ ( value ) => {
				setStoreConversation( value );
			} }
		/>
	);
};

const GeneratePatternsToggle = () => {
	const [ isGeneratingPatterns, setIsGeneratingPatternsState ] = useState(
		() => {
			// Get initial value from localStorage
			const stored = window.localStorage.getItem(
				'isGeneratingPatterns'
			);
			return stored === 'true';
		}
	);

	const setIsGeneratingPatterns = ( value ) => {
		// Update both state and localStorage
		setIsGeneratingPatternsState( value );
		window.localStorage.setItem( 'isGeneratingPatterns', value );
	};

	return (
		<ToggleControl
			label={ 'Generate Patterns' }
			checked={ isGeneratingPatterns }
			onChange={ ( value ) => {
				setIsGeneratingPatterns( value );
			} }
		/>
	);
};

const QuickOnboardingButton = ( { setShowDebug } ) => {
	const { buildSiteTask } = useBuildSiteTool();

	const [ staticSiteMetadata, setStaticSiteMetadata ] = useState( null );
	const { isEditorIframeLoaded } = useSelect( ( select ) => ( {
		isEditorIframeLoaded: select( aiStore ).getIsEditorIframeLoaded(),
	} ) );

	useEffect( () => {
		if ( process.env.NODE_ENV === 'development' ) {
			// read from local storage
			// eslint-disable-next-line no-undef
			const siteMetadata = localStorage.getItem( 'siteMeta' );
			if ( siteMetadata ) {
				setStaticSiteMetadata( JSON.parse( siteMetadata ) );
			}
		}
	}, [] );

	return (
		<button
			className="button button-primary"
			onClick={ async () => {
				buildSiteTask( staticSiteMetadata, true );
				setShowDebug( false );
			} }
			disabled={ ! staticSiteMetadata || ! isEditorIframeLoaded }
		>
			{ `Quick Onboarding` }
		</button>
	);
};

const SaveCurrentBuild = () => {
	const siteMetadata = useSiteMetadata().getSiteMetadata();
	const buildDesigns = {
		colors: [ siteMetadata.currentColor ],
		fonts: [ siteMetadata.currentFont ],
	};

	return (
		<button
			className="button button-primary"
			onClick={ async () => {
				// eslint-disable-next-line no-undef
				localStorage.setItem(
					'siteMeta',
					JSON.stringify( siteMetadata )
				);
				// eslint-disable-next-line no-undef
				localStorage.setItem(
					'buildDesigns',
					JSON.stringify( buildDesigns )
				);
			} }
		>
			{ `Save Current Build` }
		</button>
	);
};

const ModifyBuildForm = ( { setShowModifyBuildForm, setShowDebug } ) => {
	const { resetCachedContent } = useDispatch( aiStore );
	const { buildSiteTask } = useBuildSiteTool();

	const formRef = useRef();

	const saveFormData = () => {
		const form = formRef.current;
		const formData = {
			siteType: form.querySelector( '[name="siteType"]' ).value,
			siteTitle: form.querySelector( '[name="siteTitle"]' ).value,
			siteDescription: form.querySelector( '[name="siteDescription"]' )
				.value,
			topic: form.querySelector( '[name="topic"]' ).value,
			currentColor: form.querySelector( '[name="currentColor"]' ).value,
			currentFont: form.querySelector( '[name="currentFont"]' ).value,
			defaultImageStyle: form.querySelector(
				'[name="defaultImageStyle"]'
			).value,
			siteLocation: {
				name: form.querySelector( '[name="locationName"]' ).value,
			},
			patterns: Array.from(
				form.querySelectorAll( '[name="pattern"]' )
			).map( ( select ) => ( {
				id: parseInt( select.value ),
				category:
					select.options[ select.selectedIndex ].dataset.category,
			} ) ),
		};
		const buildDesigns = {
			colors: [ formData.currentColor ],
			fonts: [ formData.currentFont ],
		};

		// eslint-disable-next-line no-undef
		localStorage.setItem( 'siteMeta', JSON.stringify( formData ) );
		// eslint-disable-next-line no-undef
		localStorage.setItem( 'buildDesigns', JSON.stringify( buildDesigns ) );
		return formData;
	};

	const handleSave = () => {
		const newSiteMetadata = saveFormData();

		// if clear cached content is checked, clear cached content
		if (
			formRef.current.querySelector( '[name="resetCachedContent"]' )
				.checked
		) {
			resetCachedContent();
		}

		// if rebuild site is checked, rebuild site
		if ( formRef.current.querySelector( '[name="rebuildSite"]' ).checked ) {
			buildSiteTask( newSiteMetadata, true );
			setShowDebug( false );
		}
	};

	return (
		<div ref={ formRef }>
			<h2>Modify Build</h2>
			<SiteData editable />
			<div className="big-sky__ai-debug__checkboxes">
				<div>
					<input
						type="checkbox"
						name="resetCachedContent"
						defaultChecked={ false }
					/>
					<label htmlFor="resetCachedContent">
						Reset cached content
					</label>
				</div>
				<div>
					<input
						type="checkbox"
						name="rebuildSite"
						defaultChecked={ true }
					/>
					<label htmlFor="rebuildSite">Rebuild site</label>
				</div>
			</div>
			<div className="big-sky__ai-debug__buttons">
				<button
					className="button button-primary"
					onClick={ handleSave }
				>
					Save
				</button>
				<button
					className="button"
					onClick={ () => setShowModifyBuildForm( false ) }
				>
					Close
				</button>
			</div>
		</div>
	);
};

const ModifyBuild = ( { setShowModifyBuildForm } ) => {
	return (
		<button
			className="button"
			onClick={ async () => {
				setShowModifyBuildForm( true );
			} }
		>
			{ `Modify Build` }
		</button>
	);
};

function AssemblerDebug( { setShowDebug } ) {
	const [ showModifyBuildForm, setShowModifyBuildForm ] = useState( false );
	const { saveEntityRecord } = useDispatch( coreStore );
	const { isOnboarded } = useSiteMetadata().getSiteMetadata();
	const {
		sessionId,
		chatUsage,
		chat35Usage,
		anthropicUsage,
		isBuildingSite,
		isHandoff,
	} = useSelect( ( select ) => ( {
		chatUsage: select( aiStore ).getOpenAIChatUsage(),
		chat35Usage: select( aiStore ).getOpenAIChat35Usage(),
		anthropicUsage: select( aiStore ).getAnthropicUsage(),
		sessionId: select( aiStore ).getSessionId(),
		isBuildingSite: select( aiStore ).getIsBuildingSite(),
		isHandoff: select( aiStore ).getIsHandoff(),
	} ) );

	return (
		<Popover className="big-sky__ai-debug">
			{ showModifyBuildForm ? (
				<ModifyBuildForm
					setShowModifyBuildForm={ setShowModifyBuildForm }
					setShowDebug={ setShowDebug }
				/>
			) : (
				<>
					<h2>Site Data</h2>
					<SiteData />

					<h2>Model Usage</h2>
					<table>
						<thead>
							<tr>
								<th></th>
								<th>Prompt</th>
								<th>Completion</th>
								<th>Total</th>
								<th>Reqs</th>
								<th>Cost</th>
							</tr>
						</thead>
						<tbody>
							<UsageRow usageType="GPT 4" usage={ chatUsage } />
							<UsageRow
								usageType="GPT 3.5"
								usage={ chat35Usage }
							/>
							<UsageRow
								usageType="Claude 3"
								usage={ {
									promptTokens: anthropicUsage?.inputTokens,
									completionTokens:
										anthropicUsage?.outputTokens,
									totalTokens: anthropicUsage?.totalTokens,
									totalRequests:
										anthropicUsage?.totalRequests,
									estimatedCost:
										anthropicUsage?.estimatedCost,
								} }
							/>
						</tbody>
					</table>

					<>
						<h2>Tools</h2>
						<Tools />
					</>

					<div className="big-sky__ai-debug__buttons">
						<StoreConversationToggle />
						<GeneratePatternsToggle />
						<ResetSiteMetadataButton />
						{ ! isOnboarded && (
							<QuickOnboardingButton
								setShowDebug={ setShowDebug }
							/>
						) }
						{ isOnboarded && ( isHandoff || ! isBuildingSite ) && (
							<>
								<SaveCurrentBuild />
								<ModifyBuild
									setShowModifyBuildForm={
										setShowModifyBuildForm
									}
								/>
							</>
						) }
						{ window?.bigSkyInitialState?.lastPayload && (
							<button
								className="button"
								onClick={ async () => {
									const saved = await saveEntityRecord(
										'root',
										'site',
										{
											big_sky_last_site_design_payload:
												JSON.stringify(
													window.bigSkyInitialState
														.lastPayload
												),
										}
									);
									if (
										saved.big_sky_last_site_design_payload
									) {
										// eslint-disable-next-line
										alert(
											'Payload saved. Now you can run evals with npm run evals'
										);
									}
								} }
							>
								Store Last Payload
							</button>
						) }
						{ sessionId && (
							<>
								<a
									href={ `https://mc.a8c.com/big-sky/session/?session_uuid=${ sessionId }` }
									target="_blank"
									rel="noreferrer"
									className="button"
								>
									Session Log
								</a>
							</>
						) }
					</div>
				</>
			) }
		</Popover>
	);
}

// Component for each usage row
function UsageRow( { usageType, usage } ) {
	return (
		<tr>
			<td>{ usageType }</td>
			<td>{ usage?.promptTokens || 0 }</td>
			<td>{ usage?.completionTokens || 0 }</td>
			<td>{ usage?.totalTokens || 0 }</td>
			<td>{ usage?.totalRequests || 0 }</td>
			<td>${ ( usage?.estimatedCost || 0 ).toFixed( 2 ) }</td>
		</tr>
	);
}

function Palette( { paletteColors } ) {
	return (
		<>
			{ paletteColors.map( ( { color, slug } ) => (
				<span
					className="big-sky__ai-debug__swatch"
					key={ slug }
					style={ { backgroundColor: color } }
				></span>
			) ) }
		</>
	);
}

function SiteData( { editable = false } ) {
	const {
		currentColor,
		currentFont,
		siteType,
		topic,
		siteDescription,
		siteLocation,
		siteTitle,
		defaultImageStyle,
		patterns: savedPatterns,
		colorRequest,
	} = useSiteMetadata().getSiteMetadata();
	const { remotePatterns } = useSelect( ( select ) => {
		const store = select( aiStore );
		const userLocale = store.getUserLocale();

		return {
			remotePatterns: store.getRemotePatterns( userLocale ),
		};
	} );
	const { currentPostId } = useSelect( ( select ) => ( {
		currentPostId: select( editorStore ).getCurrentPostId(),
	} ) );

	// Backwards compatibility for savedPatterns, which wa an array in the past but is now an object.
	let patterns;
	if ( Array.isArray( savedPatterns ) ) {
		patterns = savedPatterns;
	} else {
		patterns = savedPatterns[ currentPostId ] || [];
	}

	// Get paletteColors from `useVariations()`. The data from `useStyles()`
	// is the currently set palette, which can sometimes be different than
	// the palette defined by the selected variation.
	const { getColorVariations, getColorVariationByTitle, getFontVariations } =
		useVariations();
	const variation = getColorVariationByTitle( currentColor );
	const paletteColors = variation?.settings?.color?.palette?.theme ?? [];

	const getLocationCoordinates = () => {
		if ( siteLocation && siteLocation.coordinates ) {
			return `${ siteLocation.coordinates[ 0 ] }, ${ siteLocation.coordinates[ 1 ] }`;
		}
	};
	const additionalContext = useSelect( ( select ) =>
		select( aiStore ).getAdditionalContext()
	);

	return (
		<div className="big-sky__ai-debug__site-data">
			<table>
				<tbody>
					<tr>
						<td>Site Type</td>
						<td>
							{ editable ? (
								<input
									name="siteType"
									defaultValue={ siteType }
								/>
							) : (
								siteType
							) }
						</td>
					</tr>
					<tr>
						<td>Title</td>
						<td>
							{ editable ? (
								<input
									name="siteTitle"
									defaultValue={ siteTitle }
								/>
							) : (
								siteTitle
							) }
						</td>
					</tr>
					<tr>
						<td>Description</td>
						<td>
							{ editable ? (
								<input
									name="siteDescription"
									defaultValue={ siteDescription }
								/>
							) : (
								siteDescription
							) }
						</td>
					</tr>
					<tr>
						<td>Topic</td>
						<td>
							{ editable ? (
								<input name="topic" defaultValue={ topic } />
							) : (
								topic !== 'abstract' && topic
							) }
						</td>
					</tr>
					<tr>
						<td>Color Request</td>
						<td>{ colorRequest && colorRequest }</td>
					</tr>
					<tr>
						<td>Color Palette</td>
						<td>
							{ editable ? (
								<select
									name="currentColor"
									defaultValue={ currentColor }
								>
									{ getColorVariations()
										.sort( ( a, b ) =>
											a.title.localeCompare( b.title )
										)
										.map( ( { title } ) => (
											<option
												key={ title }
												value={ title }
											>
												{ title }
											</option>
										) ) }
								</select>
							) : (
								<>
									{ currentColor }{ ' ' }
									<Palette paletteColors={ paletteColors } />
								</>
							) }
						</td>
					</tr>
					<tr>
						<td>Font</td>
						<td>
							{ editable ? (
								<select
									name="currentFont"
									defaultValue={ currentFont }
								>
									{ getFontVariations()
										.sort( ( a, b ) =>
											a.title.localeCompare( b.title )
										)
										.map( ( { title } ) => (
											<option
												key={ title }
												value={ title }
											>
												{ title }
											</option>
										) ) }
								</select>
							) : (
								currentFont
							) }
						</td>
					</tr>
					<tr>
						<td>Location Name</td>
						<td>
							{ editable ? (
								<input
									name="locationName"
									defaultValue={ JSON.stringify(
										siteLocation?.name
									) }
								/>
							) : (
								JSON.stringify( siteLocation?.name )
							) }
						</td>
					</tr>
					{ ! editable && (
						<tr>
							<td>Location Coordinates</td>
							<td>{ getLocationCoordinates() }</td>
						</tr>
					) }
					<tr>
						<td>Image Style</td>
						<td>
							{ editable ? (
								<select
									name="defaultImageStyle"
									defaultValue={
										defaultImageStyle || 'photographic'
									}
								>
									{ imageStyles.map( ( style ) => (
										<option key={ style } value={ style }>
											{ style }
										</option>
									) ) }
								</select>
							) : (
								defaultImageStyle
							) }
						</td>
					</tr>
					<tr>
						<td>Patterns</td>
						<td>
							{ editable
								? patterns?.map( ( pattern ) => {
										// filter remote patterns by category, build options
										const options = remotePatterns
											.filter(
												( remotePattern ) =>
													remotePattern.category ===
													pattern.category
											)
											.sort( ( a, b ) =>
												a.title.localeCompare( b.title )
											)
											.map( ( remotePattern ) => (
												<option
													key={ remotePattern.ID }
													value={ remotePattern.ID }
													data-category={
														remotePattern.category
													}
												>
													{ remotePattern.title } ({ ' ' }
													{ remotePattern.ID } )
												</option>
											) );

										return (
											<select
												name="pattern"
												defaultValue={ pattern.id }
												key={ pattern.id }
											>
												{ options }
											</select>
										);
								  } )
								: patterns?.map( ( pattern ) => (
										<div key={ pattern.id }>
											{ pattern.id } &middot;{ ' ' }
											{ pattern.category }
										</div>
								  ) ) }
						</td>
					</tr>
					{ additionalContext &&
						Object.keys( additionalContext ).map( ( key ) => (
							<tr key={ key }>
								<td>Additional Context: { key }</td>
								<td>
									{ JSON.stringify(
										additionalContext[ key ]
									) }
								</td>
							</tr>
						) ) }
				</tbody>
			</table>
		</div>
	);
}

function Tools() {
	const { patterns } = useSelect( ( select ) => {
		const store = select( aiStore );
		const userLocale = store.getUserLocale();

		return {
			patterns: store.getRemotePatterns( userLocale ),
		};
	} );

	const { selectedBlock } = useSelect( ( select ) => {
		const { getSelectedBlock } = select( blockEditorStore );
		return {
			selectedBlock: getSelectedBlock(),
		};
	} );

	const selectedPatternId =
		selectedBlock?.attributes?.metadata?.remotePatternId || 'None selected';

	const { convertToBlocks, insertPatterns } = usePatterns();
	const [ insertPatternId, setInsertPatternId ] = useState( null );
	const sortedPatternsById = patterns.sort( ( a, b ) => a.ID - b.ID );

	const handleInsertPattern = () => {
		const patternToInsert = sortedPatternsById.find(
			( pattern ) => pattern.ID === parseInt( insertPatternId )
		);
		const blocks = convertToBlocks( patternToInsert.html );
		patternToInsert.blocks = blocks;
		insertPatterns( [ patternToInsert ] );
	};

	const { isOnboarded } = useSiteMetadata().getSiteMetadata();

	useEffect( () => {
		setInsertPatternId( sortedPatternsById[ 0 ]?.ID );
	}, [ sortedPatternsById ] );

	return (
		<div className="big-sky__ai-debug__tools">
			{ isOnboarded && (
				<div className="big-sky__ai-debug__tools__insert-pattern">
					<p>Selected Pattern: { selectedPatternId }</p>

					<p>Insert Pattern:</p>
					<div className="big-sky__ai-debug__flex">
						<select
							onChange={ ( event ) =>
								setInsertPatternId( event.target.value )
							}
						>
							{ sortedPatternsById.map( ( pattern ) => (
								<option key={ pattern.ID } value={ pattern.ID }>
									{ pattern.ID } &middot; { pattern.title }
								</option>
							) ) }
						</select>
						<button
							className="button button-primary"
							onClick={ handleInsertPattern }
						>
							Insert
						</button>
					</div>
				</div>
			) }
		</div>
	);
}

const AssemblerDebugWrapper = () => {
	const [ showDebug, setShowDebug ] = useState( false );

	useEffect( () => {
		const handleKeyDown = ( event ) => {
			if ( event.ctrlKey && event.key === 'd' ) {
				setShowDebug( ! showDebug );
			}
		};

		document.addEventListener( 'keydown', handleKeyDown );

		return () => {
			document.removeEventListener( 'keydown', handleKeyDown );
		};
	}, [ showDebug ] );

	return showDebug ? <AssemblerDebug setShowDebug={ setShowDebug } /> : null;
};

export default AssemblerDebugWrapper;
