/**
 * WordPress dependencies
 */
import { useDispatch, useSelect } from '@wordpress/data';

/**
 * Internal dependencies
 */
import FontVariationPicker from '@components/font-variation-picker';
import { store as aiStore } from '@store';
import useCanvas from '@hooks/use-canvas';
import useSiteMetadata from '@hooks/use-site-metadata';
import useStyles from '@hooks/use-styles';
import useVariations from '@hooks/use-variations';
import { dockViews, MAX_VARIATIONS_TO_SHOW } from '@utils/constants';
import { calculateFontVariationsToShow } from '@utils/fonts';

export default function useFontToolFunctions() {
	const { zoomIn } = useCanvas();
	const { getCurrentFont } = useSelect( aiStore );
	const { getFontVariationByTitle, getFontVariations } = useVariations();
	const { setDockView, setIsToolContainerVisible, setLastCancelableAction } =
		useDispatch( aiStore );
	const { setSiteMetadata } = useSiteMetadata();
	const { currentFont: currentFontMeta } =
		useSiteMetadata().getSiteMetadata();
	const { saveStylesEntityRecord, setStyles } = useStyles();

	const cancelShowFontPicker = ( variationName ) => {
		setDockView( dockViews.MENUS );
		setStyles( { font: variationName } );
		setIsToolContainerVisible( false );
		zoomIn();
	};

	const validateArguments = ( args ) => {
		if ( ! args ) {
			return false;
		}

		const { keywords } = args;

		if ( ! keywords ) {
			return false;
		}

		return true;
	};

	const doneShowFontPicker = () => {
		saveStylesEntityRecord();
		setSiteMetadata( {}, true ); // persist the current site metadata
		setDockView( dockViews.MENUS );
		setIsToolContainerVisible( false );
		zoomIn();
	};

	const showFontPicker = ( parameters ) => {
		const currentFont = getCurrentFont() || currentFontMeta;
		const { keywords = [], fonts = [], callback = () => {} } = parameters;

		const { fontVariationsToShow, message } = calculateFontVariationsToShow(
			{
				chosenFonts: fonts,
				keywords,
				currentFont,
				getFontVariations,
				getFontVariationByTitle,
			}
		);

		setLastCancelableAction( {
			action: 'show_font_picker',
			originalVariationName: currentFont,
		} );

		callback(
			<FontVariationPicker
				fontVariations={ fontVariationsToShow }
				key="font-picker"
				maxFontsToShow={ MAX_VARIATIONS_TO_SHOW }
			/>,
			message
		);
	};

	return {
		showFontPicker,
		cancelShowFontPicker,
		doneShowFontPicker,
		validateArguments,
	};
}
