/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { useDispatch, useRegistry, useSelect } from '@wordpress/data';
import { store as blockEditorStore } from '@wordpress/block-editor';
import { store as coreStore } from '@wordpress/core-data';
import { store as editorStore } from '@wordpress/editor';
import { createBlock, rawHandler, serialize } from '@wordpress/blocks';
import { useCallback, useEffect } from '@wordpress/element';

/**
 * Internal dependencies
 */
import useAfterSave from '@hooks/use-after-save';
import useImages from '@hooks/use-images.js';
import useBlocks from '@hooks/use-blocks';
import useIsHomePage from '@hooks/use-is-home-page';
import useSiteMetadata from '@hooks/use-site-metadata.js';
import useStyles from '@hooks/use-styles';
import { store as aiStore } from '@store';
import useReplaceContent from '@ai/tasks/replace-content/use-replace-content.js';
import { unlock } from '@utils/lock-unlock';
import uuidv4 from '@utils/uuid';
import {
	createDummyPosts,
	createDummyPostTag,
	getDummyPosts,
	getDummyPostTag,
} from '@utils/dummy-posts';
import patternCategories from '@data/pattern-categories.js';
import styleVariations from '@data/section-style-variations';
import usePatternContent from './use-pattern-content.js';
import { getMediaColor } from '@utils/images';
import log from '@utils/log-debug.js';
import { patternKeys, sectionStyleRegex } from '@utils/constants';
import { getContrastingThemeColor } from '@utils/colors';

const DEFAULT_NUM_CONCEPTS = 6;

function getImageBlocksWithIds( blocks ) {
	const result = [];

	blocks.forEach( ( block ) => {
		if (
			block.name === 'core/image' &&
			block.attributes.url &&
			block.attributes.id
		) {
			result.push( block );
		}
		result.push( ...getImageBlocksWithIds( block.innerBlocks ) );
	} );

	return result;
}

export default function usePatterns() {
	const { ITEMS_KEY, ITEM_KEY, SECTIONS_KEY, SECTION_KEY } = patternKeys;
	const {
		insertBlock,
		replaceBlock,
		removeBlocks,
		updateBlockAttributes,
		setBlockEditingMode,
		updateBlockListSettings,
	} = useDispatch( blockEditorStore );
	const { editEntityRecord } = useDispatch( coreStore );
	const { setBlockRemovalRules } = unlock( useDispatch( blockEditorStore ) );
	const {
		getBlocks,
		getBlockIndex,
		getBlocksByName,
		getBlockAttributes,
		getBlock,
		getSelectedBlockClientId,
		getBlockListSettings,
	} = useSelect( blockEditorStore );
	const {
		getFirstChildBlockByName,
		getParentRootPatternBlock,
		getTemplatePartClientID,
		setMapLocationInBlocks,
		fixInvalidBlock,
	} = useBlocks();
	const { getBlockRemovalRules } = unlock( useSelect( blockEditorStore ) );
	const { generateLogoWithAI, getStockImageURL, setImagesInBlocks } =
		useImages();
	const { getRemotePatterns, userLocale } = useSelect( ( select ) => {
		const store = select( aiStore );
		const theUserLocale = store.getUserLocale();

		// NOTE: this is a workaround to get the remote patterns from the store
		store.getRemotePatterns( theUserLocale );
		return {
			getRemotePatterns: store.getRemotePatterns,
			userLocale: theUserLocale,
		};
	} );
	const { getCurrentTheme } = useSelect( coreStore );

	const isAfterSave = useAfterSave();
	const isHomePage = useIsHomePage();
	const registry = useRegistry();
	const { generateDummyPostContent, replaceContent } = useReplaceContent();
	const { updatePatternImages, getPatternImages } = usePatternContent();
	const { getSiteMetadata, setSiteMetadata } = useSiteMetadata();
	const { themeColors } = useStyles();
	const { currentPostId } = useSelect( ( select ) => ( {
		currentPostId: select( editorStore ).getCurrentPostId(),
	} ) );

	const selectedStyleVariationSet =
		styleVariations[
			// eslint-disable-next-line no-restricted-syntax
			Math.floor( Math.random() * styleVariations.length )
		];

	const getRootBlocksOfPostContent = useCallback( () => {
		const postContentBlockId = getBlocksByName( 'core/post-content' )[ 0 ];
		// get the current top blocks underneath it
		return getBlocks( postContentBlockId );
	}, [ getBlocksByName, getBlocks ] );

	const getPatternById = ( patternId ) => {
		const _patterns = getRemotePatterns( userLocale );
		return _patterns.find( ( pattern ) => pattern.ID === patternId );
	};

	const setDefaultEditMode = useCallback(
		( blocks ) => {
			registry.batch( () => {
				blocks.forEach( ( block ) => {
					setBlockEditingMode( block.clientId, 'default' );
					if ( block.attributes?.metadata?.remotePatternId ) {
						// Turn off templateLock for top-level pattern blocks.
						const blockListSettings = getBlockListSettings(
							block.clientId
						);
						updateBlockListSettings( block.clientId, {
							...blockListSettings,
							templateLock: false,
						} );
					}
					if ( block.innerBlocks?.length > 0 ) {
						setDefaultEditMode( block.innerBlocks );
					}
				} );
			} );
		},
		[
			getBlockListSettings,
			registry,
			setBlockEditingMode,
			updateBlockListSettings,
		]
	);

	const syncSiteMetadataPatterns = useCallback(
		( params = {} ) => {
			// parallel arrays of postIds and blocks
			const postIds = params.ids || [ currentPostId ];
			const blockArrays = params.blocks || [
				getRootBlocksOfPostContent(),
			];
			const persist = params.persist || false;

			// filter out empty block arrays
			const filteredBlockArrays = blockArrays.filter(
				( blocks ) => blocks.length > 0
			);
			// if all block arrays are empty, bail
			// this can happen before navigating to a new page
			if ( filteredBlockArrays.length === 0 ) {
				return;
			}

			// Create patterns object to store patterns for all posts
			const newPatternsForPosts = {};
			const currentHeaderBlocks = getBlocks(
				getTemplatePartClientID( 'header' )
			);
			const currentHeaderPattern = currentHeaderBlocks?.[ 0 ];
			const currentFooterBlocks = getBlocks(
				getTemplatePartClientID( 'footer' )
			);
			const currentFooterPattern = currentFooterBlocks?.[ 0 ];

			postIds.forEach( ( postId, index ) => {
				const blocks = blockArrays[ index ] || [];

				const rootBlocks = [
					currentHeaderPattern,
					...blocks,
					currentFooterPattern,
				];

				// Create new patterns data for current post
				const newPatterns = rootBlocks
					.filter( ( block ) => block?.attributes?.metadata )
					.map( ( block ) => ( {
						id: block.attributes?.metadata?.remotePatternId || '',
						// category and slug are the same, different codes use different names
						category:
							block.attributes?.metadata?.patternCategory || '',
						slug: block.attributes?.metadata?.patternCategory || '',
						reason: block.attributes?.metadata?.reason || '',
					} ) );

				// Add patterns for this post to the patterns object
				newPatternsForPosts[ postId ] = newPatterns;
			} );

			// Check if existingPatterns is an object, if not then create an empty object
			const { patterns: savedPatterns } = getSiteMetadata();
			const patternsObject =
				typeof savedPatterns === 'object' &&
				! Array.isArray( savedPatterns )
					? savedPatterns
					: {};

			// Merge patterns, preserving existing reasons if not overwritten
			Object.keys( newPatternsForPosts ).forEach( ( postId ) => {
				const existingPatterns = patternsObject[ postId ] || [];
				const newPatterns = newPatternsForPosts[ postId ];

				newPatternsForPosts[ postId ] = newPatterns.map(
					( newPattern ) => {
						const existingPattern = existingPatterns.find(
							( pattern ) => pattern.id === newPattern.id
						);

						// Keep existing reason if new pattern doesn't have one
						if ( existingPattern?.reason && ! newPattern.reason ) {
							return {
								...newPattern,
								reason: existingPattern.reason,
							};
						}

						return newPattern;
					}
				);
			} );
			setSiteMetadata(
				{
					patterns: {
						...patternsObject,
						...newPatternsForPosts,
					},
				},
				persist
			);
		},
		[
			currentPostId,
			getBlocks,
			getRootBlocksOfPostContent,
			getSiteMetadata,
			getTemplatePartClientID,
			setSiteMetadata,
		]
	);

	const insertPattern = useCallback(
		( pattern, index, rootClientId = null, updateSelection = true ) => {
			return insertBlock( pattern, index, rootClientId, updateSelection );
		},
		[ insertBlock ]
	);

	const buildTemplatePartId = useCallback(
		( templatePartName ) => {
			return `${ getCurrentTheme().stylesheet }//${ templatePartName }`;
		},
		[ getCurrentTheme ]
	);

	const editTemplatePart = useCallback(
		async ( templatePartId, blocks ) => {
			await editEntityRecord(
				'postType',
				'wp_template_part',
				templatePartId,
				{ blocks, content: serialize( blocks ) }
			);
			syncSiteMetadataPatterns();
		},
		[ editEntityRecord, syncSiteMetadataPatterns ]
	);

	const replacePattern = useCallback(
		async ( clientId, pattern ) => {
			// if the pattern is a header or footer, replace the entity record instead of the block
			const patternCategory =
				pattern.attributes?.metadata?.patternCategory;
			if ( [ 'header', 'footer' ].includes( patternCategory ) ) {
				const blocks = [ pattern ];
				await editTemplatePart(
					buildTemplatePartId( patternCategory ),
					blocks
				);
			} else {
				replaceBlock( clientId, pattern );
				syncSiteMetadataPatterns();
			}
		},
		[
			buildTemplatePartId,
			editTemplatePart,
			replaceBlock,
			syncSiteMetadataPatterns,
		]
	);

	const replaceTemplatePart = async ( templatePartName, blocks ) => {
		const templatePartId = buildTemplatePartId( templatePartName );
		await editTemplatePart( templatePartId, blocks );
	};

	const insertPatterns = useCallback(
		( patternList, clientId, direction, updateSelection = true ) => {
			// Temporarily disable block removal rules to prevent modal from showing when switching layout
			const blockRemovalRules = getBlockRemovalRules();
			setBlockRemovalRules( [] );

			const clientIds = [];
			const insertionId = getBlocksByName( 'core/post-content' );
			const insertionBlockId = insertionId[ 0 ];
			let firstPattern = null;

			const shouldReplace = direction === 'replace';

			if ( shouldReplace && ! clientId ) {
				const blocksToReplace = getBlocks( insertionBlockId ).map(
					( b ) => b.clientId
				);
				removeBlocks( blocksToReplace, updateSelection );
			}

			patternList.forEach( ( pattern, patternIndex ) => {
				const patternRootBlock = pattern.blocks[ 0 ];

				if ( ! firstPattern ) {
					firstPattern = patternRootBlock;
				}

				if ( shouldReplace && clientId ) {
					replacePattern( clientId, patternRootBlock );
				} else if ( insertionBlockId ) {
					if ( clientId && direction === 'before' ) {
						insertPattern(
							patternRootBlock,
							getBlockIndex( clientId ),
							insertionBlockId,
							updateSelection
						);
					} else if ( clientId ) {
						insertPattern(
							patternRootBlock,
							getBlockIndex( clientId ) + patternIndex + 1,
							insertionBlockId,
							updateSelection
						);
					} else {
						insertPattern(
							patternRootBlock,
							undefined,
							insertionBlockId,
							updateSelection
						);
					}
				} else {
					insertPattern(
						patternRootBlock,
						patternIndex,
						null,
						updateSelection
					);
				}

				clientIds.push( patternRootBlock.clientId );
			} );

			// Save the current pattern selection to the site metadata
			syncSiteMetadataPatterns();

			// Restore block removal rules
			setBlockRemovalRules( blockRemovalRules );

			return clientIds;
		},
		[
			getBlockIndex,
			getBlockRemovalRules,
			getBlocks,
			getBlocksByName,
			insertPattern,
			removeBlocks,
			replacePattern,
			setBlockRemovalRules,
			syncSiteMetadataPatterns,
		]
	);

	const filterByCategory = ( {
		availablePatterns,
		category,
		siteLocation,
		isAskingForMap = false,
		pageSlug,
	} ) => {
		const patternsInCategory = availablePatterns?.filter( ( p ) => {
			return p.category === category;
		} );

		let _patterns = patternsInCategory;

		// Special cases for contact and footer patterns
		switch ( category ) {
			case 'contact':
				// If the page is contact and the site has a location make sure the pattern has map and address.
				if ( pageSlug === 'contact' && siteLocation?.coordinates ) {
					return _patterns.filter( ( p ) => {
						return (
							p.html.toLowerCase().includes( 'wp:jetpack/map' ) &&
							p.html
								.toLowerCase()
								.match( /is-contact-address(?!-)/ )
						);
					} );
				}

				// Only return maps if that's what we're looking for
				if ( isAskingForMap ) {
					return _patterns.filter( ( p ) => {
						return p.html
							.toLowerCase()
							.includes( 'wp:jetpack/map' );
					} );
				}

				// Exclude patterns with an address if the site has none.
				if ( ! siteLocation?.name ) {
					_patterns = _patterns.filter( ( p ) => {
						return ! p.html
							.toLowerCase()
							.match( /is-contact-address(?!-)/ );
					} );
				}

				// Exclude patterns with a map if the site location coordinates are not set.
				if ( ! siteLocation?.coordinates ) {
					_patterns = _patterns.filter( ( p ) => {
						return ! p.html
							.toLowerCase()
							.includes( 'wp:jetpack/map' );
					} );
				}

				break;
			case 'footer':
				// Exclude patterns with an address if the site has none.
				if ( ! siteLocation?.name ) {
					_patterns = _patterns.filter( ( p ) => {
						return ! p.html
							.toLowerCase()
							.includes( 'is-footer-address' );
					} );
				}
				break;
		}

		// If we're on the home page, don't show patterns with 'page' category
		if ( isHomePage && pageSlug !== 'about' ) {
			_patterns = _patterns.filter( ( p ) => {
				return ! Object.keys( p.categories ).includes( 'page' );
			} );
		}
		// Using special case for about page as the page pattern is better in this context than other about patterns.
		// Will extend to other pages later.
		if ( pageSlug === 'about' ) {
			_patterns = _patterns.filter( ( p ) => {
				return (
					Object.keys( p.categories ).includes( 'page' ) &&
					Object.keys( p.categories ).includes( 'about' )
				);
			} );
		}
		return _patterns.length ? _patterns : patternsInCategory; // This shouldn't happen. If it does, fallback to all category patterns.
	};

	const convertToBlocks = ( html ) => {
		return rawHandler( { HTML: html } );
	};

	const composePatterns = ( {
		title,
		categories,
		topic,
		siteDescription,
		siteLocation,
		pageDescription,
		numberOfConcepts = DEFAULT_NUM_CONCEPTS,
		selectedConcept,
		newPage = false,
		noCache = false,
		isAskingForMap = false,
		chosenPatterns = [],
		pages = [],
		pageSlug,
	} ) => {
		const concepts = [];

		// if categories is empty or undefined, bail
		if ( ! categories || categories.length === 0 ) {
			return [];
		}

		const { siteLocation: savedSiteLocation } = getSiteMetadata();
		const chosenPatternIDs =
			selectedConcept?.patterns.map( ( p ) => p.ID ) || [];

		const patterns = getRemotePatterns( userLocale );

		for ( let i = 0; i < numberOfConcepts; i++ ) {
			concepts[ i ] = [];

			const canRepeatPattern = categories.length > 1 ? true : false;
			let styleVariationIndex = 0;

			categories.forEach( ( category ) => {
				// if the category is in the chosenPatterns array, use the provided pattern
				const chosenPatternId = chosenPatterns.find(
					( p ) => p.category === category.slug
				)?.id;
				const chosenPattern = chosenPatternId
					? patterns.find( ( p ) => p.ID === chosenPatternId )
					: choosePattern(
							filterByCategory( {
								availablePatterns: patterns,
								category: category.slug,
								siteLocation: siteLocation || savedSiteLocation,
								isAskingForMap,
								pageSlug,
							} ),
							chosenPatternIDs,
							canRepeatPattern
					  );

				if ( chosenPattern !== undefined ) {
					styleVariationIndex =
						styleVariationIndex >=
						selectedStyleVariationSet?.body.length
							? 0
							: styleVariationIndex;

					const patternStyleVariation =
						selectedStyleVariationSet?.body[ styleVariationIndex ];

					const parsedPattern = initializePattern(
						chosenPattern,
						category.slug,
						patternStyleVariation
					);

					concepts[ i ].push( {
						ID: chosenPattern.ID,
						category: category.slug,
						reason: category.reason,
						title: chosenPattern.title,
						blocks: parsedPattern,
					} );

					chosenPatternIDs.push( chosenPattern.ID );
					styleVariationIndex++;
				}
			} );
		}

		return concepts.map( ( concept ) => {
			const resolvers = [];
			for ( const section of concept ) {
				resolvers.push( {
					section,
					resolver: processSection( {
						pageTitle: title,
						pageDescription,
						selectedConcept,
						section,
						siteDescription,
						topic,
						siteLocation,
						concept,
						categories,
						newPage,
						noCache,
						pages,
					} ),
				} );
			}

			return {
				id: uuidv4(),
				patterns: concept,
				resolvers,
			};
		} );
	};

	const generateDummyPosts = async ( { blocks, siteDescription, topic } ) => {
		for ( const block of blocks ) {
			if ( block.name === 'core/query' ) {
				// check if there's an existing post tag 'dummy-post'
				let tagId = null;
				let tag = await getDummyPostTag();
				if ( ! tag?.length ) {
					// if not, create a dummy post tag
					tag = await createDummyPostTag();
					tagId = tag?.id;
				} else {
					tagId = tag?.[ 0 ]?.id;
				}

				if ( ! tagId ) {
					log.error( 'No Dummy Post Tag' );
					return;
				}

				// create dummy posts if there are none
				const posts = await getDummyPosts( tagId );
				if ( ! posts?.length ) {
					const dummyPostContent = await generateDummyPostContent( {
						siteDescription,
					} );
					// add stock image to the dummy post content
					const promises = dummyPostContent.map( async ( post ) => {
						post.featured_image = await getStockImageURL(
							post.image_search,
							1
						);
					} );
					await Promise.all( promises );
					await createDummyPosts( dummyPostContent );
				}

				// force a refresh of the query block
				const random = Math.floor( 100000 + Math.random() * 400000 ); // eslint-disable-line no-restricted-syntax
				block.attributes.query.exclude = [ random ];
				updateBlockAttributes( block.clientId, {
					query: {
						...block.attributes.query,
						exclude: [ random ],
					},
				} );
			}

			generateDummyPosts( {
				blocks: block.innerBlocks,
				siteDescription,
				topic,
			} );
		}
	};

	const processSection = async ( {
		pageTitle,
		section,
		siteDescription,
		pageDescription,
		topic,
		siteLocation,
		selectedConcept,
		categories,
		newPage = false,
		noCache = false,
		pages = [],
	} ) => {
		return Promise.all( [
			replaceContent( {
				conceptSection: section.blocks,
				sectionCategory: section.category,
				reasonForSection: section.reason,
				siteDescription,
				siteLocation,
				selectedConcept,
				topic,
				pageTitle,
				pageDescription,
				newPage,
				noCache,
			} ),
			generateDummyPosts( {
				blocks: section.blocks,
				siteDescription,
				topic,
			} ),
			setImagesInSection( section, topic, selectedConcept, newPage ),
		] ).then( () => {
			setCTAInSection( section, categories, pages );
			setSectionInternalAnchor( section );
			setMapsInSection( section, siteLocation );
			applyCoverBlockOverlayStyles( section );
		} );
	};

	const choosePattern = (
		availablePatterns,
		alreadyChosenPatternIDs = [],
		canRepeatPattern = true
	) => {
		if ( ! availablePatterns?.length ) {
			return;
		}

		// choose a random pattern, but not one that has already been chosen
		let availablePatternsNotChosen = availablePatterns.filter(
			( pattern ) => {
				return ! alreadyChosenPatternIDs.includes( pattern.ID );
			}
		);
		let patternsToChooseFrom;
		if ( alreadyChosenPatternIDs?.length ) {
			// exclude those patterns that's not allowed to show up after the last chosen pattern
			// todo not sure if this is 100% correct, revisit once we have more patterns disallowed.
			const lastPatternId =
				alreadyChosenPatternIDs[ alreadyChosenPatternIDs.length - 1 ];
			availablePatternsNotChosen = availablePatternsNotChosen.filter(
				( pattern ) => {
					return ! pattern.disallow_after.includes( lastPatternId );
				}
			);
			patternsToChooseFrom = availablePatterns.filter( ( pattern ) => {
				return ! pattern.disallow_after.includes( lastPatternId );
			} );
		}

		// if there is no patterns to choose from and we can't repeat patterns, bail
		if ( availablePatternsNotChosen.length === 0 && ! canRepeatPattern ) {
			return;
		}

		// if there is patterns not used yet, use those to avoid repeat, otherwise choose from all patterns
		// so we at least still have a pattern to show
		patternsToChooseFrom = availablePatternsNotChosen.length
			? availablePatternsNotChosen
			: patternsToChooseFrom;

		const chosenPattern =
			patternsToChooseFrom[
				Math.floor( Math.random() * patternsToChooseFrom.length ) // eslint-disable-line no-restricted-syntax
			];

		return chosenPattern;
	};

	const applyCoverBlockOverlayStyles = async ( section ) => {
		const coverBlock = getFirstChildBlockByName(
			section.blocks[ 0 ],
			'core/cover'
		);
		if ( ! coverBlock ) {
			return;
		}

		const mediaColor = await getMediaColor( coverBlock.attributes.url );
		const textColor = getContrastingThemeColor( mediaColor, themeColors );
		coverBlock.attributes.customOverlayColor = mediaColor;
		coverBlock.attributes.isUserOverlayColor = false;
		coverBlock.attributes.overlayColor = undefined;
		coverBlock.attributes.style = {
			...coverBlock.attributes.style,
			color: {
				text: textColor?.color,
			},
		};
	};

	const initializePattern = ( pattern, patternCategory, styleVariation ) => {
		let parsedPattern = convertToBlocks( pattern.html );

		// reset external media IDs so images get saved
		getImageBlocksWithIds( parsedPattern ).forEach( ( block ) => {
			block.attributes.id = null;
		} );

		if (
			parsedPattern.length > 1 ||
			parsedPattern[ 0 ]?.name !== 'core/group'
		) {
			// If the pattern does not have a core/group top level wrapper, then add it.
			parsedPattern = [
				createBlock(
					'core/group',
					{
						align: 'full',
					},
					parsedPattern
				),
			];
		}

		// explicitly set top and bottom margin to 0 if not already set
		if ( ! parsedPattern[ 0 ]?.attributes?.style?.spacing ) {
			parsedPattern[ 0 ].attributes.style = {
				...parsedPattern[ 0 ].attributes?.style,
				spacing: {
					margin: {
						top: '0',
						bottom: '0',
					},
				},
			};
		}

		parsedPattern[ 0 ].attributes.metadata = {
			...parsedPattern[ 0 ].attributes?.metadata,
			name: patternCategories.find(
				( category ) => category.slug === patternCategory
			)?.title,
			patternCategory,
			remotePatternId: pattern.ID,
		};

		// No section style support cover blocks (intro), this will change as the feature develops.
		if (
			'intro' === patternCategory &&
			getFirstChildBlockByName( parsedPattern[ 0 ], 'core/cover' )
		) {
			return parsedPattern;
		}

		let className = parsedPattern[ 0 ].attributes.className || '';
		// Remove any hardcoded section style classes.
		className = className.replaceAll( sectionStyleRegex, '' ).trim();

		if ( className ) {
			parsedPattern[ 0 ].attributes.className =
				className + ` is-style-${ styleVariation }`;
		} else {
			parsedPattern[ 0 ].attributes.className = `is-style-${ styleVariation }`;
		}

		return parsedPattern;
	};

	const getPatternCategory = ( patternRootBlockClientId ) => {
		const attrs = getBlockAttributes( patternRootBlockClientId );

		return attrs?.metadata?.patternCategory || attrs?.slug;
	};

	const getParentPatternCategory = ( block ) => {
		const parentRootPatternBlock = getParentRootPatternBlock(
			block.clientId
		);
		const patternCategory = getPatternCategory(
			parentRootPatternBlock
				? parentRootPatternBlock.clientId
				: block.clientId
		);

		return patternCategory;
	};

	const getOtherPatternsInCategory = ( patternRootBlockClientId ) => {
		const attrs = getBlockAttributes( patternRootBlockClientId );

		if ( ! attrs?.metadata ) {
			return [];
		}

		if ( ! attrs?.metadata?.patternCategory ) {
			return [];
		}

		// find the patterns that have a matching categoryName in the
		// patterns array categories object keys
		const patterns = getRemotePatterns( userLocale );
		return patterns.filter(
			( pattern ) => pattern.category === attrs?.metadata?.patternCategory
		);
	};

	const setImagesInConcept = async ( concept, topic ) => {
		const deferredUpdates = {};
		for ( const pattern of concept ) {
			await setImagesInBlocks(
				pattern.blocks,
				topic,
				true,
				deferredUpdates
			);
		}

		return deferredUpdates;
	};

	const setImagesInSection = async (
		section,
		topic,
		selectedConcept,
		newPage = false
	) => {
		// Don't set images for the team section.
		if ( [ 'team' ].includes( section.category ) ) {
			return;
		}

		if ( selectedConcept ) {
			const selectedBlockClientId = getSelectedBlockClientId();

			// when there are multiple patterns with same category, select the one that contains the selected block id, if any
			const currentPattern =
				selectedConcept.patterns.find(
					( pattern ) =>
						pattern.category === section.category &&
						pattern.blocks[ 0 ].clientId === selectedBlockClientId
				) ??
				selectedConcept.patterns.find(
					( pattern ) => pattern.category === section.category
				) ??
				section;
			const selectedSectionImages = getPatternImages(
				currentPattern.blocks[ 0 ],
				[]
			);
			return updatePatternImages( section.blocks[ 0 ], {
				selectedSectionImages,
				conceptSection: true,
			} );
		}
		const deferredUpdates = {};

		await setImagesInBlocks(
			section.blocks,
			topic,
			true,
			deferredUpdates,
			newPage
		);

		return deferredUpdates;
	};

	const setMapsInSection = ( section, siteLocation ) => {
		setMapLocationInBlocks( section.blocks, siteLocation );
	};

	const setCTAInSection = async ( section, categories, pages ) => {
		if (
			section?.category !== 'intro' &&
			section?.category !== 'about' &&
			section?.category !== 'header'
		) {
			return;
		}

		const findButtonBlock = ( block ) => {
			if ( block.name === 'core/button' ) {
				return block;
			}

			if ( block.innerBlocks?.length ) {
				for ( const innerBlock of block.innerBlocks ) {
					const buttonBlock = findButtonBlock( innerBlock );

					if ( buttonBlock ) {
						return buttonBlock;
					}
				}
			}

			return false;
		};

		const firstButtonBlock = findButtonBlock( section.blocks[ 0 ] );

		if ( ! firstButtonBlock ) {
			return;
		}

		if ( section?.category === 'header' ) {
			firstButtonBlock.attributes.url = '/about';
			firstButtonBlock.attributes.text = __( 'Learn more', 'big-sky' );
			return;
		}

		if (
			pages.find( ( page ) => page.slug === 'menu' ) &&
			section?.category === 'intro'
		) {
			firstButtonBlock.attributes.url = '/menu';
			firstButtonBlock.attributes.text = __( 'View Menu', 'big-sky' );
			return;
		}

		if (
			pages.find( ( page ) => page.slug === 'about' ) &&
			section?.category === 'about'
		) {
			firstButtonBlock.attributes.url = '/about';
			firstButtonBlock.attributes.text = __( 'Find out more', 'big-sky' );
			return;
		}

		if ( categories.find( ( cat ) => cat.slug === 'events' ) ) {
			firstButtonBlock.attributes.url = '#events-section';
			firstButtonBlock.attributes.text = __(
				'Upcoming Events',
				'big-sky'
			);
			return;
		}

		if ( categories.find( ( cat ) => cat.slug === 'services' ) ) {
			firstButtonBlock.attributes.url = '#services-section';
			firstButtonBlock.attributes.text = __( 'Our Services', 'big-sky' );
			return;
		}

		if ( categories.find( ( cat ) => cat.slug === 'pricing' ) ) {
			firstButtonBlock.attributes.url = '#pricing-section';
			firstButtonBlock.attributes.text = __( 'Pricing', 'big-sky' );
		}

		if ( categories.find( ( cat ) => cat.slug === 'posts' ) ) {
			firstButtonBlock.attributes.url = '#posts-section';
			firstButtonBlock.attributes.text = __( 'Read more', 'big-sky' );
		}
	};

	const setSectionInternalAnchor = ( section ) => {
		section.blocks[ 0 ].attributes.anchor = section.category + '-section';
	};

	const processTemplatePart = async ( {
		templatePart,
		siteDescription,
		siteLocation,
		topic,
		templatePartBlocks,
		isQuickOnboarding,
	} ) => {
		if ( process.env.REPLACE_CONTENT !== 'false' ) {
			return Promise.all( [
				replaceContent( {
					conceptSection: templatePartBlocks,
					siteDescription,
					sectionCategory: templatePart,
					siteLocation,
				} ),
				templatePart === 'header' &&
					! isQuickOnboarding &&
					generateLogoWithAI( { description: topic } ),
			] ).then( () => {
				setCTAInSection( {
					category: templatePart,
					blocks: templatePartBlocks,
				} );
			} );
		}
	};

	/**
	 * Gets all available pattern categories.
	 *
	 * @return {string[]} Array of unique pattern categories.
	 */
	const getAvailablePatternCategories = () => {
		const patterns = getRemotePatterns( userLocale );
		const categories = new Set();
		patterns.forEach( ( pattern ) => {
			categories.add( pattern.category );
		} );
		// filter out undefined categories
		return Array.from( categories ).filter( ( category ) => category );
	};

	const composeTemplatePartPattern = ( {
		templatePart,
		siteDescription,
		siteLocation,
		topic,
		pattern = null,
		isQuickOnboarding = false,
	} ) => {
		const templatePartClientIds = getBlocksByName( 'core/template-part' );

		if ( ! templatePartClientIds ) {
			return;
		}

		const slug = templatePart === 'header' ? 'header' : 'footer';

		const patterns = getRemotePatterns( userLocale );
		// if pattern is a number id, find the pattern by id
		if ( typeof pattern === 'number' ) {
			pattern = patterns.find( ( p ) => p.ID === pattern );
		}
		const selectedPattern =
			pattern ||
			choosePattern(
				filterByCategory( {
					availablePatterns: patterns,
					category: slug,
				} )
			);

		if ( selectedPattern ) {
			const blocks = convertToBlocks( selectedPattern.html );

			blocks[ 0 ].attributes.metadata = {
				...blocks[ 0 ].attributes?.metadata,
				name: patternCategories.find(
					( category ) => category.slug === slug
				)?.title,
				patternCategory: slug,
				remotePatternId: selectedPattern.ID,
			};

			const templatePartStyleVariation =
				selectedStyleVariationSet[ slug ];

			let className = blocks[ 0 ].attributes.className || '';
			// Remove any hardcoded section style classes.
			className = className.replaceAll( sectionStyleRegex, '' ).trim();

			if ( className ) {
				blocks[ 0 ].attributes.className =
					className + ` is-style-${ templatePartStyleVariation }`;
			} else {
				blocks[ 0 ].attributes.className = `is-style-${ templatePartStyleVariation }`;
			}

			const templatePartPattern = {
				ID: selectedPattern.ID,
				blocks,
				category: slug,
				title:
					selectedPattern.title.replace(
						new RegExp( slug + ':', 'i' ),
						''
					) +
					' (' +
					selectedPattern.ID +
					')',
			};

			return {
				pattern: templatePartPattern,
				resolver: processTemplatePart( {
					templatePart,
					siteDescription,
					siteLocation,
					topic,
					templatePartBlocks: blocks,
					isQuickOnboarding,
				} ),
			};
		}
	};

	const createConceptFromCurrentCanvas = () => {
		const blocks = getRootBlocksOfPostContent();

		const _patterns = blocks.map( ( block ) => {
			const patternCategory = getPatternCategory( block.clientId );
			const pattern = getPatternById(
				block.attributes?.metadata?.remotePatternId
			);

			return {
				ID: pattern?.ID,
				category: patternCategory,
				title: pattern?.title,
				blocks: [ block ],
			};
		} );

		return {
			id: uuidv4(),
			patterns: _patterns,
			resolvers: [],
		};
	};

	const createConceptFromSelectedBlock = ( clientId ) => {
		const selectedBlockClientId = clientId || getSelectedBlockClientId();

		const block = getBlock( selectedBlockClientId );
		const patternCategory = getParentPatternCategory( block );
		const pattern = getPatternById(
			getBlock( block.clientId ).attributes?.metadata.remotePatternId
		);

		const patternData = {
			ID: pattern.ID,
			category: patternCategory,
			title: pattern.title,
			blocks: [ block ],
		};

		return {
			id: uuidv4(),
			patterns: [ patternData ],
			resolvers: [],
		};
	};

	const createConceptFromHTML = ( html, patternCategory ) => {
		const blocks = convertToBlocks( html );
		const validatedPattern = fixInvalidBlock( blocks[ 0 ] );

		const patternData = {
			ID: uuidv4(),
			category: patternCategory,
			title: __( 'Generated Pattern', 'big-sky' ),
			blocks: [ validatedPattern ],
		};

		return {
			id: uuidv4(),
			patterns: [ patternData ],
			resolvers: [],
		};
	};

	const getPattern = ( category ) => {
		if ( [ 'header', 'footer' ].includes( category ) ) {
			return getBlock( getTemplatePartClientID( category ) );
		}

		const rootBlocks = getRootBlocksOfPostContent();
		return rootBlocks.find(
			( block ) => getPatternCategory( block.clientId ) === category
		);
	};

	const getComponent = ( key, category, startBlock = null ) => {
		const findBlock = ( block ) => {
			if ( ! block ) {
				return null;
			}

			if ( block.attributes?.metadata?.name === key ) {
				return block;
			}

			for ( const innerBlock of block.innerBlocks ) {
				const result = findBlock( innerBlock );
				if ( result ) {
					return result;
				}
			}

			return null;
		};

		return findBlock( startBlock || getPattern( category ) );
	};

	const getSections = ( category, block ) =>
		getComponent( SECTIONS_KEY, category, block );
	const getSection = ( category, block ) =>
		getComponent( SECTION_KEY, category, block || getSections( category ) );
	const getItems = ( category, block ) =>
		getComponent( ITEMS_KEY, category, block || getSection( category ) );
	const getItem = ( category, block ) =>
		getComponent( ITEM_KEY, category, block || getItems( category ) );

	useEffect( () => {
		if ( isAfterSave ) {
			syncSiteMetadataPatterns( { persist: true } );
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [ isAfterSave ] );

	return {
		DEFAULT_NUM_CONCEPTS,
		ITEM_KEY,
		ITEMS_KEY,
		SECTION_KEY,
		SECTIONS_KEY,
		insertPattern,
		insertPatterns,
		replacePattern,
		filterByCategory,
		convertToBlocks,
		composePatterns,
		composeTemplatePartPattern,
		setImagesInConcept,
		setImagesInSection,
		getRootBlocksOfPostContent,
		initializePattern,
		getOtherPatternsInCategory,
		getPatternCategory,
		getParentPatternCategory,
		getPatternById,
		createConceptFromCurrentCanvas,
		createConceptFromSelectedBlock,
		createConceptFromHTML,
		setDefaultEditMode,
		getAvailablePatternCategories,
		getPattern,
		getSections,
		getSection,
		getItems,
		getItem,
		replaceTemplatePart,
		syncSiteMetadataPatterns,
		setCTAInSection,
	};
}
