/**
 * WordPress dependencies
 */
import { useDispatch, useRegistry, useSelect } from '@wordpress/data';
import { store as blockEditorStore } from '@wordpress/block-editor';
import { __ } from '@wordpress/i18n';

/**
 * Internal dependencies
 */
import {
	findLastAvailableInnerBlockByClassName,
	getContentOfBlocks,
	getContentRequestData,
} from './utils.js';
import templating from '@config/templating.json';
import { store as aiStore } from '@store';
import useBlocks from '@hooks/use-blocks';
import useSiteMetadata from '@hooks/use-site-metadata';
import usePatternContent from '@hooks/use-pattern-content';
import {
	customSectionNames,
	customSectionTypes,
	sectionCachedContentIsValid,
} from './section-types/index.js';
import log from '@utils/log-debug.js';
import contentSystemPrompt from './system-prompt.js';

export default function useReplaceContent() {
	const registry = useRegistry();
	const { getBlock } = useSelect( blockEditorStore );
	const { siteDescription } = useSiteMetadata().getSiteMetadata();
	const { getCachedContent } = useSelect( aiStore );

	const { basicChatModel } = useSelect( ( select ) => ( {
		basicChatModel: select( aiStore ).getBasicChatModel(),
	} ) );

	const { runChatCompletion, addCachedContent } = useDispatch( aiStore );
	const { updateBlockAttributes } = useDispatch( blockEditorStore );
	const { setBlockIsProcessing } = useBlocks();
	const { getPatternContent, updateSectionContent } = usePatternContent();

	const replace_content = (
		content,
		chunk,
		isConceptSection,
		patternCategory
	) => {
		content
			.filter( ( cnt ) => cnt.i === 'SPARE' )
			.forEach( ( cnt ) =>
				addCachedContent( {
					category: patternCategory,
					type: cnt.t,
					content: cnt.e,
				} )
			);

		const remappedContent = content
			.filter( ( cnt ) => cnt.i !== 'SPARE' )
			.map( ( block ) => ( {
				clientId: chunk.clientIdMap[ block.i ],
				text: block.e,
				type: block.t,
			} ) );

		registry.batch( () => {
			remappedContent?.forEach( ( newContent ) => {
				const attributes = {};
				if ( patternCategory ) {
					addCachedContent( {
						category: patternCategory,
						type: newContent.type,
						content: newContent.text,
					} );
				}

				const block = getBlock( newContent.clientId );
				if ( block ) {
					setBlockIsProcessing( block, true );
					if (
						block.name === 'core/heading' ||
						block.name === 'core/paragraph'
					) {
						attributes.content = newContent.text;
					} else if ( block.name === 'core/site-title' ) {
					} else if ( block.name === 'core/button' ) {
						attributes.text = newContent.text;
					}

					updateBlockAttributes( newContent.clientId, {
						...attributes,
					} );

					setBlockIsProcessing( block, false );
				}

				if ( isConceptSection ) {
					const inMemoryBlock = chunk.blockMap[ newContent.clientId ];
					if ( inMemoryBlock ) {
						if ( inMemoryBlock.name === 'core/button' ) {
							inMemoryBlock.attributes.text = newContent.text;
						} else {
							inMemoryBlock.attributes.content = newContent.text;
						}
					}
				}
			} );
		} );

		return __( 'I have replaced the content.', 'big-sky' );
	};

	const getContent = async ( {
		blockClientId,
		conceptSection,
		language,
	} ) => {
		const isConceptSection = !! conceptSection;

		let blocks = [];
		let sectionData;
		if ( conceptSection ) {
			const parentBlock = conceptSection[ 0 ];
			blocks = conceptSection;
			sectionData = {
				name: parentBlock.attributes?.metadata?.name,
				patternCategory:
					parentBlock.attributes?.metadata?.patternCategory,
			};
		} else {
			const block = getBlock( blockClientId );
			blocks = [ block ];
		}

		const content = getContentOfBlocks( blocks ).filter(
			( block ) =>
				! templating.contentToIgnore.some( ( ignoreContent ) =>
					block.example?.includes( ignoreContent )
				)
		);

		if ( ! isConceptSection ) {
			content.forEach( ( contentBlock ) => {
				setBlockIsProcessing( contentBlock, true );
			} );
		}

		return {
			content: getContentRequestData(
				content,
				isConceptSection,
				language
			),
			sectionData,
		};
	};

	async function replaceContent( {
		blockClientId,
		conceptSection,
		siteDescription: customDescription,
		siteLocation,
		reasonForSection,
		topic,
		pageTitle,
		sectionCategory,
		requestedSubject,
		language,
		selectedConcept,
		newPage,
		numOfSections,
		noCache = false,
		inMemory = true,
		startIndex = -1,
		sectionHeading,
		replaceHeading = true,
		pageDescription,
	} = {} ) {
		// Don't replace content for the team section.
		if ( [ 'team' ].includes( sectionCategory ) ) {
			return;
		}

		const isConceptSection = !! conceptSection;
		const cachedContent = getCachedContent();

		// Check first if this is a specific section category that requires special content replacement. If it is
		// we can get specific content for those categories and bypass getContent().
		if (
			customSectionNames.includes( sectionCategory ) &&
			isConceptSection
		) {
			const isCachedContentValid =
				customSectionTypes[ sectionCategory ].isCachedContentValid ||
				sectionCachedContentIsValid;
			if (
				cachedContent?.[ sectionCategory ] &&
				isCachedContentValid( cachedContent[ sectionCategory ] ) &&
				! newPage &&
				! noCache
			) {
				return customSectionTypes[ sectionCategory ].replaceContent( {
					rootBlock: conceptSection[ 0 ],
					data: cachedContent[ sectionCategory ],
					updateBlockAttributes: inMemory
						? null
						: updateBlockAttributes,
					sectionHeading,
					replaceHeading,
					startIndex,
				} );
			}

			// If the cached content is not valid wipe the existing cached content for that section category.
			if (
				cachedContent?.[ sectionCategory ] &&
				! isCachedContentValid( cachedContent[ sectionCategory ] )
			) {
				await addCachedContent( {
					category: sectionCategory,
					content: [],
					clear: true,
				} );
			}

			const patternContent = await getPatternTypeContent( {
				patternType: sectionCategory,
				siteDescription: customDescription,
				pageDescription,
				siteLocation,
				conceptSection,
				reasonForSection,
				numOfSections,
				topic,
			} );
			return customSectionTypes[ sectionCategory ].replaceContent( {
				rootBlock: conceptSection[ 0 ],
				data: patternContent,
				updateBlockAttributes: inMemory ? null : updateBlockAttributes,
				replaceHeading,
				sectionHeading,
				startIndex,
			} );
		} else if ( cachedContent?.[ sectionCategory ] && ! newPage ) {
			return updateSectionContent( conceptSection[ 0 ], {
				sectionCategory,
				selectedSectionContent: cachedContent[ sectionCategory ],
				conceptSection: true,
			} );
		} else if (
			Object.keys( cachedContent ).find( ( key ) =>
				key.includes( sectionCategory )
			) &&
			! newPage
		) {
			let selectedSectionContent = {};

			// If there is a selected section, get the content from the section. This way we can populate alterative sections with the same
			// content and any user edits to the content will be saved.
			const selectedSection = selectedConcept
				? selectedConcept.patterns.find(
						( pattern ) => pattern.category === sectionCategory
				  )
				: null;

			if ( selectedSection ) {
				selectedSectionContent = selectedSection.blocks[ 0 ]
					? getPatternContent(
							selectedSection.blocks[ 0 ],
							sectionCategory,
							{}
					  )
					: {};
			}

			return updateSectionContent( conceptSection[ 0 ], {
				sectionCategory,
				selectedSectionContent,
				conceptSection: true,
			} );
		}

		const { content, sectionData } = await getContent( {
			blockClientId,
			conceptSection,
			language,
		} );

		let contentCall;
		if ( isConceptSection || ! blockClientId ) {
			contentCall = getReplacementContent( {
				chunk: content,
				sectionInfo: sectionData,
				customSubject: topic || requestedSubject,
				reason: reasonForSection,
				_pageTitle: pageTitle,
				pageDescription,
			} );
		} else {
			contentCall = getReplacementContent( {
				chunk: content,
				customSubject: requestedSubject,
				pageDescription,
			} );
		}

		return await contentCall;

		async function getReplacementContent( {
			chunk,
			sectionInfo,
			customSubject,
			reason,
			_pageTitle,
			_pageDescription,
			retry = false,
		} ) {
			const description = siteDescription || customDescription;
			const contentChunk = { content: chunk.contentForRequest };

			let messageContent = `The site description is: ${ description }\n\n`;

			if ( sectionInfo?.name ) {
				messageContent += `The section name is: ${ sectionInfo.name }\n\n`;
			}

			if ( reason ) {
				messageContent += `The reason this section has been chosen: ${ reason }\n\n`;
			}

			if ( _pageTitle ) {
				messageContent += `The page title is: ${ _pageTitle }\n\n`;
			}

			if ( _pageDescription ) {
				messageContent += `The description of the page is: ${ _pageDescription }, so content should be relevant to this.\n\n`;
			}

			messageContent +=
				'The content you choose should always be relevant to the section information provided above.';

			if ( customSubject ) {
				messageContent += `\nThe subject requested by the user: ${ customSubject }\n\n`;
			}

			messageContent += `The content to update is: ${ JSON.stringify(
				contentChunk
			) }`;

			const messages = [
				{
					role: 'system',
					content: contentSystemPrompt,
				},
				{
					role: 'user',
					content: messageContent,
				},
			];

			const response = await runChatCompletion(
				basicChatModel,
				messages,
				null,
				0.2,
				'json_object'
			);

			const newContent = response?.choices?.[ 0 ]?.message?.content;

			if ( newContent ) {
				try {
					const parsedArgs = JSON.parse( newContent );

					if ( ! parsedArgs.content ) {
						if ( ! retry ) {
							log.info(
								'CONTENT TOOL: content missing in response, retrying with chunk of content.'
							);

							return getReplacementContent( {
								chunk,
								sectionInfo: sectionData,
								retry: true,
							} );
						}
						log.info(
							'CONTENT TOOL: content missing in response, some content may not be updated.'
						);
						return __(
							'I encountered an error while processing your request. Please try again.',
							'big-sky'
						);
					}
					return replace_content(
						parsedArgs.content,
						chunk,
						isConceptSection,
						sectionData?.patternCategory
					);
				} catch ( e ) {
					if ( ! retry ) {
						return getReplacementContent( {
							chunk,
							sectionInfo: sectionData,
							retry: true,
						} );
					}
					console.log( e );
					log.info(
						'CONTENT TOOL: JSON parsing error some content may be missing'
					);
					return __(
						'I encountered an error while processing your request. Please try again.',
						'big-sky'
					);
				}
			}
		}
	}

	async function getPatternTypeContent( {
		patternType,
		siteDescription: description,
		siteLocation,
		pageDescription,
		topic,
		conceptSection,
		reasonForSection,
		numOfSections,
		hasRetried = false,
	} ) {
		const name = customSectionTypes[ patternType ].name;
		const prompts = customSectionTypes[ patternType ].getPrompts( {
			conceptSection,
			siteLocation,
			siteDescription,
			pageDescription,
			numOfSections,
			topic,
		} );
		let reasoning = '';
		if ( reasonForSection ) {
			reasoning = `The reason for this section is: ${ reasonForSection }, on the topic of ${ topic }.`;
		}
		let pageReasoning = '';
		if ( pageDescription ) {
			pageReasoning = `The description of the page that this section appears in is: ${ pageDescription }, so the content should be relevant to this.`;
		}

		const messages = [
			{
				role: 'system',
				content: prompts.system,
			},
			{
				role: 'user',
				content: `The site description is: ${ description }.

					${ reasoning }.

					${ pageReasoning }

					${ prompts.user }`,
			},
		];

		const response = await runChatCompletion(
			basicChatModel,
			messages,
			null,
			0.2,
			'json_object'
		);

		try {
			let content = JSON.parse(
				response?.choices?.[ 0 ]?.message?.content
			);

			// need to handle certain pattern type responses differently
			if (
				[
					'menu',
					'events',
					'services',
					'testimonials',
					'contact',
					'footer',
				].includes( patternType )
			) {
				// if content data is an object, get the first value
				if ( typeof content === 'object' ) {
					content = Object.values( content )[ 0 ];
				}
				// if content data is not an array, make it an array
				if ( ! Array.isArray( content ) ) {
					content = [ content ];
				}
			}

			if ( ! content ) {
				if ( ! hasRetried ) {
					log.info(
						`CONTENT TOOL: ${ name } data invalid, retrying.`
					);

					getPatternTypeContent( {
						patternType,
						siteDescription: description,
						siteLocation,
						conceptSection,
						reasonForSection,
						numOfSections,
						hasRetried: true,
					} );
				}

				log.error(
					`CONTENT TOOL: ${ name } data missing in response after retry, events may not be updated.`
				);

				return null;
			}

			addCachedContent( {
				category: name,
				content,
			} );

			return content;
		} catch ( e ) {
			if ( ! hasRetried ) {
				return getPatternTypeContent( {
					patternType,
					siteDescription: description,
					siteLocation,
					conceptSection,
					reasonForSection,
					numOfSections,
					hasRetried: true,
				} );
			}

			log.error(
				`CONTENT TOOL: JSON parsing error for ${ name } data, content may be missing.`
			);
		}

		return null;
	}

	async function generateDummyPostContent( {
		siteDescription: description,
	} ) {
		const numPosts = 6;
		const messages = [
			{
				role: 'system',
				content: `You are an expert at generating blog posts based on a description of the website.
					The post title should be no longer than five words.
					The post content should be a paragraph of at least three sentences, but no more than five sentences. Use long descriptive sentences, as if you are writing a blog post.
					The post category should be no longer than two words.
					The image_search should be a short description of the image you would use for the post, no longer than three words.
					You return the data as an array of JSON objects in the following format:
					{ "title": "Post Title", "content": "Post Content", "category": "Post Category", "image_search": "Image Descriptor" }`,
			},
			{
				role: 'user',
				content: `The site description is: ${ description }.
					Generate ${ numPosts } blog posts for this website based on the description.`,
			},
		];

		const response = await runChatCompletion(
			basicChatModel,
			messages,
			null,
			0.2,
			'json_object'
		);

		let posts = response?.choices?.[ 0 ]?.message?.content;

		if ( posts ) {
			posts = JSON.parse( posts );
			// if content data is an object, get the first value
			// the first value should be the array of posts, like { posts: [ ... ] }
			if ( typeof posts === 'object' && ! Array.isArray( posts ) ) {
				posts = Object.values( posts )[ 0 ];
			}
			// if content data is not an array, make it an array
			if ( ! Array.isArray( posts ) ) {
				posts = [ posts ];
			}

			return posts;
		}

		return false;
	}

	function getLastAvailableContentByClassName( block, className ) {
		const foundBlock = findLastAvailableInnerBlockByClassName(
			block,
			className
		);

		if ( foundBlock ) {
			return typeof foundBlock.attributes.content === 'string'
				? foundBlock.attributes.content
				: foundBlock.attributes.content?.originalHTML;
		}

		return null;
	}

	return {
		generateDummyPostContent,
		replaceContent,
		getPatternTypeContent,
		getLastAvailableContentByClassName,
	};
}
