/**
 * Internal dependencies
 */
import {
	findFirstAvailableInnerBlockByClassName,
	getNextAvailableContentValue,
} from '../utils.js';
import { getEventsContentSystemPrompt } from './system-prompt.js';
import { patternKeys } from '@utils/constants';

function replaceContent( {
	rootBlock,
	data: eventsData,
	updateBlockAttributes,
	startIndex = -1,
} ) {
	const { ITEMS_KEY, ITEM_KEY } = patternKeys;
	if ( ! eventsData ) {
		return;
	}

	// If this is the root pattern block, make a deep copy of the events data to avoid mutation for other patterns
	let newEventsData = null;
	if ( rootBlock?.attributes?.metadata?.patternCategory ) {
		newEventsData = JSON.parse( JSON.stringify( eventsData ) );
	} else {
		newEventsData = eventsData;
	}

	let newStartIndex = startIndex;
	// reset the start index if we're changing menu items
	if ( rootBlock?.attributes?.metadata?.name === ITEMS_KEY ) {
		newStartIndex = startIndex;
	} else if ( rootBlock?.attributes?.metadata?.name === ITEM_KEY ) {
		// increment the start index if we're going to a new menu item
		newStartIndex++;
	}

	const validClassNames = [
		'is-event-date',
		'is-event-time',
		'is-event-name',
		'is-event-description',
		'is-event-location',
	];

	if ( rootBlock?.attributes?.className ) {
		const rootBlockClassNames = rootBlock.attributes.className.split( ' ' );

		const matchingClassName = rootBlockClassNames.find( ( className ) =>
			validClassNames.includes( className )
		);

		if ( matchingClassName ) {
			const { updatedEventsData, contentValue } =
				getNextAvailableContentValue( {
					data: newEventsData,
					type: matchingClassName.replace( 'is-event-', '' ),
					offset: newStartIndex,
				} );
			newEventsData = updatedEventsData;

			if ( updateBlockAttributes ) {
				updateBlockAttributes( rootBlock.clientId, {
					content: contentValue,
				} );
			} else {
				rootBlock.attributes.content = contentValue;
			}
		}
	}

	rootBlock?.innerBlocks.forEach( ( block ) => {
		newStartIndex = replaceContent( {
			rootBlock: block,
			data: newEventsData,
			updateBlockAttributes,
			startIndex: newStartIndex,
		} );
	} );

	return newStartIndex;
}

function getPrompts( { conceptSection } ) {
	const dateBlock = conceptSection?.length
		? findFirstAvailableInnerBlockByClassName(
				conceptSection[ 0 ],
				'is-event-date'
		  )
		: null;

	let dateFormatExample = 'Jan 1, 2025';
	if ( dateBlock ) {
		dateFormatExample =
			typeof dateBlock.attributes.content === 'string'
				? dateBlock.attributes.content
				: dateBlock.attributes.content?.originalHTML;
	}

	const prompts = {
		system: getEventsContentSystemPrompt( dateFormatExample ),
		user: 'Generate 10 event names, descriptions, dates, and locations for this website based on the description and reason the section exists.',
	};
	return prompts;
}

export default {
	name: 'events',
	getPrompts,
	replaceContent,
};
