import { __ } from '@wordpress/i18n';

export function calculateFontVariationsToShow( {
	keywords,
	chosenFonts = [],
	currentFont,
	getFontVariations,
	getFontVariationByTitle,
} ) {
	let fontVariationsToShow = [];
	let message = __( 'There are no fonts available.', 'big-sky' );
	const fontVariations = getFontVariations() || [];

	if ( fontVariations.length === 0 ) {
		return { fontVariationsToShow, message };
	}

	if ( chosenFonts && chosenFonts.length ) {
		fontVariationsToShow = fontVariations.filter( ( fontVariation ) => {
			return chosenFonts.includes( fontVariation.title );
		} );
	} else if ( keywords && keywords.length ) {
		const fontVariationKeywordMatches = fontVariations.map(
			( fontVariation ) => {
				const hardcodedKeywords =
					fontVariation[ 'x-big-sky-meta' ]?.keywords || [];
				const matchingKeywords = hardcodedKeywords.filter(
					( keyword ) => keywords.includes( keyword )
				);
				return {
					...fontVariation,
					matchingKeywords: matchingKeywords.length,
				};
			}
		);

		fontVariationsToShow = fontVariationKeywordMatches
			.sort( ( a, b ) => b.matchingKeywords - a.matchingKeywords )
			.filter( ( fontVariation ) => fontVariation.matchingKeywords > 0 );
	}
	message =
		fontVariationsToShow.length > 0
			? __( 'Here are your matching fonts.', 'big-sky' )
			: __(
					"I didn't find any fonts that match, but here are all available fonts.",
					'big-sky'
			  );

	if ( fontVariationsToShow.length === 0 ) {
		fontVariationsToShow = fontVariations;
	}

	// Add the current font variation to the front of the list and remove duplicates.
	const currentFontVariation = getFontVariationByTitle( currentFont );
	if ( currentFontVariation ) {
		fontVariationsToShow = [
			currentFontVariation,
			...fontVariationsToShow.filter(
				( font ) => font.title !== currentFont
			),
		];
	}
	fontVariationsToShow = [ ...new Set( fontVariationsToShow ) ];

	return { fontVariationsToShow, message };
}
