/**
 * WordPress dependencies
 */
import { Button } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import { useDispatch, useSelect } from '@wordpress/data';
import { store as blockEditorStore } from '@wordpress/block-editor';
import { store as editorStore } from '@wordpress/editor';
import { useCallback, useEffect, useState } from '@wordpress/element';

/**
 * Internal dependencies
 */
import useAddPatternTool from '@ai/assistants/pattern/tools/add-pattern/use-add-pattern-tool.js';
import useReplacePatternTool from '@ai/assistants/pattern/tools/replace-pattern/use-replace-pattern-tool.js';
import useColorToolFunctions from '@ai/assistants/color/tools/use-tool-functions.js';
import useShowLayoutPickerTool from '@ai/assistants/layout/tools/show-layout-picker/use-show-layout-picker-tool.js';
import useButtonToolFunctions from '@ai/assistants/content/tools/button/use-tool-functions.js';
import useUndoRedo from '@hooks/use-undo-redo';
import useCanvas from '@hooks/use-canvas.js';
import usePatterns from '@hooks/use-patterns.js';
import usePages from '@hooks/use-pages';
import { store as aiStore } from '@store';
import { dockViews } from '@utils/constants';
import { mustUndoBlockWithGutenberg } from '@components/dock/utils';
import useFontToolFunctions from '@ai/assistants/font/tools/use-tool-functions.js';
import useUploadSiteLogoTool from '@ai/assistants/image/tools/upload-site-logo/use-upload-site-logo-tool.js';
import useAddressToolFunctions from '@ai/assistants/content/tools/address/use-tool-functions.js';
import { recordTracksEvent } from '@utils/tracks';
import ConfirmationDialog from '@components/onboarding/confirmation-dialog';

export default function CompletedAction( { setIsToolContainerVisible } ) {
	const [ showDeletePageDialog, setShowDeletePageDialog ] = useState( false );
	const { zoomIn } = useCanvas();
	const { replacePattern } = usePatterns();
	const { deletePage, goBackPage } = usePages();
	const {
		removeBlock: removeBlockFromEditor,
		replaceInnerBlocks,
		selectBlock,
		updateBlockAttributes,
	} = useDispatch( blockEditorStore );
	const { selectedBlock, newPageId, currentPostId } = useSelect(
		( select ) => {
			const { getSelectedBlock } = select( blockEditorStore );
			const { getNewPageId } = select( aiStore );
			const { getCurrentPostId } = select( editorStore );
			return {
				selectedBlock: getSelectedBlock(),
				newPageId: getNewPageId(),
				currentPostId: getCurrentPostId(),
			};
		}
	);

	const {
		setBlockBeforeChanges,
		setDockView,
		setLastCancelableAction,
		setCurrentToolCall,
		setNewPageId,
	} = useDispatch( aiStore );
	const { currentToolCall, lastCancelableAction, blockBeforeChanges } =
		useSelect( ( select ) => {
			const ai = select( aiStore );

			return {
				lastCancelableAction: ai.getLastCancelableAction(),
				blockBeforeChanges: ai.getBlockBeforeChanges(),
				currentToolCall: ai.getCurrentToolCall(),
			};
		} );
	const shouldShowCancel = !! lastCancelableAction?.action;

	const doResetDockView = useCallback( () => {
		if (
			shouldShowCancel &&
			currentToolCall === 'color_palette_generator'
		) {
			return;
		}
		setDockView( dockViews.MENUS );
	}, [ shouldShowCancel, currentToolCall, setDockView ] );

	const { popUndoStack, undo } = useUndoRedo( {
		onToolbarUndoCallback: doResetDockView,
	} );

	const { doneAddPattern } = useAddPatternTool();
	const { doneReplacePattern } = useReplacePatternTool();
	const { cancelGenerateColorPalette, doneGenerateColorPalette } =
		useColorToolFunctions();
	const { cancelShowFontPicker, doneShowFontPicker } = useFontToolFunctions();
	const { cancelShowLayoutPicker, doneShowLayoutPicker } =
		useShowLayoutPickerTool();
	const { cancelUpdateButtonLink } = useButtonToolFunctions();
	const { cancelUploadSiteLogo } = useUploadSiteLogoTool();
	const { cancelUpdateAddress } = useAddressToolFunctions();

	const resetDockView = useCallback( () => {
		zoomIn();
		setDockView( dockViews.MENUS );
		setIsToolContainerVisible( false );
		setCurrentToolCall( '' );
		setBlockBeforeChanges( null );
	}, [
		zoomIn,
		setDockView,
		setIsToolContainerVisible,
		setCurrentToolCall,
		setBlockBeforeChanges,
	] );

	const handleCancel = useCallback( () => {
		// If the user is creating a new page, cancel button will delete the page.
		// But we want to confirm first.
		if ( newPageId ) {
			setShowDeletePageDialog( true );
			return;
		}
		if ( ! lastCancelableAction ) return;

		recordTracksEvent( 'cancel_action', {
			action: lastCancelableAction.action,
		} );

		switch ( lastCancelableAction.action ) {
			case 'add_menu_items':
			case 'add_events_items':
			case 'add_team_items':
				for ( const clientId of lastCancelableAction.clientIds ) {
					removeBlockFromEditor( clientId );
				}
				resetDockView();
				break;
			case 'add_events_section':
			case 'add_menu_section':
			case 'add_pricing_section':
			case 'add_services_section':
			case 'add_testimonials_section':
			case 'add_team_section':
				removeBlockFromEditor( lastCancelableAction.clientId );
				resetDockView();
				break;
			case 'add_pattern':
				removeBlockFromEditor( lastCancelableAction.clientId );
				resetDockView();
				break;
			case 'replace_pattern':
				replacePattern(
					lastCancelableAction.clientId,
					lastCancelableAction.originalBlock
				);
				resetDockView();
				break;
			case 'color_palette_generator':
				cancelGenerateColorPalette( lastCancelableAction );
				break;
			case 'show_font_picker':
				cancelShowFontPicker(
					lastCancelableAction.originalVariationName
				);
				break;
			case 'show_layout_picker':
				cancelShowLayoutPicker( lastCancelableAction.originalLayout );
				break;
			case 'update_button_link':
				cancelUpdateButtonLink( lastCancelableAction );
				resetDockView();
				break;
			case 'upload_site_logo':
				cancelUploadSiteLogo( lastCancelableAction );
				resetDockView();
				break;
			case 'update_address':
				cancelUpdateAddress( lastCancelableAction );
				break;
			default:
				break;
		}
		setLastCancelableAction( null );
	}, [
		cancelGenerateColorPalette,
		cancelShowFontPicker,
		cancelShowLayoutPicker,
		cancelUpdateAddress,
		cancelUpdateButtonLink,
		cancelUploadSiteLogo,
		newPageId,
		lastCancelableAction,
		removeBlockFromEditor,
		replacePattern,
		resetDockView,
		setLastCancelableAction,
	] );

	const handleDone = useCallback( () => {
		recordTracksEvent( 'done_action', {
			action: lastCancelableAction?.action || currentToolCall,
		} );

		switch ( lastCancelableAction?.action ) {
			case 'add_pattern':
				doneAddPattern();
				break;
			case 'replace_pattern':
				doneReplacePattern();
				break;
			case 'color_palette_generator':
				doneGenerateColorPalette();
				break;
			case 'show_font_picker':
				doneShowFontPicker();
				break;
			case 'show_layout_picker':
				doneShowLayoutPicker();
				break;
			default:
				resetDockView();
				break;
		}
		if ( newPageId ) {
			setNewPageId( 0 );
		}
		setLastCancelableAction( null );
	}, [
		newPageId,
		setNewPageId,
		lastCancelableAction,
		doneAddPattern,
		currentToolCall,
		doneReplacePattern,
		doneGenerateColorPalette,
		doneShowFontPicker,
		doneShowLayoutPicker,
		resetDockView,
		setLastCancelableAction,
	] );

	// If the done button is showing, "Enter" key should trigger it.
	useEffect( () => {
		const handleKeyDown = ( event ) => {
			if ( event.key === 'Enter' ) {
				handleDone();
			}
			if ( event.key === 'Escape' ) {
				handleCancel();
			}
		};
		document.addEventListener( 'keydown', handleKeyDown );
		return () => {
			// Why setTimeout?
			// This cleanup function is called when the component unmounts, ensuring we dont have dangling handlers.
			// But when handling Escape, the PARENT component is unmounted as well in dock/index.js
			// So this component is unmounted before we can trigger the event handler because it has not bubbled here yet.
			// As a result, the handler never gets called and the `handleCancel` does not get called.
			// With setTimeout, we wait for the bubble to complete so all events triggered by the same key can be triggered.
			/* eslint-disable @wordpress/react-no-unsafe-timeout */
			setTimeout(
				() => document.removeEventListener( 'keydown', handleKeyDown ),
				1000
			);
		};
	}, [ handleDone, handleCancel ] );

	const handleUndo = useCallback( async () => {
		recordTracksEvent( 'undo_action', {
			action: currentToolCall,
		} );

		if ( mustUndoBlockWithGutenberg( currentToolCall, selectedBlock ) ) {
			undo();
		} else {
			popUndoStack();
			switch ( currentToolCall ) {
				case 'modify_contact_form':
					replaceInnerBlocks(
						blockBeforeChanges.clientId,
						blockBeforeChanges.innerBlocks
					);
					break;
				default:
					if ( blockBeforeChanges?.attributes ) {
						updateBlockAttributes(
							selectedBlock?.clientId,
							blockBeforeChanges.attributes
						);
					}
					break;
			}
		}

		if ( selectedBlock ) {
			selectBlock( selectedBlock.clientId );
		}
		resetDockView();
	}, [
		blockBeforeChanges,
		currentToolCall,
		popUndoStack,
		replaceInnerBlocks,
		resetDockView,
		selectBlock,
		selectedBlock,
		undo,
		updateBlockAttributes,
	] );

	const getConfirmButtonText = () => {
		if ( newPageId ) {
			return __( 'Create', 'big-sky' );
		}
		switch ( lastCancelableAction?.action ) {
			case 'color_palette_generator':
			case 'show_font_picker':
				return __( 'Save', 'big-sky' );
			default:
				return __( 'Done', 'big-sky' );
		}
	};

	return (
		<>
			<ConfirmationDialog
				isOpen={ showDeletePageDialog }
				onClose={ () => setShowDeletePageDialog( false ) }
				onCancel={ () => setShowDeletePageDialog( false ) }
				onConfirm={ () => {
					setShowDeletePageDialog( false );
					setNewPageId( 0 );
					const pageIdToDelete = currentPostId;
					goBackPage();
					deletePage( pageIdToDelete );
					resetDockView();
				} }
				message={ __(
					'Are you sure you want to delete this page?',
					'big-sky'
				) }
				cancelText={ __( 'Cancel', 'big-sky' ) }
				confirmText={ __( 'Delete', 'big-sky' ) }
				title={ __( 'Delete Page?', 'big-sky' ) }
			/>
			<div className="big-sky__dock__action">
				{ shouldShowCancel && (
					<Button
						className="big-sky__dock__action__cancel"
						onClick={ handleCancel }
					>
						{ __( 'Cancel', 'big-sky' ) }
					</Button>
				) }
				{ ! shouldShowCancel && (
					<Button
						className="big-sky__dock__action__undo"
						onClick={ handleUndo }
					>
						{ __( 'Undo', 'big-sky' ) }
					</Button>
				) }
				<Button
					className="big-sky__dock__action__done"
					onClick={ handleDone }
				>
					{ getConfirmButtonText() }
				</Button>
			</div>
		</>
	);
}
