import askWordPressExpertTool from './tools/ask-wordpress-expert/ask-wordpress-expert-tool.js';
import changeAssistantTool from '@ai/assistants/shared/change-assistant/change-assistant-tool';
import { ASSISTANTS } from '@ai/assistants/constants.js';

const helpToolkit = {
	name: 'help',
	tools: ( context ) => {
		// The ask WordPress expert tool is called via the askBigSkyExpertTool function if it cant find an answer in the answers array
		const tools = [ askWordPressExpertTool() ];

		if (
			context?.currentHandoffArea !== 'help' &&
			! context?.fromAssistantId
		) {
			tools.push( changeAssistantTool( ASSISTANTS.HELP ) );
		}

		return tools;
	},
};

export default helpToolkit;
