import createColorPaletteGeneratorTool from '@ai/assistants/color/tools/color-palette-generator.js';
import createShowFontPickerTool from '@ai/assistants/font/tools/show-font-picker.js';
import rewriteContentTool from '@ai/assistants/content/tools/content/rewrite-content.js';
import createUpdateButtonLinkTool from '@ai/assistants/content/tools/button/update-button-link.js';
import createUpsertSocialLinkTool from '@ai/assistants/content/tools/button/upsert-social-link.js';
import createUpdateAddressTool from '@ai/assistants/content/tools/address/update-address.js';
import makeTextBoldItalicTool from '@ai/assistants/content/tools/make-text-bold-italic/make-text-bold-italic-tool.js';
import modifyContactFormFieldsTool from '@ai/assistants/content/tools/contact-form/modify-contact-form-fields-tool.js';
import addContainerToEventsSectionTool from '@ai/assistants/content/tools/content/events/add-container-to-events-section-tool.js';
import addItemToEventsContainerTool from '@ai/assistants/content/tools/content/events/add-item-to-events-container-tool.js';
import addContainerToMenuSectionTool from '@ai/assistants/content/tools/content/menu/add-container-to-menu-section-tool.js';
import addItemToMenuContainerTool from '@ai/assistants/content/tools/content/menu/add-item-to-menu-container-tool.js';
import addContainerToServicesSectionTool from '@ai/assistants/content/tools/content/services/add-container-to-services-section-tool.js';
import addItemToTestimonialsSectionTool from '@ai/assistants/content/tools/content/testimonials/add-item-to-testimonials-section-tool.js';
import addContainerToPricingSectionTool from '@ai/assistants/content/tools/content/pricing/add-container-to-pricing-section-tool.js';
import addContainerToTeamSectionTool from '@ai/assistants/content/tools/content/team/add-container-to-team-section-tool.js';
import addMemberToTeamContainerTool from '@ai/assistants/content/tools/content/team/add-member-to-team-container-tool.js';
import makeSuggestionTool from '@ai/assistants/shared/make-suggestions/tool.js';
import createStartOverTool from '@ai/assistants/shared/start-over/start-over.js';
import changeAssistantTool from '@ai/assistants/shared/change-assistant/change-assistant-tool.js';
import { ASSISTANTS } from '@ai/assistants/constants.js';
import { isForwardedByRouter } from '@ai/assistants/shared/prompts.js';

const contentToolkit = {
	name: 'content',
	tools: ( context ) => {
		const trimmedDescription =
			context.site?.description?.substring( 0, 300 ) || '';

		// Restrict tools based on the current handoff area
		if ( context.currentHandoffArea ) {
			switch ( context.currentHandoffArea ) {
				case 'colors':
					return [
						createColorPaletteGeneratorTool(
							context.colors?.colorVariationCategories
						),
					];
				case 'fonts':
					return [
						createShowFontPickerTool(
							context.fonts?.fontCategories
						),
					];
				default:
					return [];
			}
		}

		// These tools don't require a selected block
		const tools = [
			makeSuggestionTool(),
			createUpsertSocialLinkTool( context.socialLinks?.services ),
			createUpdateAddressTool(),
			makeTextBoldItalicTool(),
			createStartOverTool(),
		];

		if ( ! isForwardedByRouter( context ) ) {
			tools.push( changeAssistantTool( ASSISTANTS.CONTENT ) );
		}

		// load pattern-specific tools
		if ( context.events?.pattern ) {
			if ( context.events?.sections ) {
				tools.push(
					addContainerToEventsSectionTool( {
						description: trimmedDescription,
						itemCount: context.events?.itemCount,
						dateFormatExample: context.events?.dateFormatExample,
						timeFormatExample: context.events?.timeFormatExample,
					} )
				);
			} else if ( context.events?.items ) {
				tools.push(
					addItemToEventsContainerTool( {
						description: trimmedDescription,
						dateFormatExample: context.events?.dateFormatExample,
						timeFormatExample: context.events?.timeFormatExample,
					} )
				);
			}
		}
		if ( context.menu?.pattern ) {
			if ( context.menu?.sections ) {
				tools.push(
					addContainerToMenuSectionTool( {
						description: trimmedDescription,
						itemCount: context.menu?.itemCount,
					} )
				);
			} else if ( context.menu?.items ) {
				tools.push( addItemToMenuContainerTool( trimmedDescription ) );
			}
		}
		if ( context.services?.sections ) {
			tools.push(
				addContainerToServicesSectionTool( {
					description: trimmedDescription,
					itemCount: context.services?.itemCount,
				} )
			);
		}
		if ( context.testimonials?.sections ) {
			tools.push(
				addItemToTestimonialsSectionTool( {
					description: trimmedDescription,
					itemCount: context.testimonials?.itemCount,
				} )
			);
		}
		if ( context.pricing?.sections ) {
			tools.push(
				addContainerToPricingSectionTool( {
					description: trimmedDescription,
					itemCount: context.pricing?.itemCount,
					tiers: context.pricing?.tiers,
				} )
			);
		}
		if ( context.team?.pattern ) {
			if ( context.team?.sections ) {
				tools.push( addContainerToTeamSectionTool() );
			} else if ( context.team?.items ) {
				tools.push( addMemberToTeamContainerTool() );
			}
		}

		if ( context?.contactForm?.isOnPage ) {
			tools.push( modifyContactFormFieldsTool() );
		}

		if ( ! context.selectedBlock ) {
			return tools;
		}

		// load Big Sky pattern tools depending on existing patterns
		if ( context.menu?.pattern && context.menu?.sections ) {
			tools.push( addItemToMenuContainerTool( trimmedDescription ) );
		}
		if ( context.events?.pattern && context.events?.sections ) {
			tools.push(
				addItemToEventsContainerTool( {
					description: trimmedDescription,
					dateFormatExample: context.events?.dateFormatExample,
					timeFormatExample: context.events?.timeFormatExample,
				} )
			);
		}
		if ( context.team?.pattern && context.team?.sections ) {
			tools.push( addMemberToTeamContainerTool() );
		}

		// Add tools that require a specific selected block type
		switch ( context.selectedBlock.type ) {
			case 'core/button':
				tools.push( createUpdateButtonLinkTool() );
				break;
			case 'core/paragraph':
			case 'core/heading':
				tools.push( rewriteContentTool() );
				break;
			default:
				break;
		}

		return tools;
	},
};

export default contentToolkit;
