/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';

/**
 * Internal dependencies
 */
import { getNextAvailableContentValue } from '../utils.js';
import { getTestimonialsContentSystemPrompt } from './system-prompt.js';
import { patternKeys } from '@utils/constants';

function replaceContent( {
	rootBlock,
	data: testimonialsData,
	updateBlockAttributes,
	startIndex = -1,
} ) {
	const { ITEMS_KEY, ITEM_KEY } = patternKeys;
	if ( ! testimonialsData ) {
		return;
	}

	// If this is the root pattern block, make a deep copy of the data to avoid mutation for other patterns
	let newTestimonialsData = null;
	if ( rootBlock?.attributes?.metadata?.patternCategory ) {
		newTestimonialsData = JSON.parse( JSON.stringify( testimonialsData ) );
	} else {
		newTestimonialsData = testimonialsData;
	}

	let newStartIndex = startIndex;
	// reset the start index if we're changing menu items
	if ( rootBlock?.attributes?.metadata?.name === ITEMS_KEY ) {
		newStartIndex = startIndex;
	} else if ( rootBlock?.attributes?.metadata?.name === ITEM_KEY ) {
		// increment the start index if we're going to a new menu item
		newStartIndex++;
	}

	const validClassNames = [
		'is-testimonial-name',
		'is-testimonial-handle',
		'is-testimonial-review',
	];

	if ( rootBlock?.attributes?.className ) {
		const rootBlockClassNames = rootBlock.attributes.className.split( ' ' );

		const matchingClassName = rootBlockClassNames.find( ( className ) =>
			validClassNames.includes( className )
		);

		if ( matchingClassName ) {
			const { updatedTestimonialsData, contentValue } =
				getNextAvailableContentValue( {
					data: newTestimonialsData,
					type: matchingClassName.replace( 'is-testimonial-', '' ),
					offset: newStartIndex,
				} );
			newTestimonialsData = updatedTestimonialsData;

			let testimonialContent = contentValue;
			if ( matchingClassName === 'is-testimonial-name' ) {
				testimonialContent = __(
					'Testimonial generated by AI',
					'big-sky'
				);
			} else if ( matchingClassName === 'is-testimonial-handle' ) {
				// translators: @generatedbyai is a fake account name we use to make it clear that an AI generated testimonial is AI generated.
				testimonialContent = __( '@generatedbyai', 'big-sky' );
			}

			if ( updateBlockAttributes ) {
				updateBlockAttributes( rootBlock.clientId, {
					content: testimonialContent,
				} );
			} else {
				rootBlock.attributes.content = testimonialContent;
			}
		}
	}

	rootBlock?.innerBlocks.forEach( ( block ) => {
		newStartIndex = replaceContent( {
			rootBlock: block,
			data: newTestimonialsData,
			updateBlockAttributes,
			startIndex: newStartIndex,
		} );
	} );

	return newStartIndex;
}

function getPrompts() {
	const prompts = {
		system: getTestimonialsContentSystemPrompt,
		user: 'Generate 4 testimonials for this website based on the description and reason the section exists.',
	};
	return prompts;
}
export default {
	name: 'testimonials',
	getPrompts,
	replaceContent,
};
