/**
 * WordPress dependencies
 */
import { useDispatch } from '@wordpress/data';

/**
 * Internal dependencies
 */
import { store as aiStore } from '@store';
import { requestJetpackToken } from '@utils/request-jetpack-token.ts';

const useJetpackToken = () => {
	const { setApiKey, setHasRetriedChatRequest } = useDispatch( aiStore );

	const withJetpackTokenRequest = async ( callback ) => {
		setHasRetriedChatRequest( false );
		const newTokenData = await requestJetpackToken();
		if ( newTokenData && newTokenData.token ) {
			setApiKey( newTokenData.token );
			callback( newTokenData );
		}
	};

	return { withJetpackTokenRequest };
};

export default useJetpackToken;
