/**
 * WordPress dependencies
 */
import { store as blockEditorStore } from '@wordpress/block-editor';
import { dispatch, useDispatch, useRegistry, useSelect } from '@wordpress/data';
import { store as coreStore } from '@wordpress/core-data';
import { __, sprintf } from '@wordpress/i18n';
import { uploadMedia } from '@wordpress/media-utils';

/**
 * Internal dependencies
 */
import useBlocks from '@hooks/use-blocks';
import useStyles from '@hooks/use-styles';
import { store as aiStore } from '@store';
import { requestJetpackToken } from '@utils/request-jetpack-token.ts';
import log from '@utils/log-debug';
import { getCanvasIframeElements } from '@utils/canvas-iframe-elements';
import {
	blobFromBase64,
	cropTransparentBlob,
	setCustomLogoCss,
	transparentFromGrayscale,
} from '@utils/images';

export default function useImages() {
	const { getImageStyle } = useSelect( ( select ) => {
		const { getSelectedBlock } = select( blockEditorStore );
		return {
			getBlock: select( blockEditorStore ).getBlock,
			getBlocksByName: select( blockEditorStore ).getBlocksByName,
			selectedBlock: getSelectedBlock(),
			isEditorIframeLoaded: select( aiStore ).getIsEditorIframeLoaded(),
			isHandoff: select( aiStore ).getIsHandoff(),
			getImageStyle: select( aiStore ).getImageStyle,
		};
	} );

	const registry = useRegistry();
	const { editEntityRecord } = useDispatch( coreStore );
	const { updateBlockAttributes } = useDispatch( blockEditorStore );
	const { setBlockIsProcessing } = useBlocks();
	const { generateImage, addCachedImage } = useDispatch( aiStore );
	const { getSurroundingContentOfBlock } = useBlocks();
	const { getThemeColors } = useStyles();

	const { useCustomCss } = useStyles();
	const [ customCss, setCustomCss ] = useCustomCss();

	const setImages = () => {};

	/**
	 * Sets images in blocks.
	 * @param {Array}   blocks
	 * @param {string}  topic
	 * @param {boolean} isConceptSection
	 * @param {Object}  blocksToUpdate
	 * @param {boolean} newPage          // If true, we don't generate cover images.
	 * @return {Promise<void>}
	 */
	const setImagesInBlocks = async (
		blocks,
		topic,
		isConceptSection = true,
		blocksToUpdate = {},
		newPage = false
	) => {
		// So the user can undo the image replacements in one step we need to get
		// all the async image urls first and then replace them using a batch update.
		const newImages = [];
		let imageIndex = 0;
		const getNewImages = async ( blocksArray ) => {
			for ( const block of blocksArray ) {
				if ( canGenerateImage( block ) ) {
					if ( block.name === 'core/cover' && ! newPage ) {
						try {
							const coverImageUrl = generateCoverImageWithAI( {
								block,
								topic,
							} );
							newImages.push( coverImageUrl );
						} catch ( error ) {
							log.error(
								'Error generating cover image: ',
								error
							);
							newImages.push( getStockImageURL( topic ) );
						}
					} else {
						newImages.push( getStockImageURL( topic ) );
					}
				}
				if ( block.innerBlocks?.length > 0 ) {
					getNewImages( block.innerBlocks );
				}
			}
		};

		const applyNewImages = ( blocksArray, images ) => {
			for ( const block of blocksArray ) {
				if ( canGenerateImage( block ) ) {
					const attrToUpdate = buildAttrToUpdateImage(
						block,
						images[ imageIndex ],
						topic
					);
					imageIndex++;
					if ( isConceptSection ) {
						// We must manually set block attributes here that are and are not on the canvas.
						block.attributes = {
							...block.attributes,
							...attrToUpdate,
						};
						// Don't update the blocks on the canvas yet, just store the updates.
						blocksToUpdate[ block.clientId ] = attrToUpdate;
					} else {
						updateBlockAttributes( block.clientId, attrToUpdate );
						setBlockIsProcessing( block, false );
					}
				}

				if ( block.innerBlocks?.length > 0 ) {
					applyNewImages( block.innerBlocks, images, imageIndex );
				}
			}
		};

		getNewImages( blocks );
		const resolvedImages = await Promise.all( newImages );

		registry.batch( () => {
			applyNewImages( blocks, resolvedImages );
		} );

		if ( isConceptSection ) {
			// This is the initial concept so fetch some extra images to cache to make rotation of site designs more interesting.
			for ( let i = 0; i < 2; i++ ) {
				getStockImageURL( topic );
			}
		}
	};

	const updateImagesInBlocks = async ( blocks, updates ) => {
		for ( const block of blocks ) {
			if ( canGenerateImage( block ) ) {
				const clientId = block.clientId;
				updateBlockAttributes( clientId, updates[ clientId ] );
				setBlockIsProcessing( block, false );
			}

			if ( block.innerBlocks?.length > 0 ) {
				await updateImagesInBlocks( block.innerBlocks, updates );
			}
		}
	};

	const getStockImageURL = async ( topic, numImages = 100 ) => {
		const image = await dispatch( aiStore ).nextStockPhoto(
			topic,
			numImages
		);
		if ( image?.URL ) {
			addCachedImage( image?.URL );
		}
		return image?.URL;
	};

	/**
	 * Generates a new image with dall-e 3.
	 * @param {string} topic
	 */
	const generateLogoWithAI = async ( { block, description } ) => {
		const inputPrompt = `You are an expert at creating line drawing logos for business websites.

		All of the logos you create follow these rules:

		- The logo must show a single person, place, or object that represents the following description: ${
			description.description ?? description
		}
		- The logo must always be a simple, black vector line drawing. Never use color.
		- The logo should NEVER contain text, words, letters, or numbers.`;

		// Use square size for logo
		const size = { width: 1024, height: 1024 };

		const response = await generateImage( {
			prompt: inputPrompt,
			size: `${ size.width }x${ size.height }`,
			response_format: 'b64_json',
			image_format: 'PNG',
		} );

		const base64Image = response?.data?.[ 0 ]?.b64_json;
		if ( base64Image ) {
			try {
				const grayscalePngBlob = blobFromBase64(
					base64Image,
					'image/png'
				);
				const transparentPngBlob =
					await transparentFromGrayscale( grayscalePngBlob );
				const croppedPngBlob =
					await cropTransparentBlob( transparentPngBlob );

				// upload to media library
				await uploadMedia( {
					filesList: [
						new File( [ croppedPngBlob ], 'site_logo.png', {
							type: 'image/png',
						} ),
					],
					// The image is marked as generated by Big Sky by setting `big_sky_generated_logo` to `-1`
					// for future compatibily. This lets us set the meta value of any child images to the post
					// ID of their parent (a positive integer).
					additionalData: { 'meta[big_sky_generated_logo]': -1 },
					// maxUploadFileSize,
					onError: ( { message } ) => log.error( message ),
					onFileChange: ( [ fileObj ] ) => {
						if ( fileObj?.id ) {
							// Batching these state changes doesn't affect the undo stack, but may
							// potentially prevent future async wonkiness.
							registry.batch( () => {
								// Set custom CSS first...
								setCustomLogoCss(
									customCss,
									setCustomCss,
									fileObj.url
								);
								// ...and logo image second so that a single undo will undo the image change.
								// `useColorizedLogo()` will pick up on that change and recalculate the required
								// custom CSS.
								editEntityRecord( 'root', 'site', undefined, {
									site_logo: fileObj.id,
								} );
							} );

							const { canvasiFrameRoot } =
								getCanvasIframeElements();
							canvasiFrameRoot?.style?.setProperty(
								'--site-logo-url',
								`url(${ fileObj.url })`
							);
							if ( block ) setBlockIsProcessing( block, false );
						}
					},
				} );

				return sprintf(
					// Translators: Placeholder represents the description of the image requested by the user.
					__( 'I have generated a new logo of: %s', 'big-sky' ),
					description
				);
			} catch ( error ) {
				log.error( 'Error:', error );
				return __(
					'I encountered an error while processing your request. Please try again.',
					'big-sky'
				);
			}
		}
	};

	const generateCoverImageWithAI = async ( { block, topic } ) => {
		const themeColors = getThemeColors();
		const colorHexes = themeColors.map( ( color ) => color.color );
		const inputPrompt = `Create a minimalistic background image to fit a versatile ${ topic } theme, and match the following color palette: ${ colorHexes.join(
			', '
		) }.
		The center of the image should be mostly empty for content placement, while the edges feature abstract, subtle design elements that evoke a sense of simplicity.
		Avoid using specific objects or textures to ensure the theme can be interpreted broadly. The image should be without any text or prominent focal points.`;

		const coverImageUrl = generateImageWithAI( {
			block,
			description: topic,
			customPrompt: inputPrompt,
			style: getImageStyle() || 'photographic',
			size: 'landscape',
		} );
		return coverImageUrl;
	};

	/**
	 * Generates a new image with AI
	 * @param {string} input             The prompt to generate the image.
	 * @param {string} input.description
	 */
	const generateImageWithAI = async ( {
		block,
		description,
		style,
		size = false,
		customPrompt = false,
		retry = false,
	} ) => {
		if ( ! block || ! canGenerateImage( block ) ) {
			return __(
				'I cannot generate an image for the currently selected block.',
				'big-sky'
			);
		}
		const jetpackToken = await requestJetpackToken( ! retry );
		if ( ! jetpackToken ) {
			return __( 'I am unable to perform your request.', 'big-sky' );
		}

		const surroundingContentOfBlock = block?.clientId
			? getSurroundingContentOfBlock( block )
			: '';
		let inputPrompt;
		if ( customPrompt ) {
			inputPrompt = customPrompt;
		} else {
			inputPrompt = `Please create an image based on this content: ${
				description ? description : surroundingContentOfBlock
			}.`;
			inputPrompt += ' The image should not contain any text.';
		}

		const aspectRatio = block.attributes.aspectRatio;
		let widthRatio, heightRatio, selectedSize;

		// Predefined sizes by our API. We need to pick the closest one to the calculated aspect ratio.
		const sizes = {
			square: { width: 1024, height: 1024 },
			portrait: { width: 768, height: 1024 },
			landscape: { width: 1024, height: 768 },
		};

		if ( size ) {
			selectedSize = sizes[ size ];
		} else if ( aspectRatio === '1' || ! aspectRatio ) {
			selectedSize = sizes.square;
		} else {
			[ widthRatio, heightRatio ] = aspectRatio
				.split( '/' )
				.map( Number );

			const calculatedAspectRatio = widthRatio / heightRatio;
			if ( calculatedAspectRatio > 1 ) {
				selectedSize = sizes.landscape;
			} else {
				selectedSize = sizes.portrait;
			}
		}
		const endpointArgs = {
			prompt: inputPrompt,
			style,
			model: 'flux',
			token: jetpackToken.token,
		};

		let isError = false;
		let response;
		try {
			response = await fetch(
				`https://public-api.wordpress.com/wpcom/v2/sites/${ jetpackToken.blogId }/ai-image`,
				{
					method: 'POST',
					headers: {
						'Content-Type': 'application/json',
					},
					body: JSON.stringify( {
						...endpointArgs,
						...selectedSize,
					} ),
				}
			);
			if ( response.status >= 300 ) {
				// retry once with a new token
				if ( ! retry ) {
					return generateImageWithAI( {
						block,
						description,
						style,
						size,
						customPrompt,
						retry: true,
					} );
				}

				log.error( 'Error calling Image Generation API: ', response );
				isError = true;
			}
		} catch ( error ) {
			// retry once with a new token
			if ( ! retry ) {
				return generateImageWithAI( {
					block,
					description,
					style,
					size,
					customPrompt,
					retry: true,
				} );
			}

			log.error( error );
			isError = true;
		}
		if ( isError ) {
			setBlockIsProcessing( block, false );

			return __(
				'I encountered an error while processing your request. Please try again.',
				'big-sky'
			);
		}
		const blob = await response.blob();

		let uploadedImageUrl = null;

		await uploadMedia( {
			filesList: [
				new File( [ blob ], `${ description || 'untitled' }.png`, {
					type: 'image/png',
				} ),
			],
			// maxUploadFileSize,
			onError: ( { message } ) => log.error( message ),
			onFileChange: ( [ fileObj ] ) => {
				if ( fileObj.id ) {
					// eslint-disable-next-line no-undef
					const img = new Image();
					img.src = fileObj.url;
					img.addEventListener( 'load', () => {
						const attrToUpdate = buildAttrToUpdateImage(
							block,
							fileObj.url,
							description
						);
						updateBlockAttributes( block.clientId, attrToUpdate );

						setBlockIsProcessing( block, false );
						uploadedImageUrl = fileObj.url;
					} );
					img.addEventListener( 'error', () => {
						setBlockIsProcessing( block, false );
					} );
				}
			},
		} );

		// Wait for the image to be uploaded and processed
		while ( ! uploadedImageUrl ) {
			await new Promise( ( resolve ) => setTimeout( resolve, 100 ) );
		}

		return uploadedImageUrl;
	};

	/**
	 * Checks if the block can have an image generated for it.
	 * @param {any} block
	 * @return {boolean} Whether the block can have an image generated for it.
	 */
	const canGenerateImage = ( block ) => {
		const imageReplaceableBlocks = [
			'core/image',
			'core/cover',
			'core/group',
			'core/site-logo',
		];

		if ( ! block || ! imageReplaceableBlocks.includes( block.name ) ) {
			return false;
		}

		// we don't want to include groups with no background image, return false
		if (
			block.name === 'core/group' &&
			! block.attributes.style?.background?.backgroundImage?.url
		) {
			return false;
		}

		return true;
	};

	const buildAttrToUpdateImage = ( block, imageUrl, description ) => {
		const attrToUpdate = {};
		switch ( block.name ) {
			case 'core/image':
				attrToUpdate.url = imageUrl;
				attrToUpdate.alt = description;
				break;
			case 'core/cover':
				attrToUpdate.url = imageUrl;
				attrToUpdate.alt = description;
				if ( block.attributes.dimRatio === 100 ) {
					// if dimRatio is default 100, set it to 50, otherwise the bg image will not be visible
					attrToUpdate.dimRatio = 50;
				}
				break;
			case 'core/group':
				attrToUpdate.style = {
					background: {
						backgroundImage: {
							source: `file`,
							url: decodeURIComponent( imageUrl ),
						},
					},
				};
				break;
		}
		return attrToUpdate;
	};

	const getNewStockImage = async ( { block, topic } ) => {
		if ( ! canGenerateImage( block ) ) {
			return __(
				'I cannot generate an image for the currently selected block.',
				'big-sky'
			);
		}

		const image = await getStockImageURL( topic );

		if ( ! image ) {
			setBlockIsProcessing( block, false );
			return __(
				'I could not retrieve a new stock image. Please try again.',
				'big-sky'
			);
		}
		const attrToUpdate = buildAttrToUpdateImage( block, image, topic );

		updateBlockAttributes( block.clientId, attrToUpdate );

		setBlockIsProcessing( block, false );

		return sprintf(
			// Translators: Placeholder represents the topic of the image requested by the user.
			__( 'I have inserted a new stock image of: %s', 'big-sky' ),
			topic
		);
	};

	return {
		setImagesInBlocks,
		getStockImageURL,
		setImages,
		generateLogoWithAI,
		generateImageWithAI,
		getNewStockImage,
		canGenerateImage,
		updateImagesInBlocks,
		buildAttrToUpdateImage,
	};
}
