/**
 * WordPress dependencies
 */
import { store as blockEditorStore } from '@wordpress/block-editor';
import { useDispatch, useSelect } from '@wordpress/data';
import { __, sprintf } from '@wordpress/i18n';
import { store as coreStore } from '@wordpress/core-data';

/**
 * Internal dependencies
 */
import useBlocks from '@hooks/use-blocks';
import { store as aiStore } from '@store';
import useSiteMetadata from '@hooks/use-site-metadata';
import useGeocoder from '@hooks/use-geocoder';
import { dockViews } from '@utils/constants';

function useAddressToolFunctions() {
	const { getBlock, getBlocks, getBlocksByName } = useSelect(
		( select ) => ( {
			getBlock: select( blockEditorStore ).getBlock,
			getBlocks: select( blockEditorStore ).getBlocks,
			getBlocksByName: select( blockEditorStore ).getBlocksByName,
		} )
	);

	const { updateBlockAttributes } = useDispatch( blockEditorStore );
	const { setLastCancelableAction } = useDispatch( aiStore );
	const { getSiteMetadata, setSiteMetadata } = useSiteMetadata();
	const { getTemplatePartClientID, getContentBlockClientID } = useBlocks();
	const { getCoordinates } = useGeocoder();
	const { setDockView } = useDispatch( aiStore );

	const settings = useSelect( ( select ) => {
		const _settings = select( coreStore ).getEntityRecord( 'root', 'site' );
		return { siteTitle: _settings?.title ?? '' };
	} );

	const validateAddressArgs = ( args ) => !! ( args && args.address );

	const updateAddress = async ( parameters ) => {
		try {
			const newAddress = await updateAddressOnPage( parameters );
			const newCoordinates = await updateMapMarker( parameters );

			const { siteLocation } = getSiteMetadata();
			const oldAddress = siteLocation?.name;

			setLastCancelableAction( {
				action: 'update_address',
				address: oldAddress,
				coordinates: siteLocation?.coordinates,
			} );

			setSiteMetadata(
				{
					siteLocation: {
						name: newAddress,
						coordinates: newCoordinates,
					},
				},
				true
			);

			return sprintf(
				// Translators: %s is the new address.
				__( 'Your address has been updated to "%s".', 'big-sky' ),
				newAddress
			);
		} catch ( error ) {
			console.error( 'Error updating address:', error );
			return false;
		}
	};

	const cancelUpdateAddress = async ( parameters ) => {
		try {
			await updateAddressOnPage( { ...parameters, isUndo: true } );
			await updateMapMarker( { ...parameters, isUndo: true } );

			setSiteMetadata(
				{
					siteLocation: {
						name: parameters.address,
						coordinates: parameters.coordinates,
					},
				},
				true
			);

			setDockView( dockViews.MENUS );

			return true;
		} catch ( error ) {
			console.error( 'Error canceling address update:', error );
			return false;
		}
	};

	const updateAddressOnPage = async ( parameters ) => {
		const { address: newAddress } = parameters;
		const { siteLocation } = getSiteMetadata();
		const oldAddress = siteLocation?.name;
		if ( ! oldAddress ) {
			// There's nothing to update on the page.
			return newAddress;
		}

		const headerBlocks = getBlocks( getTemplatePartClientID( 'header' ) );
		const footerBlocks = getBlocks( getTemplatePartClientID( 'footer' ) );
		const contentBlocks = getBlocks( getContentBlockClientID() );

		const blocks = [ ...headerBlocks, ...footerBlocks, ...contentBlocks ];

		blocks.forEach( ( block ) =>
			findAndUpdateAddress( block, oldAddress, newAddress )
		);

		return newAddress;
	};

	const findAndUpdateAddress = ( block, oldAddress, newAddress ) => {
		if (
			block.name === 'core/heading' ||
			block.name === 'core/paragraph'
		) {
			const content = block.attributes.content || '';
			const normalizedContent = normalizeAddress(
				content?.text || content
			);
			const normalizedOldAddress = normalizeAddress(
				oldAddress?.name || oldAddress
			);

			if ( ! normalizedContent.includes( normalizedOldAddress ) )
				return false;

			const regex = new RegExp(
				escapeRegExp( oldAddress ).replace( /[\s,]+/g, '[\\s,]*' ),
				'gi'
			);
			const newContent = content.replace( regex, newAddress ?? '' );
			updateBlockAttributes( block.clientId, { content: newContent } );

			return true;
		}

		if ( block.innerBlocks && block.innerBlocks.length > 0 ) {
			return block.innerBlocks.forEach( ( innerBlock ) =>
				findAndUpdateAddress( innerBlock, oldAddress, newAddress )
			);
		}

		return false;
	};

	const updateMapMarker = async ( parameters ) => {
		const { address: newAddress, coordinates, isUndo } = parameters;

		const newCoordinates = isUndo
			? coordinates
			: await getCoordinates( newAddress );
		if ( ! newCoordinates && ! isUndo ) return;

		const mapBlocks = getBlocksByName( 'jetpack/map' );

		mapBlocks.forEach( ( mapBlockClientId ) => {
			const mapBlock = getBlock( mapBlockClientId );
			const updatedPoints = newCoordinates
				? [
						{
							placeTitle: newAddress ?? '',
							title: settings.siteTitle || ( newAddress ?? '' ),
							caption: '',
							/* eslint-disable-next-line no-restricted-syntax */
							id: `place.${ Math.random() }`,
							coordinates: {
								latitude: newCoordinates[ 0 ],
								longitude: newCoordinates[ 1 ],
							},
						},
				  ]
				: [];

			updateBlockAttributes( mapBlock.clientId, {
				mapCenter: {
					lat: newCoordinates?.[ 0 ] ?? 0,
					lng: newCoordinates?.[ 1 ] ?? 0,
				},
				points: updatedPoints,
			} );
		} );

		return newCoordinates;
	};

	const normalizeAddress = ( address ) =>
		address.replace( /[\s,]+/g, '' ).toLowerCase();
	const escapeRegExp = ( string ) =>
		string.replace( /[.*+?^${}()|[\]\\]/g, '\\$&' );

	return {
		updateAddress,
		validateAddressArgs,
		cancelUpdateAddress,
	};
}

export default useAddressToolFunctions;
