/**
 * WordPress dependencies
 */
import { store as blockEditorStore } from '@wordpress/block-editor';
import { useDispatch, useSelect } from '@wordpress/data';
import { useEffect } from '@wordpress/element';
import { __ } from '@wordpress/i18n';

/**
 * External dependencies
 */
import { useChat, useToolkits } from '@automattic/big-sky-agents';

/**
 * Internal dependencies
 */
import colorToolkit from './toolkit.js';
import useColorToolFunctions from './tools/use-tool-functions.js';
import useCanvas from '@hooks/use-canvas';
import { store as aiStore } from '@store';
import useVariations from '@hooks/use-variations';
import useChangeAssistantTool from '@ai/assistants/shared/change-assistant/use-change-assistant-tool.js';
import useCreateToolCallback from '../shared/use-create-tool-callback.js';
import useSiteMetadata from '@hooks/use-site-metadata';

const useColorToolkit = () => {
	const { createToolCallback } = useCreateToolCallback();
	const { getVariationKeywords } = useVariations();
	const { changeAssistant } = useChangeAssistantTool();
	const { agentSay } = useChat();
	const { setCallbacks, setContext } = useToolkits();

	const { getSiteMetadata } = useSiteMetadata();
	const { siteTitle, siteDescription, topic } = getSiteMetadata();

	const {
		generateColorPalette,
		validateGenerateColorPaletteArgs,
		validateUpdateComponentColorArgs,
		updateComponentColor,
	} = useColorToolFunctions();

	const { zoomOut } = useCanvas();

	const { getIsHandoff, currentHandoffArea, selectedBlock } = useSelect(
		( select ) => {
			const { getSelectedBlock } = select( blockEditorStore );
			const { getIsHandoff: _getIsHandoff, getCurrentHandoffArea } =
				select( aiStore );
			return {
				selectedBlock: getSelectedBlock(),
				getIsHandoff: _getIsHandoff,
				currentHandoffArea: getCurrentHandoffArea(),
			};
		}
	);

	const {
		setChatComponents,
		setIsToolContainerVisible,
		setToolComponent,
		setShowThread,
	} = useDispatch( aiStore );

	useEffect( () => {
		setCallbacks( colorToolkit.name, {
			color_palette_generator: createToolCallback( {
				name: 'color_palette_generator',
				action: ( _args ) => {
					generateColorPalette( {
						..._args,
						callback: ( component, message ) => {
							if ( getIsHandoff() ) {
								setChatComponents( [ component ] );
							} else {
								zoomOut();
								setToolComponent( component );
								setIsToolContainerVisible( true );
							}
							agentSay( message );
						},
					} );
				},
				validator: ( _args ) =>
					validateGenerateColorPaletteArgs( _args ),
				failValidatorAction: () =>
					agentSay(
						__(
							'Please provide a color or a category to view color options.',
							'big-sky'
						)
					),
				closeChatWhenDone: () => ! getIsHandoff(),
			} ),
			update_component_color: createToolCallback( {
				name: 'update_component_color',
				validator: ( _args ) =>
					validateUpdateComponentColorArgs( _args ),
				failValidatorAction: ( _, error ) => {
					agentSay( error.message );
				},
				action: async ( _args ) => {
					agentSay( await updateComponentColor( _args ) );
				},
				closeChatWhenDone: () => false,
			} ),
			change_assistant: createToolCallback( {
				name: 'change_assistant',
				action: ( _args ) => {
					setShowThread( true );
					changeAssistant( _args );
				},
				closeChatWhenDone: () => false,
			} ),
		} );
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [ setCallbacks ] );

	useEffect( () => {
		const context = {
			site: {
				siteTitle,
				siteDescription,
				topic,
			},
			selectedBlock: {
				clientId: selectedBlock?.clientId,
				type: selectedBlock?.type,
				name: selectedBlock?.name,
			},
			colors: {
				colorVariationCategories: getVariationKeywords( 'color' ),
			},
		};

		if ( getIsHandoff() ) {
			context.currentHandoffArea = currentHandoffArea;
		}

		setContext( colorToolkit.name, context );

		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [ setContext, getIsHandoff, currentHandoffArea, selectedBlock ] );
};

export default useColorToolkit;
