/**
 * WordPress dependencies
 */
import { store as coreDataStore } from '@wordpress/core-data';
import { useDispatch, useSelect } from '@wordpress/data';
import { store as editorStore } from '@wordpress/editor';
import { useCallback, useEffect } from '@wordpress/element';

/**
 * Internal dependencies
 */
import { store as aiStore } from '@store';
import { unlock } from '@utils/lock-unlock';
import log from '@utils/log-debug.js';

export default function useUndoRedo( { onToolbarUndoCallback } = {} ) {
	const { isEditorReady } = useSelect( ( select ) => ( {
		isEditorReady: select( aiStore ).getIsEditorReady(),
	} ) );

	const { getUndoManager } = unlock( useSelect( coreDataStore ) );
	const { undo: undoCommand, redo: redoCommand } = useDispatch( editorStore );

	const onToolbarUndo = useCallback( () => {
		if ( onToolbarUndoCallback ) {
			onToolbarUndoCallback();
		}
	}, [ onToolbarUndoCallback ] );

	// add click handler to the undo button
	useEffect( () => {
		if ( ! isEditorReady ) {
			return;
		}

		const undoButton = document.querySelector( '.editor-history__undo' );
		if ( undoButton ) {
			undoButton.addEventListener( 'click', onToolbarUndo );
		}

		return () => {
			if ( undoButton ) {
				undoButton.removeEventListener( 'click', onToolbarUndo );
			}
		};
	}, [ isEditorReady, onToolbarUndo ] );

	const undo = () => {
		try {
			undoCommand();
		} catch ( error ) {
			log.error( error );
		}
	};

	const redo = () => {
		try {
			redoCommand();
		} catch ( error ) {
			log.error( error );
		}
	};

	const popUndoStack = () => {
		// pop the undo stack
		// this doesn't actually update the block itself, but it does remove the last item (update) from the undo stack
		getUndoManager().undo();
	};

	return {
		undo,
		redo,
		popUndoStack,
	};
}
