/**
 * WordPress dependencies
 */
import { useDispatch, useSelect } from '@wordpress/data';
import { useCallback } from '@wordpress/element';
import { store as blockEditorStore } from '@wordpress/block-editor';

/**
 * Internal dependencies
 */
import usePages from '@hooks/use-pages';
import usePatterns from '@hooks/use-patterns';
import useSiteMetadata from '@hooks/use-site-metadata';

export default function useAddPageTool() {
	const { getSelectedBlock } = useSelect( blockEditorStore );

	const { updateBlockAttributes } = useDispatch( blockEditorStore );
	const { createPage } = usePages();
	const { syncSiteMetadataPatterns } = usePatterns();
	const { getSiteMetadata } = useSiteMetadata();

	const addPage = useCallback(
		async ( parameters ) => {
			const { topic, siteDescription, siteLocation } = getSiteMetadata();

			const selectedBlock = getSelectedBlock();
			const isLinkBlock = [ 'core/button' ].includes(
				selectedBlock?.name
			);
			const { title, categories } = parameters;

			const { newPage, rootBlocks } = await createPage(
				title,
				categories,
				topic,
				siteDescription,
				siteLocation
			);
			syncSiteMetadataPatterns( {
				ids: [ newPage.id ],
				blocks: [ rootBlocks ],
				persist: true,
			} );

			if ( isLinkBlock ) {
				updateBlockAttributes( selectedBlock.clientId, {
					url: `/?page_id=${ newPage.id }`,
				} );
			}

			return { title, categories, pageId: newPage.id };
		},
		[
			createPage,
			getSelectedBlock,
			getSiteMetadata,
			syncSiteMetadataPatterns,
			updateBlockAttributes,
		]
	);

	return { addPage };
}
