/**
 * WordPress dependencies
 */
import { useDispatch } from '@wordpress/data';
import { useChat } from '@automattic/big-sky-agents';

/**
 * Internal dependencies
 */
import { store as aiStore } from '@store';

export default function useChatThread() {
	const { agentSay, clearMessages } = useChat();
	const { setShowThread } = useDispatch( aiStore );

	const initializeThread = ( assistantMessage ) => {
		clearMessages();
		setShowThread( true );
		agentSay( assistantMessage );
	};

	return {
		initializeThread,
	};
}
