/**
 * WordPress dependencies
 */
import { useDispatch, useRegistry, useSelect } from '@wordpress/data';
import { store as blockEditorStore } from '@wordpress/block-editor';
import { __, sprintf } from '@wordpress/i18n';

/**
 * Internal dependencies
 */
import useReplaceContent from '@ai/tasks/replace-content/use-replace-content.js';
import useBlocks from '@hooks/use-blocks';
import usePatterns from '@hooks/use-patterns';
import useSiteMetadata from '@hooks/use-site-metadata';
import { store as aiStore } from '@store';
import setDelay from '@utils/set-delay';

export default function useContentToolFunctions() {
	const { getCachedContent } = useSelect( aiStore );
	const { addCachedContent, setBlockBeforeChanges, setLastCancelableAction } =
		useDispatch( aiStore );
	const { getBlock, getBlockOrder, getBlockParents } =
		useSelect( blockEditorStore );
	const { insertBlock, updateBlockAttributes } =
		useDispatch( blockEditorStore );
	const {
		copyBlockWithInnerBlocks,
		setBlockIsProcessing,
		getFirstChildBlockByName,
		scrollToBlock,
	} = useBlocks();
	const {
		ITEM_KEY,
		SECTION_KEY,
		getParentPatternCategory,
		getPattern,
		getSections,
		getSection,
		getItems,
		getItem,
	} = usePatterns();
	const { getSiteMetadata } = useSiteMetadata();

	const { replaceContent } = useReplaceContent();
	const registry = useRegistry();

	const shouldReplaceContent = ( category ) => {
		return ! [ 'team' ].includes( category );
	};

	const replaceContentAgent = ( { content, message } ) => {
		const remappedContent = content.map( ( block ) => ( {
			clientId: block.i,
			text: block.e,
		} ) );

		registry.batch( () => {
			remappedContent?.forEach( ( newContent ) => {
				const attributes = {};
				const block = getBlock( newContent.clientId );
				if ( block ) {
					// Setting the previous block attributes allows us to undo the change later.

					setBlockBeforeChanges( block );
					if (
						block.name === 'core/heading' ||
						block.name === 'core/paragraph'
					) {
						attributes.content = newContent.text;
					} else if ( block.name === 'core/site-title' ) {
					} else if ( block.name === 'core/button' ) {
						attributes.text = newContent.text;
					}

					updateBlockAttributes( newContent.clientId, {
						...attributes,
					} );
				}
			} );
		} );

		return message
			? message
			: __( 'I have replaced the content.', 'big-sky' );
	};

	const getCurrentPricingContent = () => {
		const cachedContent = getCachedContent();
		if ( cachedContent?.pricing && cachedContent.pricing[ 0 ]?.tiers ) {
			return {
				title: cachedContent.pricing[ 0 ]?.title,
				description: cachedContent.pricing[ 0 ]?.description,
				tiers: cachedContent.pricing[ 0 ].tiers.map(
					( tier ) => tier.name
				),
			};
		}
		return {};
	};

	const getItemsCount = (
		category,
		block = getItems( category, getPattern( category ) )
	) => {
		if ( ! block ) {
			return 0;
		}

		let count = 0;
		for ( const innerBlock of block.innerBlocks ) {
			if ( innerBlock.attributes?.metadata?.name === ITEM_KEY ) {
				count++;
			}
			count += getItemsCount( category, innerBlock );
		}
		return count;
	};

	const validateMenuSectionArgs = ( args ) => {
		if ( ! args.title ) {
			throw new Error(
				__( 'Please provide a title for the menu section.', 'big-sky' )
			);
		}
	};

	const validateAddContentSectionArgs = ( args ) => {
		if ( ! getPattern( args.category ) ) {
			throw new Error(
				sprintf(
					// translators: %s is the name of the pattern category.
					__( `No %s pattern found.`, 'big-sky' ),
					args.category
				)
			);
		}

		if ( ! getSections( args.category ) ) {
			throw new Error(
				sprintf(
					// translators: %s is the name of the pattern category.
					__( `No %s sections block found.`, 'big-sky' ),
					args.category
				)
			);
		}

		switch ( args.category ) {
			case 'menu':
				validateMenuSectionArgs( args );
				break;
			default:
				break;
		}

		return true;
	};

	const getStartIndex = ( category, title ) => {
		const cachedContent = getCachedContent();
		let startIndex = cachedContent?.[ category ]?.length - 1 || -1;
		if ( category === 'menu' ) {
			startIndex =
				cachedContent?.[ category ]?.find(
					( item ) => item.name === title
				)?.items?.length - 1 || -1;
		} else if ( category === 'pricing' ) {
			startIndex =
				cachedContent?.[ category ]?.[ 0 ]?.tiers?.length - 1 || -1;
		}
		return startIndex;
	};

	const addSection = async ( {
		category,
		title,
		items,
		useCacheKey = false,
	} ) => {
		try {
			const { siteDescription } = getSiteMetadata();
			const sectionsBlock = getSections( category );
			const sectionBlock = getSection( category, sectionsBlock );

			// insert the menu section block
			const blockToInsert = copyBlockWithInnerBlocks( sectionBlock );
			await insertBlock(
				blockToInsert,
				getBlockOrder( sectionsBlock.clientId ).length,
				sectionsBlock.clientId,
				false
			);
			if ( shouldReplaceContent( category ) ) {
				setBlockIsProcessing( blockToInsert, true );
			}
			scrollToBlock( blockToInsert.clientId, 'center' );
			await setDelay( 500 );

			// update the section heading
			if ( title ) {
				const headingBlock = getFirstChildBlockByName(
					blockToInsert,
					'core/heading'
				);
				if ( headingBlock ) {
					headingBlock.attributes.content = title;
					updateBlockAttributes(
						headingBlock.clientId,
						headingBlock.attributes
					);
				}
			}

			// update the section content
			const itemsBlock = getItems( category, blockToInsert );
			const replaceContentArgs = {
				conceptSection: [ itemsBlock ],
				sectionCategory: category,
				siteDescription,
				sectionHeading: title,
				replaceHeading: false,
				inMemory: false,
				startIndex: getStartIndex( category, title ),
			};

			let itemsToAdd = 0;
			const itemsCount = getItemsCount( category, itemsBlock );
			// if the section has a title, then we should use it as a key under the cached content
			if ( useCacheKey && items ) {
				await addCachedContent( {
					category,
					content: [ { name: title, items } ],
					append: true,
				} );
				itemsToAdd = items.length - itemsCount;
			} else if ( items ) {
				await addCachedContent( {
					category,
					content: items,
					append: true,
				} );
				itemsToAdd = items.length - itemsCount;
			} else {
				replaceContentArgs.noCache = true;
				replaceContentArgs.numOfSections = 1;
			}

			if ( shouldReplaceContent( category ) ) {
				// replace content of the existing items
				await replaceContent( replaceContentArgs );
			}

			// check if we need to add more items
			if ( itemsToAdd > 0 ) {
				await addItems( {
					clientId: itemsBlock.clientId,
					count: itemsToAdd,
					category,
					cacheOffset: itemsCount - 1,
				} );
			}

			if ( shouldReplaceContent( category ) ) {
				await setDelay( 500 );
				setBlockIsProcessing( blockToInsert, false );
			}

			// Set the last cancelable action to allow the user to undo the pattern insertion
			setLastCancelableAction( {
				action: `add_${ category }_section`,
				clientId: blockToInsert.clientId,
			} );

			return sprintf(
				// translators: %s is the name of the pattern category.
				__( 'I have added a new %s section.', 'big-sky' ),
				category
			);
		} catch ( error ) {
			return sprintf(
				// translators: %s is the name of the pattern category.
				__(
					'There was an issue updating the content for the new %s section.',
					'big-sky'
				),
				category
			);
		}
	};

	const getParentComponentByName = ( childBlock, name ) => {
		if ( childBlock.attributes?.metadata?.name === name ) {
			return childBlock;
		}

		const groupBlockParentIds = getBlockParents( childBlock.clientId );
		for ( const groupBlockParentId of groupBlockParentIds ) {
			const groupBlockParent = getBlock( groupBlockParentId );
			if ( groupBlockParent.attributes?.metadata?.name === name ) {
				return groupBlockParent;
			}
		}
	};

	const validateAddItemsArgs = ( args ) => {
		if ( ! getPattern( args.category ) ) {
			throw new Error(
				sprintf(
					// translators: %s is the name of the pattern category.
					__( 'No %s pattern found.', 'big-sky' ),
					args.category
				)
			);
		}

		const sections = getSections( args.category );
		// if there is no selected block and there are sections, throw an error
		if ( ! args.clientId ) {
			if ( sections ) {
				throw new Error(
					sprintf(
						// translators: %s is the name of the pattern category.
						__( 'Select a %s section to add items to.', 'big-sky' ),
						args.category
					)
				);
			}

			return true;
		}

		// if there's no sections, check if the selected block is within the pattern
		if (
			! sections &&
			getParentPatternCategory( getBlock( args.clientId ) ) ===
				args.category
		) {
			return true;
		}

		return true;
	};

	const addItems = async ( {
		category,
		clientId,
		count,
		items,
		useCacheKey = false,
	} ) => {
		try {
			const { topic } = getSiteMetadata();
			let title = topic;
			let itemsBlock;

			if ( clientId ) {
				const block = getBlock( clientId );
				const sectionBlock = getParentComponentByName(
					block,
					SECTION_KEY
				);

				if ( sectionBlock ) {
					const headingBlock = getFirstChildBlockByName(
						sectionBlock,
						'core/heading'
					);
					title =
						headingBlock?.attributes?.content?.text ||
						headingBlock?.attributes?.content ||
						title;
					itemsBlock = getItems( category, sectionBlock );
				}
			}

			itemsBlock =
				itemsBlock || getItems( category, getPattern( category ) );

			// get the item block
			const itemBlock = getItem( category, itemsBlock );

			// insert the item block
			const blocksToInsert = [];
			const insertIndex = getBlockOrder( itemsBlock.clientId ).length;
			for ( let i = 0; i < count; i++ ) {
				const blockToInsert = copyBlockWithInnerBlocks( itemBlock );
				await insertBlock(
					blockToInsert,
					insertIndex + i,
					itemsBlock.clientId,
					false
				);
				if ( shouldReplaceContent( category ) ) {
					setBlockIsProcessing( blockToInsert, true );
				}
				blocksToInsert.push( blockToInsert );
			}

			// get the start index to replace content
			const startIndex = getStartIndex( category, title );

			if ( items ) {
				if ( useCacheKey ) {
					await addCachedContent( {
						category,
						content: [ { name: title, items } ],
						append: true,
					} );
				} else {
					await addCachedContent( {
						category,
						content: items,
						append: true,
					} );
				}
			}

			if ( shouldReplaceContent( category ) ) {
				let newStartIndex = startIndex;
				for ( const blockToInsert of blocksToInsert ) {
					await replaceContent( {
						conceptSection: [ blockToInsert ],
						sectionCategory: category,
						sectionHeading: title,
						startIndex: newStartIndex,
						inMemory: false,
					} );
					await setDelay( 500 );
					setBlockIsProcessing( blockToInsert, false );
					newStartIndex++;
				}
			}

			setLastCancelableAction( {
				action: `add_${ category }_items`,
				clientIds: blocksToInsert.map( ( block ) => block.clientId ),
			} );

			return sprintf(
				// translators: %s is the name of the pattern category.
				__( 'I have added new %s items.', 'big-sky' ),
				category
			);
		} catch ( error ) {
			return sprintf(
				// translators: %s is the name of the pattern category.
				__(
					'There was an issue updating the content for the new %s item.',
					'big-sky'
				),
				category
			);
		}
	};

	return {
		replaceContentAgent,
		addSection,
		validateAddContentSectionArgs,
		addItems,
		validateAddItemsArgs,
		getItemsCount,
		getCurrentPricingContent,
	};
}
