import { createColorHash, updateThemePalette } from '../colors.js';
import { BIG_SKY_CUSTOM_THEME_PREFIX } from './constants.js';
import { colord } from 'colord';

// This custom scheme is applied to the template theme
// It sorts the custom colors by brightness (darkest to lightest) and updates the color palette
const darkTheme = ( { templateTheme, colors } ) => {
	// Create array of color entries to sort
	const colorEntries = Object.entries( colors ).map( ( [ key, value ] ) => [
		key,
		value.color,
	] );

	// Sort colors by brightness (darkest to lightest)
	const sortedColors = colorEntries.sort(
		( a, b ) =>
			colord( a[ 1 ] ).brightness() - colord( b[ 1 ] ).brightness()
	);

	// Create new colors object with desired order
	const reorderedColors = {
		color1: sortedColors[ 0 ][ 1 ], // Darkest color
		color2: sortedColors[ 3 ][ 1 ], // Lightest color
		color3: sortedColors[ 1 ][ 1 ], // Second darkest
		color4: sortedColors[ 2 ][ 1 ], // Second lightest
	};

	// Apply the reordered colors to the theme
	updateThemePalette( {
		theme: templateTheme,
		colors: reorderedColors,
	} );

	templateTheme.title = `${ BIG_SKY_CUSTOM_THEME_PREFIX } Dark Theme - ${ createColorHash(
		Object.fromEntries(
			Object.entries( colors ).map( ( [ key, value ] ) => [
				key,
				value.color,
			] )
		)
	) }`;

	return templateTheme;
};

export default darkTheme;
