/**
 * External dependencies
 */
import { useSelect } from '@wordpress/data';
import { useEffect, useRef, useState } from '@wordpress/element';
import {
	__unstableAnimatePresence as AnimatePresence,
	__unstableMotion as motion,
	Popover,
} from '@wordpress/components';
import {
	ChatModelService,
	ChatModelType,
	useAgentExecutor,
	useChat,
	useChatSettings,
} from '@automattic/big-sky-agents';

/**
 * Internal dependencies
 */
import './style.scss';
import OnboardingLandingView from '@components/onboarding/landing-view';
import OnboardingChatView from '@components/onboarding/chat-view';
import { store as aiStore } from '@store';
import { recordTracksEvent } from '@utils/tracks';
import setDelay from '@utils/set-delay';
import { useRouting } from '@hooks/use-routing';
import useSiteMetadata from '@hooks/use-site-metadata';

export default function Onboarding() {
	const { topic } = useSiteMetadata().getSiteMetadata();
	const { apiKey, sessionId } = useSelect( ( select ) => ( {
		apiKey: select( aiStore ).getApiKey(),
		sessionId: select( aiStore ).getSessionId(),
	} ) );
	const [ showChat, setShowChat ] = useState( !! topic );
	const previousTopicRef = useRef( topic );
	const isLocalGraph = window.bigSkyInitialState?.isLocalGraph;
	const { setStoreConversation } = useChat();

	const { setAiStep } = useRouting();

	useEffect( () => {
		if ( previousTopicRef.current !== null && topic === null ) {
			setShowChat( false );
		}
		previousTopicRef.current = topic;
	}, [ topic ] );

	setAiStep( 'onboarding' );

	useChatSettings( {
		apiKey,
		feature: 'big-sky',
		service: isLocalGraph
			? ChatModelService.LOCAL_GRAPH
			: ChatModelService.WPCOM_JETPACK_AI,
		model: ChatModelType.GPT_4O,
		sessionId,
		stream: false,
	} );

	useAgentExecutor();

	// Add body overflow class on mount and remove on unmount
	useEffect( () => {
		document.body.classList.add( 'big-sky-onboarding-open' );
		return () => {
			document.body.classList.remove( 'big-sky-onboarding-open' );
		};
	}, [] );

	const hasRecordedEvent = useRef( false );
	useEffect( () => {
		if ( ! hasRecordedEvent.current ) {
			recordTracksEvent( 'onboarding_started' );
			hasRecordedEvent.current = true;
		}
	}, [] );

	useEffect( () => {
		if ( setStoreConversation ) {
			// We decided during the Big Sky call 2024-11-01 that we want to store all convos by default dyring the launch.
			setStoreConversation( true );
		}
	}, [ setStoreConversation ] );

	const handleShowChatView = async () => {
		setShowChat( true );

		// wait for animations before sending the message.
		await setDelay( 1500 );
	};

	const contentAnimationProps = {
		initial: { opacity: 0 },
		animate: { opacity: 1 },
		exit: { opacity: 0 },
		transition: {
			ease: 'easeInOut',
			duration: 0.3,
			delay: 0.3,
		},
	};

	return (
		<Popover className="big-sky-onboarding">
			<motion.div
				className="big-sky-onboarding__content"
				key="onboarding-content"
				{ ...contentAnimationProps }
			>
				<AnimatePresence mode="wait">
					{ ! showChat ? (
						<OnboardingLandingView
							key="onboarding-landing-view"
							onSendMessage={ handleShowChatView }
							onClickSuggestion={ handleShowChatView }
						/>
					) : (
						<OnboardingChatView key="onboarding-chat-view" />
					) }
				</AnimatePresence>
			</motion.div>
		</Popover>
	);
}
