/**
 * WordPress dependencies
 */
import { createBlock } from '@wordpress/blocks';
import { store as blockEditorStore } from '@wordpress/block-editor';
import { useDispatch, useSelect } from '@wordpress/data';
import { __, sprintf } from '@wordpress/i18n';

/**
 * Internal dependencies
 */
import useBlocks from '@hooks/use-blocks';
import useVariations from '@hooks/use-variations';
import useValidation from '@hooks/use-validation';
import { store as aiStore } from '@store';

function useButtonToolFunctions() {
	const { getBlock, getBlocksByName } = useSelect( blockEditorStore );
	const { insertBlock, updateBlockAttributes } =
		useDispatch( blockEditorStore );
	const { setLastCancelableAction } = useDispatch( aiStore );

	const { scrollToBlock } = useBlocks();
	const { getSocialLinkVariations } = useVariations();
	const { isValidClientId, blockExists } = useValidation();

	const validateButtonArgs = ( args ) => {
		if ( ! args ) {
			return false;
		}

		const { clientId } = args;

		// check if client ID is null or invalid and that we have a valid block.
		if ( ! isValidClientId( clientId ) || ! blockExists( clientId ) ) {
			return false;
		}

		return true;
	};

	const updateButtonLink = async ( parameters ) => {
		const { clientId, url } = parameters;

		// If the button is a social link, update other social link buttons using the same service
		const block = getBlock( clientId );
		updateBlockAttributes( clientId, {
			url,
		} );

		// Set the last cancelable action to allow the user to undo
		setLastCancelableAction( {
			action: 'update_button_link',
			clientId,
			originalUrl: block.attributes.url,
		} );

		return __( 'The link has been updated.', 'big-sky' );
	};

	const cancelUpdateButtonLink = ( { clientId, originalUrl } ) => {
		updateBlockAttributes( clientId, {
			url: originalUrl,
		} );
	};

	const validateSocialLinkArgs = ( args ) => {
		if ( ! args ) {
			throw new Error(
				__(
					"Sorry, I can't perform this action at this time.",
					'big-sky'
				)
			);
		}

		const { service } = args;
		const variations = getSocialLinkVariations();
		const variation = variations.find( ( v ) => v.name === service );
		if ( ! variation ) {
			throw new Error(
				__(
					'Sorry, the provided social media service is not supported.',
					'big-sky'
				)
			);
		}

		return true;
	};

	const upsertSocialLink = ( { service, url } ) => {
		// Get all social-links blocks
		const socialLinksBlocks = getBlocksByName( 'core/social-links' );
		let action = '';
		socialLinksBlocks.forEach( ( id, index ) => {
			const socialLinksBlock = getBlock( id );
			// Check if the block already has a social link for the service
			const socialLinkBlock = socialLinksBlock.innerBlocks.find(
				( block ) =>
					block.name === 'core/social-link' &&
					block.attributes.service === service
			);
			if ( ! socialLinkBlock ) {
				const newSocialLinkBlock = createBlock( 'core/social-link', {
					url,
					service,
				} );

				insertBlock(
					newSocialLinkBlock,
					socialLinksBlock.innerBlocks.length,
					id,
					index === 0
				);
				action = 'added';
			} else {
				// Update the existing block
				updateBlockAttributes( socialLinkBlock.clientId, {
					url,
				} );
				action = 'updated';
				if ( index === 0 ) {
					scrollToBlock( socialLinkBlock.clientId, 'center', true );
				}
			}
		} );

		return sprintf(
			// Translators: %1$s is the social link service, %2$s is the action (added/updated).
			__( 'The social link button for %1$s has been %2$s.', 'big-sky' ),
			service,
			action
		);
	};

	return {
		updateButtonLink,
		validateButtonArgs,
		cancelUpdateButtonLink,
		upsertSocialLink,
		validateSocialLinkArgs,
	};
}

export default useButtonToolFunctions;
