/**
 * WordPress dependencies
 */
import { Button } from '@wordpress/components';
import { resizeCornerNE } from '@wordpress/icons';
import { useDispatch, useSelect } from '@wordpress/data';

/**
 * Internal dependencies
 */
import { store as aiStore } from '@store';

export default function ResizeButton() {
	const { isMaximized } = useSelect( ( select ) => ( {
		isMaximized: select( aiStore ).getIsMaximized(),
	} ) );
	const { setIsMaximized } = useDispatch( aiStore );
	return (
		<Button
			icon={ resizeCornerNE }
			className="big-sky__chat-navigation__resize"
			size="compact"
			onClick={ () => setIsMaximized( ! isMaximized ) }
		/>
	);
}
