/**
 * WordPress dependencies
 */
import { useSelect } from '@wordpress/data';
import { store as editorStore } from '@wordpress/editor';
import { useMemo } from '@wordpress/element';
import { store as coreStore } from '@wordpress/core-data';

export default function useIsHomePage() {
	const { homePageId } = useSelect(
		( select ) => ( {
			homePageId: select( coreStore ).getEntityRecord( 'root', 'site' )
				?.page_on_front,
		} ),
		[]
	);

	const { currentPostId } = useSelect( ( select ) => ( {
		currentPostId: select( editorStore ).getCurrentPostId(),
	} ) );

	const isHomePage = useMemo( () => {
		return currentPostId === homePageId;
	}, [ currentPostId, homePageId ] );

	return isHomePage;
}
