import { assistantIds, ASSISTANTS } from '../../constants.js';

const changeAssistantTool = ( currentAssistantId ) => {
	return {
		name: 'change_assistant',
		description:
			"Changes the active assistant and reinitializes the chat with the user's request",
		parameters: {
			type: 'object',
			properties: {
				toAssistantId: {
					type: 'string',
					enum:
						currentAssistantId === ASSISTANTS.ROUTER
							? assistantIds
							: [ ASSISTANTS.ROUTER, ASSISTANTS.HELP ],
					description: 'The ID of the assistant to switch to',
				},
				fromAssistantId: {
					type: 'string',
					enum: [ currentAssistantId ],
					description:
						'The ID of the assistant that was active before the change',
				},
				reason: {
					type: 'string',
					description:
						'The reason for the assistant change. This will be used for debugging purposes.',
				},
				userRequest: {
					type: 'string',
					description: `The original user request that will be passed to the new assistant. DO NOT MODIFY THIS VALUE unless explicitly instructed to do so when toAssistantId is ${ ASSISTANTS.HELP }.`,
				},
			},
			required: [ 'toAssistantId', 'fromAssistantId', 'userRequest' ],
		},
	};
};

export default changeAssistantTool;
