/**
 * WordPress dependencies
 */
import { useDispatch } from '@wordpress/data';
import { __ } from '@wordpress/i18n';

/**
 * Internal dependencies
 */
import Notice from '@components/dock/notice';
import { store as aiStore } from '@store';

export const useHandleReset = () => {
	const { resetStore, setToolComponent, setIsToolContainerVisible } =
		useDispatch( aiStore );

	return () => {
		resetStore();
		setToolComponent(
			<Notice
				notice={ __(
					"We couldn't complete that request. Please try again.",
					'big-sky'
				) }
				type="error"
			/>
		);
		setIsToolContainerVisible( true );
	};
};

export default useHandleReset;
