/**
 * WordPress dependencies
 */
import { useDispatch, useSelect } from '@wordpress/data';
import { useEffect } from '@wordpress/element';
import { __, sprintf } from '@wordpress/i18n';
import { store as blockEditorStore } from '@wordpress/block-editor';

/**
 * External dependencies
 */
import { useChat, useToolkits } from '@automattic/big-sky-agents';

/**
 * Internal dependencies
 */
import pageToolkit from './toolkit.js';
import { store as aiStore } from '@store';
import useChangeAssistantTool from '@ai/assistants/shared/change-assistant/use-change-assistant-tool.js';
import useCreateToolCallback from '@ai/assistants/shared/use-create-tool-callback.js';
import useAddPageTool from '@ai/assistants/page/tools/add-page/use-add-page-tool.js';
import useNavigateToPageTool from '@ai/assistants/page/tools/navigate-to-page/use-navigate-to-page-tool.js';
import usePages from '@hooks/use-pages.js';
import { animationTiming, dockViews } from '@utils/constants';
import { ChatSuggestions } from '@ai/assistants/shared/make-suggestions/index.js';
import setDelay from '@utils/set-delay.js';
import patternCategories from '@data/pattern-categories.js';
import useSiteMetadata from '@hooks/use-site-metadata.js';

const usePageToolkit = () => {
	const { createToolCallback } = useCreateToolCallback();
	const { changeAssistant } = useChangeAssistantTool();
	const { agentSay } = useChat();
	const { setCallbacks, setContext } = useToolkits();
	const { saveAndGoToPage } = usePages();
	const { addPage } = useAddPageTool();
	const { navigateToPage } = useNavigateToPageTool();
	const { getPages } = usePages();
	const { getSiteMetadata } = useSiteMetadata();
	const { siteTitle, siteDescription, siteLocation, topic } =
		getSiteMetadata();

	const { setShowThread, setChatComponents, setDockView, setNewPageId } =
		useDispatch( aiStore );

	const { selectedBlock } = useSelect( ( select ) => {
		const { getSelectedBlock } = select( blockEditorStore );
		return {
			selectedBlock: getSelectedBlock(),
		};
	} );

	// Register the toolkit's callbacks
	useEffect( () => {
		setCallbacks( pageToolkit.name, {
			add_page: createToolCallback( {
				name: 'add_page',
				action: async ( _args ) => {
					agentSay(
						sprintf(
							// Translators: %s is the page title.
							__( 'Generating the %s page', 'big-sky' ),
							_args.title
						)
					);
					const { title, categories, pageId } =
						await addPage( _args );
					const categorySlugs = categories.map(
						( category ) => category.slug
					);
					agentSay(
						sprintf(
							/* translators: %1$s: page title, %2$s: list of category slugs */
							__(
								'A new page titled "%1$s" has been generated with "%2$s" sections.',
								'big-sky'
							),
							title,
							categorySlugs.join( ', ' )
						)
					);
					const suggestions = {
						actions: [ __( 'Save and take me there', 'big-sky' ) ],
					};
					setChatComponents( [
						<ChatSuggestions
							key="suggestions"
							args={ suggestions }
							respond={ async () => {
								saveAndGoToPage( pageId );
								setDockView( dockViews.MENUS );
								setChatComponents( [] );
								setNewPageId( pageId );
							} }
						/>,
					] );
				},
				closeChatWhenDone: () => false,
			} ),
			navigate_to_page: createToolCallback( {
				name: 'navigate_to_page',
				action: async ( _args ) => {
					const { pageTitle, postId, goHome } =
						await navigateToPage( _args );
					agentSay(
						sprintf(
							/* translators: %s: page title */
							__(
								'Found the %s page. Taking you there',
								'big-sky'
							),
							pageTitle
						)
					);
					await setDelay(
						animationTiming.assistantMessageThinkingTime
					);
					await saveAndGoToPage( postId, goHome );
					setDockView( dockViews.MENUS );
				},
				closeChatWhenDone: () => false,
			} ),
			change_assistant: createToolCallback( {
				name: 'change_assistant',
				action: ( _args ) => {
					setShowThread( true );
					changeAssistant( _args );
				},
				closeChatWhenDone: () => false,
			} ),
		} );
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [ setCallbacks ] );

	useEffect( () => {
		const context = {
			site: {
				siteTitle,
				siteDescription,
				siteLocation,
				topic,
			},
			selectedBlock: {
				clientId: selectedBlock?.clientId,
				type: selectedBlock?.type,
				name: selectedBlock?.name,
			},
			categories: patternCategories.map( ( category ) => category.slug ),
			pages: getPages(),
		};

		setContext( pageToolkit.name, context );
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [ getPages, setContext, selectedBlock ] );
};

export default usePageToolkit;
