/**
 * WordPress dependencies
 */
import { __, sprintf } from '@wordpress/i18n';
import { store as blockEditorStore } from '@wordpress/block-editor';
import { useDispatch, useSelect } from '@wordpress/data';

/**
 * Internal dependencies
 */
import usePatterns from '@hooks/use-patterns';
import useSiteMetadata from '@hooks/use-site-metadata';
import useBlocks from '@hooks/use-blocks';
import useCanvas from '@hooks/use-canvas';
import { store as aiStore } from '@store';
import { animationTiming, dockViews } from '@utils/constants';
import PatternPickerMenu from '@components/dock/menus/pattern-picker';
import usePatternToolValidation from '@ai/assistants/pattern/use-pattern-tool-validation.js';
import setDelay from '@utils/set-delay';

export default function useAddPatternTool() {
	const { insertPatterns, composePatterns } = usePatterns();
	const { getSiteMetadata } = useSiteMetadata();
	const { zoomIn, zoomOut, createDummySection } = useCanvas();
	const { insertBlock } = useDispatch( blockEditorStore );
	const { validateAddPatternArgs } = usePatternToolValidation();

	const {
		getContentBlockClientID,
		scrollToBlock,
		getParentRootPatternBlock,
	} = useBlocks();

	const {
		setIsToolContainerVisible,
		setToolComponent,
		setLastCancelableAction,
		setDockView,
	} = useDispatch( aiStore );

	const {
		getBlock,
		getBlockIndex,
		getBlockParentsByBlockName,
		getBlocksByName,
		getBlocks,
		getSelectedBlock,
	} = useSelect( ( select ) => ( {
		getBlock: select( blockEditorStore ).getBlock,
		getBlockIndex: select( blockEditorStore ).getBlockIndex,
		getBlockParentsByBlockName:
			select( blockEditorStore ).getBlockParentsByBlockName,
		getBlocksByName: select( blockEditorStore ).getBlocksByName,
		getBlocks: select( blockEditorStore ).getBlocks,
		getSelectedBlock: select( blockEditorStore ).getSelectedBlock,
	} ) );

	const addPattern = async ( {
		clientId,
		categories,
		direction,
		siteLocation: newSiteLocation,
		isAskingForMap,
	} ) => {
		try {
			await validateAddPatternArgs( {
				clientId,
				categories,
				direction,
				siteLocation: newSiteLocation,
				isAskingForMap,
			} );

			const { topic, siteDescription, siteLocation } = getSiteMetadata();
			const zoomedOut = zoomOut();

			const insertionId = getBlocksByName( 'core/post-content' );
			const insertionBlockId = insertionId[ 0 ];
			// get the parent template-part/post-content block of the last selected block
			const parentBlock = getBlock(
				getBlockParentsByBlockName( clientId, [
					'core/template-part',
					'core/post-content',
				] )[ 0 ]
			);

			// if the parent block is a template-part, determine if it's a header or footer
			// and set the insertion block id accordingly
			let blockIndex;
			if ( parentBlock?.name === 'core/template-part' ) {
				if ( parentBlock.attributes.slug === 'header' ) {
					blockIndex = 0;
				} else if ( parentBlock.attributes.slug === 'footer' ) {
					blockIndex = getBlocks( getContentBlockClientID() ).length;
				}
			} else {
				// get the root pattern block of the selected block
				const offset = direction === 'before' ? 0 : 1;
				const rootPatternBlock = getParentRootPatternBlock( clientId );
				if ( ! rootPatternBlock ) {
					blockIndex = getBlockIndex( clientId ) + offset;
				} else {
					blockIndex =
						getBlockIndex( rootPatternBlock.clientId ) + offset;
				}
			}

			// Insert a dummy loading section while the patterns are resolving
			const dummySection = createDummySection();
			insertBlock( dummySection, blockIndex, insertionBlockId );

			let scrolled = false;

			zoomedOut.then( () => {
				if ( scrolled ) {
					return;
				}

				scrolled = true;
				// Select the in-progress new (dummy) section.
				scrollToBlock( dummySection.clientId, 'center' );
			} );

			// compose one pattern first so we can generate some content, in case it doesn't exist in the cache
			const initialPatternOptions = composePatterns( {
				categories: [ { slug: categories[ 0 ] } ],
				topic,
				siteLocation: newSiteLocation || siteLocation,
				siteDescription,
				numberOfConcepts: 1,
				isAskingForMap,
			} );

			if ( ! initialPatternOptions ) {
				zoomIn();
				return __(
					"I'm having trouble finding options for that. Please try a different request.",
					'big-sky'
				);
			}

			// Resolve all promises while showing a loading indicator on the canvas for the pattern.
			const initialResolvers = initialPatternOptions.flatMap(
				( concept ) =>
					concept.resolvers.map( ( resolver ) => resolver.resolver )
			);
			await Promise.all( initialResolvers );

			// create the other pattern options
			const morePatternOptions =
				window.localStorage.getItem( 'isGeneratingPatterns' ) !== 'true'
					? composePatterns( {
							categories: [ { slug: categories[ 0 ] } ],
							topic,
							siteLocation: newSiteLocation || siteLocation,
							siteDescription,
							selectedConcept: initialPatternOptions[ 0 ],
							isAskingForMap,
					  } )
					: [];

			// Resolve all promises while showing a loading indicator on the canvas for the pattern.
			const moreResolvers = initialPatternOptions.flatMap( ( concept ) =>
				concept.resolvers.map( ( resolver ) => resolver.resolver )
			);
			await Promise.all( moreResolvers );

			// Often this all happens too quickly, so make sure we have a few seconds of loading before inserting
			// to allow the user time to adjust to what is happening on the screen.
			await setDelay( animationTiming.visualAdjustmentWaitTime );

			// Insert the first pattern in the list when all patterns and content have been resolved.
			insertPatterns(
				initialPatternOptions[ 0 ].patterns,
				dummySection.clientId,
				'replace'
			);

			// Select the newly inserted pattern
			if ( ! scrolled ) {
				scrolled = true;
				scrollToBlock(
					initialPatternOptions[ 0 ].patterns[ 0 ].blocks[ 0 ]
						.clientId,
					'center',
					true
				);
			}
			const generatingPatterns =
				window.localStorage.getItem( 'isGeneratingPatterns' ) ===
				'true';
			// combine the initial and more pattern options
			const patternOptions = ! generatingPatterns
				? [ ...initialPatternOptions, ...morePatternOptions ]
				: [];

			// Display the dock pattern picker menu to allow selection of other patterns
			if ( ! generatingPatterns ) {
				setToolComponent(
					<PatternPickerMenu
						patterns={ patternOptions }
						key="pattern-picker"
					/>
				);
				setIsToolContainerVisible( true );
			}

			// Set the last cancelable action to allow the user to undo the pattern insertion
			setLastCancelableAction( {
				action: 'add_pattern',
				clientId:
					initialPatternOptions[ 0 ].patterns[ 0 ].blocks[ 0 ]
						.clientId,
			} );

			return sprintf(
				// Translators: Placeholder represents the category of the section requested by the user.
				__( 'I have added a new %s section to the page.', 'big-sky' ),
				categories[ 0 ]
			);
		} catch ( error ) {
			throw error;
		}
	};

	const doneAddPattern = () => {
		setDockView( dockViews.MENUS );
		setIsToolContainerVisible( false );
		zoomIn().then( () => {
			scrollToBlock( getSelectedBlock()?.clientId, 'start', true );
		} );
	};

	return {
		addPattern,
		doneAddPattern,
	};
}
