import imageAssistantPrompt from './prompt.js';
import imageToolkit from './toolkit.js';
import { ASSISTANTS } from '../constants.js';
import useImageToolkit from './use-image-toolkit.js';

const imageAssistant = {
	id: ASSISTANTS.IMAGE,
	description:
		'Helps with all requests related to image and site logo generating and replacement.',
	instructions: ( context = {} ) => {
		return imageAssistantPrompt( context );
	},
	tags: [ ASSISTANTS.IMAGE ],
	toolkits: [ imageToolkit ],
	toolkitHook: useImageToolkit,
	onStart: ( invoke, clearMessages = () => {}, agentMessage = null ) => {
		if ( agentMessage ) {
			clearMessages();
			invoke.agentSay( agentMessage );
		}
	},
};

export default imageAssistant;
