export default function useNavigateToPageTool() {
	const navigateToPage = async ( parameters ) => {
		const { page, goHome } = parameters;
		const pageTitle = page.split( ':' )[ 0 ];
		const postId = parseInt( page.split( ':' )[ 1 ] );

		return { pageTitle, postId, goHome };
	};

	return {
		navigateToPage,
	};
}
