/**
 * External dependencies
 */
import { nanoid } from 'nanoid';

const blockNameMap = {
	PRIMARY_HEADING: 'h1', // Not a real block type. Just a placeholder.
	'core/heading': 'h2',
	'core/paragraph': 'p',
	'core/site-title': 'st',
	'core/button': 'b',
};

/**
 * Retrieves the example content from pattern blocks recursively.
 *
 * @param {Array}  blocks          - The array of blocks to retrieve content from.
 * @param {string} patternCategory - The category for the pattern the block is in.
 * @return {Array} - The array of block content objects.
 */
export function getContentOfBlocks( blocks, patternCategory ) {
	const content = [];
	blocks.forEach( ( block ) => {
		if ( block.innerBlocks?.length > 0 ) {
			content.push(
				...getContentOfBlocks(
					block.innerBlocks,
					patternCategory ||
						block?.attributes?.metadata?.patternCategory
				)
			);
		} else if (
			block.name === 'core/heading' ||
			block.name === 'core/paragraph' ||
			block.name === 'core/button'
		) {
			const name =
				patternCategory === 'intro' && block.name === 'core/heading'
					? 'PRIMARY_HEADING'
					: block.name;

			let example;
			if ( block.name === 'core/button' ) {
				example =
					typeof block.attributes.text === 'string'
						? block.attributes.text
						: 'Click here';
			} else {
				example =
					typeof block.attributes.content === 'string'
						? block.attributes.content
						: block.attributes.content?.originalHTML;
			}
			content.push( {
				name,
				clientId: block.clientId,
				example,
				block,
			} );
		}
	} );

	return content;
}

/**
 * Generates the request data for content generation.
 *
 * @param {Array}   content          - The array of content blocks.
 * @param {boolean} isConceptSection - Is this content for a new concept section.
 * @param {string}  language         - The language of the content.
 * @return {Object} - The request data for content generation.
 */
export function getContentRequestData( content, isConceptSection, language ) {
	// Set some average content lengths for block types. Used when generating spare content for caching.
	const blockLengths = {
		'core/heading': 30,
		'core/paragraph': 60,
		'core/button': 8,
	};

	// Count the number of blocks of each type to ensure we have enough spare content for caching.
	const blockCounts = {
		'core/heading': 0,
		'core/paragraph': 0,
		'core/button': 0,
	};

	// If there is no content for this pattern, we need to generate some spare content for caching.
	if ( content.length === 0 && isConceptSection ) {
		const contentForRequest = [];
		Object.keys( blockCounts ).forEach( ( key ) => {
			while ( blockCounts[ key ] < 3 ) {
				blockCounts[ key ]++;
				contentForRequest.push( {
					t: blockNameMap[ key ],
					i: 'SPARE',
					ln: blockLengths[ key ],
				} );
			}
		} );
		return {
			contentForRequest,
			clientIdMap: {},
			blockMap: null,
		};
	}

	// Map of clientIds to shorter 4 character ids for the request. This is used when content is returned
	// to make the content back to the original block.
	const clientIdMap = {};

	// If we are dealing with in memory blocks, like layout concepts, they
	// can't be retrieved with `getBlock` as they are not in the block editor
	// store if not in the canvas, so we keep a map of these so we can manually
	// update them later.
	const blockMap = {};

	// Map all the content to shorter ids and names.
	const contentForRequest = content.map( ( block ) => {
		blockCounts[ block.name ]++;
		const id = nanoid( 4 );
		clientIdMap[ id ] = block.clientId;
		if ( isConceptSection ) {
			blockMap[ block.clientId ] = block.block;
		}
		let data;
		if ( ! isConceptSection ) {
			data = {
				t: blockNameMap[ block.name ],
				i: id,
				e: block.example,
			};
		} else {
			data = {
				t: blockNameMap[ block.name ],
				i: id,
				ln: block.example.length,
			};
		}
		if ( language ) {
			data.l = language;
		}
		return data;
	} );

	// If we are dealing with in memory blocks this is the initial concept so we need to add
	// some spare content to cover alternative concepts that may have more content.
	if ( isConceptSection ) {
		Object.keys( blockCounts ).forEach( ( key ) => {
			while ( blockCounts[ key ] < 3 ) {
				blockCounts[ key ]++;
				contentForRequest.push( {
					t: blockNameMap[ key ],
					i: 'SPARE',
					ln: blockLengths[ key ],
				} );
			}
		} );
	}

	return {
		contentForRequest,
		clientIdMap,
		blockMap: isConceptSection ? blockMap : null,
	};
}

export function findFirstAvailableInnerBlockByClassName(
	parentBlock,
	className
) {
	if ( parentBlock?.innerBlocks ) {
		const matchingBlock = parentBlock.innerBlocks.find(
			( block ) => block.attributes.className === className
		);

		if ( matchingBlock ) {
			return matchingBlock;
		}

		for ( let i = 0; i < parentBlock.innerBlocks.length; i++ ) {
			const matchingInnerBlock = findFirstAvailableInnerBlockByClassName(
				parentBlock.innerBlocks[ i ],
				className
			);

			if ( matchingInnerBlock ) {
				return matchingInnerBlock;
			}
		}
	}

	return false;
}

export function findLastAvailableInnerBlockByClassName(
	parentBlock,
	className
) {
	if ( parentBlock?.innerBlocks ) {
		// Search innerBlocks in reverse order
		for ( let i = parentBlock.innerBlocks.length - 1; i >= 0; i-- ) {
			const block = parentBlock.innerBlocks[ i ];

			if ( block.attributes.className === className ) {
				return block;
			}

			const matchingInnerBlock = findLastAvailableInnerBlockByClassName(
				block,
				className
			);

			if ( matchingInnerBlock ) {
				return matchingInnerBlock;
			}
		}
	}

	return false;
}

export function getNextAvailableContentValue( { data, type, offset } ) {
	let contentValue = '';

	// handle case where offset is -1, which could happen if the current pattern doesn't have an items block
	if ( offset === -1 ) {
		offset = 0;
	}
	offset = offset % data.length;

	for ( let i = offset; i < data.length; i++ ) {
		if ( data[ i ][ type ] ) {
			contentValue = data[ i ][ type ];

			return { data, contentValue };
		}
	}

	return { data, contentValue };
}

export function removeBlockFromHtml( html, blockName ) {
	let pattern = `<!--\\s*wp:${ blockName }\\s*.*?-->.*?<!--\\s*/wp:${ blockName }\\s*-->`;
	if ( blockName === 'navigation' ) {
		pattern = `<!--\\s*wp:${ blockName }\\s*[^/]*?/-->`;
	}

	// Use the replace() method to remove the matched wp:buttons block
	const result = html.replace( new RegExp( pattern, 'gs' ), '' );

	return result;
}
