/**
 * External dependencies
 */
import classNames from 'classnames';

/**
 * WordPress dependencies
 */
import { Popover } from '@wordpress/components';

import { useSelect } from '@wordpress/data';

/**
 * Internal dependencies
 */
import './style.scss';

import { store as aiStore } from '@store';

export default function ToolContainer() {
	const { isHandoff, isToolContainerVisible, toolComponent } = useSelect(
		( select ) => {
			const ai = select( aiStore );

			return {
				isHandoff: ai.getIsHandoff(),
				isToolContainerVisible: ai.isToolContainerVisible(),
				toolComponent: ai.getToolComponent(),
			};
		}
	);
	if ( ! isToolContainerVisible || ! toolComponent ) {
		return;
	}

	const toolComponentKey = toolComponent.key;

	return (
		<Popover
			className={ classNames( 'big-sky__dock__tool-container', {
				'big-sky__assistant-ai-actions__popover-content': true,
				'big-sky__dock__wrapper': true,
				'big-sky__dock__use-hover-effects':
					! [
						'color-picker',
						'font-picker',
						'site-layout-picker',
						'pattern-picker',
					].includes( toolComponentKey ) && ! isHandoff,
				'big-sky__dock__handoff': isHandoff,
			} ) }
			animate={ false }
		>
			{ toolComponent }
		</Popover>
	);
}
