/**
 * WordPress dependencies
 */
import { useDispatch } from '@wordpress/data';
import { useEffect } from '@wordpress/element';
import { useChat, withToolCall } from '@automattic/big-sky-agents';

/**
 * Internal dependencies
 */
import ChatActions from '@components/chat/actions';
import useLog from '@hooks/use-log';
import { store as aiStore } from '@store';
import useIsThinking from '@hooks/use-is-thinking';

export function ChatSuggestions( { args, respond } ) {
	const { agentSay, isRunning } = useChat();
	const { message, actions } = args || {};
	const { logUserChoice } = useLog();
	const { setHasRetriedChatRequest } = useDispatch( aiStore );
	const { isThinking = false } = useIsThinking() || {};

	useEffect( () => {
		if ( message ) {
			agentSay( message );
		}
	}, [ agentSay, message ] );

	let actionsToUse = [];

	if ( isThinking || isRunning ) {
		return null;
	} else if ( actions && actions.length > 0 ) {
		actionsToUse = actions.map( ( action ) => {
			return {
				key: action.key || action.actionName || action,
				actionName: action.actionName || action,
				description: action.description || '',
				type: action.type,
				onClick: () => {
					const responseMessage = () => {
						if ( action?.message ) {
							return action.message;
						}

						return action.actionName && action.description
							? `${ action.actionName } - ${ action.description }`
							: action.description || action.actionName || action;
					};

					logUserChoice( responseMessage() );
					setHasRetriedChatRequest( false );
					respond( responseMessage() );
				},
			};
		} );
	} else {
		return null;
	}

	return (
		<ChatActions
			key={ actionsToUse.map( ( action ) => action.key ).join( '-' ) }
			actions={ actionsToUse }
			animationVariants={ {
				initial: { opacity: 0, y: 10 },
				animate: { opacity: 1, y: 0 },
				exit: { opacity: 0, y: 10 },
				transition: {
					ease: [ 0.42, 0.24, 0.02, 1 ],
					duration: 0.2,
					delay: 0.05,
				},
			} }
		/>
	);
}

export default withToolCall( 'make_suggestions', ChatSuggestions );
