/**
 * WordPress dependencies
 */
import {
	__unstableAnimatePresence as AnimatePresence,
	__unstableMotion as motion,
} from '@wordpress/components';
import { useEffect } from '@wordpress/element';
import { useSelect } from '@wordpress/data';

/**
 * Internal dependencies
 */
import useBigSkyIcon from '@hooks/use-big-sky-icon';
import useIsThinking from '@hooks/use-is-thinking';
import { store as aiStore } from '@store/index';

export default function BigSkyIcon( {
	color = 'blue',
	instanceId,
	onClick,
	width = 32,
	height = 32,
} ) {
	const { RiveComponent, inactiveInput, typingInput, thinkingInput } =
		useBigSkyIcon( color );

	const { isTyping } = useSelect( ( select ) => {
		const ai = select( aiStore );
		return {
			isTyping: ai.getIsTyping(),
		};
	} );
	const { isThinking } = useIsThinking();

	useEffect( () => {
		if ( typingInput ) {
			typingInput.value = isTyping;
		}

		if ( thinkingInput ) {
			thinkingInput.value = isThinking;
		}

		if ( inactiveInput ) {
			inactiveInput.value = ! isTyping && ! isThinking;
		}
	}, [ isTyping, isThinking, typingInput, thinkingInput, inactiveInput ] );

	const iconAnimate = {
		default: {
			scale: 1,
			y: 0,
			x: 0,
		},
		waiting: {
			scale: [ 0.5, 0.6, 0.5 ],
			y: [ -10, 10, -10 ],
			x: [ 0, 0, 0 ],
			transition: {
				ease: 'easeInOut',
				repeat: Infinity,
				duration: 1.5,
			},
		},
		waitingEnter: {
			scale: 0.5,
			y: -10,
			x: 0,
			transition: { ease: 'easeInOut', duration: 0.3 },
		},
		waitingExit: {
			scale: 1,
			y: 0,
			x: 0,
			transition: { ease: 'easeInOut', duration: 0.3 },
		},
	};

	return (
		<AnimatePresence mode="wait">
			<motion.div
				variants={ iconAnimate }
				initial={ { x: 0 } }
				className="big-sky__assistant-icon"
				id={ instanceId }
				onClick={ ( e ) => {
					if ( onClick ) {
						onClick( e );
					}

					return e.preventDefault();
				} }
			>
				<RiveComponent style={ { width, height } } />
			</motion.div>
		</AnimatePresence>
	);
}
