/**
 * WordPress dependencies
 */
import { useDispatch } from '@wordpress/data';

/**
 * Internal dependencies
 */
import { answers } from './answers.js';
import { useChat } from '@automattic/big-sky-agents';
import useAskOdieToolFunctions from '../ask-odie/use-ask-odie-tool.js';
import { messageRoles } from '@utils/constants';
import { store as aiStore } from '@store/index';
import { ASSISTANTS } from '@ai/assistants/constants.js';
export default function useAskWordPressExpertTool() {
	const { ask_odie } = useAskOdieToolFunctions();
	const { clearMessages } = useChat();
	const { setAssistant } = useDispatch( aiStore );

	const askWordPressExpert = async ( parameters ) => {
		// At this point we can clear the `Help request forwarded from another assistant:` system message.
		clearMessages( messageRoles.SYSTEM );
		setAssistant( ASSISTANTS.ROUTER );

		// get the answer from the answers array
		const answer = answers.find( ( ans ) =>
			ans.slug.includes( parameters.slug )
		);

		if ( ! answer || ! answer?.answer ) {
			return ask_odie( parameters );
		}

		return answer?.answer;
	};
	return {
		askWordPressExpert,
	};
}
