const addItemToTestimonialsSectionTool = ( { description, itemCount = 3 } ) => {
	return {
		name: 'add_item_to_testimonials_section',
		description: `Adds a new testimonials section to the testimonials pattern.
			The description of the site is: ${ description }.
			Generate ${ itemCount } testimonial items according to the description of the site.
			NEVER ask any follow up questions.
		`,
		parameters: {
			type: 'object',
			properties: {
				items: {
					type: 'array',
					description: `A list of testimonial items to add to the testimonials section.`,
					items: {
						type: 'object',
						description:
							'The testimonial item to add to the testimonials section.',
						properties: {
							name: {
								type: 'string',
								description:
									'The name of the person giving the testimonial, in the format of "First Name" "Last Initial", e.g. "John D."',
							},
							handle: {
								type: 'string',
								description:
									'The social media handle of the person giving the testimonial, e.g. "@john_doe"',
							},
							review: {
								type: 'string',
								description:
									'The review given by the person, no more than 2 sentences long.',
							},
						},
						additionalProperties: false,
						required: [ 'name', 'handle', 'review' ],
					},
				},
			},
			required: [ 'items' ],
		},
		strict: true,
	};
};

export default addItemToTestimonialsSectionTool;
