import VariationPicker from '@components/variation-picker';
import useSiteMetadata from '@hooks/use-site-metadata';

const FontVariationPicker = ( { fontVariations, maxFontsToShow = 12 } ) => {
	const { currentFont } = useSiteMetadata().getSiteMetadata();

	return (
		<VariationPicker
			variations={ fontVariations }
			currentVariation={ currentFont }
			maxToShow={ maxFontsToShow }
			type="font"
		/>
	);
};

export default FontVariationPicker;
