/**
 * External dependencies
 */
import { __ } from '@wordpress/i18n';
import { useRef } from '@wordpress/element';
import { __unstableMotion as motion } from '@wordpress/components';

/**
 * Internal dependencies
 */
import ChatInput from '@components/chat/input';
import OnboardingSiteSuggestions from '@components/onboarding/site-suggestions';
import BigSkyIcon from '@components/big-sky-icon';

export default function OnboardingLandingView( {
	onSendMessage,
	onClickSuggestion,
} ) {
	const inputRef = useRef( null );

	const defaultEasing = [ 0.42, 0.24, 0.02, 1 ];
	const defaultTransition = {
		ease: defaultEasing,
		duration: 0.2,
	};
	const defaultAnimationProps = {
		initial: { opacity: 0, y: 10 },
		animate: { opacity: 1, y: 0 },
		exit: {
			opacity: 0,
			y: 10,
			transition: {
				...defaultTransition,
				delay: 0.3,
			},
		},
		transition: defaultTransition,
	};

	const transitionToChat = {
		ease: defaultEasing,
		duration: 0.6,
		delay: defaultAnimationProps.exit.transition.delay + 0.5,
	};

	const assistantButtonAnimationProps = {
		exit: () => {
			const assistantButton = document.querySelector(
				'.big-sky-onboarding__assistant-button'
			);
			const buttonRect = assistantButton.getBoundingClientRect();
			const landingView = document.querySelector(
				'.big-sky-onboarding__landing-view'
			);
			const landingViewStyle = window.getComputedStyle( landingView );
			const paddingTop = parseInt( landingViewStyle.paddingTop, 10 );
			const targetY = paddingTop - buttonRect.top;
			return { y: targetY };
		},
		transition: transitionToChat,
	};

	const chatInputAnimationProps = {
		exit: () => {
			const chatInput = document.querySelector(
				'.big-sky-onboarding__chat-input'
			);
			const chatInputRect = chatInput.getBoundingClientRect();
			const windowHeight = window.innerHeight;
			const landingView = document.querySelector(
				'.big-sky-onboarding__landing-view'
			);
			const landingViewStyle = window.getComputedStyle( landingView );
			const paddingBottom = parseInt(
				landingViewStyle.paddingBottom,
				10
			);
			const targetY = windowHeight - chatInputRect.bottom - paddingBottom;
			return { y: targetY };
		},
		transition: transitionToChat,
	};

	return (
		<div className="big-sky-onboarding__landing-view">
			<motion.div
				className="big-sky-onboarding__assistant-button"
				{ ...assistantButtonAnimationProps }
			>
				<BigSkyIcon width={ 100 } height={ 100 } color="white" />
			</motion.div>
			<motion.h1
				className="big-sky-onboarding__title"
				{ ...defaultAnimationProps }
			>
				{ __( 'Make a website as unique as you.', 'big-sky' ) }
			</motion.h1>
			<motion.div
				className="big-sky-onboarding__chat-input"
				{ ...chatInputAnimationProps }
			>
				<ChatInput
					ref={ inputRef }
					prompt={ __( 'What do you want to build?', 'big-sky' ) }
					onSendMessage={ onSendMessage }
				/>
			</motion.div>

			<OnboardingSiteSuggestions
				onClickSuggestion={ onClickSuggestion }
				animationVariants={ defaultAnimationProps }
			/>
		</div>
	);
}
