import { answerSlugs } from './answers.js';

export default function askWordPressExpertTool() {
	return {
		name: 'ask_wordpress_expert',
		description: `
			Classify the user question into one of the given answer slugs.
			Use this tool for all WordPress (both WordPress.com and self-hosted) questions.
			`,
		parameters: {
			type: 'object',
			properties: {
				question: {
					type: 'string',
					description: 'The user question to classify.',
				},
				slug: {
					type: 'string',
					description: 'The slug of the answer to return.',
					enum: answerSlugs,
				},
			},
			required: [ 'question', 'slug' ],
		},
		strict: true,
	};
}
