/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';

/**
 * Internal dependencies
 */
import { useChat } from '@automattic/big-sky-agents';
import useResetSite from '@hooks/use-reset-site';

export default function useSiteToolFunctions() {
	const { resetSite } = useResetSite();
	const { clearMessages } = useChat();

	const startOverActions = [
		{
			actionName: __( 'Yes', 'big-sky' ),
			description: __( 'Permanently reset my site', 'big-sky' ),
			type: 'confirmationDestructive',
			key: 'start-over-yes',
		},
		{
			actionName: __( 'No', 'big-sky' ),
			description: __( 'I have changed my mind', 'big-sky' ),
			type: 'confirmation',
			key: 'start-over-no',
		},
	];
	const startOver = async ( userConfirmation ) => {
		if ( userConfirmation.startsWith( startOverActions[ 0 ].actionName ) ) {
			resetSite();
		}

		clearMessages();
	};

	return {
		startOver,
		startOverActions,
	};
}
