import colorAssistantPrompt from './prompt.js';
import colorAssistantToolkit from './toolkit.js';
import { ASSISTANTS } from '../constants.js';
import useColorToolkit from './use-color-toolkit.js';

const colorAssistant = {
	id: ASSISTANTS.COLOR,
	description:
		'An assistant that handles website colors and color palette creation and selection.',
	instructions: ( context ) => {
		return colorAssistantPrompt( context );
	},
	tags: [ ASSISTANTS.COLOR ],
	toolkits: [ colorAssistantToolkit ],
	toolkitHook: useColorToolkit,
	onStart: ( invoke, clearMessages = () => {}, agentMessage = null ) => {
		if ( agentMessage ) {
			clearMessages();
			invoke.agentSay( agentMessage );
		}
	},
};

export default colorAssistant;
