import { useCallback, useEffect, useState } from '@wordpress/element';
import { subscribe, useDispatch, useSelect } from '@wordpress/data';
import { store as blockEditorStore } from '@wordpress/block-editor';

import useStyles from '@hooks/use-styles';
import { store as aiStore } from '@store';
import { getContrastingThemeColor } from '@utils/colors';
import { getMediaColor } from '@utils/images';

function EditorSubscriptions() {
	const [ isUpdatingCoverImage, setIsUpdatingCoverImage ] = useState( false );
	const [ coverBlockUrls, setCoverBlockUrls ] = useState( [] );
	const {
		getBlock,
		getBlocksByName,
		getBlockAttributes,
		isEditorIframeLoaded,
		isHandoff,
		selectedBlock,
	} = useSelect( ( select ) => {
		const { getSelectedBlock } = select( blockEditorStore );
		return {
			getBlock: select( blockEditorStore ).getBlock,
			getBlocksByName: select( blockEditorStore ).getBlocksByName,
			getBlockAttributes: select( blockEditorStore ).getBlockAttributes,
			selectedBlock: getSelectedBlock(),
			isEditorIframeLoaded: select( aiStore ).getIsEditorIframeLoaded(),
			isHandoff: select( aiStore ).getIsHandoff(),
			getImageStyle: select( aiStore ).getImageStyle,
		};
	} );
	const { updateBlockAttributes } = useDispatch( blockEditorStore );
	const { themeColors } = useStyles();

	useEffect( () => {
		if ( ! isEditorIframeLoaded ) return;

		// get the cover block urls
		const coverBlockIds = getBlocksByName( 'core/cover' );
		setCoverBlockUrls(
			coverBlockIds.map( ( id ) => {
				const block = getBlock( id );
				return { clientId: block.clientId, url: block.attributes.url };
			} )
		);
	}, [ getBlock, getBlocksByName, isEditorIframeLoaded, isHandoff ] );

	const coverBlockUrlChanged = useCallback(
		( coverBlock ) => {
			const coverBlockId = coverBlock.clientId;
			// check if blockId exists in coverBlockUrls
			const coverBlockUrl = coverBlockUrls.find(
				( block ) => block.clientId === coverBlockId
			)?.url;
			return coverBlock.attributes.url !== coverBlockUrl;
		},
		[ coverBlockUrls ]
	);

	// if the selected block is a cover block and the url has changed, reset the custom overlay color
	const handleCoverBlockChange = useCallback( async () => {
		setIsUpdatingCoverImage( true );
		const mediaColor = await getMediaColor( selectedBlock.attributes.url );
		setCoverBlockUrls( ( prevUrls ) => {
			const existingBlockIndex = prevUrls.findIndex(
				( block ) => block.clientId === selectedBlock.clientId
			);
			if ( existingBlockIndex !== -1 ) {
				return prevUrls.map( ( block, index ) =>
					index === existingBlockIndex
						? {
								...block,
								url: selectedBlock.attributes.url,
						  }
						: block
				);
			}
			return [
				...prevUrls,
				{
					clientId: selectedBlock.clientId,
					url: selectedBlock.attributes.url,
				},
			];
		} );

		const textColor = getContrastingThemeColor( mediaColor, themeColors );

		const attributes = getBlockAttributes( selectedBlock.clientId );
		await updateBlockAttributes( selectedBlock.clientId, {
			overlayColor: undefined,
			customOverlayColor: mediaColor,
			isUserOverlayColor: false,
			style: {
				...attributes?.style,
				color: {
					text: textColor?.color,
				},
			},
		} );
		setIsUpdatingCoverImage( false );
	}, [
		selectedBlock,
		updateBlockAttributes,
		getBlockAttributes,
		themeColors,
	] );

	useEffect( () => {
		if ( ! selectedBlock ) return;

		let unsubscribe = () => {};
		if (
			selectedBlock.name === 'core/cover' &&
			selectedBlock.attributes.url &&
			coverBlockUrlChanged( selectedBlock ) &&
			! isUpdatingCoverImage
		) {
			unsubscribe = subscribe( handleCoverBlockChange );
		}

		return () => {
			unsubscribe();
		};
	}, [
		selectedBlock,
		handleCoverBlockChange,
		coverBlockUrlChanged,
		isUpdatingCoverImage,
	] );

	return <></>;
}

export default EditorSubscriptions;
