/**
 * WordPress dependencies
 */
import { Button, Modal } from '@wordpress/components';

export default function ConfirmationDialog( {
	isOpen,
	onClose,
	onCancel,
	onConfirm,
	cancelText,
	confirmText,
	message,
	title,
} ) {
	return (
		isOpen && (
			<Modal
				className="big-sky-onboarding__dialog"
				onRequestClose={ onClose }
				title={ title }
			>
				<p>{ message }</p>
				<div className="big-sky-onboarding__dialog-buttons">
					<Button variant="secondary" onClick={ onCancel }>
						{ cancelText }
					</Button>
					<Button variant="primary" onClick={ onConfirm }>
						{ confirmText }
					</Button>
				</div>
			</Modal>
		)
	);
}
