/**
 * WordPress dependencies
 */
import {
	privateApis as blockEditorPrivateApis,
	__unstableEditorStyles as EditorStyles,
	__unstableIframe as Iframe,
} from '@wordpress/block-editor';
import {
	__experimentalHStack as HStack,
	__unstableMotion as motion,
	__experimentalVStack as VStack,
} from '@wordpress/components';
import { useResizeObserver, useThrottle } from '@wordpress/compose';
import { useLayoutEffect, useMemo, useState } from '@wordpress/element';

/**
 * Internal dependencies
 */
import { unlock } from '@utils/lock-unlock';
import useStyles from '@hooks/use-styles';
import useSiteMetadata from '@hooks/use-site-metadata';
import useVariations from '@hooks/use-variations';

const { useGlobalStyle, useGlobalStylesOutput } = unlock(
	blockEditorPrivateApis
);

const firstFrame = {
	start: {
		scale: 1,
		opacity: 1,
	},
};

const secondFrame = {
	start: {
		scale: 0,
		opacity: 0,
	},
};

const normalizedWidth = 248;
const normalizedHeight = 152;

const normalizedColorSwatchSize = 52;

// Throttle options for useThrottle. Must be defined outside of the component,
// so that the object reference is the same on each render.
const THROTTLE_OPTIONS = {
	leading: true,
	trailing: true,
};

const StylesPreview = ( { label, type } ) => {
	const [ fontFamily = 'serif' ] = useGlobalStyle( 'typography.fontFamily' );
	const [ fontWeight ] = useGlobalStyle( 'typography.fontWeight' );
	const [ fontStyle = 'normal' ] = useGlobalStyle( 'typography.fontStyle' );
	const [ textTransform = 'none' ] = useGlobalStyle(
		'typography.textTransform'
	);
	const [ textColor = 'black' ] = useGlobalStyle( 'color.text' );

	const [ h1FontFamily ] = useGlobalStyle(
		'elements.h1.typography.fontFamily'
	);
	const [ headingFontFamily ] = useGlobalStyle(
		'elements.heading.typography.fontFamily'
	);
	const headerFontFamily = h1FontFamily ?? headingFontFamily ?? fontFamily;

	const [ h1FontWeight ] = useGlobalStyle(
		'elements.h1.typography.fontWeight'
	);
	const [ headingFontWeight ] = useGlobalStyle(
		'elements.heading.typography.fontWeight'
	);
	const headerFontWeight = h1FontWeight ?? headingFontWeight ?? fontWeight;

	const [ h1FontStyle ] = useGlobalStyle(
		'elements.h1.typography.fontStyle'
	);
	const [ headingFontStyle ] = useGlobalStyle(
		'elements.heading.typography.fontStyle'
	);
	const headerFontStyle = h1FontStyle ?? headingFontStyle ?? fontStyle;

	const [ h1TextTransform ] = useGlobalStyle(
		'elements.h1.typography.textTransform'
	);
	const [ headingTextTransform ] = useGlobalStyle(
		'elements.heading.typography.textTransform'
	);
	const headerTextTransform =
		h1TextTransform ?? headingTextTransform ?? textTransform;

	const [ h1Color ] = useGlobalStyle( 'elements.h1.color.text' );
	const [ headingColor ] = useGlobalStyle( 'elements.heading.color.text' );
	const headerColor = h1Color ?? headingColor ?? textColor;

	const [ backgroundColor = 'white' ] = useGlobalStyle( 'color.background' );
	const [ styles ] = useGlobalStylesOutput();
	const [ containerResizeListener, { width } ] = useResizeObserver();
	const [ throttledWidth, setThrottledWidthState ] = useState( width );
	const [ ratioState, setRatioState ] = useState();

	const setThrottledWidth = useThrottle(
		setThrottledWidthState,
		250,
		THROTTLE_OPTIONS
	);

	const { currentColor } = useSiteMetadata().getSiteMetadata();
	const { getColorVariationByTitle } = useVariations();
	const colorVariation = getColorVariationByTitle( currentColor );

	const activeBackgroundColor = () => {
		const backgroundVar = colorVariation?.styles?.color?.background;
		const colorNumber = backgroundVar
			?.split( '--' )[ 4 ]
			.replace( ')', '' );

		// Return the theme color where the slug matches the color number
		return colorVariation?.settings?.color?.palette?.theme?.find(
			( color ) => {
				return color.slug === colorNumber;
			}
		)?.color;
	};

	const activeTextColor = () => {
		const textVar = colorVariation?.styles?.color?.text;
		const colorNumber = textVar?.split( '--' )[ 4 ].replace( ')', '' );

		// Return the theme color where the slug matches the color number
		return colorVariation?.settings?.color?.palette?.theme?.find(
			( color ) => {
				return color.slug === colorNumber;
			}
		)?.color;
	};

	const getVariationBackgroundColor = () => {
		if ( type === 'color' ) {
			return backgroundColor ? backgroundColor : '#ffffff';
		}

		return activeBackgroundColor() || '#ffffff';
	};

	// Must use useLayoutEffect to avoid a flash of the iframe at the wrong
	// size before the width is set.
	useLayoutEffect( () => {
		if ( width ) {
			setThrottledWidth( width );
		}
	}, [ width, setThrottledWidth ] );

	// Must use useLayoutEffect to avoid a flash of the iframe at the wrong
	// size before the width is set.
	useLayoutEffect( () => {
		const newRatio = throttledWidth ? throttledWidth / normalizedWidth : 1;
		const ratioDiff = newRatio - ( ratioState || 0 );

		// Only update the ratio state if the difference is big enough
		// or if the ratio state is not yet set. This is to avoid an
		// endless loop of updates at particular viewport heights when the
		// presence of a scrollbar causes the width to change slightly.
		const isRatioDiffBigEnough = Math.abs( ratioDiff ) > 0.1;

		if ( isRatioDiffBigEnough || ! ratioState ) {
			setRatioState( newRatio );
		}
	}, [ throttledWidth, ratioState ] );

	// Set a fallbackRatio to use before the throttled ratio has been set.
	const fallbackRatio = width ? width / normalizedWidth : 1;
	// Use the throttled ratio if it has been calculated, otherwise
	// use the fallback ratio. The throttled ratio is used to avoid
	// an endless loop of updates at particular viewport heights.
	// See: https://github.com/WordPress/gutenberg/issues/55112
	const ratio = ratioState ? ratioState : fallbackRatio;

	const { paletteColors, getThemeColors } = useStyles();

	const getColorsToShow = () => {
		const colorsToShow = getThemeColors();
		// filter out the background color
		return colorsToShow
			.filter( ( color ) => color.color !== backgroundColor )
			.slice( 0, 3 );
	};

	// Reset leaked styles from WP common.css and remove main content layout padding and border.
	const editorStyles = useMemo( () => {
		if ( styles ) {
			return [
				...styles,
				{
					css: 'html{overflow:hidden}body{min-width: 0;padding: 0;border: none;}',
					isGlobalStyles: true,
				},
			];
		}

		return styles;
	}, [ styles ] );
	const isReady = !! width;

	return (
		<>
			<div style={ { position: 'relative' } }>
				{ containerResizeListener }
			</div>
			{ isReady && (
				<Iframe
					className="edit-site-global-styles-preview__iframe"
					style={ {
						width: '100%',
						height: normalizedHeight * ratio,
					} }
					tabIndex={ -1 }
				>
					<EditorStyles styles={ editorStyles } />
					<motion.div
						style={ {
							height: normalizedHeight * ratio,
							width: '100%',
							background: getVariationBackgroundColor(),
							cursor: 'pointer',
						} }
						initial="start"
					>
						<motion.div
							variants={ firstFrame }
							style={ {
								height: '100%',
								overflow: 'hidden',
							} }
						>
							<HStack
								spacing={ 10 * ratio }
								justify="center"
								style={ {
									height: '100%',
									overflow: 'hidden',
								} }
							>
								{ type === 'font' && (
									<motion.div
										style={ {
											color:
												activeTextColor() || '#000000',
											whiteSpace: 'nowrap',
											paddingLeft: '5px',
											lineHeight: 1,
											padding: '0.2em',
										} }
										animate={ { scale: 1, opacity: 1 } }
										initial={ { scale: 0.1, opacity: 0 } }
										transition={ {
											delay: 0.3,
											type: 'tween',
										} }
									>
										<span
											style={ {
												fontFamily: headerFontFamily,
												fontSize: 100 * ratio,
												fontWeight: headerFontWeight,
												fontStyle: headerFontStyle,
												textTransform:
													headerTextTransform,
											} }
										>
											A
										</span>
										<span
											style={ {
												fontFamily,
												fontSize: 90 * ratio,
												fontWeight,
												fontStyle,
												textTransform,
											} }
										>
											a
										</span>
									</motion.div>
								) }
								{ type === 'color' && (
									<HStack
										spacing={ 0 }
										alignment="center"
										style={ { margin: '0 0 0 10px' } }
										className="big-sky__color-swatch"
									>
										{ getColorsToShow().map(
											( { slug, color }, index ) => (
												<motion.div
													key={ ` ${ slug }_${ index }` }
													style={ {
														height:
															normalizedColorSwatchSize *
															ratio,
														width:
															normalizedColorSwatchSize *
															ratio,
														background: color,
														borderRadius: '100%',
														margin: '0 0 0 -10px',
														border: `3px solid ${ getVariationBackgroundColor() }`,
														boxSizing:
															'content-box',
													} }
													animate={ {
														scale: 1,
														opacity: 1,
													} }
													initial={ {
														scale: 0.1,
														opacity: 0,
													} }
													transition={ {
														delay:
															index === 1
																? 0.2
																: 0.1,
													} }
												/>
											)
										) }
									</HStack>
								) }
							</HStack>
						</motion.div>
						<motion.div
							style={ {
								height: '100%',
								width: '100%',
								position: 'absolute',
								top: 0,
								overflow: 'hidden',
								filter: 'blur(60px)',
								opacity: 0.1,
							} }
						>
							<HStack
								spacing={ 0 }
								justify="flex-start"
								style={ {
									height: '100%',
									overflow: 'hidden',
								} }
							>
								{ paletteColors
									.slice( 0, 4 )
									.map( ( { color }, index ) => (
										<div
											key={ index }
											style={ {
												height: '100%',
												background: color,
												flexGrow: 1,
											} }
										/>
									) ) }
							</HStack>
						</motion.div>
						<motion.div
							variants={ secondFrame }
							style={ {
								height: '100%',
								width: '100%',
								overflow: 'hidden',
								position: 'absolute',
								top: 0,
							} }
						>
							<VStack
								spacing={ 3 * ratio }
								justify="center"
								style={ {
									height: '100%',
									overflow: 'hidden',
									padding: 10 * ratio,
									boxSizing: 'border-box',
								} }
							>
								{ label && (
									<div
										style={ {
											fontSize: 40 * ratio,
											fontFamily: headerFontFamily,
											color:
												type === 'color'
													? headerColor
													: '#1e1e1e',
											fontWeight: headerFontWeight,
											fontStyle: headerFontStyle,
											textTransform: headerTextTransform,
											lineHeight: '1em',
											textAlign: 'center',
										} }
									>
										{ label }
									</div>
								) }
							</VStack>
						</motion.div>
					</motion.div>
				</Iframe>
			) }
		</>
	);
};

export default StylesPreview;
