/**
 * WordPress dependencies
 */
import { __unstableMotion as motion, Notice } from '@wordpress/components';
import { useDispatch, useSelect } from '@wordpress/data';
import {
	createInterpolateElement,
	useEffect,
	useRef,
} from '@wordpress/element';
import { useChat } from '@automattic/big-sky-agents';
import { __, sprintf } from '@wordpress/i18n';

/**
 * Internal dependencies
 */
import './style.scss';
import SiteSpecsList from '@ai/assistants/spec/tools/confirm-site-specs/site-specs-list';
import ChatMessage from '@components/chat/message';
import ThinkingMessage from '@components/thinking-message';
import useIsThinking from '@hooks/use-is-thinking';
import { store as aiStore } from '@store';
import { messageRoles } from '@utils/constants';
import { fetchJetpackToken } from '@utils/request-jetpack-token.ts';
import { recordTracksEvent } from '@utils/tracks';

export default function ChatThread( { onlyShowLastRequest = false } ) {
	const { hasRetriedChatRequest, getSessionId } = useSelect( ( select ) => ( {
		hasRetriedChatRequest: select( aiStore ).getHasRetriedChatRequest(),
		getSessionId: select( aiStore ).getSessionId(),
	} ) );
	const { messages, error, setError } = useChat();
	const messagesEndRef = useRef();
	const { isThinking } = useIsThinking();
	const { setApiKey, setHasRetriedChatRequest } = useDispatch( aiStore );
	const scrollToBottom = () => {
		const doScroll = () => {
			messagesEndRef.current?.scrollIntoView( { behavior: 'smooth' } );
		};
		const timeoutId = setTimeout( doScroll, 100 );
		return () => clearTimeout( timeoutId );
	};

	const isUnauthorizedError = error === 'Unauthorized';
	useEffect( () => {
		// If this is an Unauthorized error, fetch a new token and try again
		const retry = async () => {
			await fetchJetpackToken( setApiKey, false );
			setError( '' );
			setHasRetriedChatRequest( true );
		};

		if ( isUnauthorizedError && ! hasRetriedChatRequest ) {
			retry();
		}
	}, [
		error,
		isUnauthorizedError,
		messages,
		setApiKey,
		setError,
		hasRetriedChatRequest,
		setHasRetriedChatRequest,
	] );

	useEffect( () => {
		scrollToBottom();
	}, [ messages ] );

	const dismissError = () => {
		recordTracksEvent( 'error_dismissed' );
		setError( '' );
	};

	return (
		<motion.div
			className="big-sky__chat-thread"
			initial={ { opacity: 0, y: 10 } }
			animate={ {
				opacity: 1,
				y: 0,
				transition: {
					duration: 0.2,
				},
			} }
			exit={ {
				opacity: 0,
				y: -10,
				transition: {
					duration: 0.2,
				},
			} }
			key="chat-thread"
		>
			<motion.div className="big-sky__chat-thread__scroller" layoutRoot>
				{ messages
					?.filter( ( message, index, array ) => {
						// Find the last user message index
						const lastUserIndex = array
							.map( ( m ) => m.role )
							.lastIndexOf( messageRoles.USER );

						// Only show messages from the last user message onwards
						return (
							index >=
								( onlyShowLastRequest ? lastUserIndex : 0 ) &&
							message.content !== '' &&
							message.role !== messageRoles.TOOL &&
							message.role !== messageRoles.SYSTEM &&
							( message.role !== messageRoles.ASSISTANT ||
								message.tool_calls?.[ 0 ]?.function.name !==
									'make_suggestions' )
						);
					} )
					.map( ( message, index ) => {
						return (
							<ChatMessage message={ message } key={ index } />
						);
					} ) }
				{ error &&
					( ! isUnauthorizedError || hasRetriedChatRequest ) && (
						<Notice
							status="error"
							politeness="assertive"
							onRemove={ dismissError }
						>
							{ error.includes( 'openai_error' )
								? __(
										'Whoops! Our AI is having some issues right now. Please try again later.',
										'big-sky'
								  )
								: createInterpolateElement(
										sprintf(
											/* translators: %s is a reference for debugging, support is an inline link "contact support" to the support page */
											__(
												'Something went wrong. Please try again or <support></support> with reference: %s',
												'big-sky'
											),
											getSessionId
										),
										{
											support: (
												<a
													href="https://wordpress.com/help?help-center=wapuu"
													target="_blank"
													rel="noopener noreferrer"
												>
													{ __(
														'contact support',
														'big-sky'
													) }
												</a>
											),
										}
								  ) }
						</Notice>
					) }
				{ isThinking && <ThinkingMessage /> }
				<SiteSpecsList onLoad={ scrollToBottom } />
				<div
					className="big-sky__chat-thread__end"
					ref={ messagesEndRef }
				/>
			</motion.div>
		</motion.div>
	);
}
