import helpAssistantPrompt from './prompt.js';
import helpToolkit from './toolkit.js';
import { ASSISTANTS } from '../constants.js';
import useHelpToolkit from './use-help-toolkit.js';

const helpAssistant = {
	id: ASSISTANTS.HELP,
	description: `An assistant that helps answer questions about WordPress, website building, and providing technical guidance.`,
	instructions: ( context ) => {
		return helpAssistantPrompt( context );
	},
	tags: [ ASSISTANTS.HELP ],
	toolkits: [ helpToolkit ],
	toolkitHook: useHelpToolkit,
	onStart: ( invoke, clearMessages = () => {}, agentMessage = null ) => {
		if ( agentMessage ) {
			clearMessages();
			invoke.agentSay( agentMessage );
		}
	},
};

export default helpAssistant;
