/**
 * Internal dependencies
 */
import { siteTypeIds } from '@data/site-categories';

export default function confirmSiteSpecsTool() {
	return {
		name: 'confirm_site_specs',
		description:
			'Confirms that the gathered specifications are correct. The required specifications to build a site are: siteType, topic, siteDescription, and siteTitle. The content of your message should ALWAYS be null.',
		parameters: {
			type: 'object',
			properties: {
				siteType: {
					type: 'string',
					description:
						"The type of site being created. Prefer making assumptions about the siteType based on information the user provides rather than asking the user direct. For example a site that sounds like a business, eg. for a consultancy, gymn, florist, or plumber assume the siteType is 'business'. For a site that sounds like a food and beverage establishment, eg. a restaurant, cafe, bar, fastfood, assume the siteType is 'foodAndBeverage'. For a site like a photography portfolio assume the siteType is 'portfolio'. For a site like a daily journal about travel assume the siteType is 'blog'. Only ask the user for clarification as a last resort if you really can't determine the siteType from the information provided. If you do ask the user for clarification, provide a list of options from the available site types.",
					enum: siteTypeIds,
				},
				topic: {
					type: 'string',
					description:
						"Your task is to determine the main topic of the site using no more than two words. It should not be something generic like ecommerce, and should not be the site name. Avoid using ambiguous terms that have multiple meanings. For businesses and professional sites always use the primary business type as the topic, for example with 'a law firm specializing in divorce' set topic as 'law firm' not 'divorce', or for 'a doctor who treats drug addiction' use 'doctor' not 'drug addiction'",
				},
				siteDescription: {
					type: 'string',
					description:
						"Never ask for this. It is a summary of the sites specifications gathered so far e.g. 'A technical bookshop called TechReads, specialising in machine learning books'. If the user has not provided enough information for you to create a useful description ask follow up questions to flesh it out. Do not include the location in the description. Use the user's language.",
				},
				siteTitle: {
					type: 'string',
					description:
						"The title of the site. Look for obvious titles in the information provided, for example: If a business name is provided this should be used. eg. for 'I want a site for my coffee shop Java Jivin' the siteTitle should be 'Java Jivin'. If an explicit title is provided, use that. eg. for 'I want a site for my photography portfolio called My Photos' the siteTitle should be 'My Photos'. If there is an obvious title just use it and don't ask for confirmation. If you can't determine the title, ask the user to provide one.",
				},
				siteLocationName: {
					type: [ 'string', 'null' ],
					description:
						"The physical location of the business or person as a single string. Ask for this if the user has not provided one and it seems like something that should have a location, eg. a bar, a coffee shop, a restaurant, a gym, etc. This should only be a location on a map in a readable form. e.g. 123 Main Street, Los Angeles, CA, USA. Use the post code if provided but do not ask for it. If you cannot determine the location, do not include this. Ask for an actual street address if it seems like a something that should have one and it hasn't been provided already. If the type of site doesn't seem like it should have a location, don't ask for this.",
				},
				inputPlaceholder: {
					type: 'string',
					description:
						"The placeholder text for the input field. The value should be something like, 'Click below to make changes'.",
				},
				actions: {
					type: 'array',
					description:
						"You must provide one action for the user to confirm the specifications. [ { 'actionName': 'Looks good', 'description': 'Build $siteTitle' } ].",
					items: {
						type: 'object',
						additionalProperties: false,
						required: [ 'actionName', 'description' ],
						properties: {
							actionName: {
								type: 'string',
							},
							description: {
								type: 'string',
							},
						},
					},
				},
			},
			required: [
				'siteType',
				'topic',
				'siteDescription',
				'siteTitle',
				'siteLocationName',
				'inputPlaceholder',
				'actions',
			],
		},
		strict: true,
	};
}
