/**
 * WordPress dependencies
 */
import { useDispatch } from '@wordpress/data';
import { __ } from '@wordpress/i18n';
import { Button } from '@wordpress/components';
import { plus } from '@wordpress/icons';
import { useChat } from '@automattic/big-sky-agents';

/**
 * Internal dependencies
 */
import { ChatSuggestions } from '@ai/assistants/shared/make-suggestions';
import useChatThread from '@components/chat/thread/use-thread.js';
import { store as aiStore } from '@store';
import useJetpackToken from '@hooks/use-jetpack-token';

export default function NavigationDockMenus( { onAssistantMenuClick } ) {
	const assistantMenuTitle = __( 'Add Page', 'big-sky' );
	const assistantPrompt = __( 'Describe the page to add', 'big-sky' );
	const assistantMenuSlug = 'add-page';
	const { userSay } = useChat();
	const { initializeThread } = useChatThread();
	const { setChatComponents } = useDispatch( aiStore );
	const { withJetpackTokenRequest } = useJetpackToken();

	const onClickAddPage = async () => {
		onAssistantMenuClick(
			assistantMenuTitle,
			assistantMenuSlug,
			assistantPrompt
		);
		initializeThread(
			__( 'What kind of page would you like to add?', 'big-sky' )
		);

		// TODO - Smart suggestion for pages.
		const args = {
			actions: [
				__( 'A contact page', 'big-sky' ),
				__( 'A blog page', 'big-sky' ),
			],
		};
		const respond = async ( message ) => {
			await withJetpackTokenRequest( () => userSay( message ) );
			setChatComponents( [] );
		};

		setChatComponents( [
			<ChatSuggestions
				key="suggestions"
				args={ args }
				respond={ respond }
			/>,
		] );

		// TODO - Add page to the nav menu.
	};

	return (
		<>
			<Button
				onClick={ () => onClickAddPage() }
				icon={ plus }
				iconPosition="left"
			>
				{ assistantMenuTitle }
			</Button>
		</>
	);
}
