import pageAssistantPrompt from './prompt.js';
import pageToolkit from './toolkit.js';
import { ASSISTANTS } from '../constants.js';
import usePageToolkit from './use-page-toolkit.js';

const pageAssistant = {
	id: ASSISTANTS.PAGE,
	description:
		'Helps with all requests related to adding a new page, and navigating between pages.',
	instructions: ( context = {} ) => {
		return pageAssistantPrompt( context );
	},
	tags: [ ASSISTANTS.PAGE ],
	toolkits: [ pageToolkit ],
	toolkitHook: usePageToolkit,
	onStart: ( invoke, clearMessages = () => {}, agentMessage = null ) => {
		if ( agentMessage ) {
			clearMessages();
			invoke.agentSay( agentMessage );
		}
	},
};

export default pageAssistant;
