/**
 * WordPress dependencies
 */
import { useSelect } from '@wordpress/data';
import { store as blockEditorStore } from '@wordpress/block-editor';

/**
 * Internal dependencies
 */
import { getPendingActionMessage } from '@components/dock/utils';
import { store as aiStore } from '@store';

export default function PendingActionMessage() {
	const { currentToolCall } = useSelect( ( select ) => ( {
		currentToolCall: select( aiStore ).getCurrentToolCall(),
	} ) );

	const { getSelectedBlock } = useSelect( blockEditorStore );
	const selectedBlock = getSelectedBlock();

	return (
		<p className="big-sky__dock__pending-action-message">
			{ getPendingActionMessage( currentToolCall, selectedBlock ) }
		</p>
	);
}
