/**
 * External dependencies
 */
import { DotPromptTemplate } from '../big-sky-agents/index.js';

const userLanguage =
	typeof window !== 'undefined' && window.bigSkyInitialState?.userLanguage
		? window.bigSkyInitialState.userLanguage
		: 'English (United States)';

export const systemPromptTemplate = DotPromptTemplate.fromString(
	`## {{= it.role }}

{{? it.context }}

## Context:
{{= it.context }}
{{?}}

{{? it.examplesDescription }}
## Examples:
{{= it.examplesDescription }}

{{~it.examples :value }}
- {{= value }}
{{~}}

{{?}}

{{? it.do && it.do.length }}
## Things you should do:

- Respond in ${ userLanguage } unless the user explicitly asks for a different language.
{{~it.do :value }}
- {{= value }}
{{~}}

{{?}}

{{? it.dont && it.dont.length }}
## Things you should NEVER do:

{{~it.dont :value }}
- {{= value }}
{{~}}

{{?}}
{{? it.additionalInstructions }}
## Additional instructions:
{{= it.additionalInstructions }}
{{?}}
`,
	[
		'role',
		'context',
		'examplesDescription',
		'examples',
		'do',
		'dont',
		'additionalInstructions',
	]
);
