/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';

// Initially just starting with about, contact for most sites and also menu for food and beverage sites.
// This will be expanded on over time as we get a better understanding of user needs.
export const pageNames = {
	about: __( 'About', 'big-sky' ),
	contact: __( 'Contact', 'big-sky' ),
	menu: __( 'Menu', 'big-sky' ),
};

export const pageDefinitions = {
	about: {
		slug: 'about',
		pageName: pageNames.about,
		sections: [
			{
				slug: 'about',
				reason: 'General information about the site owner or business. Main heading should be same as page title.',
			},
		],
	},
	contact: {
		slug: 'contact',
		pageName: pageNames.contact,
		sections: [
			{
				slug: 'contact',
				reason: 'Contact form and contact information.',
			},
		],
	},
	menu: {
		slug: 'menu',
		pageName: pageNames.menu,
		sections: [
			{
				slug: 'menu',
				reason: 'Full menu listing.',
			},
		],
	},
};

export const pagesBySlug = Object.keys( pageDefinitions ).reduce(
	( acc, key ) => {
		acc[ pageDefinitions[ key ].slug ] = pageDefinitions[ key ];
		return acc;
	},
	{}
);
