/**
 * WordPress dependencies
 */
import { useContext } from '@wordpress/element';
import { useDispatch, useSelect } from '@wordpress/data';

/**
 * Internal dependencies
 */
import { Context } from './context.jsx';

/**
 * A custom react hook exposing the chat context for use.
 *
 * This exposes the `chat` value provided via the
 * <a href="#ChatProvider">Chat Provider</a> to a component implementing
 * this hook.
 *
 * It acts similarly to the `useContext` react hook.
 *
 * @example
 * ```js
 * import {
 *   ChatProvider,
 *   createChat,
 *   useChat,
 * } from '@automattic/big-sky-';
 *
 * const chat = createChat();
 *
 * const SomeChildUsingChat = ( props ) => {
 *   const chat = useChat();
 *   // ...logic implementing the chat in other react hooks.
 * };
 *
 *
 * const ParentProvidingChat = ( props ) => {
 *   return <ChatProvider value={ chat }>
 *     <SomeChildUsingChat { ...props } />
 *   </ChatProvider>
 * };
 * ```
 *
 * @return {Function}  A custom react hook exposing the chat context value.
 */

export default function useChat() {
	const agentStore = useContext( Context );

	const {
		reset,
		setEnabled,
		call,
		addSystemMessage,
		userSay,
		userSayRepeat,
		clearMessages,
		setToolResult,
		runChatCompletion,
		agentSay,
		submitToolOutputs,
		setService,
		setModel,
		setTemperature,
		setApiKey,
		setFeature,
		setBaseUrl,
		setSessionId,
		setStream,
		setGraphConfig,
		setError,
		setStoreConversation,
		incrementChangeAgentCount,
		resetChangeAgentCount,
	} = useDispatch( agentStore );

	const {
		baseUrl,
		service,
		temperature,
		apiKey,
		error,
		model,
		loading,
		running,

		enabled,
		messages,
		documents,
		assistantMessage,
		pendingToolCalls,
		additionalMessages,
		toolOutputs,
		isAvailable,
		isChatAvailable,
		isAwaitingUserInput,
		feature,
		stream,
		graphConfig,
		isStoringConversation,
		getChangeAgentCount,
	} = useSelect( ( select ) => ( {
		baseUrl: select( agentStore ).getBaseUrl(),
		service: select( agentStore ).getService(),
		temperature: select( agentStore ).getTemperature(),
		apiKey: select( agentStore ).getApiKey(),
		error: select( agentStore ).getError(),
		model: select( agentStore ).getModel(),
		loading: select( agentStore ).isLoading(),
		running: select( agentStore ).isRunning(),
		enabled: select( agentStore ).isEnabled(),
		messages: select( agentStore ).getMessages(),
		documents: select( agentStore ).getDocuments(),
		assistantMessage: select( agentStore ).getAssistantMessage(),
		pendingToolCalls: select( agentStore ).getPendingToolCalls(),
		additionalMessages: select( agentStore ).getAdditionalMessages(),
		toolOutputs: select( agentStore ).getToolOutputs(),
		isAvailable: select( agentStore ).isAvailable(),
		isChatAvailable: select( agentStore ).isChatAvailable(),
		isAwaitingUserInput: select( agentStore ).isAwaitingUserInput(),
		feature: select( agentStore ).getFeature(),
		stream: select( agentStore ).getStream(),
		graphConfig: select( agentStore ).getGraphConfig(),
		isStoringConversation: select( agentStore ).isStoringConversation(),
		getChangeAgentCount: select( agentStore ).getChangeAgentCount,
	} ) );

	return {
		// running state
		enabled,
		setEnabled,
		loading,
		running,
		error,
		setError,

		// graph config
		graphConfig,
		setGraphConfig,

		// stream
		stream,
		setStream,

		// auth
		apiKey,
		setApiKey,

		// logging
		feature,
		setFeature,
		setSessionId,

		// llm
		model,
		setModel,
		service,
		setService,
		baseUrl,
		setBaseUrl,
		temperature,
		setTemperature,

		// messages
		messages,
		clearMessages,
		userSay,
		userSayRepeat,
		addSystemMessage,
		assistantMessage,
		agentSay,

		// documents
		documents,

		// tools
		call,
		setToolResult,
		pendingToolCalls,
		toolOutputs,

		// chat
		isChatAvailable,
		runChatCompletion,

		// assistants
		submitToolOutputs,
		isAvailable,
		isAwaitingUserInput,
		additionalMessages,
		reset,
		isStoringConversation,
		setStoreConversation,

		// change agent count
		getChangeAgentCount,
		incrementChangeAgentCount,
		resetChangeAgentCount,
	};
}
