/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';

export const messageRoles = {
	SYSTEM: 'system',
	USER: 'user',
	ASSISTANT: 'assistant',
	LOCAL_ASSISTANT: 'local_assistant', // Used for messages that are shown to user but not returned to the model.
	USER_CHOICE: 'user_choice',
	CONFIRM: 'confirm',
	ACTION: 'action',
	COMPONENT: 'component',
	THINKING: 'thinking',
	ERROR: 'error',
	TOOL_CALL: 'tool_call',
	TOOL: 'tool',
	SCREENSHOT: 'screenshot',
	SET_TOPIC: 'set_topic',
	SET_COLOR: 'set_color',
	SET_FONT: 'set_font',
	SET_SITE_TITLE: 'set_site_title',
	SET_SITE_DESCRIPTION: 'set_site_description',
	SET_SITE_LOCATION: 'set_site_location',
	SAVE_PAGE: 'save_page',
};

export const isChatMessage = ( message ) =>
	typeof message.content === 'string' &&
	( message.role === messageRoles.USER ||
		message.role === messageRoles.ASSISTANT ||
		message.role === messageRoles.TOOL );

export const typeAnotherRequest = __( 'Type another request ', 'big-sky' );

