/**
 * WordPress dependencies
 */
import { useDispatch, useSelect } from '@wordpress/data';
import { store as coreStore } from '@wordpress/core-data';
import { store as editorStore } from '@wordpress/editor';

/**
 * Internal dependencies
 */
import logSession from '@utils/log-session';
import logSiteMetadata from '@utils/log-metadata';
import { store as aiStore } from '@store';
import uuidv4 from '@utils/uuid';
import logAction from '@utils/log-action';
import { messageRoles } from '@utils/constants';

// a delta object to store changes to metadata to avoid racing condition
let deltaMetadata = {};
export default function useSiteMetadata() {
	const { shouldLogSessions, sessionId, getSiteMetadata } = useSelect(
		( select ) => ( {
			getSiteMetadata: select( aiStore ).getSiteMetadata,
			sessionId: select( aiStore ).getSessionId(),
			shouldLogSessions: select( aiStore ).shouldLogSessions(),
		} )
	);
	const { currentPostId } = useSelect( ( select ) => ( {
		currentPostId: select( editorStore ).getCurrentPostId(),
	} ) );
	const { setSiteMetadata: setSiteMetadataState } = useDispatch( aiStore );

	const { saveEditedEntityRecord, editEntityRecord } =
		useDispatch( coreStore );

	const isOnboarded = ( metadata ) => {
		return (
			!! metadata.siteTitle &&
			!! metadata.siteDescription &&
			!! metadata.topic
		);
	};

	const setSiteMetadata = async (
		payload,
		persist = false,
		confirmingSpecs = false
	) => {
		const siteMetadata = getSiteMetadata();
		deltaMetadata = { ...deltaMetadata, ...payload };
		const newSiteMetadata = { ...siteMetadata, ...deltaMetadata };
		// remove the mode property from the metadata
		delete newSiteMetadata.mode;

		if ( ! confirmingSpecs ) {
			newSiteMetadata.isOnboarded = isOnboarded( newSiteMetadata );
		} else {
			newSiteMetadata.isOnboarded = false;
		}
		const newSiteMetadataJson = JSON.stringify( newSiteMetadata );
		// log any changes to the site metadata
		if (
			isOnboarded( newSiteMetadata ) &&
			newSiteMetadataJson !== JSON.stringify( siteMetadata )
		) {
			logSiteMetadata( sessionId, newSiteMetadata );
		}
		// update the state
		setSiteMetadataState( newSiteMetadata );

		editEntityRecord(
			'root',
			'site',
			undefined,
			{
				big_sky_site_metadata: newSiteMetadataJson,
			},
			{ undoIgnore: true }
		);

		if ( persist ) {
			await saveEditedEntityRecord( 'root', 'site', undefined, {
				big_sky_site_metadata: newSiteMetadataJson,
			} );
		}

		// now log the message if necessary
		if ( payload.siteTitle && shouldLogSessions ) {
			logSession( sessionId, payload.siteTitle );
		}
	};

	const setCurrentColor = ( currentColor ) => {
		if ( shouldLogSessions ) {
			logAction(
				sessionId,
				currentColor,
				messageRoles.SET_COLOR,
				uuidv4()
			);
		}
		return setSiteMetadata( { currentColor } );
	};
	const setCurrentFont = ( currentFont ) => {
		if ( shouldLogSessions ) {
			logAction(
				sessionId,
				currentFont,
				messageRoles.SET_FONT,
				uuidv4()
			);
		}
		return setSiteMetadata( { currentFont } );
	};
	const setTopic = ( topic ) => {
		if ( shouldLogSessions ) {
			logAction( sessionId, topic, messageRoles.SET_TOPIC, uuidv4() );
		}
		return setSiteMetadata( { topic } );
	};
	const setSiteDescription = ( siteDescription ) => {
		if ( shouldLogSessions ) {
			logAction(
				sessionId,
				siteDescription,
				messageRoles.SET_SITE_DESCRIPTION,
				uuidv4()
			);
		}
		return setSiteMetadata( { siteDescription } );
	};
	const setSiteLocation = ( siteLocation ) => {
		if ( shouldLogSessions ) {
			logAction(
				sessionId,
				siteLocation,
				messageRoles.SET_SITE_LOCATION,
				uuidv4()
			);
		}
		return setSiteMetadata( { siteLocation } );
	};
	const setSiteTitle = ( siteTitle ) => {
		if ( shouldLogSessions ) {
			logAction(
				sessionId,
				siteTitle,
				messageRoles.SET_SITE_TITLE,
				uuidv4()
			);
		}
		return setSiteMetadata( { siteTitle } );
	};

	const getPagePatterns = ( pageId = null ) => {
		const patterns = getSiteMetadata().patterns;
		if ( pageId ) {
			return patterns[ pageId ];
		}
		return patterns[ currentPostId ];
	};

	return {
		getPagePatterns,
		getSiteMetadata,
		setCurrentColor,
		setCurrentFont,
		setTopic,
		setSiteDescription,
		setSiteLocation,
		setSiteTitle,
		setSiteMetadata,
	};
}
