export const ASSISTANTS = {
	ROUTER: 'big-sky-router',
	SPEC: 'big-sky-spec',
	CONTENT: 'big-sky-content',
	COLOR: 'big-sky-color',
	PATTERN: 'big-sky-pattern',
	FONT: 'big-sky-font',
	PAGE: 'big-sky-page',
	HELP: 'big-sky-help',
	LAYOUT: 'big-sky-layout',
	IMAGE: 'big-sky-image',
};

// Remove the spec assistant because it's only used for onboarding, and should not be routed to.
export const assistantIds = Object.values( ASSISTANTS ).filter(
	( id ) => id !== ASSISTANTS.SPEC
);
