const makeTextBoldItalicTool = () => {
	return {
		name: 'make_text_bold_italic',
		description: `Use if the user asks for selected text, or substring of selected text, to be made bold or italic, or both. Never format the text yourself with markdown, use this tool instead.
			If you don't know the content of the selected block, or the user has selected a block without content, still call this tool but return text parameter as 'no text selected'.`,
		parameters: {
			type: 'object',
			properties: {
				text: {
					type: 'string',
					description: 'The text to format',
				},
				substring: {
					type: 'string',
					description:
						'Any substring of "text" to be formatted. If not provided the entire text will be formatted.',
				},
				formats: {
					type: 'array',
					description:
						'The formats the user wants to apply. Emphasise should map to italic.',
					items: {
						type: 'string',
						description: 'A slug of format to apply.',
						enum: [ 'bold', 'italic' ],
					},
				},
			},
			required: [ 'text', 'formats' ],
		},
		strict: false,
	};
};

export default makeTextBoldItalicTool;
