/**
 * WordPress dependencies
 */
import { useDispatch, useSelect } from '@wordpress/data';
import { store as blockEditorStore } from '@wordpress/block-editor';

/**
 * Internal dependencies
 */
import useImages from '@hooks/use-images.js';
import useBlocks from '@hooks/use-blocks.js';
import { store as aiStore } from '@store';

export default function useGenerateSiteLogoTool() {
	const { generateLogoWithAI } = useImages();
	const { getBlock } = useSelect( blockEditorStore );
	const { setBlockIsProcessing, findSiteLogoBlock } = useBlocks();
	const { setBlockBeforeChanges } = useDispatch( aiStore );

	const generateSiteLogo = async ( description ) => {
		// Find the core/site-logo block
		const siteLogoBlock = findSiteLogoBlock();

		if ( ! siteLogoBlock ) {
			return {
				error: 'No site logo block found',
			};
		}

		setBlockBeforeChanges( siteLogoBlock );
		setBlockIsProcessing( getBlock( siteLogoBlock.clientId ), true );

		const logo = await generateLogoWithAI( {
			description,
			block: siteLogoBlock,
		} );

		setBlockIsProcessing( getBlock( siteLogoBlock.clientId ), false );

		return logo;
	};

	return { generateSiteLogo };
}
