/**
 * WordPress dependencies
 */
import { uploadMedia } from '@wordpress/media-utils';
/**
 * External dependencies
 */
import { checkIfBrowserSupported, takeScreenshot } from '@xata.io/screenshot';
/**
 * Internal dependencies
 */
import logAction from './log-action.js';
import { messageRoles } from './constants.js';
import log from './log-debug.js';

function base64ToFile( base64String, filename ) {
	// Decode base64
	const arr = base64String.split( ',' );
	const mime = arr[ 0 ].match( /:(.*?);/ )[ 1 ];
	const bstr = atob( arr[ 1 ] );
	let n = bstr.length;
	const u8arr = new Uint8Array( n );

	while ( n-- ) {
		u8arr[ n ] = bstr.charCodeAt( n );
	}

	// Return a new File() object
	return new File( [ u8arr ], filename, { type: mime } );
}

const logScreenshot = async ( sessionId ) => {
	/**
	 * Only do this if your browser supports it.
	 * To check, visit https://caniuse.com/mdn-api_mediadevices_getdisplaymedia
	 */
	if ( checkIfBrowserSupported() ) {
		const screenshot = await takeScreenshot();

		/**
		 * This is a base64-encoded string representing the screenshot.
		 * It can go directly into an <img>'s `src` attribute, or be sent to a server to store.
		 */
		const imageFile = base64ToFile( screenshot, 'screenshot.jpg' );

		const handleFileChange = ( files ) => {
			const fileUrl = files[ 0 ].url;
			// if it's a string, upload as a comment - if it's a blob, it's not uploaded yet
			if ( typeof fileUrl === 'string' && fileUrl.startsWith( 'http' ) ) {
				logAction(
					sessionId,
					`<img style="max-width: 100%" src="${ fileUrl }" />`,
					messageRoles.SCREENSHOT
				);
			}
		};

		const handleFileError = ( error ) => {
			log.error( 'file error', error );
		};

		await uploadMedia( {
			filesList: [ imageFile ],
			onFileChange: handleFileChange,
			onError: handleFileError,
		} );
	}
};

export default logScreenshot;
