/**
 * WordPress dependencies
 */
import { useDispatch, useSelect } from '@wordpress/data';

/**
 * Internal dependencies
 */
import doLogSession from '@utils/log-session';
import logScreenshotForSession from '@utils/log-screenshot';
import realLogMessage from '@utils/log-message';
import realLogAction from '@utils/log-action';
import { store as aiStore } from '@store';
import { messageRoles } from '@utils/constants';

export default function useLog() {
	const { isSessionLogged, shouldLogSessions, sessionId } = useSelect(
		( select ) => ( {
			sessionId: select( aiStore ).getSessionId(),
			shouldLogSessions: select( aiStore ).shouldLogSessions(),
			isSessionLogged: select( aiStore ).isSessionLogged(),
		} )
	);

	const setSessionSessionLogged = useDispatch( aiStore ).setSessionLogged;

	async function logMessage( message ) {
		if ( await maybeLogSession() ) {
			return await realLogMessage( sessionId, message );
		}
	}

	async function logAction(
		content,
		messageRole = '',
		messageId = '',
		parentMessageId = ''
	) {
		if ( await maybeLogSession() ) {
			return await realLogAction(
				sessionId,
				content,
				messageRole,
				messageId,
				parentMessageId
			);
		}
	}

	/**
	 * Logs a tool call.
	 *
	 * @param {*} content
	 * @param {*} messageId
	 */
	async function logToolCall( content, messageId = '' ) {
		return await logAction( content, messageRoles.TOOL_CALL, messageId );
	}

	/**
	 * Logs an assistant message.
	 *
	 * @param {*} content
	 * @param {*} messageId
	 */
	async function logAssistantMessage( content, messageId = '' ) {
		return await logAction( content, messageRoles.ASSISTANT, messageId );
	}

	/**
	 * Logs a user message typed in the chat input.
	 *
	 * @param {*} content
	 * @param {*} messageId
	 * @param {*} parentMessageId
	 */
	async function logUserMessage( content, messageId, parentMessageId ) {
		return await logAction(
			content,
			messageRoles.USER,
			messageId,
			parentMessageId
		);
	}

	/**
	 * Logs a user choice of a suggestion.
	 *
	 * @param {*} content
	 * @param {*} messageId
	 * @param {*} parentMessageId
	 */
	async function logUserChoice( content, messageId, parentMessageId ) {
		return await logAction(
			content,
			messageRoles.USER_CHOICE,
			messageId,
			parentMessageId
		);
	}

	async function maybeLogSession() {
		if ( isSessionLogged ) {
			return true;
		}

		if ( ! shouldLogSessions ) {
			return false;
		}

		await doLogSession( sessionId );
		setSessionSessionLogged( true );
		return true;
	}

	async function logError( error ) {
		if ( typeof error === 'object' ) {
			error = JSON.stringify( error );
		}
		return await logAction( error, messageRoles.ERROR );
	}

	async function logScreenshot() {
		if ( await maybeLogSession() ) {
			return await logScreenshotForSession( sessionId );
		}
	}

	return {
		logSession: maybeLogSession,
		logScreenshot,
		logMessage,
		logAction,
		logError,
		logUserChoice,
		logUserMessage,
		logToolCall,
		logAssistantMessage,
	};
}
