/**
 * WordPress dependencies
 */
import { applyFilters } from '@wordpress/hooks';

/**
 * External dependencies
 */
import { colord } from 'colord';
import { FastAverageColor } from 'fast-average-color';

async function imgFromBlob( blob, width = 1024, height = 1024 ) {
	const img = document.createElement( 'img' );
	img.width = width / 2;
	img.height = height / 2;

	const src = window.URL.createObjectURL( blob );
	const loaded = new Promise( ( resolve ) => {
		const onload = () => {
			window.URL.revokeObjectURL( src );
			img.removeEventListener( 'load', onload );
			resolve();
		};
		img.addEventListener( 'load', onload );
	} );
	img.src = src;
	await loaded;

	return img;
}

function getCanvasElement( width = 1024, height = 1024 ) {
	const canvas = document.createElement( 'canvas' );
	canvas.width = width;
	canvas.height = height;

	return canvas;
}

export function blobFromBase64( base64Data, mimeType ) {
	const binaryData = atob( base64Data );
	const byteArray = new Uint8Array( binaryData.length );
	for ( let i = 0; i < binaryData.length; i++ ) {
		byteArray[ i ] = binaryData.charCodeAt( i );
	}

	return new Blob( [ byteArray ], { type: mimeType } );
}

export async function transparentFromGrayscale( blob, contrastFactor = 1.5 ) {
	const img = await imgFromBlob( blob );
	const canvas = getCanvasElement();

	const ctx = canvas.getContext( '2d' );
	ctx.drawImage( img, 0, 0, canvas.width, canvas.height );

	const imageData = ctx.getImageData( 0, 0, canvas.width, canvas.height );
	const { data } = imageData;

	for ( let i = 0; i < data.length; i += 4 ) {
		const grayscaleRaw = data[ i ]; // R = G = B in grayscale
		// Make whites whiter and blacks blacker. Avoids pale-but-not-quite-white pixels that look weird when we
		// convert white to alpha.
		const grayscaleValue =
			window.Math.round( ( grayscaleRaw - 128 ) * contrastFactor ) + 128;
		const grayscaleClamped = window.Math.max(
			window.Math.min( grayscaleValue, 255 ),
			0
		);
		data[ i + 3 ] = 255 - grayscaleClamped; // Set alpha to grayscale value (0 = transparent, 255 = opaque)
		// Make RGB black
		data[ i ] = 0;
		data[ i + 1 ] = 0;
		data[ i + 2 ] = 0;
	}

	ctx.putImageData( imageData, 0, 0 );

	return await new Promise( ( resolve ) => canvas.toBlob( resolve ) );
}

export async function cropTransparentBlob( blob ) {
	const img = await imgFromBlob( blob );
	const canvas = getCanvasElement();

	const ctx = canvas.getContext( '2d' );
	ctx.drawImage( img, 0, 0, canvas.width, canvas.height );

	const imageData = ctx.getImageData( 0, 0, canvas.width, canvas.height );
	const { data } = imageData;

	let firstX = Infinity;
	let lastX = -Infinity;
	let firstY = Infinity;
	let lastY = -Infinity;
	for ( let x = 0; x < canvas.width; x++ ) {
		for ( let y = 0; y < canvas.height; y++ ) {
			const offset = y * canvas.width + x;
			const alpha = data[ offset * 4 + 3 ];
			if ( ! alpha ) {
				continue;
			}

			if ( x < firstX ) {
				firstX = x;
			}
			if ( x > lastX ) {
				lastX = x;
			}
			if ( y < firstY ) {
				firstY = y;
			}
			if ( y > lastY ) {
				lastY = y;
			}
		}
	}

	let width = 1 + lastX - firstX;
	let height = 1 + lastY - firstY;

	// Keep it square.
	if ( width < height ) {
		const delta = ( height - width ) / 2;
		width = height;
		firstX -= Math.floor( delta );
	} else if ( height < width ) {
		const delta = ( width - height ) / 2;
		height = width;
		firstY -= Math.floor( delta );
	}

	// Setting the canvas's width/height resets the canvas (even if the width/height have not changed),
	// which is what we want. We want to clear the canvas and draw our new, cropped image.
	canvas.setAttribute( 'width', width );
	canvas.setAttribute( 'height', height );

	ctx.putImageData(
		imageData,
		-firstX, // `.putImageData()`'s parameters are weird. This is correct.
		-firstY, // ^^^
		firstX,
		firstY,
		width,
		height
	);

	return await new Promise( ( resolve ) => canvas.toBlob( resolve ) );
}

export async function colorize( blob, color ) {
	const img = await imgFromBlob( blob );
	const canvas = getCanvasElement();

	const ctx = canvas.getContext( '2d' );
	ctx.fillStyle = color;
	ctx.fillRect( 0, 0, canvas.width, canvas.height );

	ctx.globalCompositeOperation = 'destination-atop';
	ctx.drawImage( img, 0, 0, canvas.width, canvas.height );

	return await new Promise( ( resolve ) => canvas.toBlob( resolve ) );
}

export function setCustomLogoCss( customCss, setCustomCss, siteLogoUrl ) {
	const siteLogoPath = new window.URL( siteLogoUrl ).pathname;

	// Ensure we have the correct CSS Masking rules in our site's Custom CSS.
	// NOTE: Has to use sub-properties for mask to avoid being filtered out.
	// NOTE: Whitespace and quoting is importent. Matches the value as normalized by WP.com.
	const logoCSS = `/* START Big Sky */
.wp-block-site-logo .custom-logo[src*="${ siteLogoPath }"] {
	background-color: currentcolor;
	-webkit-mask-image: url('${ siteLogoUrl }');
	-webkit-mask-position: center center;
	-webkit-mask-size: cover;
	-webkit-mask-repeat: no-repeat;
	mask-image: url('${ siteLogoUrl }');
	mask-position: center center;
	mask-size: cover;
	mask-repeat: no-repeat;
	object-position: -1024px -1024px;
}

/* END Big Sky */`;

	if ( customCss.includes( logoCSS ) ) {
		return;
	}

	// Replace any existing Big Sky rules with the fresh ones.
	const newCss = customCss
		.replace( /\/\* START Big Sky \*\/.*\/\* END Big Sky \*\//s, '' )
		.trim();

	setCustomCss( newCss + `\n\n${ logoCSS }` );
}

export function removeCustomLogoCss( customCss, setCustomCss ) {
	if ( ! customCss.includes( '/* START Big Sky */' ) ) {
		return;
	}

	// Remove Big Sky rules.
	const newCss = customCss
		.replace( /\/\* START Big Sky \*\/.*\/\* END Big Sky \*\//s, '' )
		.trim();

	setCustomCss( newCss );
}

/**
 * Retrieves the FastAverageColor singleton.
 *
 * @return {FastAverageColor} The FastAverageColor singleton.
 */
export function retrieveFastAverageColor() {
	if ( ! retrieveFastAverageColor.fastAverageColor ) {
		retrieveFastAverageColor.fastAverageColor = new FastAverageColor();
	}
	return retrieveFastAverageColor.fastAverageColor;
}

/**
 * Computes the average color of an image.
 *
 * @param {string} url The url of the image.
 *
 * @return {Promise<string>} Promise of an average color as a hex string.
 */
const DEFAULT_BACKGROUND_COLOR = '#ffffff';
export const getMediaColor = async ( url ) => {
	if ( ! url ) {
		return DEFAULT_BACKGROUND_COLOR;
	}

	// making the default color rgb for compat with FAC
	const { r, g, b, a } = colord( DEFAULT_BACKGROUND_COLOR ).toRgb();

	try {
		const imgCrossOrigin = applyFilters(
			'media.crossOrigin',
			undefined,
			url
		);
		const color = await retrieveFastAverageColor().getColorAsync( url, {
			// The default color is white, which is the color
			// that is returned if there's an error.
			// colord returns alpga 0-1, FAC needs 0-255
			defaultColor: [ r, g, b, a * 255 ],
			// Errors that come up don't reject the promise,
			// so error logging has to be silenced
			// with this option.
			silent: process.env.NODE_ENV === 'production',
			crossOrigin: imgCrossOrigin,
		} );
		return color.hex;
	} catch ( error ) {
		// If there's an error return the fallback color.
		return DEFAULT_BACKGROUND_COLOR;
	}
};

export const ALLOWED_IMAGE_FILE_TYPES = [ 'jpeg', 'jpg', 'png', 'gif', 'svg' ];
export const isAllowedImageFile = ( fileName ) => {
	const regex = new RegExp(
		`\\.(${ ALLOWED_IMAGE_FILE_TYPES.join( '|' ) })$`,
		'i'
	);
	return regex.test( fileName );
};
