import contentAssistantPrompt from './prompt.js';
import contentToolkit from '@ai/assistants/content/toolkit.js';
import { ASSISTANTS } from '@ai/assistants/constants.js';
import useContentToolkit from './use-content-toolkit.js';

export const agentDetails = {};

const contentAssistant = {
	id: ASSISTANTS.CONTENT,
	description: 'An assistant that handles content generation and editing.',
	instructions: ( context ) => {
		return contentAssistantPrompt( context );
	},
	tags: [ ASSISTANTS.CONTENT ],
	toolkits: [ contentToolkit ],
	toolkitHook: useContentToolkit,
	onToolResult: ( _, value, invoke ) => {
		invoke.userSay( value );
	},
	onStart: ( invoke, clearMessages = () => {}, agentMessage = null ) => {
		if ( agentMessage ) {
			clearMessages();
			invoke.agentSay( agentMessage );
		}
	},
};

export default contentAssistant;
