import createColorPaletteGeneratorTool from './tools/color-palette-generator.js';
import createUpdateComponentColorTool from './tools/update-component-color.js';
import changeAssistantTool from '@ai/assistants/shared/change-assistant/change-assistant-tool.js';
import { ASSISTANTS } from '@ai/assistants/constants.js';
import { isForwardedByRouter } from '@ai/assistants/shared/prompts.js';

const colorAssistantToolkit = {
	name: 'color',
	tools: ( context ) => {
		const tools = [
			createColorPaletteGeneratorTool(
				context.colors?.colorVariationCategories
			),
			createUpdateComponentColorTool(),
		];

		if (
			context.currentHandoffArea !== 'colors' &&
			! isForwardedByRouter( context )
		) {
			tools.push( changeAssistantTool( ASSISTANTS.COLOR ) );
		}

		return tools;
	},
};

export default colorAssistantToolkit;
