/**
 * External dependencies
 */
import { store as blockEditorStore } from '@wordpress/block-editor';
import { useSelect } from '@wordpress/data';
import { useRef } from '@wordpress/element';

/**
 * Internal dependencies
 */
import './style.scss';
import { store as aiStore } from '@store';
import ItemPicker from '@components/item-picker';
import useScrollButtons from '@hooks/use-scroll-buttons';

export default function PatternPickerMenu( { patterns } ) {
	const pickerRef = useRef();

	const { leftScrollButton, rightScrollButton } = useScrollButtons(
		pickerRef,
		'.components-grid'
	);

	const { lastCancelableAction } = useSelect( ( select ) => {
		const ai = select( aiStore );
		const blockEditor = select( blockEditorStore );

		return {
			lastCancelableAction: ai.getLastCancelableAction(),
			selectedBlock: blockEditor.getSelectedBlock(),
		};
	}, [] );

	if (
		! [ 'add_pattern', 'replace_pattern' ].includes(
			lastCancelableAction?.action
		)
	) {
		return null;
	}

	return (
		<div className="big-sky__dock__menu__pattern-picker" ref={ pickerRef }>
			{ leftScrollButton }

			<ItemPicker
				items={ patterns }
				clientId={ lastCancelableAction.clientId }
			/>

			{ rightScrollButton }
		</div>
	);
}
