/**
 * WordPress dependencies
 */
import { useAgents, useChat } from '@automattic/big-sky-agents';
import { useDispatch, useSelect } from '@wordpress/data';
import { cloneElement, useEffect, useRef } from '@wordpress/element';
import { __ } from '@wordpress/i18n';

/**
 * Internal dependencies
 */
import './style.scss';
import HandoffStep from './step.js';
import { ChatSuggestions } from '@ai/assistants/shared/make-suggestions';
import useShowLayoutPickerTool from '@ai/assistants/layout/tools/show-layout-picker/use-show-layout-picker-tool.js';
import useColorToolFunctions from '@ai/assistants/color/tools/use-tool-functions';
import useFontToolFunctions from '@ai/assistants/font/tools/use-tool-functions';
import Chat from '@components/chat';
import useChatThread from '@components/chat/thread/use-thread.js';
import useCanvas from '@hooks/use-canvas';
import useJetpackToken from '@hooks/use-jetpack-token';
import { useRouting } from '@hooks/use-routing';
import { store as aiStore } from '@store';
import { dockViews } from '@utils/constants';
import useSiteMetadata from '@hooks/use-site-metadata';
import { recordTracksEvent } from '@utils/tracks';
import { colorAssistant, fontAssistant } from '@ai/assistants';

export default function Handoff() {
	const { getSiteMetadata } = useSiteMetadata();
	const { setAiStep } = useRouting();

	const {
		chosenCategories,
		chosenColors,
		chosenFonts,
		handoffStep,
		chosenCustomColors,
	} = useSelect( ( select ) => {
		const ai = select( aiStore );

		return {
			chosenCategories: ai.getChosenCategories(),
			chosenColors: ai.getChosenColors(),
			chosenCustomColors: ai.getChosenCustomColors(),
			chosenFonts: ai.getChosenFonts(),
			handoffStep: ai.getHandoffStep(),
		};
	} );
	const {
		setDockView,
		setIsBuildingSite,
		setIsHandoff,
		setIsToolContainerVisible,
		setCurrentHandoffArea,
		setCurrentUserMessage,
		setChatComponents,
		setShowChatContinueButton,
		setChatContinueButtonOnClick,
		setHandoffStep,
		setShowEditorWelcome,
		setAssistant,
	} = useDispatch( aiStore );

	const { zoomIn } = useCanvas();
	const { userSay } = useChat();
	const { activeAgent } = useAgents();
	const { initializeThread } = useChatThread();
	const { generateColorPalette } = useColorToolFunctions();
	const { showFontPicker } = useFontToolFunctions();
	const { showSiteLayouts } = useShowLayoutPickerTool();
	const { withJetpackTokenRequest } = useJetpackToken();

	const { currentColor, currentFont } = getSiteMetadata();
	const prevColorRef = useRef();
	const prevFontRef = useRef();
	const handoffStartRef = useRef();

	const setupEditorWelcome = () => {
		setShowEditorWelcome( true );
		initializeThread(
			__(
				'Welcome to the editor! Click around to edit your layout, images, or copy. You can also give me commands.',
				'big-sky'
			)
		);
		const suggestions = [
			{
				key: 'help-edit-text',
				actionName: __( 'How do I edit text?', 'big-sky' ),
			},
			{
				key: 'help-edit-images',
				actionName: __( 'How do I change images?', 'big-sky' ),
			},
		];

		const respond = async ( message ) => {
			await withJetpackTokenRequest( () => userSay( message ) );
			setCurrentUserMessage( '' );
			setChatComponents( [] );
		};

		setChatComponents( [
			<ChatSuggestions
				key="suggestions"
				args={ { actions: suggestions } }
				respond={ respond }
			/>,
		] );
	};

	const finishHandoff = () => {
		recordTracksEvent( 'handoff_finished' );
		setIsBuildingSite( false );
		setIsHandoff( false );
		setCurrentHandoffArea( null );
		setDockView( dockViews.CHAT );
		setIsToolContainerVisible( false );

		setAiStep( 'edit' );

		zoomIn();
		setupEditorWelcome();
	};

	useEffect( () => {
		if ( ! handoffStartRef.current ) {
			recordTracksEvent( 'handoff_start' );
			handoffStartRef.current = true;
		}
	}, [] );

	const changeStep = ( key ) => {
		// clear any chat components
		setChatComponents( [] );
		const nextIndex = steps.findIndex( ( step ) => step.key === key );
		setHandoffStep( nextIndex );
	};

	useEffect( () => {
		if (
			steps[ handoffStep ].key !== 'ask-ai-colors' &&
			steps[ handoffStep ].key !== 'ask-ai-fonts'
		) {
			setShowChatContinueButton( false );
			prevColorRef.current = currentColor;
			prevFontRef.current = currentFont;
			return;
		}

		if (
			steps[ handoffStep ].key === 'ask-ai-colors' &&
			activeAgent?.id !== colorAssistant.id
		) {
			setAssistant( colorAssistant.id );
		}
		if (
			steps[ handoffStep ].key === 'ask-ai-fonts' &&
			activeAgent?.id !== fontAssistant.id
		) {
			setAssistant( fontAssistant.id );
		}

		if ( prevColorRef.current !== currentColor ) {
			prevColorRef.current = currentColor;
			setShowChatContinueButton( true );
			setChatContinueButtonOnClick( () => {
				changeStep( 'pick-fonts' );
				setCurrentHandoffArea( 'fonts' );
			} );
		}

		if ( prevFontRef.current !== currentFont ) {
			prevFontRef.current = currentFont;
			setShowChatContinueButton( true );
			setChatContinueButtonOnClick( () => {
				changeStep( 'pick-layouts' );
				setCurrentHandoffArea( 'layouts' );
			} );
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [
		currentColor,
		currentFont,
		handoffStep,
		setShowChatContinueButton,
		setChatContinueButtonOnClick,
		changeStep,
		setCurrentHandoffArea,
	] );

	const steps = [
		{
			key: 'welcome',
			component: (
				<HandoffStep
					title={ __( 'Welcome!', 'big-sky' ) }
					description={ __(
						'Pick your color palette, font pairing, and layout in three quick steps. Edit details after in the Editor.',
						'big-sky'
					) }
					primaryCta={ {
						text: __( 'Continue', 'big-sky' ),
						onClick: () => {
							recordTracksEvent( 'handoff_continue_click', {
								step: 'welcome',
							} );
							changeStep( 'pick-colors' );
							setCurrentHandoffArea( 'colors' );
						},
					} }
					tertiaryCta={ {
						text: __( 'Do this later', 'big-sky' ),
						onClick: () => {
							recordTracksEvent( 'handoff_do_later_click' );
							finishHandoff();
						},
					} }
				/>
			),
		},
		{
			key: 'pick-colors',
			component: (
				<HandoffStep
					onBackClick={ () => {
						recordTracksEvent( 'handoff_back_click', {
							step: 'pick-colors',
						} );
						changeStep( 'welcome' );
					} }
					title={ __( 'Pick your colors', 'big-sky' ) }
					description={ __(
						"Select from our AI-recommended color palettes to complement your site's design.",
						'big-sky'
					) }
					component={ ( setHandoffComponent ) => {
						generateColorPalette( {
							colors: chosenColors,
							custom_colors: chosenCustomColors,
							category: '',
							callback: ( component ) => {
								setHandoffComponent( component );
							},
						} );
					} }
					primaryCta={ {
						text: __( 'Continue', 'big-sky' ),
						onClick: () => {
							recordTracksEvent( 'handoff_continue_click', {
								step: 'pick-colors',
							} );
							changeStep( 'pick-fonts' );
							setCurrentHandoffArea( 'fonts' );
						},
					} }
					tertiaryCta={ {
						text: __( 'Ask AI for new color palettes', 'big-sky' ),
						onClick: () => {
							recordTracksEvent( 'handoff_ask_ai_click', {
								step: 'pick-colors',
							} );
							changeStep( 'ask-ai-colors' );
						},
						isAi: true,
					} }
					onSkip={ () => {
						recordTracksEvent( 'handoff_skip_click', {
							step: 'pick-colors',
						} );
						changeStep( 'pick-fonts' );
						setCurrentHandoffArea( 'fonts' );
					} }
				/>
			),
		},
		{
			key: 'ask-ai-colors',
			component: (
				<HandoffStep
					onBackClick={ () => {
						recordTracksEvent( 'handoff_back_click', {
							step: 'ask-ai-colors',
						} );
						changeStep( 'pick-colors' );
					} }
					title={ __( 'Ask AI', 'big-sky' ) }
					component={
						<Chat
							showNavigation={ false }
							useBlockContext={ false }
							agentQuestion={ __(
								'Give me a little more information on the type of colors you want, or I can suggest colors for you.',
								'big-sky'
							) }
							defaultPrompt={ __(
								'"Show me modern colors"',
								'big-sky'
							) }
							onlyShowLastRequest={ true }
						/>
					}
					isChatMode={ true }
					onSkip={ () => {
						recordTracksEvent( 'handoff_skip_click', {
							step: 'ask-ai-colors',
						} );
						changeStep( 'pick-fonts' );
						setCurrentHandoffArea( 'fonts' );
					} }
				/>
			),
		},
		{
			key: 'pick-fonts',
			component: (
				<HandoffStep
					onBackClick={ () => {
						recordTracksEvent( 'handoff_back_click', {
							step: 'pick-fonts',
						} );
						changeStep( 'pick-colors' );
						setCurrentHandoffArea( 'colors' );
					} }
					title={ __( 'Pick your font pair', 'big-sky' ) }
					description={ __(
						'Select from these curated fonts pairing.',
						'big-sky'
					) }
					component={ ( setHandoffComponent ) => {
						showFontPicker( {
							keywords: [],
							fonts: chosenFonts,
							callback: ( component ) => {
								setHandoffComponent( component );
							},
						} );
					} }
					primaryCta={ {
						text: __( 'Continue', 'big-sky' ),
						onClick: () => {
							changeStep( 'pick-layouts' );
							setCurrentHandoffArea( 'layouts' );
						},
					} }
					tertiaryCta={ {
						text: __(
							'Ask AI to combine more font pairings',
							'big-sky'
						),
						onClick: () => {
							changeStep( 'ask-ai-fonts' );
						},
						isAi: true,
					} }
					onSkip={ () => {
						recordTracksEvent( 'handoff_skip_click', {
							step: 'pick-fonts',
						} );
						changeStep( 'pick-layouts' );
						setCurrentHandoffArea( 'layouts' );
					} }
				/>
			),
		},
		{
			key: 'ask-ai-fonts',
			component: (
				<HandoffStep
					onBackClick={ () => {
						recordTracksEvent( 'handoff_back_click', {
							step: 'ask-ai-fonts',
						} );
						changeStep( 'pick-fonts' );
					} }
					title={ __( 'Ask AI', 'big-sky' ) }
					component={
						<Chat
							showNavigation={ false }
							useBlockContext={ false }
							agentQuestion={ __(
								"Give me a little more information on the type of fonts you'd like. I can also suggest some fonts for you.",
								'big-sky'
							) }
							defaultPrompt={ __(
								'"Show me modern fonts"',
								'big-sky'
							) }
							onlyShowLastRequest={ true }
						/>
					}
					isChatMode={ true }
					onSkip={ () => {
						recordTracksEvent( 'handoff_skip_click', {
							step: 'ask-ai-fonts',
						} );
						changeStep( 'pick-layouts' );
						setCurrentHandoffArea( 'layouts' );
					} }
				/>
			),
		},
		{
			key: 'pick-layouts',
			component: (
				<HandoffStep
					onBackClick={ () => {
						recordTracksEvent( 'handoff_back_click', {
							step: 'pick-layouts',
						} );
						changeStep( 'pick-fonts' );
						setCurrentHandoffArea( 'fonts' );
					} }
					title={ __( 'Pick a layout', 'big-sky' ) }
					description={ __(
						'Finally, pick from these layouts to organize your content and enhance user experience.',
						'big-sky'
					) }
					component={ ( setHandoffComponent ) => {
						showSiteLayouts( {
							categories: chosenCategories,
							callback: ( component ) => {
								setHandoffComponent( component );
							},
						} );
					} }
					primaryCta={ {
						text: __( 'Continue', 'big-sky' ),
						onClick: () => {
							recordTracksEvent( 'handoff_finish' );
							finishHandoff();
						},
					} }
					tertiaryCta={ false }
					onSkip={ () => {
						recordTracksEvent( 'handoff_skip_click', {
							step: 'pick-layouts',
						} );
						finishHandoff();
					} }
				/>
			),
		},
	];

	return (
		<div key={ steps[ handoffStep ].key }>
			{ cloneElement( steps[ handoffStep ].component ) }
		</div>
	);
}
