/**
 * WordPress dependencies
 */
import { store as blockEditorStore } from '@wordpress/block-editor';
import { useDispatch, useSelect } from '@wordpress/data';
import { useEffect } from '@wordpress/element';
import { __ } from '@wordpress/i18n';

/**
 * External dependencies
 */
import { useChat, useToolkits } from '@automattic/big-sky-agents';

/**
 * Internal dependencies
 */
import contentToolkit from './toolkit.js';
import useButtonToolFunctions from '@ai/assistants/content/tools/button/use-tool-functions.js';
import useContentToolFunctions from '@ai/assistants/content/tools/content/use-tool-functions.js';
import useContactFormToolFunctions from '@ai/assistants/content/tools/contact-form/use-tool-functions.js';
import useAddressToolFunctions from '@ai/assistants/content/tools/address/use-tool-functions.js';
import useFormatContentToolFunctions from '@ai/assistants/content/tools/make-text-bold-italic/use-tool-functions.js';
import useStartOverToolFunctions from '@ai/assistants/shared/start-over/use-tool-functions.js';
import useChangeAssistantTool from '@ai/assistants/shared/change-assistant/use-change-assistant-tool.js';
import { ChatSuggestions } from '@ai/assistants/shared/make-suggestions/index.js';
import patternCategories from '@data/pattern-categories.js';
import imageStyles from '@data/image-styles.json' assert { type: 'json' };
import usePatterns from '@hooks/use-patterns';
import useReplaceContent from '@ai/tasks/replace-content/use-replace-content.js';
import useSiteMetadata from '@hooks/use-site-metadata';
import useVariations from '@hooks/use-variations';
import { store as aiStore } from '@store';
import { dockViews } from '@utils/constants';
import useCreateToolCallback from '@ai/assistants/shared/use-create-tool-callback.js';

const useContentToolkit = () => {
	const { createToolCallback, withPendingToolCallback } =
		useCreateToolCallback();
	const { agentSay } = useChat();
	const { setCallbacks, setContext } = useToolkits();
	const { isContactFormOnPage, modifyContactForm, validateContactFormArgs } =
		useContactFormToolFunctions();
	const {
		replaceContentAgent,
		addSection,
		getItemsCount,
		validateAddContentSectionArgs,
		addItems,
		validateAddItemsArgs,
		getCurrentPricingContent,
	} = useContentToolFunctions();
	const { changeAssistant } = useChangeAssistantTool();
	const { updateButtonLink, upsertSocialLink, validateSocialLinkArgs } =
		useButtonToolFunctions();
	const { updateAddress } = useAddressToolFunctions();
	const { formatContent, validateFormatContentArgs } =
		useFormatContentToolFunctions();
	const { startOver, startOverActions } = useStartOverToolFunctions();
	const { getParentPatternCategory, getPattern, getSections, getItems } =
		usePatterns();
	const { getLastAvailableContentByClassName } = useReplaceContent();
	const {
		topic,
		siteTitle,
		siteDescription,
		siteLocation,
		defaultImageStyle,
	} = useSiteMetadata().getSiteMetadata();
	const { getVariationKeywords, getSocialLinkVariations } = useVariations();

	const { selectedBlock, getIsHandoff, currentHandoffArea } = useSelect(
		( select ) => {
			const { getSelectedBlock } = select( blockEditorStore );
			const {
				getOnSelectedBlockAction: _getOnSelectedBlockAction,
				getIsHandoff: _getIsHandoff,
				getCurrentHandoffArea,
			} = select( aiStore );
			return {
				selectedBlock: getSelectedBlock(),
				getIsHandoff: _getIsHandoff,
				currentHandoffArea: getCurrentHandoffArea(),
				getOnSelectedBlockAction: _getOnSelectedBlockAction,
			};
		}
	);
	const { setChatComponents, setDockView, setShowThread, setHideInput } =
		useDispatch( aiStore );

	const callbacks = {
		add_item_to_events_container: createToolCallback( {
			name: 'add_item_to_events_container',
			action: async ( args ) => {
				agentSay(
					await addItems( {
						...args,
						category: 'events',
					} )
				);
			},
			validator: ( args ) =>
				validateAddItemsArgs( { ...args, category: 'events' } ),
			failValidatorAction: ( _, error ) => {
				setShowThread( true );
				agentSay( error.message );
			},
		} ),
		add_container_to_events_section: createToolCallback( {
			name: 'add_container_to_events_section',
			action: async ( args ) => {
				agentSay(
					await addSection( {
						...args,
						category: 'events',
					} )
				);
			},
			validator: ( args ) =>
				validateAddContentSectionArgs( {
					...args,
					category: 'events',
				} ),
			failValidatorAction: ( _, error ) => {
				setShowThread( true );
				agentSay( error.message );
			},
		} ),
		add_item_to_menu_container: createToolCallback( {
			name: 'add_item_to_menu_container',
			action: async ( args ) => {
				agentSay(
					await addItems( {
						...args,
						category: 'menu',
						useCacheKey: true,
					} )
				);
			},
			validator: ( args ) =>
				validateAddItemsArgs( { ...args, category: 'menu' } ),
			failValidatorAction: ( _, error ) => {
				setShowThread( true );
				console.warn( 'Failed to add menu items', error );
				agentSay( error.message );
			},
		} ),
		add_container_to_menu_section: createToolCallback( {
			name: 'add_container_to_menu_section',
			action: async ( args ) => {
				agentSay(
					await addSection( {
						...args,
						category: 'menu',
						useCacheKey: true,
					} )
				);
			},
			validator: ( args ) =>
				validateAddContentSectionArgs( {
					...args,
					category: 'menu',
				} ),
			failValidatorAction: ( _, error ) => {
				setShowThread( true );
				console.warn( 'Failed to add menu section', error );
				agentSay( error.message );
			},
		} ),

		add_container_to_pricing_section: createToolCallback( {
			name: 'add_container_to_pricing_section',
			action: async ( args ) => {
				const { title, description } = getCurrentPricingContent();
				// restructure the arg items, converting it to tiers with a title and description
				args.items = [
					{
						title,
						description,
						tiers: args.items,
					},
				];
				agentSay(
					await addSection( { ...args, category: 'pricing' } )
				);
			},
			validator: ( args ) =>
				validateAddContentSectionArgs( {
					...args,
					category: 'pricing',
				} ),
			failValidatorAction: ( _, error ) => {
				setShowThread( true );
				agentSay( error.message );
			},
		} ),
		add_container_to_services_section: createToolCallback( {
			name: 'add_container_to_services_section',
			action: async ( args ) => {
				agentSay(
					await addSection( { ...args, category: 'services' } )
				);
			},
			validator: ( args ) =>
				validateAddContentSectionArgs( {
					...args,
					category: 'services',
				} ),
			failValidatorAction: ( _, error ) => {
				setShowThread( true );
				agentSay( error.message );
			},
		} ),
		add_member_to_team_container: createToolCallback( {
			name: 'add_member_to_team_container',
			action: async ( args ) => {
				agentSay(
					await addItems( {
						...args,
						category: 'team',
					} )
				);
			},
		} ),
		add_container_to_team_section: createToolCallback( {
			name: 'add_container_to_team_section',
			action: async ( args ) => {
				agentSay(
					await addSection( {
						...args,
						category: 'team',
					} )
				);
			},
		} ),
		add_item_to_testimonials_section: createToolCallback( {
			name: 'add_item_to_testimonials_section',
			action: async ( args ) => {
				agentSay(
					await addSection( {
						...args,
						category: 'testimonials',
					} )
				);
			},
			validator: ( args ) =>
				validateAddContentSectionArgs( {
					...args,
					category: 'testimonials',
				} ),
			failValidatorAction: ( _, error ) => {
				setShowThread( true );
				agentSay( error.message );
			},
		} ),
		modify_contact_form_fields: createToolCallback( {
			name: 'modify_contact_form_fields',
			action: async ( args ) => {
				agentSay( await modifyContactForm( args ) );
			},
			validator: ( args ) => validateContactFormArgs( args ),
			failValidatorAction: ( _, error ) => {
				agentSay( error.message );
			},
		} ),
		rewrite_content: createToolCallback( {
			name: 'rewrite_content',
			action: async ( _args ) => {
				agentSay( replaceContentAgent( _args ) );
			},
		} ),
		update_button_link: createToolCallback( {
			name: 'update_button_link',
			action: async ( _args ) => {
				agentSay( await updateButtonLink( _args ) );
			},
		} ),
		upsert_social_link: createToolCallback( {
			name: 'upsert_social_link',
			action: ( _args ) => {
				agentSay( upsertSocialLink( _args ) );
			},
			validator: ( _args ) => validateSocialLinkArgs( _args ),
			failValidatorAction: ( _, error ) => {
				setShowThread( true );
				console.warn( 'Failed to upsert social link', error );
				agentSay( error.message );
			},
		} ),
		update_address: createToolCallback( {
			name: 'update_address',
			action: async ( _args ) => {
				agentSay( await updateAddress( _args ) );
			},
		} ),
		make_text_bold_italic: createToolCallback( {
			name: 'make_text_bold_italic',
			failValidatorAction: ( _, error ) => {
				setShowThread( true );
				agentSay( error.message );
			},
			validator: ( _args ) => validateFormatContentArgs( _args ),
			action: async ( _args ) => {
				agentSay( await formatContent( _args ) );
			},
		} ),
		start_over: createToolCallback( {
			name: 'start_over',
			action: async () => {
				setShowThread( true );
				setHideInput( true );
				const args = {
					actions: startOverActions,
				};
				agentSay(
					__(
						'All customizations and pages on your site will be permanently reset. Are you sure you want to start over?',
						'big-sky'
					)
				);
				setChatComponents( [
					<ChatSuggestions
						key="suggestions"
						args={ args }
						respond={ async ( _args ) => {
							startOver( _args );
							setDockView( dockViews.MENUS );
							setChatComponents( [] );
							setHideInput( false );
						} }
					/>,
				] );
			},
			closeChatWhenDone: () => false,
		} ),
		change_assistant: createToolCallback( {
			name: 'change_assistant',
			action: ( _args ) => {
				setShowThread( true );
				changeAssistant( _args );
			},
			closeChatWhenDone: () => false,
		} ),
	};

	useEffect( () => {
		setCallbacks( contentToolkit.name, callbacks );
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [ setCallbacks ] );

	const availableCategories = patternCategories.map(
		( category ) => category.slug
	);

	useEffect( () => {
		const blockVariations = getSocialLinkVariations();
		const context = {
			patterns: {
				patternCategories: availableCategories,
			},
			site: {
				siteTitle,
				siteDescription,
				siteLocation,
				topic,
			},
			colors: {
				colorVariationCategories: getVariationKeywords( 'color' ),
			},
			fonts: {
				fontCategories: getVariationKeywords( 'font' ),
			},
			socialLinks: {
				services: blockVariations.map(
					( variation ) => variation.name
				),
			},
			events: {
				pattern: getPattern( 'events' ),
				sections: getSections( 'events' ),
				items: getItems( 'events', getPattern( 'events' ) ),
				itemCount: getItemsCount( 'events' ),
				dateFormatExample: getLastAvailableContentByClassName(
					getPattern( 'events' ),
					'is-event-date'
				),
				timeFormatExample: getLastAvailableContentByClassName(
					getPattern( 'events' ),
					'is-event-time'
				),
			},
			menu: {
				pattern: getPattern( 'menu' ),
				sections: getSections( 'menu' ),
				items: getItems( 'menu', getPattern( 'menu' ) ),
				itemCount: getItemsCount( 'menu' ),
			},
			services: {
				pattern: getPattern( 'services' ),
				sections: getSections( 'services' ),
				itemCount: getItemsCount( 'services' ),
			},
			testimonials: {
				pattern: getPattern( 'testimonials' ),
				sections: getSections( 'testimonials' ),
				itemCount: getItemsCount( 'testimonials' ),
			},
			pricing: {
				tiers: getCurrentPricingContent().tiers,
				pattern: getPattern( 'pricing' ),
				sections: getSections( 'pricing' ),
				itemCount: getItemsCount( 'pricing' ),
			},
			team: {
				pattern: getPattern( 'team' ),
				sections: getSections( 'team' ),
				items: getItems( 'team', getPattern( 'team' ) ),
				itemCount: getItemsCount( 'team' ),
			},
		};

		if ( isContactFormOnPage() ) {
			context.contactForm = {
				isOnPage: true,
			};
		}

		if ( getIsHandoff() ) {
			context.currentHandoffArea = currentHandoffArea;
			setContext( contentToolkit.name, context );
			return;
		}

		if ( ! selectedBlock ) {
			setContext( contentToolkit.name, context );
			return;
		}

		const patternCategory = getParentPatternCategory( selectedBlock );

		switch ( selectedBlock.name ) {
			case 'core/image':
			case 'core/site-logo':
			case 'core/cover':
				context.selectedBlock = {
					type: selectedBlock.name,
					clientId: selectedBlock.clientId,
					parentPatternCategory: patternCategory,
				};
				context.images = {
					styles: imageStyles,
					defaultStyle: defaultImageStyle,
				};
				setContext( contentToolkit.name, context );
				break;
			case 'core/paragraph':
			case 'core/heading':
				context.selectedBlock = {
					type: selectedBlock.name,
					clientId: selectedBlock.clientId,
					parentPatternCategory: patternCategory,
					content:
						typeof selectedBlock.attributes.content === 'string'
							? selectedBlock.attributes.content
							: selectedBlock.attributes.content?.originalHTML ||
							  selectedBlock.attributes.content?.text,
				};
				setContext( contentToolkit.name, context );
				break;
			default:
				context.selectedBlock = {
					type: selectedBlock.name,
					clientId: selectedBlock.clientId,
					parentPatternCategory: patternCategory,
				};
				setContext( contentToolkit.name, context );
				break;
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [
		selectedBlock,
		setContext,
		getIsHandoff,
		currentHandoffArea,
		siteLocation,
	] );

	return {
		createToolCallback,
		withPendingToolCallback,
		callbacks,
	};
};

export default useContentToolkit;
