/**
 * WordPress dependencies
 */
import { useDispatch, useSelect } from '@wordpress/data';
import { useCallback, useEffect } from '@wordpress/element';
import { __, sprintf } from '@wordpress/i18n';
import { store as blockEditorStore } from '@wordpress/block-editor';

/**
 * External dependencies
 */
import { useChat, useToolkits } from '@automattic/big-sky-agents';

/**
 * Internal dependencies
 */
import imageToolkit from './toolkit.js';
import imageStyles from '@data/image-styles.json' assert { type: 'json' };
import useSiteMetadata from '@hooks/use-site-metadata';
import { store as aiStore } from '@store';

import useGenerateImageTool from '@ai/assistants/image/tools/generate-image/use-generate-image-tool.js';
import useGetStockImageTool from '@ai/assistants/image/tools/get-stock-image/use-get-stock-image-tool.js';
import useGenerateSiteLogoTool from '@ai/assistants/image/tools/generate-site-logo/use-generate-site-logo-tool.js';
import useUploadSiteLogoTool from '@ai/assistants/image/tools/upload-site-logo/use-upload-site-logo-tool.js';
import useCreateToolCallback from '@ai/assistants/shared/use-create-tool-callback.js';
import useChangeAssistantTool from '@ai/assistants/shared/change-assistant/use-change-assistant-tool.js';
import Notice from '@components/dock/notice';
import { ALLOWED_IMAGE_FILE_TYPES, isAllowedImageFile } from '@utils/images';
import useImageToolValidation from './use-image-tool-validation.js';

export default function useImageToolkit() {
	const { validateArguments } = useImageToolValidation();
	const { createToolCallback, withPendingToolCallback } =
		useCreateToolCallback();
	const { changeAssistant } = useChangeAssistantTool();
	const { agentSay } = useChat();
	const { setCallbacks, setContext } = useToolkits();

	const { generateImage } = useGenerateImageTool();
	const { getStockImage } = useGetStockImageTool();
	const { generateSiteLogo } = useGenerateSiteLogoTool();
	const { uploadSiteLogo } = useUploadSiteLogoTool();
	const {
		defaultImageStyle,
		topic,
		siteTitle,
		siteDescription,
		siteLocation,
	} = useSiteMetadata().getSiteMetadata();

	const {
		selectedBlock,
		getInputtedFiles,
		inputtedFiles,
		onInputtedFilesAction,
	} = useSelect( ( select ) => {
		const { getSelectedBlock } = select( blockEditorStore );
		const {
			getInputtedFiles: _getInputtedFiles,
			getOnInputtedFilesAction,
		} = select( aiStore );
		return {
			selectedBlock: getSelectedBlock(),
			getInputtedFiles: _getInputtedFiles,
			inputtedFiles: _getInputtedFiles(),
			onInputtedFilesAction: getOnInputtedFilesAction(),
		};
	} );

	const {
		setShowThread,
		setOnInputtedFilesAction,
		setInputtedFiles,
		setToolComponent,
		setIsToolContainerVisible,
	} = useDispatch( aiStore );

	useEffect( () => {
		setCallbacks( imageToolkit.name, {
			generate_image: createToolCallback( {
				name: 'generate_image',
				action: async ( _args ) => {
					agentSay( await generateImage( _args ) );
				},
				validator: ( _args ) => validateArguments( _args ),
				failValidatorAction: ( _, error ) => agentSay( error.message ),
				closeChatWhenDone: () => true,
			} ),
			get_stock_image: createToolCallback( {
				name: 'get_stock_image',
				action: async ( _args ) => {
					agentSay( await getStockImage( _args ) );
				},
				validator: ( _args ) => validateArguments( _args ),
				failValidatorAction: ( _, error ) => agentSay( error.message ),
				closeChatWhenDone: () => true,
			} ),
			generate_site_logo: createToolCallback( {
				name: 'generate_site_logo',
				action: async ( _args ) => {
					agentSay( await generateSiteLogo( _args ) );
				},
				closeChatWhenDone: () => true,
			} ),
			upload_site_logo: createToolCallback( {
				name: 'upload_site_logo',
				action: async () => {
					const file = getInputtedFiles()?.[ 0 ];
					if ( ! file ) {
						setOnInputtedFilesAction( {
							action: 'upload_site_logo',
						} );
						setShowThread( true );
						agentSay(
							__(
								"Drag your logo into this chat box and I'll add it to your site.",
								'big-sky'
							)
						);
					} else {
						doUploadSiteLogo();
					}
				},
				closeChatWhenDone: () => false,
			} ),
			change_assistant: createToolCallback( {
				name: 'change_assistant',
				action: ( _args ) => {
					setShowThread( true );
					changeAssistant( _args );
				},
				closeChatWhenDone: () => false,
			} ),
		} );
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [ setCallbacks ] );

	useEffect( () => {
		const context = {
			images: {
				styles: imageStyles,
				defaultStyle: defaultImageStyle,
			},
			site: {
				siteTitle,
				siteDescription,
				siteLocation,
				topic,
			},
			selectedBlock: {
				clientId: selectedBlock?.clientId,
				type: selectedBlock?.type,
				name: selectedBlock?.name,
			},
		};

		if ( selectedBlock ) {
			context.selectedBlock = {
				type: selectedBlock?.name,
				clientId: selectedBlock?.clientId,
			};
		}

		setContext( imageToolkit.name, context );

		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [ setContext, selectedBlock ] );

	const doUploadSiteLogo = useCallback( () => {
		const file = getInputtedFiles()?.[ 0 ];
		const fileName = file.name;
		if ( isAllowedImageFile( fileName ) ) {
			setOnInputtedFilesAction( {} );
			withPendingToolCallback( async () => {
				const result = await uploadSiteLogo( file.file );
				setToolComponent(
					<Notice notice={ result } type="info" delay={ -1 } />
				);
				setIsToolContainerVisible( true );
			} )();
		} else {
			setOnInputtedFilesAction( {
				action: 'upload_site_logo',
			} );
			setInputtedFiles( [] );
			agentSay(
				sprintf(
					// Translators: %s is a comma-separated list of allowed file types.
					__(
						'Please provide a valid image file. Allowed file types: %s',
						'big-sky'
					),
					ALLOWED_IMAGE_FILE_TYPES.join( ', ' )
				)
			);
		}
	}, [
		getInputtedFiles,
		setOnInputtedFilesAction,
		withPendingToolCallback,
		uploadSiteLogo,
		setToolComponent,
		setIsToolContainerVisible,
		agentSay,
		setInputtedFiles,
	] );

	useEffect( () => {
		if ( ! inputtedFiles?.length ) {
			return;
		}

		if ( onInputtedFilesAction?.action ) {
			switch ( onInputtedFilesAction.action ) {
				case 'upload_site_logo':
					doUploadSiteLogo();
					break;
				default:
					break;
			}
		}
	}, [ inputtedFiles, onInputtedFilesAction, doUploadSiteLogo ] );
}
