/**
 * WordPress dependencies
 */
import { useDispatch, useSelect } from '@wordpress/data';
import { store as editorStore } from '@wordpress/editor';
import { store as coreDataStore } from '@wordpress/core-data';
import { store as blockEditorStore } from '@wordpress/block-editor';
import apiFetch from '@wordpress/api-fetch';

/**
 * Internal dependencies
 */
import log from '@utils/log-debug.js';
import { isSimpleSite } from '@utils/site-type.js';

function preloadImageAndLogError( url ) {
	/* global Image */
	const image = new Image();
	image.onerror = ( error ) => {
		log.error(
			'Error preloading image after uploading from pexels',
			error,
			{ url }
		);
	};
	image.src = url;
}

function getExternalImages( blocks, siteUrl ) {
	const result = [];

	blocks.forEach( ( block ) => {
		if (
			[ 'core/image', 'core/cover' ].includes( block.name ) &&
			block.attributes.url &&
			! block.attributes.url.startsWith( siteUrl ) &&
			block.attributes.url.includes( 'pexels.com' ) // We only want to upload images from Pexels
		) {
			result.push( block );
		}
		result.push( ...getExternalImages( block.innerBlocks, siteUrl ) );
	} );

	return result;
}

let isUploading = false;

export default function useSaveExternalImages() {
	const { postType, postId } = useSelect( ( select ) => ( {
		postType: select( editorStore ).getCurrentPostType(),
		postId: select( editorStore ).getCurrentPostId(),
	} ) );
	const isSavingPage = useSelect( ( select ) =>
		select( coreDataStore ).isSavingEntityRecord(
			'postType',
			postType,
			postId
		)
	);
	const { editorBlocks, siteUrl } = useSelect(
		( select ) => ( {
			editorBlocks: select( editorStore ).getEditorBlocks(),
			mediaUpload: select( blockEditorStore ).getSettings().mediaUpload,
			siteUrl: select( coreDataStore ).getSite()?.url || '',
		} ),
		[]
	);

	const { updateBlockAttributes } = useDispatch( blockEditorStore );
	const { savePost } = useDispatch( editorStore );

	function uploadImages() {
		const isWpcomSimple = isSimpleSite();
		const mediaUrl = isWpcomSimple
			? `https://${ window.Jetpack_Editor_Initial_State.siteFragment }`
			: siteUrl;
		const externalImages = getExternalImages( editorBlocks, mediaUrl );

		const externalImagesWithSourceId = externalImages.map( ( image ) => {
			const pexelsIdMatch = image.attributes.url.match(
				/pexels\.com\/photos\/(\d+)\//
			);
			image.pexelsID = pexelsIdMatch ? pexelsIdMatch[ 1 ] : null;
			return image;
		} );

		const externalImagesFromPexels = externalImagesWithSourceId.filter(
			( image ) => image.pexelsID
		);

		const responsePromises = [];

		if ( externalImagesFromPexels.length > 0 ) {
			const path = isWpcomSimple
				? 'rest/v1.1/external-media-upload'
				: 'wpcom/v2/external-media/copy/pexels';

			const mediaData = externalImagesFromPexels.map( ( item ) => ( {
				guid: JSON.stringify( {
					url: item.attributes.url,
					caption: item.pexelsID,
					name: `image-pexels-${ item.pexelsID }.jpg`,
					title: 'Uploaded from Pexels ' + item.pexelsID,
				} ),
			} ) );
			const pexelsUploadViaEndpointPromise = apiFetch( {
				path,
				method: 'POST',
				data: {
					[ isWpcomSimple ? 'external_ids' : 'media' ]: isWpcomSimple
						? mediaData.map( ( item ) => item.guid )
						: mediaData,
					service: 'pexels', // WPCOM.
					post_id: postId,
				},
			} ).then(
				( response ) =>
					new Promise( ( resolve, reject ) => {
						const responseMedia = response?.media ?? response;
						log.info( 'Uploaded images', responseMedia );
						if (
							externalImagesFromPexels.length !==
							responseMedia.length
						) {
							reject( 'Not all images were uploaded' );
						}
						for ( let i = 0; i < responseMedia.length; i++ ) {
							const error =
								responseMedia[ i ].error ??
								responseMedia[ i ].errors ??
								null;
							if ( null !== error ) {
								reject( error );
								continue;
							}

							preloadImageAndLogError(
								responseMedia[ i ]?.URL ??
									responseMedia[ i ].url
							);
							updateBlockAttributes(
								externalImagesFromPexels[ i ].clientId,
								{
									id:
										responseMedia[ i ]?.ID ??
										responseMedia[ i ].id,
									url:
										responseMedia[ i ]?.URL ??
										responseMedia[ i ].url,
								}
							);
						}
						resolve();
					} )
			);
			responsePromises.push( pexelsUploadViaEndpointPromise );
		} else {
			Promise.resolve( [] );
		}

		return Promise.all( responsePromises );
	}

	const importExternalImagesNow = async () => {
		isUploading = true;
		await uploadImages();
		isUploading = false;
	};

	const importExternalImagesOnSave = async () => {
		if (
			isUploading ||
			! isSavingPage ||
			process.env.PERSIST === 'false'
		) {
			return;
		}
		isUploading = true;
		const uploaded = await uploadImages();
		if ( uploaded.length > 0 ) {
			await savePost();
		}
		isUploading = false;
	};

	return { importExternalImagesNow, importExternalImagesOnSave };
}
