/**
 * WordPress dependencies
 */
import { __unstableMotion as motion } from '@wordpress/components';
import { useSelect } from '@wordpress/data';

/**
 * Internal dependencies
 */
import useIsThinking from '@hooks/use-is-thinking';
import { store as aiStore } from '@store';

export default function ChatComponents() {
	const { chatComponents } = useSelect( ( select ) => ( {
		chatComponents: select( aiStore ).getChatComponents(),
	} ) );
	const { isThinking } = useIsThinking();

	if ( ! chatComponents || chatComponents.length === 0 || isThinking ) {
		return null;
	}

	return (
		<motion.div
			className="big-sky__chat-components"
			initial={ { opacity: 0, y: 20 } }
			layout
			animate={ {
				opacity: 1,
				y: 0,
				transition: {
					duration: 0.2,
					delay: 0.14,
				},
			} }
			exit={ {
				opacity: 0,
				y: -10,
				transition: {
					duration: 0.2,
				},
			} }
			key="chat-components-actions"
		>
			{ chatComponents.map( ( component, index ) => (
				<div
					className="big-sky__chat-components-component"
					key={ index }
				>
					{ component }
				</div>
			) ) }
		</motion.div>
	);
}
