import addPageTool from './tools/add-page/add-page-tool.js';
import navigateToPageTool from './tools/navigate-to-page/navigate-to-page-tool.js';
import changeAssistantTool from '@ai/assistants/shared/change-assistant/change-assistant-tool';
import { ASSISTANTS } from '@ai/assistants/constants.js';
import { isForwardedByRouter } from '@ai/assistants/shared/prompts.js';

const pageToolkit = {
	name: 'page',
	tools: ( context ) => {
		const tools = [
			addPageTool( context.categories ),
			navigateToPageTool( context.pages ),
			...( ! isForwardedByRouter( context )
				? [ changeAssistantTool( ASSISTANTS.PAGE ) ]
				: [] ),
		];

		return tools;
	},
};

export default pageToolkit;
