/**
 * WordPress dependencies
 */
import { createBlock, parse, serialize } from '@wordpress/blocks';

/**
 * Internal dependencies
 */
import { getNextAvailableContentValue } from '../utils.js';
import { getContactsContentSystemPrompt } from './system-prompt.js';
import { patternKeys } from '@utils/constants';

/**
 * Wrap all map blocks in a div with a min-height that matches the map's height
 * to avoid layout shifts. The map block only sets a height on the fully loaded
 * map. While it's loading, it may be a shorter size.
 *
 * @param {Object} block The block to recursively check.
 * @return {Object} The modified block.
 */
function wrapMapBlock( block ) {
	if ( block.name === 'jetpack/map' ) {
		return createBlock(
			'core/group',
			{
				style: {
					dimensions: {
						minHeight: block.attributes.mapHeight,
					},
				},
			},
			[ block ]
		);
	}

	block.innerBlocks = block.innerBlocks.map( ( innerBlock ) =>
		wrapMapBlock( innerBlock )
	);
	return block;
}

function preprocessSection( pattern ) {
	const parsedPattern = parse( pattern.html );
	pattern.html = serialize(
		parsedPattern.map( ( patternBlock ) => wrapMapBlock( patternBlock ) )
	);
}

function replaceContent( {
	rootBlock,
	data: contactData,
	updateBlockAttributes,
	startIndex = -1,
} ) {
	const { ITEMS_KEY, ITEM_KEY } = patternKeys;
	if ( ! contactData ) {
		return;
	}

	// If this is the root pattern block, make a deep copy of the contact data to avoid mutation for other patterns
	let newContactData = null;
	if ( rootBlock?.attributes?.metadata?.patternCategory ) {
		newContactData = JSON.parse( JSON.stringify( contactData ) );
	} else {
		newContactData = contactData;
	}

	let newStartIndex = startIndex;
	// reset the start index if we're changing menu items
	if ( rootBlock?.attributes?.metadata?.name === ITEMS_KEY ) {
		newStartIndex = -1;
	} else if ( rootBlock?.attributes?.metadata?.name === ITEM_KEY ) {
		// increment the start index if we're going to a new menu item
		newStartIndex++;
	}

	const validClassNames = [
		'is-contact-description',
		// 123 Example Street
		// San Franciso, CA 12345
		'is-contact-address',
		// (123) 456-7890
		// hi@mygroovydomain.com
		'is-contact-phone-email',
		// 123 Example Street,
		// San Francisco, CA 12345
		// (123)-456-7890
		'is-contact-address-phone',
		// MondayFriday
		// 9am-5pm
		'is-contact-opening-hours',
	];

	if ( rootBlock?.attributes?.className ) {
		const rootBlockClassNames = rootBlock.attributes.className.split( ' ' );

		const matchingClassName = rootBlockClassNames.find( ( className ) =>
			validClassNames.includes( className )
		);

		if ( matchingClassName ) {
			const { updatedContactData, contentValue } =
				getNextAvailableContentValue( {
					data: newContactData,
					type: matchingClassName.replace( 'is-contact-', '' ),
					offset: newStartIndex,
				} );
			newContactData = updatedContactData;

			if ( updateBlockAttributes ) {
				updateBlockAttributes( rootBlock.clientId, {
					content: contentValue,
				} );
			} else {
				rootBlock.attributes.content = contentValue;
			}
		}
	}

	rootBlock?.innerBlocks.forEach( ( block ) => {
		newStartIndex = replaceContent( {
			rootBlock: block,
			data: newContactData,
			updateBlockAttributes,
			startIndex: newStartIndex,
		} );
	} );

	return newStartIndex;
}

function getPrompts( { siteLocation } ) {
	const prompts = {
		system: getContactsContentSystemPrompt( siteLocation?.name ),
		user: `Generate the contact information for this website based on the description and reason the section exists.`,
	};
	return prompts;
}
export default {
	name: 'contact',
	getPrompts,
	replaceContent,
	preprocessSection,
};
