import {
	createColorHash,
	generateHslPalette,
	updateThemePalette,
} from '../colors.js';
import { BIG_SKY_CUSTOM_THEME_PREFIX } from './constants.js';

// This custom scheme is applied to the template theme
// Creates a random HSL palette from the user_requested colors and updates the color palette
const hslTheme = ( { templateTheme, colors } ) => {
	// Build an array of colors
	// For colors that the user did not request, create an empty string
	const colorsArray = Object.values( colors ).map( ( color ) =>
		color.user_requested ? color.color : ''
	);

	// check if the colorsArray has all empty strings, if so, return null
	const allEmpty = colorsArray.every( ( color ) => color === '' );
	if ( allEmpty ) {
		return null;
	}

	// if all colors are already set, return null
	const allSet = colorsArray.every( ( color ) => color !== '' );
	if ( allSet ) {
		return null;
	}

	const hslPalette = generateHslPalette( colorsArray );

	// convert randomHSLPalette to an object like this: { color1: '#000000', color2: '#000000', color3: '#000000', color4: '#000000', color5: '#000000' }
	const newColors = hslPalette.reduce( ( acc, color, index ) => {
		acc[ `color${ index + 1 }` ] = color;
		return acc;
	}, {} );

	updateThemePalette( {
		theme: templateTheme,
		colors: newColors,
	} );

	templateTheme.title = `${ BIG_SKY_CUSTOM_THEME_PREFIX } HSL Theme - ${ createColorHash(
		newColors
	) }`;

	return templateTheme;
};

export default hslTheme;
