/**
 * WordPress dependencies
 */
import { useEffect, useMemo, useState } from '@wordpress/element';
import {
	Button,
	__experimentalGrid as Grid,
	__experimentalHStack as HStack,
	__experimentalVStack as VStack,
} from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import { arrowLeft, arrowRight } from '@wordpress/icons';
import { Variation } from '@components/style-variations-container.js';

/**
 * Internal dependencies
 */
import './style.scss';

/**
 * Sort variations so that the current variation is the first in the list.
 *
 * @param {Array}  variations            Array of variation objects.
 * @param {string} currentVariationTitle Title of current variation.
 * @param {number} numSuggestions        Number of suggestions to show. Assumes the variations are sorted with the suggested ones first.
 * @return {Array} New Array of variation objects.
 */
const prepareVariations = (
	variations,
	currentVariationTitle,
	numSuggestions
) => {
	const preparedVariations = [
		...variations.filter( ( v ) => v.title !== currentVariationTitle ),
	].filter( ( v ) => v );
	if ( numSuggestions > 0 ) {
		const suggestedVariations = preparedVariations.slice(
			0,
			numSuggestions // if the current variation is the original variation, we need to offset the number of suggestions
		);
		return [ ...suggestedVariations ];
	}
	return preparedVariations;
};

/**
 * Slice variations ensuring the list does not exceed the specified count and
 * filters out the current variation.
 *
 * @param {Array}  variations Array of variation objects.
 * @param {number} start      Starting index for the slice.
 * @param {number} count      Number of items to include in the slice.
 * @return {Array}            Sliced array of variation objects without the current variation.
 */
const sliceVariations = ( variations, start, count ) => {
	return variations.slice( start, start + count );
};

const VariationPicker = ( {
	variations,
	currentVariation,
	maxToShow = 6,
	type,
	numSuggestions = 0,
} ) => {
	// const [ originalVariation, setOriginalVariation ] = useState( null );
	const [ variationsToShow, setVariationsToShow ] = useState( [] );
	const [ firstIndex, setFirstIndex ] = useState( 0 );

	const numOfOtherVariationsToShow = maxToShow;

	const sortedVariations = useMemo(
		() => prepareVariations( variations, currentVariation, numSuggestions ),
		[ variations, currentVariation, numSuggestions ]
	);

	useEffect( () => {
		if ( ! sortedVariations ) {
			return;
		}

		// set initial variations
		const initialVariations = sliceVariations(
			sortedVariations,
			0,
			numOfOtherVariationsToShow
		);
		setVariationsToShow( initialVariations );
	}, [ numOfOtherVariationsToShow, sortedVariations ] );

	const revealPrevious = () => {
		const nextFirstIndex = Math.max(
			0,
			firstIndex - numOfOtherVariationsToShow
		);
		setFirstIndex( nextFirstIndex );

		const newVariations = sliceVariations(
			sortedVariations,
			nextFirstIndex,
			numOfOtherVariationsToShow
		);

		setVariationsToShow( newVariations );
	};

	const revealNext = () => {
		const nextFirstIndex = Math.min(
			firstIndex + numOfOtherVariationsToShow,
			Math.floor( sortedVariations.length / numOfOtherVariationsToShow ) *
				numOfOtherVariationsToShow
		);
		setFirstIndex( nextFirstIndex );

		const newVariations = sliceVariations(
			sortedVariations,
			nextFirstIndex,
			numOfOtherVariationsToShow
		);
		setVariationsToShow( newVariations );
	};

	if ( sortedVariations && sortedVariations.length === 0 ) {
		return (
			<div>
				{ __(
					'There was a problem retrieving options. Please try again.',
					'big-sky'
				) }
			</div>
		);
	}

	return (
		<div className="big-sky__dock__menu__variation-picker">
			{ variationsToShow && variationsToShow.length > 0 && (
				<VStack spacing={ 2 }>
					<Grid columns={ 3 } className="big-sky__variations">
						{ variationsToShow.map( ( variation, index ) => (
							<Variation
								key={ index }
								variation={ variation }
								type={ type }
							/>
						) ) }
					</Grid>
					{ sortedVariations.length > maxToShow && (
						<HStack
							className="big-sky__wizard-actions"
							alignment="edge"
						>
							<Button
								aria-label={ __(
									'Previous options',
									'big-sky'
								) }
								variant="tertiary"
								size="small"
								icon={ arrowLeft }
								onClick={ revealPrevious }
								disabled={ firstIndex === 0 }
								align="left"
							/>
							<Button
								aria-label={ __( 'Next options', 'big-sky' ) }
								align="right"
								variant="tertiary"
								size="small"
								icon={ arrowRight }
								onClick={ revealNext }
								disabled={
									firstIndex + numOfOtherVariationsToShow >=
									sortedVariations.length - 1
								}
							/>
						</HStack>
					) }
				</VStack>
			) }
		</div>
	);
};

export default VariationPicker;
