import { DotPromptTemplate } from '@automattic/big-sky-agents';

const specContext = new DotPromptTemplate( {
	template: `{{? it.additionalContext }}
{{
	const {
		siteGoals,
		siteType,
		topic,
		siteDescription,
		siteTitle,
		siteLocation,
		colorRequest
	} = it.additionalContext;
}}
{{? siteGoals }}The user has indicated that their goals with the site are to {{~siteGoals :item}}{{=item}}, {{~}}{{?}}
{{? siteType }}The site type is {{=siteType}}.{{?}}
{{? topic }}The site's topic is {{=topic}}.{{?}}
{{? siteDescription }}The site's description is: {{=siteDescription}}{{?}}
{{? siteTitle }}The site's title is "{{=siteTitle}}".{{?}}
{{? siteLocation?.name }}The site's location is {{=siteLocation.name}}.{{?}}
{{? colorRequest }}The user has requested the following color scheme: {{=colorRequest}}{{?}}
{{?}}`,
	inputVariables: [ 'additionalContext' ],
} );

export default specContext;
