/**
 * WordPress dependencies
 */
import { useDispatch, useSelect } from '@wordpress/data';
import { store as blockEditorStore } from '@wordpress/block-editor';

/**
 * Internal dependencies
 */
import { store as aiStore } from '@store';
import useImages from '@hooks/use-images';
import {
	customSectionNames,
	customSectionTypes,
} from '@ai/tasks/replace-content/section-types';

export default function usePatternContent() {
	const { getCachedContent } = useSelect( ( select ) => {
		// need to do this to initialize the cached content in state
		const { getCachedContent: _getCachedContent } = select( aiStore );
		_getCachedContent();
		return {
			getCachedContent: _getCachedContent,
		};
	}, [] );
	const { canGenerateImage, buildAttrToUpdateImage } = useImages();
	const { updateBlockAttributes } = useDispatch( blockEditorStore );

	const updateSectionContent = (
		block,
		sectionData,
		cacheIndexes = {
			'core/heading': 0,
			'core/paragraph': 0,
			'core/button': 0,
		}
	) => {
		const cachedContent = getCachedContent();

		const sectionCategory = sectionData.sectionCategory;
		if ( customSectionNames.includes( sectionCategory ) ) {
			return customSectionTypes[ sectionCategory ].replaceContent(
				block,
				cachedContent?.[ sectionCategory ]
			);
		}

		if ( block.innerBlocks?.length > 0 ) {
			block.innerBlocks.forEach( ( innerBlock ) =>
				updateSectionContent( innerBlock, sectionData, cacheIndexes )
			);
			return;
		}
		if (
			block.name !== 'core/heading' &&
			block.name !== 'core/paragraph' &&
			block.name !== 'core/button'
		) {
			return;
		}
		const cacheKey = `${ sectionCategory }-${ block.name.replace(
			'core/',
			''
		) }`;

		const cached = cachedContent[ cacheKey ]
			? cachedContent[ cacheKey ]
			: [];

		const selectedSectionContent =
			sectionData.selectedSectionContent[ cacheKey ];

		let newContent;
		// First we see if there is fresh content from a selected section so any user changes are not lost.
		// If all the content from the selected section has been used already fall back to the general cached
		// content. We only maintain a single cache index as `about-paragraph[0]` in section cache will most
		// likely have the same content as `about-paragraph[0]` in the general cache, so when switching to the
		// general cache we want to continue from where we left off in the section cache.
		if (
			selectedSectionContent?.length > 0 &&
			selectedSectionContent?.length - 1 >= cacheIndexes[ block.name ]
		) {
			newContent = selectedSectionContent[ cacheIndexes[ block.name ] ];

			cacheIndexes[ block.name ]++;
		} else {
			newContent = cached[ cacheIndexes[ block.name ] ];
			cacheIndexes[ block.name ]++;
		}

		if (
			block.name === 'core/heading' ||
			block.name === 'core/paragraph'
		) {
			if ( ! sectionData.conceptSection ) {
				updateBlockAttributes( block.clientId, {
					content: newContent,
				} );
			} else {
				block.attributes.content = newContent;
			}
		}

		if ( block.name === 'core/button' ) {
			if ( ! sectionData.conceptSection ) {
				updateBlockAttributes( block.clientId, {
					text: newContent,
				} );
			} else {
				block.attributes.text = newContent;
			}
		}

		if ( cached?.length - 1 === cacheIndexes[ block.name ] ) {
			cacheIndexes[ block.name ] = 0;
		}
	};

	const getPatternContent = ( block, patternCategory, content ) => {
		if ( customSectionNames.includes( patternCategory ) ) {
			return [];
		}

		switch ( block.name ) {
			case 'core/heading':
				if ( block.attributes.content ) {
					const cacheKey = `${ patternCategory }-heading`;
					content[ cacheKey ] = [
						...( content[ cacheKey ] ? content[ cacheKey ] : [] ),
						typeof block.attributes.content === 'string'
							? block.attributes.content
							: block.attributes.content?.text,
					];
				}
				break;
			case 'core/paragraph':
				if ( block.attributes.content ) {
					const cacheKey = `${ patternCategory }-paragraph`;
					content[ cacheKey ] = [
						...( content[ cacheKey ] ? content[ cacheKey ] : [] ),
						typeof block.attributes.content === 'string'
							? block.attributes.content
							: block.attributes.content?.text,
					];
				}
				break;
			case 'core/button':
				if ( block.attributes.text ) {
					const cacheKey = `${ patternCategory }-button`;
					content[ cacheKey ] = [
						...( content[ cacheKey ] ? content[ cacheKey ] : [] ),
						typeof block.attributes.text === 'string'
							? block.attributes.text
							: block.attributes.text?.originalHTML,
					];
				}
				break;
		}
		if ( block.innerBlocks?.length > 0 ) {
			block.innerBlocks.forEach( ( innerBlock ) =>
				getPatternContent( innerBlock, patternCategory, content )
			);
		}
		return content;
	};

	const updatePatternImages = (
		block,
		imageData,
		usedImages = { index: 0, urls: [] }
	) => {
		const cachedContent = getCachedContent();
		let cachedImages = cachedContent.images ? cachedContent.images : [];
		const selectedSectionImages = imageData?.selectedSectionImages || [];

		// Extract Pexels IDs from selectedSectionImages URLs
		const selectedPexelsIds = selectedSectionImages
			.map( ( url ) => {
				const match = url.match( /image-pexels-(\d+)/ );
				return match ? match[ 1 ] : null;
			} )
			.filter( Boolean );

		// Filter cachedImages to remove ones with matching Pexels IDs
		cachedImages = [
			...selectedSectionImages,
			...cachedImages.filter( ( url ) => {
				const match = url.match( /photos\/(\d+)\/pexels-photo/ );
				return match
					? ! selectedPexelsIds.includes( match[ 1 ] )
					: true;
			} ),
		];

		if ( canGenerateImage( block ) ) {
			const index = usedImages.index;
			usedImages.index = ( usedImages.index + 1 ) % cachedImages.length;
			const imageUrl = cachedImages[ index ];
			const attrToUpdate = buildAttrToUpdateImage( block, imageUrl );
			if ( ! imageData.conceptSection ) {
				updateBlockAttributes( block.clientId, attrToUpdate );
			} else {
				block.attributes = {
					...block.attributes,
					...attrToUpdate,
				};
			}
		}

		if ( block.innerBlocks?.length > 0 ) {
			block.innerBlocks.forEach( ( innerBlock ) =>
				updatePatternImages( innerBlock, imageData, usedImages )
			);
		}
	};

	const getPatternImages = ( block, images ) => {
		switch ( block.name ) {
			case 'core/image':
			case 'core/cover':
				if ( block.attributes.url ) {
					images.push( block.attributes.url );
				}
				break;
			case 'core/group':
				if (
					block.attributes.style?.background?.backgroundImage?.url
				) {
					images.push(
						block.attributes.style?.background?.backgroundImage?.url
					);
				}
				break;
		}
		if ( block.innerBlocks?.length > 0 ) {
			block.innerBlocks.forEach( ( innerBlock ) =>
				getPatternImages( innerBlock, images )
			);
		}
		return images;
	};

	return {
		updateSectionContent,
		getPatternContent,
		updatePatternImages,
		getPatternImages,
	};
}
