/**
 * WordPress dependencies
 */
import apiFetch from '@wordpress/api-fetch';

/**
 * Internal dependencies
 */
import log from '@utils/log-debug.js';
import { getIsDevMode } from '@utils/tracks.js';

export default async function logSession(
	sessionId,
	name = 'Big Sky Session'
) {
	try {
		return await apiFetch( {
			path:
				'/wpcom/v2/big-sky/v1/session/' +
				encodeURIComponent( sessionId ),
			method: 'POST',
			data: {
				name,
				is_test: getIsDevMode(),
			},
		} );
	} catch ( error ) {
		log.error( 'Error logging session: ', error );
	}
}
