/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';

/**
 * Internal dependencies
 */
import useGeocoder from '@hooks/use-geocoder.js';
import useBuildSite from '@hooks/use-build-site.js';
import useSiteMetadata from '@hooks/use-site-metadata';

export default function useBuildSiteTool() {
	const { getCoordinates } = useGeocoder();
	const { setSiteMetadata } = useSiteMetadata();
	const { buildSite } = useBuildSite();

	const buildSiteTask = async ( parameters, isQuickOnboarding = false ) => {
		const siteMetadataToUpdate = {
			siteType: parameters.siteType,
			topic: parameters.topic,
			siteTitle: parameters.siteTitle,
			siteDescription: parameters.siteDescription,
			defaultImageStyle:
				parameters.imageStyle || parameters.defaultImageStyle,
			siteLocation: {
				name:
					parameters.siteLocationName ||
					parameters.siteLocation?.name,
			},
		};
		if ( parameters.colorRequest ) {
			siteMetadataToUpdate.colorRequest = parameters.colorRequest;
		}
		if ( siteMetadataToUpdate.siteLocation.name ) {
			siteMetadataToUpdate.siteLocation.coordinates =
				await getCoordinates( siteMetadataToUpdate.siteLocation.name );
		}
		setSiteMetadata( siteMetadataToUpdate );

		buildSite( siteMetadataToUpdate, isQuickOnboarding );
		return __( 'Designing your site now', 'big-sky' );
	};

	return {
		buildSiteTask,
	};
}
