export default function replacePatternTool(
	categories = [],
	parentPatternCategory = null,
	siteLocationName = null
) {
	return {
		name: 'replace_pattern',
		description: `Replaces an existing pattern with an alternative one. If the user is asking for a map, the category must be "contact".
		If the category is not specified in the user request, default the category to ${
			parentPatternCategory ? `"${ parentPatternCategory }"` : '"null"'
		}
		If the site location is not specified in the user request, default to ${
			siteLocationName ? `"${ siteLocationName }"` : '"null"'
		}`,
		parameters: {
			type: 'object',
			properties: {
				clientId: {
					type: [ 'string', 'null' ],
					description: 'The clientId of the block to replace.',
				},
				category: {
					type: 'string',
					description: `The category of the pattern to use as a replacement.`,
					enum: categories,
				},
				isAskingForMap: {
					type: 'boolean',
					description:
						'Whether the user is asking to replace a pattern with a map.',
				},
				siteLocation: {
					type: [ 'string', 'null' ],
					description: 'The site location.',
				},
			},
			required: [
				'clientId',
				'category',
				'isAskingForMap',
				'siteLocation',
			],
		},
		strict: true,
	};
}
