/**
 * Internal dependencies
 */
import { getNextAvailableContentValue } from '../utils.js';
import { getServicesContentSystemPrompt } from './system-prompt.js';
import { patternKeys } from '@utils/constants';

const updateContent = (
	rootBlock,
	contentValue,
	updateBlockAttributes = null
) => {
	const updates = {
		'core/button': { text: contentValue },
		'core/paragraph': rootBlock.attributes?.className?.includes(
			'is-service-link'
		)
			? { content: `<a href="#">${ contentValue }</a>` }
			: { content: contentValue },
		default: { content: contentValue },
	};

	const blockUpdates = updates[ rootBlock.name ] || updates.default;

	if ( updateBlockAttributes ) {
		updateBlockAttributes( rootBlock.clientId, blockUpdates );
	} else {
		Object.assign( rootBlock.attributes, blockUpdates );
	}
};

function replaceContent( {
	rootBlock,
	data: servicesData,
	updateBlockAttributes,
	startIndex = -1,
} ) {
	const { ITEMS_KEY, ITEM_KEY } = patternKeys;
	if ( ! servicesData ) {
		return;
	}

	// If this is the root pattern block, make a deep copy of the data to avoid mutation for other patterns
	let newServicesData = null;
	if ( rootBlock?.attributes?.metadata?.patternCategory ) {
		newServicesData = JSON.parse( JSON.stringify( servicesData ) );
	} else {
		newServicesData = servicesData;
	}

	let newStartIndex = startIndex;
	// reset the start index if we're changing menu items
	if ( rootBlock?.attributes?.metadata?.name === ITEMS_KEY ) {
		newStartIndex = startIndex;
	} else if ( rootBlock?.attributes?.metadata?.name === ITEM_KEY ) {
		// increment the start index if we're going to a new menu item
		newStartIndex++;
	}

	const validClassNames = [
		'is-service-name',
		'is-service-description',
		'is-service-link',
	];

	if ( rootBlock?.attributes?.className ) {
		const rootBlockClassNames = rootBlock.attributes.className.split( ' ' );

		const matchingClassName = rootBlockClassNames.find( ( className ) =>
			validClassNames.includes( className )
		);

		if ( matchingClassName ) {
			const { updatedServicesData, contentValue } =
				getNextAvailableContentValue( {
					data: newServicesData,
					type: matchingClassName.replace( 'is-service-', '' ),
					offset: newStartIndex,
				} );
			newServicesData = updatedServicesData;

			updateContent( rootBlock, contentValue, updateBlockAttributes );
		}
	}

	for ( const block of rootBlock.innerBlocks ) {
		newStartIndex = replaceContent( {
			rootBlock: block,
			data: newServicesData,
			updateBlockAttributes,
			startIndex: newStartIndex,
		} );
	}

	return newStartIndex;
}
function getPrompts() {
	const prompts = {
		system: getServicesContentSystemPrompt,
		user: 'Generate 6 services relevant for this website based on the description and reason the section exists.',
	};

	return prompts;
}

export default {
	name: 'services',
	getPrompts,
	replaceContent,
};
