/**
 * WordPress dependencies
 */
import { useDispatch } from '@wordpress/data';
import { store as coreStore, useEntityRecord } from '@wordpress/core-data';
import { __ } from '@wordpress/i18n';
import { uploadMedia } from '@wordpress/media-utils';

/**
 * Internal dependencies
 */
import useBlocks from '@hooks/use-blocks';
import log from '@utils/log-debug.js';
import { store as aiStore } from '@store/index.js';

export default function useUploadSiteLogoTool() {
	const {
		scrollToFirstBlockByName,
		findSiteLogoBlock,
		setBlockIsProcessing,
	} = useBlocks();
	const { setInputtedFiles, setLastCancelableAction } =
		useDispatch( aiStore );
	const { editEntityRecord } = useDispatch( coreStore );

	const { record: siteData, editedRecord: editedSiteData } = useEntityRecord(
		'root',
		'site'
	);

	const uploadSiteLogo = async ( imageFile ) => {
		try {
			const siteLogoBlock = findSiteLogoBlock();
			scrollToFirstBlockByName( 'core/site-logo' );

			// upload to media library
			await uploadMedia( {
				filesList: [ imageFile ],
				onError: ( { message } ) => log.error( message ),
				onFileChange: ( [ fileObj ] ) => {
					if ( siteLogoBlock ) {
						setBlockIsProcessing( siteLogoBlock, true );
					}

					if ( fileObj?.id ) {
						const settings = editedSiteData ?? siteData;
						setLastCancelableAction( {
							action: 'upload_site_logo',
							originalId: settings.site_logo,
						} );
						setInputtedFiles( [] );

						editEntityRecord( 'root', 'site', undefined, {
							site_logo: fileObj.id,
						} );

						if ( siteLogoBlock ) {
							setBlockIsProcessing( siteLogoBlock, false );
						}
					}
				},
			} );

			if ( siteLogoBlock ) {
				return __( 'I have updated the site logo.', 'big-sky' );
			}

			return __(
				`I have updated the site logo! However, I notice neither the header nor footer contains a site logo.\n\nTo change the header/footer layouts, ask me to "**replace the header/footer section**".`,
				'big-sky'
			);
		} catch ( error ) {
			log.error( 'Error:', error );
			return __(
				'I encountered an error while processing your request. Please try again.',
				'big-sky'
			);
		}
	};

	const cancelUploadSiteLogo = ( lastCancelableAction ) => {
		editEntityRecord( 'root', 'site', undefined, {
			site_logo: lastCancelableAction.originalId,
		} );
	};

	return {
		uploadSiteLogo,
		cancelUploadSiteLogo,
	};
}
