export const getCanvasIframeElements = () => {
	const canvasiFrame = document.querySelector( '[name="editor-canvas"]' );
	const canvasiFrameDocument = canvasiFrame?.contentDocument ?? null;
	const canvasiFrameRoot = canvasiFrameDocument?.documentElement ?? null;
	const canvasiFrameWindow = canvasiFrame?.contentWindow ?? null;
	const canvasiFrameBody = canvasiFrameDocument?.body ?? null;
	return {
		canvasiFrame,
		canvasiFrameRoot,
		canvasiFrameDocument,
		canvasiFrameWindow,
		canvasiFrameBody,
	};
};
