/**
 * Internal dependencies
 */
import { patternsBySlug } from './pattern-categories.js';
import { pagesBySlug } from './page-definitions.js';

export const blog = {
	homepageSections: {
		required: [
			patternsBySlug.intro,
			patternsBySlug.posts,
			patternsBySlug.newsletter,
		],
		optional: [],
	},
	subPages: {
		required: [ pagesBySlug.about, pagesBySlug.contact ],
		optional: [],
	},
};

export const business = {
	homepageSections: {
		required: [ patternsBySlug.intro, patternsBySlug.about ],
		optional: [
			patternsBySlug.services,
			patternsBySlug.pricing,
			patternsBySlug.events,
			patternsBySlug.testimonials,
			patternsBySlug.gallery,
		],
	},
	subPages: {
		required: [ pagesBySlug.about, pagesBySlug.contact ],
		optional: [],
	},
};

export const foodAndBeverage = {
	homepageSections: {
		required: [ patternsBySlug.intro, pagesBySlug.about ],
		optional: [ patternsBySlug.gallery, patternsBySlug.testimonials ],
	},
	subPages: {
		required: [ pagesBySlug.about, pagesBySlug.menu, pagesBySlug.contact ],
		optional: [],
	},
};

export const personal = {
	homepageSections: {
		required: [
			patternsBySlug.intro,
			patternsBySlug.about,
			patternsBySlug.gallery,
		],
		optional: [],
	},
	subPages: {
		required: [ pagesBySlug.about, pagesBySlug.contact ],
		optional: [],
	},
};

export const portfolio = {
	homepageSections: {
		required: [
			patternsBySlug.intro,
			patternsBySlug.about,
			patternsBySlug.gallery,
		],
		optional: [ patternsBySlug.testimonials ],
	},
	subPages: {
		required: [ pagesBySlug.about, pagesBySlug.contact ],
		optional: [],
	},
};

const templates = {
	business,
	foodAndBeverage,
	portfolio,
	blog,
	personal,
};

export const getBaseTemplate = ( siteType, siteGoals = [] ) => {
	const template = templates[ siteType ] || templates.business;
	// force a newsletter pattern if the site goals include newsletter
	if (
		Array.isArray( siteGoals ) &&
		siteGoals.includes( 'newsletter' ) &&
		! template.homepageSections.required.includes(
			patternsBySlug.newsletter
		)
	) {
		template.homepageSections.required.push( patternsBySlug.newsletter );
	}

	return template;
};
