	nextStockPhoto( topic, numImages ) {
		// we use a redux thunk so we can log the message to the server
		return async ( { dispatch, resolveSelect } ) => {
			const photos = await resolveSelect.getPhotosFromPexels(
				topic,
				numImages
			);

			if ( ! photos?.photos || photos?.photos.length === 0 ) {
				debugLog.error( 'Photos not set - falling back to default' );
				photos.photos = [ fallbackPhoto ];
			}

			// Remove duplicate photos from the array based on photo.ID
			photos.photos = photos.photos.filter(
				( photo, index, self ) =>
					index === self.findIndex( ( t ) => t.ID === photo.ID )
			);

			// Shuffle the photos array so we get different combinations in the future
			// eslint-disable-next-line no-restricted-syntax
			photos.photos.sort( () => 0.5 - Math.random() ); // Shuffle the array

			let photo;
			if ( photos.photos.length === 1 ) {
				// We have run out of photos. We will have duplicates, but its better than having no photos.
				photo = photos.photos[ 0 ];
			} else {
				// remove and return the first photo
				photo = photos.photos.shift();
			}

			dispatch( {
				type: 'SET_STOCK_PHOTOS',
				payload: photos,
				topic,
			} );

			return photo;
		};
	},
