/**
 * WordPress dependencies
 */
import { useDispatch, useSelect } from '@wordpress/data';

/**
 * Internal dependencies
 */
import { store as aiStore } from '@store';
import { recordTracksEvent } from '@utils/tracks';
import log from '@utils/log-debug.js';
import getSystemPrompt from './system-prompt.js';
import createGetSiteStructureTool from './tool.js';
import { getBaseTemplate } from '@data/site-structure-templates.js';

function validateModelSections( modelSections, siteTemplate ) {
	const homepageSections = [];

	// First process required sections in template order
	siteTemplate.homepageSections.required.forEach( ( templateSection ) => {
		const modelSection = modelSections.find(
			( section ) => section.slug === templateSection.slug
		);

		// If model provided this required section, use its reason, otherwise fallback to description
		homepageSections.push( {
			...templateSection,
			reason: modelSection?.reason || templateSection.description,
		} );
	} );

	// Then process optional sections in template order, only including those the model selected
	siteTemplate.homepageSections.optional.forEach( ( templateSection ) => {
		const modelSection = modelSections.find(
			( section ) => section.slug === templateSection.slug
		);
		if ( modelSection ) {
			homepageSections.push( {
				...templateSection,
				reason: modelSection.reason,
			} );
		}
	} );

	return homepageSections;
}

function validateModelPages( modelPages, siteTemplate ) {
	const { required = [], optional = [] } = siteTemplate.subPages;
	const validatedPages = [];

	// Process required pages first
	required.forEach( ( templatePage ) => {
		const modelPage = modelPages.find(
			( page ) => page.slug === templatePage.slug
		);
		if ( modelPage ) {
			// Use model's page with potentially customized name
			validatedPages.push( {
				...templatePage,
				pageName: modelPage.name,
				description: modelPage.description,
			} );
		} else {
			// Fall back to template definition
			validatedPages.push( templatePage );
		}
	} );

	// Add optional pages that were selected by the model
	optional.forEach( ( templatePage ) => {
		const modelPage = modelPages.find(
			( page ) => page.slug === templatePage.slug
		);
		if ( modelPage ) {
			validatedPages.push( {
				...templatePage,
				pageName: modelPage.name,
				description: modelPage.description,
			} );
		}
	} );

	return validatedPages;
}

const constructSiteStructure = ( baseTemplate, response ) => {
	const homepageSections = validateModelSections(
		response.sections,
		baseTemplate
	);
	const subPages = validateModelPages( response.pages, baseTemplate );

	return {
		homepageSections,
		subPages,
	};
};

export default function useGetSiteStructure() {
	const { runChatCompletion, setChosenCategories } = useDispatch( aiStore );
	const { chatModel } = useSelect( ( select ) => ( {
		chatModel: select( aiStore ).getAdvancedChatModel(),
	} ) );

	const getSiteStructure = async ( siteMeta, siteGoals ) => {
		recordTracksEvent( 'get_site_structure_started' );

		const messages = [
			{
				role: 'system',
				content: getSystemPrompt( siteMeta, siteGoals ),
			},
		];

		const siteStructureTools = [
			{
				type: 'function',
				function: createGetSiteStructureTool(
					siteMeta.siteType,
					siteGoals
				),
			},
		];

		const response = await runChatCompletion(
			chatModel,
			messages,
			siteStructureTools
		);

		const toolCalls = response?.choices?.[ 0 ]?.message?.tool_calls;
		if (
			! toolCalls ||
			toolCalls.length === 0 ||
			toolCalls[ 0 ].function.name !== 'getSiteStructure'
		) {
			log.error(
				'getSiteStructure tool call missing in model response.'
			);
			return;
		}

		try {
			const structureResponse = JSON.parse(
				toolCalls[ 0 ].function.arguments
			);

			if ( ! structureResponse || ! structureResponse.sections ) {
				log.error( 'Invalid structure response format' );
				return;
			}
			const baseTemplate = getBaseTemplate(
				siteMeta.siteType,
				siteGoals
			);
			const siteStructure = constructSiteStructure(
				baseTemplate,
				structureResponse
			);

			const savedCategories = siteStructure?.homepageSections?.map(
				( section ) => ( {
					slug: section.slug,
					reason: section.reason,
				} )
			);

			setChosenCategories( savedCategories );

			return siteStructure;
		} catch ( e ) {
			log.error( 'get_site_structure JSON parsing error:', e.message );
		}
	};

	return {
		getSiteStructure,
	};
}
