/**
 * WordPress dependencies
 */
import { useEffect } from '@wordpress/element';
import { useDispatch, useSelect } from '@wordpress/data';

/**
 * External dependencies
 */
import { useToolkits } from '@automattic/big-sky-agents';

/**
 * Internal dependencies
 */
import useSiteMetadata from '@hooks/use-site-metadata';
import siteSpecToolkit from './toolkit.js';
import { store as aiStore } from '@store';
import useCreateToolCallback from '@ai/assistants/shared/use-create-tool-callback.js';
import useResetSite from '@hooks/use-reset-site.js';
import useBuildSiteTool from './tools/build-site/use-build-site-tool.js';

export default function useSiteSpecToolkit() {
	const { setChatComponents } = useDispatch( aiStore );
	const { getSiteMetadata } = useSiteMetadata();
	const { setCallbacks, setContext } = useToolkits();
	const additionalContext = useSelect( ( select ) =>
		select( aiStore ).getAdditionalContext()
	);
	const { resetSite } = useResetSite();
	const { createToolCallback } = useCreateToolCallback();
	const siteMetadata = getSiteMetadata();
	const { buildSiteTask } = useBuildSiteTool();

	useEffect( () => {
		setCallbacks( siteSpecToolkit.name, {
			build_site: async ( args ) => {
				await buildSiteTask( args );
			},
			start_over: createToolCallback( {
				name: 'start_over',
				action: async () => {
					resetSite();
					setChatComponents( [] );
				},
			} ),
		} );
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [ setCallbacks ] );

	useEffect( () => {
		// If user has returned to onboarding via backbutton we need to add the site metadata to the additional context
		// as there is no site data in the chat thread in this case.
		if ( setContext && ( additionalContext || siteMetadata.siteTitle ) ) {
			setContext( siteSpecToolkit.name, {
				additionalContext: {
					...additionalContext,
					...siteMetadata,
				},
			} );
		}
	}, [ additionalContext, setContext, siteMetadata ] );
}
