/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * Internal dependencies
 */
import './style.scss';
import useSiteMetadata from '@hooks/use-site-metadata';

export default function ThinkingMessage() {
	const isOnboarded = useSiteMetadata().getSiteMetadata().isOnboarded;

	const thinkingMessageClassnames = classnames( 'big-sky__thinking-message', {
		'big-sky__thinking-message__is-light': ! isOnboarded,
	} );

	return (
		<div className={ thinkingMessageClassnames }>
			<div className="big-sky__thinking-message__three-dots"></div>
		</div>
	);
}
