/**
 * WordPress dependencies
 */
import { useDispatch, useSelect } from '@wordpress/data';
import { store as blockEditorStore } from '@wordpress/block-editor';
import { useCallback } from '@wordpress/element';

/**
 * Internal dependencies
 */
import CoverImageDockMenuItem from '@components/dock/menus/cover-image';
import ButtonDockMenus from '@components/dock/menus/button';
import GroupDockMenus from '@components/dock/menus/group';
import ImageDockMenus from '@components/dock/menus/images';
import NavigationDockMenus from '@components/dock/menus/navigation';
import NavigationItemDockMenus from '@components/dock/menus/navigation-item';
import TextDockMenus from '@components/dock/menus/text';
import SectionColorVariationPickerMenu from '@components/dock/menus/section-color-variation-picker';
import ColorPickerMenu from '@components/dock/menus/color-picker';
import useSiteMetadata from '@hooks/use-site-metadata';
import { getDefaultChatSettings } from '@components/dock/utils';
import { store as aiStore } from '@store';
import { dockViews } from '@utils/constants';
import { recordTracksEvent } from '@utils/tracks';

export default function ButtonBarMenus( {
	setChatSettings,
	setUseBlockContext,
} ) {
	const { isOnboarded } = useSiteMetadata().getSiteMetadata();

	const { selectedBlock } = useSelect( ( select ) => {
		const { getSelectedBlock } = select( blockEditorStore );
		return {
			selectedBlock: getSelectedBlock(),
		};
	} );
	const { setDockView, setLastSelectedBlock, setHideInput } =
		useDispatch( aiStore );

	const getMenus = useCallback( () => {
		if ( ! selectedBlock || ! isOnboarded ) {
			return null;
		}

		const getMenuProps = ( additionalChatSettings = {} ) => {
			return {
				onAssistantMenuClick: (
					dockMenuTitle,
					assistantMenuSlug = null,
					dockPrompt = getDefaultChatSettings( isOnboarded ).prompt,
					useBlockContext = true
				) => {
					recordTracksEvent( 'dock_menu_item_click', {
						selected_block: selectedBlock?.name || null,
						menu_slug: assistantMenuSlug,
					} );
					setHideInput( false );
					setUseBlockContext( useBlockContext );
					setLastSelectedBlock( selectedBlock );
					setDockView( dockViews.CHAT );
					setChatSettings( {
						title: dockMenuTitle,
						prompt: dockPrompt,
						...additionalChatSettings,
					} );
				},
			};
		};

		const isPatternSelected =
			selectedBlock?.attributes?.metadata?.remotePatternId;

		switch ( selectedBlock.name ) {
			case 'core/group':
			case 'core/post-content':
				if ( ! isPatternSelected ) {
					return null;
				}
				return (
					<>
						<GroupDockMenus
							{ ...getMenuProps( { showThread: true } ) }
						/>
						<SectionColorVariationPickerMenu />
					</>
				);
			case 'core/paragraph':
			case 'core/heading':
				return (
					<>
						<TextDockMenus { ...getMenuProps() } />
						<ColorPickerMenu />
					</>
				);
			case 'core/image':
			case 'core/site-logo':
				return (
					<>
						<ImageDockMenus { ...getMenuProps() } />
					</>
				);
			case 'core/cover':
				return (
					<>
						<GroupDockMenus
							{ ...getMenuProps( { showThread: true } ) }
						/>
						<CoverImageDockMenuItem
							{ ...getMenuProps() }
							imageUrl={ selectedBlock?.attributes?.url }
						/>
					</>
				);
			case 'core/button':
			case 'core/social-link':
				return (
					<>
						<ButtonDockMenus { ...getMenuProps() } />
					</>
				);
			case 'core/navigation-link':
				return (
					<>
						<NavigationItemDockMenus
							{ ...getMenuProps() }
							selectedBlock={ selectedBlock }
						/>
					</>
				);
			case 'core/page-list':
			case 'core/navigation':
				return (
					<>
						<NavigationDockMenus { ...getMenuProps() } />
					</>
				);
			default:
				return null;
		}
	}, [
		selectedBlock,
		isOnboarded,
		setHideInput,
		setUseBlockContext,
		setLastSelectedBlock,
		setDockView,
		setChatSettings,
	] );

	const menus = getMenus();

	return (
		<>
			{ menus && <div className="big-sky__dock-ai-menus">{ menus }</div> }
		</>
	);
}
