/**
 * WordPress dependencies
 */
import { useDispatch, useSelect } from '@wordpress/data';
import { store as coreStore } from '@wordpress/core-data';
import { store as editorStore } from '@wordpress/editor';
import { __ } from '@wordpress/i18n';

/**
 * Internal dependencies
 */
import { store as aiStore } from '@store';
import useStyles from '@hooks/use-styles';
import useSiteMetadata from '@hooks/use-site-metadata';
import { deleteDummyPosts } from '@utils/dummy-posts';
import { defaultSiteMetadata } from '@utils/constants';
import usePages from '@hooks/use-pages';
import useCanvas from '@hooks/use-canvas';
import { useChat } from '@automattic/big-sky-agents';
import { ASSISTANTS } from '@ai/assistants/constants';

export default function useResetSite() {
	const { getCurrentPostId } = useSelect( editorStore );
	const { savePost } = useDispatch( editorStore );
	const { saveEntityRecord } = useDispatch( coreStore );
	const { resetStyles } = useStyles();
	const { setSiteMetadata } = useSiteMetadata();
	const { homePageId, deleteAllPagesExceptHome } = usePages();
	const { clearBlocks } = useCanvas();
	const { clearMessages, agentSay } = useChat();
	const {
		clearSelectedSectionContent,
		resetCachedContent,
		setShowThread,
		setIsHandoff,
		setAssistant,
		setHandoffStep,
	} = useDispatch( aiStore );

	const resetSite = async () => {
		const currentPostId = getCurrentPostId();

		deleteDummyPosts();
		deleteAllPagesExceptHome();
		resetStyles();
		setSiteMetadata( { ...defaultSiteMetadata }, true );
		resetCachedContent();
		clearSelectedSectionContent();
		setIsHandoff( false );
		setHandoffStep( 0 );
		// also clear siteMeta and buildDesigns from local storage
		// eslint-disable-next-line no-undef
		localStorage.removeItem( 'siteMeta' );
		// eslint-disable-next-line no-undef
		localStorage.removeItem( 'buildDesigns' );
		clearMessages();
		if ( homePageId !== currentPostId ) {
			await savePost(); //save the current post to prevent user getting a second confirmation.
			// If not on the front page, clear the content of the front page.
			saveEntityRecord( 'postType', 'page', {
				id: homePageId,
				content: '',
			} );

			window.location.href = `/wp-admin/site-editor.php?canvas=edit`;
			return;
		}
		await clearBlocks(); //saveEntityRecord doen't seem to work if on the current page so clear the blocks instead.
		await savePost();
	};

	const buildNewSite = () => {
		clearMessages();
		setIsHandoff( false );
		setShowThread( true );
		setAssistant( ASSISTANTS.SPEC );
		agentSay(
			__(
				'Here are your current site specifications. Would you like to modify them or start over?',
				'big-sky'
			)
		);
	};

	return {
		resetSite,
		buildNewSite,
	};
}
