/**
 * WordPress dependencies
 */
import { store as blockEditorStore } from '@wordpress/block-editor';
import { useDispatch, useSelect } from '@wordpress/data';
import { useEffect } from '@wordpress/element';

/**
 * External dependencies
 */
import { useChat, useToolkits } from '@automattic/big-sky-agents';

/**
 * Internal dependencies
 */
import patternToolkit from './toolkit.js';
import { store as aiStore } from '@store';
import useChangeAssistantTool from '@ai/assistants/shared/change-assistant/use-change-assistant-tool.js';
import useCreateToolCallback from '@ai/assistants/shared/use-create-tool-callback.js';
import useSiteMetadata from '@hooks/use-site-metadata';
import usePatterns from '@hooks/use-patterns';
import usePatternToolValidation from './use-pattern-tool-validation.js';
import useAddPatternTool from './tools/add-pattern/use-add-pattern-tool.js';
import useRemoveBlockTool from './tools/remove-block/use-remove-block-tool.js';
import useReplacePatternTool from './tools/replace-pattern/use-replace-pattern-tool.js';
import useGeneratePatternTool from './tools/generate-pattern/use-generate-pattern-tool.js';

const usePatternToolkit = () => {
	const { createToolCallback } = useCreateToolCallback();
	const { changeAssistant } = useChangeAssistantTool();
	const { agentSay } = useChat();
	const { setCallbacks, setContext } = useToolkits();
	const { selectedBlock } = useSelect( ( select ) => {
		const { getSelectedBlock } = select( blockEditorStore );
		return {
			selectedBlock: getSelectedBlock(),
		};
	} );
	const { getSiteMetadata } = useSiteMetadata();
	const { siteTitle, siteDescription, topic, siteLocation } =
		getSiteMetadata();

	const { getParentPatternCategory } = usePatterns();

	const { addPattern } = useAddPatternTool();
	const { removeBlock } = useRemoveBlockTool();
	const { replacePattern } = useReplacePatternTool();
	const { generatePattern } = useGeneratePatternTool();

	const {
		validateAddPatternArgs,
		validateGeneratePatternArgs,
		validateRemoveBlockArgs,
		validateReplacePatternArgs,
	} = usePatternToolValidation();

	const { setShowThread } = useDispatch( aiStore );

	const { getIsHandoff } = useSelect( ( select ) => {
		const { getIsHandoff: _getIsHandoff } = select( aiStore );
		return {
			getIsHandoff: _getIsHandoff,
		};
	} );

	// Register the toolkit's callbacks
	useEffect( () => {
		setCallbacks( patternToolkit.name, {
			...( window.localStorage.getItem( 'isGeneratingPatterns' ) ===
			'true'
				? {
						generate_pattern: createToolCallback( {
							name: 'generate_pattern',
							action: async ( _args ) => {
								const message = await generatePattern( _args );
								agentSay( message );
							},
							validator: validateGeneratePatternArgs,
							failValidatorAction: ( _, error ) => {
								setShowThread( true );
								agentSay( error.message );
							},
							closeChatWhenDone: () => ! getIsHandoff(),
						} ),
				  }
				: {
						add_pattern: createToolCallback( {
							name: 'add_pattern',
							action: async ( _args ) => {
								const message = await addPattern( _args );
								agentSay( message );
							},
							validator: validateAddPatternArgs,
							failValidatorAction: ( _, error ) => {
								setShowThread( true );
								agentSay( error.message );
							},
							closeChatWhenDone: () => ! getIsHandoff(),
						} ),
				  } ),
			remove_block: createToolCallback( {
				name: 'remove_block',
				action: async ( _args ) => {
					const message = await removeBlock( _args );
					agentSay( message );
				},
				validator: validateRemoveBlockArgs,
				failValidatorAction: ( _, error ) => {
					setShowThread( true );
					agentSay( error.message );
				},
				closeChatWhenDone: () => ! getIsHandoff(),
			} ),
			replace_pattern: createToolCallback( {
				name: 'replace_pattern',
				action: async ( _args ) => {
					const message = await replacePattern( _args );
					agentSay( message );
				},
				validator: validateReplacePatternArgs,
				failValidatorAction: ( _, error ) => {
					setShowThread( true );
					agentSay( error.message );
				},
				closeChatWhenDone: () => ! getIsHandoff(),
			} ),
			change_assistant: createToolCallback( {
				name: 'change_assistant',
				action: ( _args ) => {
					setShowThread( true );
					changeAssistant( _args );
				},
				closeChatWhenDone: () => false,
			} ),
		} );
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [ setCallbacks ] );

	let patternCategory = null;
	if ( selectedBlock ) {
		patternCategory = getParentPatternCategory( selectedBlock );
	}

	useEffect( () => {
		const context = {
			selectedBlock: {
				clientId: selectedBlock?.clientId,
				name: selectedBlock?.name,
				type: selectedBlock?.type,
				parentPatternCategory: patternCategory,
			},
			site: {
				siteTitle,
				siteDescription,
				siteLocation,
				topic,
			},
		};

		setContext( patternToolkit.name, context );

		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [ setContext, selectedBlock, topic, patternCategory ] );
};

export default usePatternToolkit;
