/**
 * WordPress dependencies
 */
import { useDispatch } from '@wordpress/data';
import { useEffect } from '@wordpress/element';
import Markdown from 'react-markdown';

/**
 * Internal dependencies
 */
import './style.scss';
import { store as aiStore } from '@store';

export default function Notice( { notice, type = 'info', delay = 3000 } ) {
	const { setIsToolContainerVisible, setToolComponent } =
		useDispatch( aiStore );

	// fade out the notice after 3 seconds
	useEffect( () => {
		let removeTimeout;
		if ( delay > 0 ) {
			removeTimeout = setTimeout( () => {
				setIsToolContainerVisible( false );
				setToolComponent( null );
			}, delay );
		}

		return () => {
			if ( removeTimeout ) {
				clearTimeout( removeTimeout );
			}
		};
	}, [ delay, setIsToolContainerVisible, setToolComponent ] );

	return (
		<div
			className={ `big-sky__dock__notice big-sky__dock__notice-${ type }` }
		>
			<Markdown>{ notice }</Markdown>
		</div>
	);
}
