/**
 * WordPress dependencies
 */
import { __, sprintf } from '@wordpress/i18n';
import {
	Button,
	__unstableMotion as motion,
	TextareaControl,
} from '@wordpress/components';
import {
	forwardRef,
	useCallback,
	useEffect,
	useMemo,
	useRef,
	useState,
} from '@wordpress/element';
import { useDispatch, useSelect } from '@wordpress/data';
import {
	useChat,
	useNextToolCall,
	useToolkits,
} from '@automattic/big-sky-agents';

/**
 * Internal dependencies
 */
import { store as aiStore } from '@store';
import useIsThinking from '@hooks/use-is-thinking';
import useJetpackToken from '@hooks/use-jetpack-token';
import useSiteMetadata from '@hooks/use-site-metadata';
import { ArrowUp, AskAI } from '@components/icons';
import { dockViews, messageRoles } from '@utils/constants';
import ChatMessage from '@components/chat/message';
import useLog from '@hooks/use-log';
import { recordTracksEvent } from '@utils/tracks';
import { assistantsById } from '@ai/assistants/index.js';
import './style.scss';

const ChatInput = forwardRef(
	( { prompt, onSendMessage, animationProps = {} }, ref ) => {
		const { userSay, pendingToolCalls, resetChangeAgentCount } = useChat();
		const { setContext } = useToolkits();
		const { withJetpackTokenRequest } = useJetpackToken();
		const { isThinking } = useIsThinking();
		const { logUserMessage } = useLog();
		const currentToolCall = useMemo( () => {
			if ( pendingToolCalls.length > 0 ) {
				return pendingToolCalls[ 0 ].function?.name;
			}
		}, [ pendingToolCalls ] );

		const { args, respond } = useNextToolCall( currentToolCall );

		const [ submitButtonEnabled, setSubmitButtonEnabled ] =
			useState( false );
		const [ errorTooLong, setErrorTooLong ] = useState( '' );
		const [ hasRecordedLongMessage, setHasRecordedLongMessage ] =
			useState( false );
		const {
			setChatComponents,
			setCurrentUserMessage,
			setShowThread,
			setShowChatContinueButton,
			setIsTyping,
		} = useDispatch( aiStore );
		const {
			currentUserMessage,
			showChatContinueButton,
			chatContinueButtonOnClick,
			dockView,
			hideInput,
			currentAssistant,
		} = useSelect( ( select ) => {
			const ai = select( aiStore );
			return {
				currentUserMessage: ai.currentUserMessage(),
				showChatContinueButton: ai.getShowChatContinueButton(),
				chatContinueButtonOnClick: ai.getChatContinueButtonOnClick(),
				dockView: ai.getDockView(),
				hideInput: ai.getHideInput(),
				currentAssistant: ai.getAssistant(),
			};
		} );
		const typingTimeout = useRef( null );

		const { isOnboarded } = useSiteMetadata().getSiteMetadata();
		useEffect( () => {
			if ( ! ref?.current ) {
				return;
			}

			ref.current?.parentNode?.style?.setProperty(
				'--big-sky-textarea-component-font-family',
				window.getComputedStyle( ref.current ).fontFamily
			);
		}, [ ref ] );
		useEffect( () => {
			if ( ! isThinking && ref?.current ) {
				ref?.current?.focus();
			}
		}, [ ref, isThinking ] );
		const characterLimit = isOnboarded ? 600 : 1200;
		const adjustTextAreaHeight = useCallback( () => {
			const textarea = ref.current;
			if ( textarea ) {
				const parent = textarea.parentNode;
				// Set the after content to the full text. The ::after pseudo-element
				// will grow in height with its content. The textarea's height is tied
				// to ::after's height via CSS Grid.
				parent.dataset.all = textarea.value;
				if ( textarea.selectionStart === textarea.selectionEnd ) {
					// Set the before content to equal the text to the left of the cursor.
					// This way, the ::before pseudo-element's height will be the same as
					// the cursor's position in the textarea's scrollHeight.
					parent.dataset.before = textarea.value.slice(
						0,
						textarea.selectionStart
					);
				} else {
					// If there's an active selection, don't try to do anything interesting.
					parent.dataset.before = '';
					return;
				}

				const before = window.getComputedStyle( parent, ':before' );
				const height = Math.round( parseFloat( before.height ) );
				if ( textarea.scrollHeight === height ) {
					// If we're typing in the last line of text in the textarea,
					// make sure we're scrolled all the way to the bottom.
					textarea.scrollTop = textarea.scrollHeight;
				} else if (
					height <=
					parseInt(
						window.getComputedStyle( textarea ).minHeight,
						10
					)
				) {
					// If we're typing in the first line (or so) of text in the textarea,
					// make sure we're scrolled all the way to the top.
					textarea.scrollTop = 0;
				}
			}
		}, [ ref ] );

		useEffect( () => {
			if (
				[ dockViews.MENUS, dockViews.CHAT, dockViews.HANDOFF ].includes(
					dockView
				)
			) {
				adjustTextAreaHeight();
			}
		}, [ currentUserMessage, adjustTextAreaHeight, dockView ] );

		const handleInputChange = ( value ) => {
			setCurrentUserMessage( value );
			if ( value.length <= characterLimit ) {
				if ( errorTooLong ) {
					setErrorTooLong( '' );
				}
				setHasRecordedLongMessage( false );
			} else {
				if ( ! hasRecordedLongMessage ) {
					recordTracksEvent( 'chat_input_message_too_long', {
						is_onboarded: isOnboarded,
					} );
					setHasRecordedLongMessage( true );
				}
				const inputError = sprintf(
					// Translators: Placeholder is a number representing the maximum number of characters allowed in a message.
					__( 'Your message is too long %1$s/%2$s', 'big-sky' ),
					value.length,
					characterLimit
				);
				setErrorTooLong( inputError );
			}
		};

		const sendMessage = useCallback( async () => {
			recordTracksEvent( 'chat_input_send_message' );
			setErrorTooLong( '' );
			setChatComponents( [] );
			resetChangeAgentCount();
			setContext(
				assistantsById( currentAssistant )?.toolkits[ 0 ]?.name,
				{
					fromAssistantId: null,
				}
			);
			if ( args && respond ) {
				await withJetpackTokenRequest( () => {
					respond( currentUserMessage );
				} );
			} else {
				logUserMessage( currentUserMessage );
				await withJetpackTokenRequest( () => {
					userSay( currentUserMessage );
				} );
			}
			setChatComponents( [] );
			setCurrentUserMessage( '' );
			setSubmitButtonEnabled( false );
			setShowThread( true );
			const textarea = ref.current;
			textarea?.style?.removeProperty( 'height' );
			// eslint-disable-next-line react-hooks/exhaustive-deps
		}, [
			setChatComponents,
			args,
			respond,
			setCurrentUserMessage,
			setShowThread,
			ref,
			withJetpackTokenRequest,
			currentUserMessage,
			logUserMessage,
			userSay,
		] );

		const handleSubmit = useCallback( async () => {
			setSubmitButtonEnabled( false );

			if ( onSendMessage ) {
				await onSendMessage( currentUserMessage );
			}
			sendMessage();
		}, [ onSendMessage, currentUserMessage, sendMessage ] );

		const handleTextAreaKeyDown = ( e ) => {
			if ( ( e.key === 'Enter' && ! e.shiftKey ) || e.key === 'Escape' ) {
				e.preventDefault();
			} else {
				setIsTyping( true );
			}
		};

		// detect enter key and send message
		const handleTextAreaKeyUp = ( e ) => {
			if ( e.key === 'Enter' && ! e.shiftKey && ! errorTooLong ) {
				if ( currentUserMessage.trim() === '' ) {
					e.preventDefault();
					setCurrentUserMessage( '' );
					return;
				}

				handleSubmit();
			}

			if ( typingTimeout.current ) {
				clearTimeout( typingTimeout.current );
			}

			typingTimeout.current = setTimeout( () => {
				setIsTyping( false );
			}, 800 );
		};

		if ( hideInput ) {
			return null;
		}

		return (
			<>
				{ errorTooLong && (
					<div className="big-sky__chat-input__error-too-long">
						<ChatMessage
							message={ {
								content: errorTooLong,
								role: messageRoles.ERROR,
								type: messageRoles.ERROR,
							} }
						/>
					</div>
				) }
				<motion.div
					className="big-sky__chat-input"
					{ ...animationProps }
					key="chat-input"
				>
					{ showChatContinueButton && (
						<>
							<Button
								className="big-sky__chat-input__continue"
								onClick={ chatContinueButtonOnClick }
							>
								{ __( 'Continue', 'big-sky' ) }
							</Button>

							<div className="big-sky__chat-input__ask-ai">
								<Button
									icon={ AskAI }
									onClick={ () => {
										setShowChatContinueButton( false );
									} }
								>
									{ __(
										'Try something different',
										'big-sky'
									) }
								</Button>
							</div>
						</>
					) }

					{ ! showChatContinueButton && (
						<>
							<TextareaControl
								className="big-sky__chat-input__textarea"
								resizable="false"
								rows={ 1 }
								__nextHasNoMarginBottom={ true }
								onChange={ ( content ) => {
									handleInputChange( content );
									setSubmitButtonEnabled(
										content.length > 0
									);
								} }
								label={ __( 'Chat Input', 'big-sky' ) }
								hideLabelFromVision={ true }
								placeholder={ prompt }
								value={ currentUserMessage }
								onKeyUp={ handleTextAreaKeyUp }
								onKeyDown={ handleTextAreaKeyDown }
								disabled={ isThinking }
								ref={ ref }
							/>

							<Button
								icon={ ArrowUp }
								className="big-sky__chat-input__submit"
								onClick={ handleSubmit }
								disabled={
									! submitButtonEnabled ||
									isThinking ||
									errorTooLong
								}
							/>
						</>
					) }
				</motion.div>
			</>
		);
	}
);

export default ChatInput;
