/**
 * External dependencies
 */
import { select } from '@wordpress/data';

/**
 * Internal dependencies
 */
import logAction from '@utils/log-action';
import { store as aiStore } from '@store';
import { messageRoles } from '@utils/constants';

const showLogs =
	( typeof window !== 'undefined' &&
		window?.bigSkyInitialState?.isDevMode ) ||
	false;

function addToPayload( content, arg, separator = ' ' ) {
	return content && content.length > 0 ? content + separator + arg : arg;
}

const logger = {
	info: showLogs ? console.info.bind( window.console ) : () => {},
	warn: showLogs ? console.warn.bind( window.console ) : () => {},
	_actionFromError( ...args ) {
		const sessionId = select( aiStore ).getSessionId();
		let errorMessage;
		if ( sessionId ) {
			errorMessage = args.reduce(
				( payload, arg ) => {
					if ( typeof arg === 'string' ) {
						payload.content = addToPayload( payload.content, arg );
					} else if ( arg instanceof Error ) {
						payload.content = addToPayload(
							payload.content,
							`${ arg?.name }: ${ arg?.message }`
						);
						if ( arg?.stack ) {
							payload.trace = addToPayload(
								payload.trace,
								arg?.stack
							);
						}
					} else if ( arg instanceof Response ) {
						payload.content = addToPayload(
							payload.content,
							`Response: ${ arg?.url } ${ arg?.status } ${ arg?.statusText }`,
							`\n\n`
						);
					} else if (
						typeof arg === 'object' &&
						! Array.isArray( arg )
					) {
						Object.entries( arg ).forEach( ( [ key, value ] ) => {
							try {
								if ( typeof value !== 'string' ) {
									value = JSON.stringify( value );
								}
								payload[ key ] = value;
							} catch ( e ) {
								// Do nothing
							}
						} );
					} else {
						try {
							payload.extra = addToPayload(
								payload.extra,
								JSON.stringify( arg )
							);
						} catch ( e ) {
							// Do nothing
						}
					}
					return payload;
				},
				{
					content: '',
					trace: '',
				}
			);
		}

		return [ sessionId, errorMessage ];
	},
	error( ...args ) {
		const [ sessionId, errorMessage ] = logger._actionFromError( ...args );
		if ( errorMessage ) {
			logAction( sessionId, errorMessage, messageRoles.ERROR );
		}

		console.error.call( window.console, ...args );
	},
	// attemptFailedNumber: which attempt just failed. 0-indexed.
	errorBreakLoop( attemptFailedNumber, ...args ) {
		if ( attemptFailedNumber > 1 ) {
			// This is the third try:
			// 0. from error()->logAction
			// 1. from this function's first call
			// 2. right now
			console.error.call( window.console, '[LOOP!]', ...args );
			return;
		}

		const [ sessionId, errorMessage ] = logger._actionFromError( ...args );
		if ( errorMessage ) {
			logAction(
				sessionId,
				errorMessage,
				messageRoles.ERROR,
				'',
				'',
				attemptFailedNumber + 1
			);
		}

		console.error.call( window.console, '[LOOP?]', ...args );
	},
};

export default logger;
