/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
import { Button } from '@wordpress/components';
import { useEffect, useRef, useState } from '@wordpress/element';
import { chevronLeft } from '@wordpress/icons';
import { __ } from '@wordpress/i18n';

/**
 * Internal dependencies
 */
import { AskAI } from '@components/icons';

export default function HandoffStep( {
	title = '\u00A0',
	description = '',
	component = null,
	primaryCta = {
		text: __( 'Continue', 'big-sky' ),
		onClick: () => {},
	},
	tertiaryCta = {
		text: __( 'Cancel', 'big-sky' ),
		onClick: () => {},
		isAi: false,
	},
	onBackClick = null,
	isChatMode = false,
	onSkip = null,
} ) {
	const [ didLoad, setDidLoad ] = useState( false );
	const [ handoffComponent, setHandoffComponent ] = useState( null );

	const componentRef = useRef( component );

	useEffect( () => {
		componentRef.current = component;
	}, [ component ] );

	useEffect( () => {
		if ( ! didLoad ) {
			if (
				componentRef.current &&
				typeof componentRef.current === 'function'
			) {
				componentRef.current( setHandoffComponent );
			} else {
				setHandoffComponent( componentRef.current );
			}
			setDidLoad( true );
		}
	}, [ didLoad ] );

	return (
		<div className="big-sky__dock__handoff-step">
			<div className="big-sky__dock__handoff-step__title-actions">
				{ onBackClick && (
					<div className="big-sky__dock__handoff-step__title-actions__back">
						<Button icon={ chevronLeft } onClick={ onBackClick } />
					</div>
				) }
				<div className="big-sky__dock__handoff-step__title-actions__title">
					{ title }
				</div>
				{ onSkip && (
					<div className="big-sky__dock__handoff-step__title-actions__skip">
						<Button onClick={ onSkip }>
							{ __( 'Skip', 'big-sky' ) }
						</Button>
					</div>
				) }
			</div>
			{ description && (
				<div className="big-sky__dock__handoff-step__description">
					{ description }
				</div>
			) }
			<div className="big-sky__dock__handoff-step__component">
				{ handoffComponent }
			</div>
			{ ! isChatMode && (
				<div className="big-sky__dock__handoff-step__buttons">
					<Button
						className="big-sky__dock__handoff-step__buttons__button big-sky__dock__handoff-step__buttons__button-primary"
						onClick={ primaryCta.onClick }
					>
						{ primaryCta.text }
					</Button>
					{ tertiaryCta && (
						<Button
							className={ classnames(
								'big-sky__dock__handoff-step__buttons__button big-sky__dock__handoff-step__buttons__button-tertiary',
								{
									'big-sky__dock__handoff-step__buttons__button-tertiary-ai':
										tertiaryCta.isAi,
								}
							) }
							onClick={ () => {
								tertiaryCta.onClick( setHandoffComponent );
							} }
						>
							{ tertiaryCta.isAi ? (
								<>
									<AskAI />
									{ tertiaryCta.text }
								</>
							) : (
								tertiaryCta.text
							) }
						</Button>
					) }
				</div>
			) }
		</div>
	);
}
