/**
 * WordPress dependencies
 */
import { __, sprintf } from '@wordpress/i18n';
import { store as blockEditorStore } from '@wordpress/block-editor';
import { useDispatch, useSelect } from '@wordpress/data';

/**
 * Internal dependencies
 */
import usePatterns from '@hooks/use-patterns';
import useSiteMetadata from '@hooks/use-site-metadata';
import useBlocks from '@hooks/use-blocks';
import useCanvas from '@hooks/use-canvas';
import { animationTiming, dockViews } from '@utils/constants';
import setDelay from '@utils/set-delay';
import PatternPickerMenu from '@components/dock/menus/pattern-picker';
import usePatternToolValidation from '@ai/assistants/pattern/use-pattern-tool-validation.js';
import { store as aiStore } from '@store';

export default function useReplacePatternTool() {
	const { getSiteMetadata } = useSiteMetadata();
	const { zoomIn, zoomOut } = useCanvas();

	const {
		getPattern,
		composePatterns,
		getPatternCategory,
		createConceptFromCurrentCanvas,
		createConceptFromSelectedBlock,
		setCTAInSection,
	} = usePatterns();

	const {
		getFirstChildBlockByClientIdDOMElement,
		getParentRootTemplatePartBlock,
		isTemplatePart,
		setBlockIsProcessing,
		scrollToBlock,
		getParentRootPatternBlock,
	} = useBlocks();

	const {
		setIsToolContainerVisible,
		setToolComponent,
		setLastCancelableAction,
		setDockView,
	} = useDispatch( aiStore );

	const { __unstableGetEditorMode, getSelectedBlock } = useSelect(
		( select ) => ( {
			__unstableGetEditorMode:
				select( blockEditorStore ).__unstableGetEditorMode,
			getSelectedBlock: select( blockEditorStore ).getSelectedBlock,
		} )
	);

	const { validateReplacePatternArgs } = usePatternToolValidation();

	const replacePattern = async ( {
		clientId,
		category,
		siteLocation: newSiteLocation,
		isAskingForMap,
	} ) => {
		try {
			await validateReplacePatternArgs( {
				clientId,
				category,
				siteLocation: newSiteLocation,
				isAskingForMap,
			} );

			const { topic, siteLocation, siteDescription } = getSiteMetadata();
			if ( __unstableGetEditorMode() !== 'zoom-out' ) {
				zoomOut();
			}

			let replaceBlock, patternCategory;

			if ( clientId && clientId !== 'undefined' ) {
				const rootPatternBlock =
					getParentRootTemplatePartBlock( clientId );
				if ( ! rootPatternBlock ) {
					replaceBlock = getParentRootPatternBlock( clientId );
				} else {
					// if this is a template part, get the first child block
					// need to use the DOM here because templates aren't editable in zoom out mode
					replaceBlock = getFirstChildBlockByClientIdDOMElement(
						rootPatternBlock.clientId
					);
				}
				patternCategory = getPatternCategory( replaceBlock.clientId );
			}

			// if the selected block doesn't match the category, try to find the matching pattern on the page
			if ( patternCategory !== category ) {
				replaceBlock = getPattern( category );
				if ( [ 'header', 'footer' ].includes( category ) ) {
					replaceBlock = getFirstChildBlockByClientIdDOMElement(
						replaceBlock.clientId
					);
				}
			}

			setBlockIsProcessing( replaceBlock, true );
			scrollToBlock( replaceBlock.clientId, 'center' );

			const selectedConceptCanvas = createConceptFromCurrentCanvas();

			const patternOptions = composePatterns( {
				categories: [ { slug: category } ],
				topic,
				siteLocation: newSiteLocation || siteLocation,
				siteDescription,
				selectedConcept: selectedConceptCanvas,
				isAskingForMap,
			} );

			if ( ! patternOptions ) {
				setBlockIsProcessing( replaceBlock, false );
				zoomIn();
				return __(
					"I'm having trouble finding options for that. Please try a different request.",
					'big-sky'
				);
			}

			// Resolve all promises while showing a loading indicator on the canvas for the pattern.
			const resolvers = patternOptions.flatMap( ( concept ) =>
				concept.resolvers.map( ( resolver ) => resolver.resolver )
			);
			await Promise.all( resolvers );

			// add the current section to the pattern options as the first option
			const currentSection = createConceptFromSelectedBlock(
				replaceBlock.clientId
			);
			patternOptions.unshift( currentSection );

			// Often this all happens too quickly, so make sure we have a few seconds of loading before inserting
			// to allow the user time to adjust to what is happening on the screen.
			await setDelay( animationTiming.visualAdjustmentWaitTime );

			setBlockIsProcessing( replaceBlock, false );

			if ( category === 'header' ) {
				patternOptions
					.flatMap( ( concept ) => concept.patterns )
					.forEach( ( pattern ) =>
						setCTAInSection( {
							category: pattern.category,
							blocks: pattern.blocks,
						} )
					);
			}

			// Display the dock pattern picker menu to allow selection of other patterns
			setToolComponent(
				<PatternPickerMenu
					patterns={ patternOptions }
					key="pattern-picker"
				/>
			);
			setIsToolContainerVisible( true );

			// Set the last cancelable action to allow the user to undo the pattern insertion
			setLastCancelableAction( {
				action: 'replace_pattern',
				clientId:
					patternOptions[ 0 ].patterns[ 0 ].blocks[ 0 ].clientId,
				originalBlock: replaceBlock,
				isTemplatePart: isTemplatePart( replaceBlock.clientId ),
			} );

			return sprintf(
				// Translators: Placeholder represents the category of the section requested by the user.
				__(
					'I have replaced the section with a new %s section.',
					'big-sky'
				),
				category
			);
		} catch ( error ) {
			throw error;
		}
	};

	const doneReplacePattern = () => {
		setDockView( dockViews.MENUS );
		setIsToolContainerVisible( false );
		zoomIn().then( () => {
			scrollToBlock( getSelectedBlock()?.clientId, 'start', true );
		} );
	};

	return {
		replacePattern,
		doneReplacePattern,
	};
}
