/**
 * WordPress dependencies
 */
import { store as blocksStore } from '@wordpress/blocks';
import { useSelect } from '@wordpress/data';

/**
 * Internal dependencies
 */
import useImages from '@hooks/use-images';
import useSiteMetadata from '@hooks/use-site-metadata';
import patternExample1 from '../pattern-examples/1.js';
import patternExample2 from '../pattern-examples/2.js';
import patternExample3 from '../pattern-examples/3.js';
import patternExample4 from '../pattern-examples/4.js';
import patternExample5 from '../pattern-examples/5.js';
import patternExample6 from '../pattern-examples/6.js';
import patternExample7 from '../pattern-examples/7.js';
import patternExample8 from '../pattern-examples/8.js';
import patternExample9 from '../pattern-examples/9.js';
import patternExample10 from '../pattern-examples/10.js';

export default function useUserPrompt() {
	const { getStockImageURL } = useImages();
	const { siteTitle, siteDescription, siteLocation, topic, siteType } =
		useSiteMetadata().getSiteMetadata();

	const allowedBlocks = useSelect(
		( select ) =>
			select( blocksStore )
				.getBlockTypes()
				.map( ( block ) => block.name )
				.filter(
					( block ) =>
						! [
							'core/media-text',
							'jetpack/contact-info',
							'jetpack/address',
							'jetpack/email',
							'jetpack/phone',
						].includes( block )
				),
		[]
	);

	const getUserPrompt = async ( userPrompt ) => {
		// Get 10 stock images
		const urls = await Promise.all(
			Array.from( { length: 20 }, () => getStockImageURL( topic ) )
		);

		const portraitUrls = urls.slice( 0, 10 ).map( ( url ) => {
			// Remove all existing width, height, and fit parameters
			const cleanedUrl = url.replace( /[?&](w|h|fit)=[^&]+/g, '' );
			// Add our desired dimensions
			return (
				cleanedUrl +
				( cleanedUrl.includes( '?' ) ? '&' : '?' ) +
				'fit=crop&w=800&h=1200&orientation=portrait'
			);
		} );
		const landscapeUrls = urls.slice( 10 ).map( ( url ) => {
			// Remove all existing width, height, and fit parameters
			const cleanedUrl = url.replace( /[?&](w|h|fit)=[^&]+/g, '' );
			// Add our desired dimensions
			return (
				cleanedUrl +
				( cleanedUrl.includes( '?' ) ? '&' : '?' ) +
				'fit=crop&w=1200&h=800&orientation=landscape'
			);
		} );

		return `
You are a WordPress block pattern generator specializing in creating complex, structured content patterns using a container-based approach. Your task is to analyze the given information and generate a WordPress block pattern that meets the specified requirements.

First, let's review the key information you'll be working with:

1. Available Images:
<portrait_image_urls>
${ portraitUrls.join( '\n' ) }
</portrait_image_urls>

<landscape_image_urls>
${ landscapeUrls.join( '\n' ) }
</landscape_image_urls>

2. Website Configuration:
<website_details>
Title: ${ siteTitle }
Description: ${ siteDescription }
Location: ${ siteLocation?.name }
Topic: ${ topic }
Type: ${ siteType }
</website_details>

3. Allowed Blocks:
<allowed_blocks>
${ allowedBlocks.join( ', ' ) }
</allowed_blocks>

4. Pattern Examples:
<pattern_examples>
Example 1:
${ patternExample1 }

Example 2:
${ patternExample2 }

Example 3:
${ patternExample3 }

Example 4:
${ patternExample4 }

Example 5:
${ patternExample5 }

Example 6:
${ patternExample6 }

Example 7:
${ patternExample7 }

Example 8:
${ patternExample8 }

Example 9:
${ patternExample9 }

Example 10:
${ patternExample10 }
</pattern_examples>

Now, let's break down the process of generating the WordPress block pattern:

1. Pattern Examples Analysis:
<thought_process>
- Review all provided pattern examples carefully
- Note unique layout approaches and structural variations
- Identify creative container relationships and hierarchies
- List interesting compositional techniques that could be adapted
- Look for non-standard groupings and unconventional arrangements
</thought_process>

2. Analyze the pattern structure:
<thought_process>
- Examine the user prompt: ${ userPrompt }
- Extract and note any specific numerical requirements (e.g., "top 5", "3 sections")
- List the main components needed for this pattern based on the user prompt
- Determine how these components relate to the website configuration and topic
- Outline the overall structure and hierarchy of the pattern
- List specific blocks you plan to use based on the allowed blocks and pattern examples
</thought_process>

3. Create container hierarchy:
<thought_process>
- Design a container structure that accommodates all necessary components
- Ensure container structure exactly matches any numerical requirements from the user prompt
- List each container with a unique and descriptive ID
- Describe how containers are nested logically and efficiently
- Outline how each container, no matter how small, will be broken down into sub-phases
</thought_process>

4. Plan content grouping:
<thought_process>
- List the content divided into logical, independently renderable groups
- Specify which container each content group will be associated with
- Ensure content groups match any numerical requirements specified in the user prompt
- Describe how content groups relate to each other and the overall pattern structure
- Identify which pattern example's layout approach could be adapted
- Note specific structural elements to incorporate from that example
- Explain how the chosen layout serves the content purpose
</thought_process>

Now, generate the WordPress block pattern following these rules:

1. Container Syntax:
   Phase 1 must use:
   <!-- CONTAINER_ID:unique_name type="container-type" -->

   Subsequent phases must wrap content with:
   <!-- CONTAINER_REF:unique_name -->
   [content]
   <!-- /CONTAINER_REF:unique_name -->

2. Phase Structure:
   - Start each phase with /begin-phase-[number] and end with /end-phase-[number]
   - Block Structure Requirements:
     1. Every phase must be complete and valid
     2. Every phase must contain both opening and closing tags
     3. No partial blocks or incomplete HTML allowed
     4. No splitting blocks across phases
     5. No orphaned HTML tags
     6. Phase 1 must only contain CONTAINER_ID markers
     7. All content phases must be wrapped in CONTAINER_REF tags

3. Content Phases:
- Each content phase fills ONE complete container
- Must be wrapped in correct CONTAINER_REF tags
- Must be completely self-contained
- Place individual column blocks in separate containers and phases

4. Layout Requirements:
- Avoid defaulting to basic column-based layouts unless specifically requested
- Draw inspiration from provided pattern examples' structural approaches
- Choose a layout style that best serves the content and user intent

5. Content Rules
   - wp:heading blocks should always include "align":"wide" in their attributes
   - wp:columns blocks should contain images with the same orientation, all portrait or all landscape
   - Use wp:spacer blocks to add space between sections of the pattern
   - The outermost wp:group block must include one of these className variations randomly chosen: "is-style-section-default", "is-style-section-1", "is-style-section-2", "is-style-section-3"
   - If the user wants a subscription form, use the jetpack/subscriptions block if available

6. Generation Rules:
   - Phase 1: Output outermost container with CONTAINER_ID markers
   - Phase 2+: Fill each container with content wrapped in CONTAINER_REF tags
   - Each content container must be filled in order
   - Max 50 lines per phase
   - Layout Selection:
     * Review pattern examples for diverse layout approaches
     * Choose a layout style that differs from basic column structures
     * Adapt interesting techniques from example patterns
     * Consider the specific content type and purpose when selecting layout
   - Numerical Requirements:
     * Create exactly the number of containers specified in the user prompt
     * Fill all numerically-specified containers completely
     * Never generate fewer items than specifically requested

6. Image Guidelines:
   - Never use inline height/width styles in the image block
   - Always retain all image attributes in the URL
   - Do not mix portrait and landscape images in patterns
   - Keep images the same size across columns
   - Use the correct format:
     <!-- wp:image {"sizeSlug":"full","linkDestination":"none"} -->
     <figure class="wp-block-image size-full">
       <img src="[IMAGE_URL]" alt="[Alt]"/>
     </figure>
     <!-- /wp:image -->

7. Additional Requirements:
   - Break down each container into sub-phases for more granular control
   - Ensure clear mapping between CONTAINER_ID and CONTAINER_REF
   - Work from outside containers inward
   - Generate independently renderable sections
   - Do not use \`\`\` or any other code block formatting

Example of correct pattern structure, this is only an example, please create different structures based on the user prompt:

<example_pattern_structure>
/begin-phase-1
<!-- wp:group -->
<div class="wp-block-group">
  <!-- CONTAINER_ID:intro type="content" -->
  <!-- CONTAINER_ID:grid type="grid" -->
  <!-- CONTAINER_ID:footer type="content" -->
</div>
<!-- /wp:group -->
/end-phase-1

/begin-phase-2
<!-- CONTAINER_REF:intro -->
<!-- wp:heading -->
<h2>Title</h2>
<!-- /wp:heading -->
<!-- /CONTAINER_REF:hero -->
/end-phase-2

/begin-phase-3
<!-- CONTAINER_REF:grid -->
<!-- wp:columns -->
<div class="wp-block-columns">
  <!-- wp:column -->
  <div class="wp-block-column">
    <!-- CONTAINER_ID:col1 type="content" -->
  </div>
  <!-- /wp:column -->
  <!-- wp:column -->
  <div class="wp-block-column">
    <!-- CONTAINER_ID:col2 type="content" -->
  </div>
  <!-- /wp:column -->
</div>
<!-- /wp:columns -->
<!-- /CONTAINER_REF:grid -->
/end-phase-3

/begin-phase-4
<!-- CONTAINER_REF:footer -->
<!-- wp:paragraph -->
<p>Footer content</p>
<!-- /wp:paragraph -->
<!-- /CONTAINER_REF:footer -->
/end-phase-4
</example_pattern_structure>

IMPORTANT:
 - Generate the complete pattern in a single response without asking for permission to continue or checking if more phases should be added. Generate ALL phases needed to complete the pattern, including all requested items and content. Every content phase must be properly wrapped in CONTAINER_REF tags that match their corresponding CONTAINER_ID markers.
 - Don't use any introduction text or any other text other than the phases and content.
 - DO NOT mix portrait and landscape image orientations in patterns.

 Begin generating the WordPress block pattern now, following the phase structure and rules outlined above. Remember to use the allowed blocks, consider the website configuration, and incorporate the available images appropriately.`;
	};

	return {
		getUserPrompt,
	};
}
