/**
 * WordPress dependencies
 */
import { Button } from '@wordpress/components';
import { chevronLeft } from '@wordpress/icons';

/**
 * Internal dependencies
 */
import './style.scss';
import useSiteMetadata from '@hooks/use-site-metadata';
import ResizeButton from '@components/chat/navigation/resize-button';

export default function ChatNavigation( { title, onClickBackButton } ) {
	const { isOnboarded } = useSiteMetadata().getSiteMetadata();

	return (
		<div className="big-sky__chat-navigation">
			{ isOnboarded && (
				<Button
					icon={ chevronLeft }
					className="big-sky__chat-navigation__back"
					onClick={ () => onClickBackButton() }
					size="compact"
				/>
			) }
			{ title && <h4>{ title }</h4> }
			<ResizeButton />
		</div>
	);
}
