/**
 * WordPress dependencies
 */
import { useDispatch, useSelect } from '@wordpress/data';
import { useCallback, useEffect } from '@wordpress/element';
import { store as blockEditorStore } from '@wordpress/block-editor';

/**
 * Internal dependencies
 */
import { store as aiStore } from '@store';
import CompletedAction from './completed-action.js';
import Handoff from './handoff/index.js';
import PendingActionMessage from './pending-action.js';
import ButtonBarMenus from '@components/dock/menus';
import BigSkyIcon from '@components/big-sky-icon';
import { getDefaultChatSettings } from '@components/dock/utils';
import useSiteMetadata from '@hooks/use-site-metadata';
import { dockViews } from '@utils/constants.js';
import { recordTracksEvent } from '@utils/tracks';
import { useChat } from '@automattic/big-sky-agents';

export default function ButtonBar( { setChatSettings, setUseBlockContext } ) {
	const { isOnboarded } = useSiteMetadata().getSiteMetadata();
	const { setDockView, setIsToolContainerVisible, setHideInput } =
		useDispatch( aiStore );
	const {
		dockView,
		isAssistantButtonVisible,
		isBuildingSite,
		selectedBlock,
	} = useSelect( ( select ) => {
		const ai = select( aiStore );
		const blockEditor = select( blockEditorStore );

		return {
			isAssistantButtonVisible: ai.getIsAssistantButtonVisible(),
			isBuildingSite: ai.getIsBuildingSite(),
			dockView: ai.getDockView(),
			selectedBlock: blockEditor.getSelectedBlock(),
		};
	} );
	const { setShowThread } = useDispatch( aiStore );
	const { clearMessages } = useChat();

	const openAssistant = useCallback( () => {
		if (
			isBuildingSite ||
			[ dockViews.PENDING, dockViews.HANDOFF ].includes( dockView )
		) {
			return;
		}
		recordTracksEvent( 'dock_assistant_icon_click', {
			selected_block: selectedBlock?.name || null,
		} );
		clearMessages();
		setHideInput( false );
		setUseBlockContext( false );
		setShowThread( false );
		setDockView( dockViews.CHAT );
		setChatSettings( getDefaultChatSettings( isOnboarded ) );
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [
		isBuildingSite,
		dockView,
		setUseBlockContext,
		setShowThread,
		setDockView,
		setChatSettings,
		isOnboarded,
	] );

	useEffect( () => {
		const handleKeyDown = ( event ) => {
			// Open Assistant on cmd+j
			if (
				isOnboarded &&
				( event.metaKey || event.ctrlKey ) &&
				event.key === 'j'
			) {
				event.preventDefault();
				if ( dockView === dockViews.CHAT ) {
					setDockView( dockViews.MENUS );
				} else {
					openAssistant();
				}
			}
		};

		document.addEventListener( 'keydown', handleKeyDown );
		return () => {
			document.removeEventListener( 'keydown', handleKeyDown );
		};
	}, [ openAssistant, dockView, setDockView, isOnboarded ] );

	return (
		<>
			{ ( ! [
				dockViews.CHAT,
				dockViews.COMPLETED,
				dockViews.HANDOFF,
			].includes( dockView ) ||
				( dockView === dockViews.HANDOFF &&
					isAssistantButtonVisible ) ) && (
				<BigSkyIcon
					onClick={ openAssistant }
					width={ 48 }
					height={ 48 }
				/>
			) }
			{ dockView === dockViews.HANDOFF && (
				<Handoff setChatSettings={ setChatSettings } />
			) }
			{ dockView === dockViews.COMPLETED && (
				<CompletedAction
					setIsToolContainerVisible={ setIsToolContainerVisible }
				/>
			) }
			{ dockView === dockViews.PENDING && <PendingActionMessage /> }
			{ dockView === dockViews.MENUS && (
				<ButtonBarMenus
					setUseBlockContext={ setUseBlockContext }
					setChatSettings={ setChatSettings }
				/>
			) }
		</>
	);
}
