/**
 * External dependencies
 */
import { __, sprintf } from '@wordpress/i18n';
import { useSelect } from '@wordpress/data';
import { store as blockEditorStore } from '@wordpress/block-editor';
/**
 * Internal dependencies
 */
import useValidation from '@hooks/use-validation';
import useGeocoder from '@hooks/use-geocoder';
import useSiteMetadata from '@hooks/use-site-metadata';
import usePatterns from '@hooks/use-patterns';
import { customSectionNames } from '@ai/tasks/replace-content/section-types';

export default function usePatternToolValidation() {
	const { isValidClientId, blockExists } = useValidation();
	const { getCoordinates } = useGeocoder();
	const { getPattern } = usePatterns();
	const { setSiteMetadata } = useSiteMetadata();
	const { canRemoveBlock } = useSelect( blockEditorStore );

	const validateBlockArgs = ( args ) => {
		if ( ! args ) {
			throw new Error(
				__(
					'I could not complete your request at this time. Please try again.',
					'big-sky'
				)
			);
		}

		return true;
	};

	const validateClientIdArgs = ( args ) => {
		const { clientId } = args;

		// check if client ID is null or invalid and that we have a valid block.
		if ( ! isValidClientId( clientId ) || ! blockExists( clientId ) ) {
			throw new Error(
				__( 'Please select a block and try again.', 'big-sky' )
			);
		}

		return true;
	};

	const validateAddPatternArgs = async ( args ) => {
		validateBlockArgs( args );
		validateClientIdArgs( args );

		const onlyOneAllowedSections = [
			'header',
			'footer',
			...customSectionNames,
		];
		for ( const category of args.categories ) {
			const pattern = getPattern( category );
			if ( onlyOneAllowedSections.includes( category ) && pattern ) {
				args.doReplace = true; // set a flag to replace the existing section
				// update args to pass into the replace_pattern tool callback
				args.clientId = pattern.clientId;
				args.category = category;
				throw new Error(
					sprintf(
						// Translators: %s is the name of a pattern category.
						__(
							'You already have a %s section on this page. Please remove the existing section before adding a new one, or select the existing section and ask to replace it.',
							'big-sky'
						),
						category
					)
				);
			}
		}

		await validateMapRequestArgs( args );

		return true;
	};

	const validateGeneratePatternArgs = async ( args ) => {
		validateBlockArgs( args );
		validateClientIdArgs( args );

		await validateMapRequestArgs( args );

		return true;
	};

	const validateMapRequestArgs = async ( args ) => {
		if (
			( args.categories && ! args.categories.includes( 'contact' ) ) ||
			( args.category && args.category !== 'contact' )
		) {
			return;
		}

		if ( args.isAskingForMap && ! args.siteLocation ) {
			throw new Error(
				__(
					'I notice there is no location set for this site. What would you like the site location to be?',
					'big-sky'
				)
			);
		}

		// set site location in site metadata, pass it back in the args
		if ( args.siteLocation ) {
			const newSiteLocation = {
				name: args.siteLocation,
				coordinates: await getCoordinates( args.siteLocation ),
			};
			setSiteMetadata(
				{
					siteLocation: newSiteLocation,
				},
				true
			);
			args.siteLocation = newSiteLocation;
		}
	};

	const validateRemoveBlockArgs = ( args ) => {
		try {
			validateBlockArgs( args );
			validateClientIdArgs( args );
		} catch ( error ) {
			throw new Error(
				`I was not able to find a block to remove. ${ error?.message }`
			);
		}

		const { clientId } = args;
		if ( ! canRemoveBlock( clientId ) ) {
			throw new Error(
				__( "I'm sorry, but I cannot remove that block.", 'big-sky' )
			);
		}

		return true;
	};

	const validateReplacePatternArgs = async ( args ) => {
		validateBlockArgs( args );

		// if the clientId is provided, we need to validate it
		// it's ok if it's not provided, we'll find the block by category
		if ( args.clientId ) {
			validateClientIdArgs( args );
		}

		const replaceBlock = getPattern( args.category );
		if ( ! replaceBlock ) {
			throw new Error(
				sprintf(
					// Translators: Placeholder represents the category of the section requested by the user.
					__(
						'It looks like there is no %s section on this page. Would you like to add one?',
						'big-sky'
					),
					args.category
				)
			);
		}

		await validateMapRequestArgs( args );

		return true;
	};

	return {
		validateBlockArgs,
		validateClientIdArgs,
		validateMapRequestArgs,
		validateAddPatternArgs,
		validateGeneratePatternArgs,
		validateRemoveBlockArgs,
		validateReplacePatternArgs,
	};
}
