/**
 * WordPress dependencies
 */
import { useDispatch, useSelect } from '@wordpress/data';
import { store as blockEditorStore } from '@wordpress/block-editor';
import { store as coreStore } from '@wordpress/core-data';
import { store as editorStore } from '@wordpress/editor';

/**
 * Internal dependencies
 */
import { store as aiStore } from '@store';
import { recordTracksEvent } from '@utils/tracks';
import useGetBuildDesigns from '@ai/tasks/get-build-designs/use-get-build-designs';
import useGetSiteStructure from '@ai/tasks/get-site-structure/use-get-site-structure';
import usePatterns from '@hooks/use-patterns';
import useCanvas from '@hooks/use-canvas';
import useSiteMetadata from '@hooks/use-site-metadata';
import useStyles from '@hooks/use-styles';
import usePages from '@hooks/use-pages';
import setDelay from '@utils/set-delay';
import { useRouting } from '@hooks/use-routing';
import { BIG_SKY_CUSTOM_THEME_PREFIX } from '@utils/custom-color-variations/constants';

export default function useBuildSite() {
	const { getBuildDesigns } = useGetBuildDesigns();
	const { getSiteStructure } = useGetSiteStructure();
	const { setAiStep } = useRouting();
	const { patternCategories, additionalContext } = useSelect(
		( select ) => ( {
			chatModel: select( aiStore ).getAdvancedChatModel(),
			patternCategories: select( aiStore ).getPatternCategories(),
			additionalContext: select( aiStore ).getAdditionalContext(),
		} )
	);

	const {
		composePatterns,
		composeTemplatePartPattern,
		syncSiteMetadataPatterns,
	} = usePatterns();
	delete patternCategories.header;
	delete patternCategories.footer;
	const { currentPostId } = useSelect( ( select ) => ( {
		currentPostId: select( editorStore ).getCurrentPostId(),
	} ) );
	const { setSiteMetadata } = useSiteMetadata();
	const { savePost } = useDispatch( editorStore );
	const { setImageStyle } = useDispatch( aiStore );
	const { editEntityRecord } = useDispatch( coreStore );
	const { __unstableSetEditorMode } = useDispatch( blockEditorStore );

	const { addPatternsToCanvas } = useCanvas();

	const { getBlocksByName, getBlocks } = useSelect( blockEditorStore );
	const { pushGeneratedVariations, setIsHandoff } = useDispatch( aiStore );

	const { getCurrentTheme, saveStylesEntityRecord, setStyles } = useStyles();
	const { createPage } = usePages();

	const buildSite = async ( siteMeta, isQuickOnboarding = false ) => {
		const designElementsPromise = getBuildDesigns(
			siteMeta,
			isQuickOnboarding
		);

		const siteStructurePromise = getSiteStructure(
			siteMeta,
			additionalContext.siteGoals
		);
		const [ designElements, siteStructure ] = await Promise.all( [
			designElementsPromise,
			siteStructurePromise,
		] );

		const {
			topic,
			siteTitle,
			siteDescription,
			siteLocation,
			color,
			font,
			patterns,
		} = designElements;

		setImageStyle( siteMeta.defaultImageStyle );
		editEntityRecord( 'root', 'site', undefined, {
			title: siteTitle,
		} );

		setStyles( { color, font, updateBlocks: false } );

		// Adding the pages but not awaiting them so as not to block the home page build.
		// Awaiting them after patterns added to canvas, by which time they have mostly
		// completed. We may need to revist this if we start adding more pages.
		const addSubPages = Promise.all(
			siteStructure.subPages.map( async ( page, index ) => {
				return await createPage(
					page.pageName,
					page.sections,
					topic,
					siteDescription,
					siteLocation,
					false,
					index + 1,
					null,
					page.description,
					page.slug
				);
			} )
		);

		const header = composeTemplatePartPattern( {
			templatePart: 'header',
			siteDescription,
			siteLocation,
			topic,
			isQuickOnboarding,
			pattern: patterns?.find(
				( pattern ) => pattern.category === 'header'
			)?.id,
		} );

		const footer = composeTemplatePartPattern( {
			templatePart: 'footer',
			siteDescription,
			siteLocation,
			topic,
			pattern: patterns?.find(
				( pattern ) => pattern.category === 'footer'
			)?.id,
		} );

		// set patterns
		const concepts = composePatterns( {
			categories: siteStructure.homepageSections,
			topic,
			siteDescription,
			siteLocation,
			numberOfConcepts: 1,
			chosenPatterns: patterns?.filter(
				( pattern ) =>
					pattern.category !== 'header' &&
					pattern.category !== 'footer'
			),
			pages: siteStructure.subPages,
		} );

		const firstConceptPatternsAndResolvers = concepts[ 0 ].patterns.map(
			( pattern, index ) => ( {
				pattern,
				resolver: concepts[ 0 ].resolvers[ index ].resolver,
			} )
		);

		await addPatternsToCanvas(
			[ header, ...firstConceptPatternsAndResolvers, footer ],
			true,
			isQuickOnboarding
		);

		// homepage patterns
		const homePageBlocks = getBlocks(
			getBlocksByName( 'core/post-content' )
		);

		const createPageResponses = await addSubPages;
		// need to sync both homepage and subpages patterns
		createPageResponses.push( {
			rootBlocks: homePageBlocks,
			newPage: { id: currentPostId },
		} );
		createPageResponses.map( ( response ) =>
			response.rootBlocks.map( ( block ) => {
				const slug = block.attributes?.metadata?.patternCategory;
				const pageSection = siteStructure.homepageSections.find(
					( section ) => section.slug === slug
				);
				block.attributes.metadata.reason = pageSection?.reason;
				// block.attributes?.metadata?.description = pageSection.description;
				return block;
			} )
		);
		syncSiteMetadataPatterns( {
			ids: createPageResponses.map( ( response ) => response.newPage.id ),
			blocks: createPageResponses.map(
				( response ) => response.rootBlocks
			),
			persist: true,
		} );

		if ( ! concepts?.[ 0 ]?.patterns.length ) {
			// set editor mode back to edit
			__unstableSetEditorMode( 'edit' );
			return;
		}

		await saveStylesEntityRecord();
		await savePost( currentPostId );

		// Add the current theme as a custom variation
		const themeCopy = JSON.parse( JSON.stringify( getCurrentTheme() ) );
		themeCopy.title = BIG_SKY_CUSTOM_THEME_PREFIX;
		pushGeneratedVariations( [ themeCopy ] );
		// Set styles here again to ensure the theme is applied
		setStyles( { color, font } );

		setSiteMetadata( { isOnboarded: true }, true );
		recordTracksEvent( 'building_site_finished' );

		await setDelay( 1000 );
		setAiStep( 'handoff' );

		// start handoff
		setIsHandoff( true );
	};

	return {
		buildSite,
	};
}
