/**
 * External dependencies
 */
import classNames from 'classnames';
import Markdown from 'react-markdown';
import { useChat } from '@automattic/big-sky-agents';

/**
 * WordPress dependencies
 */
import { Icon, __unstableMotion as motion } from '@wordpress/components';
import { useDispatch } from '@wordpress/data';
import { warning } from '@wordpress/icons';
import { store as blockEditorStore } from '@wordpress/block-editor';
import { cloneElement } from '@wordpress/element';

/**
 * Internal dependencies
 */
import './style.scss';
import { messageRoles } from '@utils/constants';
import { AI } from '@components/icons';
import useJetpackToken from '@hooks/use-jetpack-token';

const markdownComponents = {
	a: ( props ) => {
		const { href } = props;
		return (
			<a href={ href } target="_blank" rel="noopener noreferrer">
				{ props.children }
			</a>
		);
	},
};

export default function ChatMessage( { message } ) {
	const { userSay } = useChat();
	const { selectBlock } = useDispatch( blockEditorStore );
	const { withJetpackTokenRequest } = useJetpackToken();

	const handleOnClick = async () => {
		if ( message.clientId ) {
			selectBlock( message.clientId );
			return;
		}
		if ( message.role === messageRoles.ACTION ) {
			await withJetpackTokenRequest( () => userSay( message.content ) );
		}
	};

	const formatMessageContent = ( content ) => {
		let messageContent = content;
		if ( typeof messageContent === 'string' ) {
			messageContent = (
				<Markdown components={ markdownComponents }>
					{ messageContent }
				</Markdown>
			);
		} else if (
			typeof messageContent === 'object' &&
			! Array.isArray( messageContent )
		) {
			messageContent = cloneElement( messageContent );
		} else if ( Array.isArray( messageContent ) ) {
			messageContent = (
				<Markdown components={ markdownComponents }>
					{ messageContent[ 0 ]?.text }
				</Markdown>
			);
		}

		return messageContent;
	};

	const cssClasses = classNames( 'big-sky__chat-message', {
		'big-sky__chat-message__user': message.role === messageRoles.USER,
		'big-sky__chat-message__error': message.role === messageRoles.ERROR,
		'big-sky__chat-message__thinking':
			message.role === messageRoles.THINKING,
		'big-sky__chat-message__component':
			message.role === messageRoles.COMPONENT,
		'big-sky__chat-message__action': message.role === messageRoles.ACTION,
		'big-sky__chat-message__has-block-link': !! message.clientId,
	} );

	const animations = {
		initial: {
			y: 40,
			opacity: 0,
			transition: { duration: 0.3 },
		},
		exit: {
			opacity: 0,
			transition: { duration: 0 },
		},
		animate: {
			y: 0,
			opacity: 1,
			transition: { duration: 0.3 },
		},
	};

	return (
		<motion.div
			layoutScroll
			initial={ animations.initial }
			exit={ animations.exit }
			animate={ animations.animate }
			className={ cssClasses }
			role="button"
			tabIndex="0"
			onClick={ handleOnClick }
			onKeyDown={ handleOnClick }
			onFocus={ handleOnClick }
		>
			{ ( message.role === messageRoles.ASSISTANT ||
				message.role === messageRoles.THINKING ||
				message.role === messageRoles.TOOL ) && (
				<div className="big-sky__chat-message__avatar">
					<Icon icon={ AI } />
				</div>
			) }
			<div className="big-sky__chat-message__content">
				{ message.role === messageRoles.ERROR && (
					<Icon icon={ warning } />
				) }
				{ formatMessageContent( message.content ) }
			</div>
		</motion.div>
	);
}
