/**
 * WordPress dependencies
 */
import { createBlock } from '@wordpress/blocks';
import { store as blockEditorStore } from '@wordpress/block-editor';
import { useDispatch, useSelect } from '@wordpress/data';
import { store as coreStore } from '@wordpress/core-data';
import { useCallback } from '@wordpress/element';
import { store as editorStore } from '@wordpress/editor';

/**
 * Internal dependencies
 */
import { getCanvasIframeElements } from '@utils/canvas-iframe-elements';

export default function useBlocks() {
	const {
		getBlockHierarchyRootClientId,
		getBlock,
		getBlocksByName,
		getClientIdsOfDescendants,
		getBlockParentsByBlockName,
		getBlocks,
	} = useSelect( blockEditorStore );
	const { selectBlock, updateBlockAttributes } =
		useDispatch( blockEditorStore );
	const { replaceBlock } = useDispatch( editorStore );

	const settings = useSelect( ( select ) => {
		const _settings = select( coreStore ).getEntityRecord( 'root', 'site' );
		return {
			siteTitle: _settings?.title ?? '',
		};
	} );

	const getContentOfBlocks = ( blocks ) => {
		const content = [];
		blocks.forEach( ( block ) => {
			if ( block.innerBlocks?.length > 0 ) {
				content.push( ...getContentOfBlocks( block.innerBlocks ) );
			} else if (
				block.name === 'core/heading' ||
				block.name === 'core/paragraph' ||
				block.name === 'core/site-title' ||
				block.name === 'core/image' ||
				block.name === 'core/button'
			) {
				let example;
				if ( block.name === 'core/button' ) {
					example = block.attributes.text || 'Button';
				} else if ( block.name === 'core/site-title' ) {
					example = 'Acme Inc';
				} else {
					example = block.attributes.content;
				}
				content.push( {
					name: block.name,
					clientId: block.clientId,
					example,
				} );
			}
		} );

		return content;
	};

	const getSurroundingContentOfBlock = ( block ) => {
		const rootParentBlockClientID = getBlockHierarchyRootClientId(
			block?.clientId
		);
		const rootParentBlock = getBlock( rootParentBlockClientID );
		const allChildBlocksClientIDs = getClientIdsOfDescendants(
			rootParentBlock?.clientId
		);

		let surroundingContent = '';
		allChildBlocksClientIDs.forEach( ( clientID ) => {
			const childBlock = getBlock( clientID );
			if (
				childBlock.name === 'core/heading' ||
				childBlock.name === 'core/paragraph'
			) {
				surroundingContent += ' ' + childBlock.attributes.content;
			}
		} );

		return surroundingContent;
	};

	const getBlockElement = ( block ) => {
		const { canvasiFrameWindow } = getCanvasIframeElements();
		if (
			canvasiFrameWindow &&
			canvasiFrameWindow.document.readyState === 'complete'
		) {
			return canvasiFrameWindow.document.getElementById(
				`block-${ block.clientId }`
			);
		}

		return null;
	};

	const setBlockIsProcessing = ( block, isProcessing ) => {
		const blockElement = getBlockElement( block );
		if ( blockElement ) {
			blockElement.classList.remove( 'big-sky__is-processing' );
			blockElement.classList.remove( 'big-sky__has-processed' );
			blockElement.classList.add(
				isProcessing
					? 'big-sky__is-processing'
					: 'big-sky__has-processed'
			);
		}
	};

	const getContentBlockClientID = () => {
		return getBlocksByName( 'core/post-content' )[ 0 ];
	};

	const getTemplatePartClientID = ( name ) => {
		const templatePartClientIds = getBlocksByName( 'core/template-part' );

		if ( ! templatePartClientIds ) {
			return;
		}

		const slug = name === 'header' ? 'header' : 'footer';

		return templatePartClientIds.find( ( clientId ) => {
			const block = getBlock( clientId );
			return block.attributes.slug === slug;
		} );
	};

	const setMapLocationInBlocks = ( blocks, location ) => {
		if ( ! location || ! location.coordinates ) {
			return;
		}

		for ( const block of blocks ) {
			if ( block.name === 'jetpack/map' ) {
				const lat = location.coordinates[ 0 ];
				const lng = location.coordinates[ 1 ];

				block.attributes.mapCenter = {
					lat,
					lng,
				};

				block.attributes.points = [
					{
						placeTitle: location.name,
						title: settings.siteTitle || location.name,
						caption: '',
						/* eslint-disable-next-line no-restricted-syntax */
						id: 'place.' + Math.random(),
						coordinates: {
							latitude: lat,
							longitude: lng,
						},
					},
				];

				updateBlockAttributes( block?.clientId, {
					mapCenter: {
						lat,
						lng,
					},
					points: [
						{
							placeTitle: location.name,
							title: settings.siteTitle || location.name,
							caption: '',
							/* eslint-disable-next-line no-restricted-syntax */
							id: 'place.' + Math.random(),
							coordinates: {
								latitude: lat,
								longitude: lng,
							},
						},
					],
				} );
			}

			if ( block.innerBlocks?.length > 0 ) {
				setMapLocationInBlocks( block.innerBlocks, location );
			}
		}
	};

	const selectBlockInViewport = (
		iframeElement,
		canvasContentWindow,
		blockClientId
	) => {
		const boundingRect = iframeElement.getBoundingClientRect();
		let top = boundingRect.top;
		let bottom = boundingRect.bottom;
		if ( boundingRect.height > canvasContentWindow.innerHeight ) {
			top =
				boundingRect.top +
				( boundingRect.height - canvasContentWindow.innerHeight );
			bottom =
				boundingRect.bottom -
				( boundingRect.height - canvasContentWindow.innerHeight );
		}
		if ( top >= 0 && bottom <= canvasContentWindow.innerHeight ) {
			selectBlock( blockClientId );
			return true;
		}

		return false;
	};

	const scrollToBlock = async (
		blockClientId,
		position = 'start',
		selectBlockAfterScroll = false
	) => {
		if ( blockClientId ) {
			const { canvasiFrameWindow } = getCanvasIframeElements();
			if (
				canvasiFrameWindow &&
				canvasiFrameWindow.document.readyState === 'complete'
			) {
				// Access the element within the iframe content
				const iframeElement =
					canvasiFrameWindow.document.getElementById(
						`block-${ blockClientId }`
					);

				// Scroll to the element within the iframe content
				if ( iframeElement ) {
					iframeElement.scrollIntoView( {
						behavior: 'smooth',
						block: position,
					} );
				}

				if ( selectBlockAfterScroll ) {
					if (
						selectBlockInViewport(
							iframeElement,
							canvasiFrameWindow,
							blockClientId
						)
					) {
						return;
					}

					const handleScroll = ( clientId ) => {
						if (
							selectBlockInViewport(
								iframeElement,
								canvasiFrameWindow,
								clientId
							)
						) {
							canvasiFrameWindow.document.removeEventListener(
								'scroll',
								scrollHandlerFn
							);
						}
					};

					const scrollHandlerFn = () => handleScroll( blockClientId );
					canvasiFrameWindow.document.addEventListener(
						'scroll',
						scrollHandlerFn
					);
				}
			}
		}
	};

	const scrollToFirstBlockByName = (
		blockName,
		position = 'start',
		select = false
	) => {
		const blocks = getBlocksByName( blockName );
		if ( blocks.length > 0 ) {
			scrollToBlock( blocks[ 0 ], position, select );
		}
	};

	const getParentRootPatternBlock = ( clientId ) => {
		const block = getBlock( clientId );
		if ( ! block ) {
			return null;
		}

		if ( block.attributes?.metadata?.patternCategory ) {
			return block;
		}

		const parentGroupClientIds = getBlockParentsByBlockName(
			clientId,
			'core/group'
		);

		if ( ! parentGroupClientIds ) {
			return;
		}

		let parentRootPatternBlock = null;

		parentGroupClientIds.forEach( ( groupClientId ) => {
			const groupBlock = getBlock( groupClientId );

			if ( groupBlock?.attributes?.metadata?.patternCategory ) {
				parentRootPatternBlock = groupBlock;
			}
		} );

		return parentRootPatternBlock;
	};

	const getFirstChildBlockByName = useCallback( ( block, searchName ) => {
		if ( block.name === searchName ) {
			return block;
		}

		if ( block.innerBlocks?.length ) {
			for ( const innerBlock of block.innerBlocks ) {
				const _block = getFirstChildBlockByName(
					innerBlock,
					searchName
				);

				if ( _block ) {
					return _block;
				}
			}
		}

		return false;
	}, [] );

	const getParentRootTemplatePartBlock = ( clientId ) => {
		const block = getBlock( clientId );
		if ( block.name === 'core/template-part' ) {
			return block;
		}

		const parentGroupClientIds = getBlockParentsByBlockName(
			clientId,
			'core/template-part'
		);
		if ( ! parentGroupClientIds ) {
			return;
		}
		return getBlock( parentGroupClientIds[ 0 ] );
	};

	const isTemplatePart = ( clientId ) => {
		const rootTemplatePartBlock =
			getParentRootTemplatePartBlock( clientId );
		if ( ! rootTemplatePartBlock ) {
			return getBlock( clientId ).name === 'core/template-part';
		}
		return rootTemplatePartBlock.name === 'core/template-part';
	};

	// Function to recursively copy a block and its inner blocks
	const copyBlockWithInnerBlocks = ( block ) => {
		// Create a copy of the current block
		const newBlock = createBlock( block.name, block.attributes );

		// If the block has inner blocks, copy them recursively
		if ( block.innerBlocks && block.innerBlocks.length ) {
			newBlock.innerBlocks = block.innerBlocks.map( ( innerBlock ) =>
				copyBlockWithInnerBlocks( innerBlock )
			);
		}

		return newBlock;
	};

	const getFirstChildBlockByClientIdDOMElement = ( clientId ) => {
		const { canvasiFrameWindow } = getCanvasIframeElements();
		const domElement = canvasiFrameWindow.document.getElementById(
			`block-${ clientId }`
		);
		return getBlock(
			domElement
				.querySelector( 'div[data-type="core/group"]' )
				.getAttribute( 'data-block' )
		);
	};

	const blockHasContent = ( block ) => {
		// There is no content if there is no block.
		if ( ! block ) {
			return false;
		}
		switch ( block.name ) {
			case 'core/paragraph':
				return block.attributes?.content?.length > 0;
			case 'core/image':
				return block.attributes?.url?.length > 0;
			case 'core/quote':
				return block.attributes?.value?.length > 0;
			default:
				return true;
		}
	};

	const fixInvalidBlock = ( block ) => {
		const { clientId, name, attributes, innerBlocks } = block;
		const replacement = createBlock( name, attributes, innerBlocks );
		if ( blockHasContent( replacement ) ) {
			replaceBlock( clientId, replacement );
		}

		// traverse all of the inner blocks and fix them
		block?.innerBlocks?.forEach( ( innerBlock ) => {
			fixInvalidBlock( innerBlock );
		} );

		return block;
	};

	const findSiteLogoBlock = () => {
		const headerClientId = getTemplatePartClientID( 'header' );
		const headerContentBlocks = getBlocks( headerClientId );
		const footerClientId = getTemplatePartClientID( 'footer' );
		const footerContentBlocks = getBlocks( footerClientId );

		const searchForSiteLogoBlock = ( blocks ) => {
			for ( const block of blocks ) {
				if ( block.name === 'core/site-logo' ) {
					return block;
				}

				if ( block.innerBlocks && block.innerBlocks.length > 0 ) {
					const found = searchForSiteLogoBlock( block.innerBlocks );
					if ( found ) {
						return found;
					}
				}
			}
		};

		return searchForSiteLogoBlock( [
			...headerContentBlocks,
			...footerContentBlocks,
		] );
	};

	return {
		getSurroundingContentOfBlock,
		getContentOfBlocks,
		getContentBlockClientID,
		getTemplatePartClientID,
		setBlockIsProcessing,
		setMapLocationInBlocks,
		scrollToBlock,
		scrollToFirstBlockByName,
		getFirstChildBlockByName,
		getParentRootPatternBlock,
		getParentRootTemplatePartBlock,
		isTemplatePart,
		copyBlockWithInnerBlocks,
		getFirstChildBlockByClientIdDOMElement,
		fixInvalidBlock,
		findSiteLogoBlock,
	};
}
