import fontAssistantPrompt from './prompt.js';
import fontToolkit from './toolkit.js';
import { ASSISTANTS } from '../constants.js';
import useFontToolkit from './use-font-toolkit.js';

const fontAssistant = {
	id: ASSISTANTS.FONT,
	description: 'An assistant that handles font selection.',
	instructions: ( context ) => {
		return fontAssistantPrompt( context );
	},
	tags: [ ASSISTANTS.FONT ],
	toolkits: [ fontToolkit ],
	toolkitHook: useFontToolkit,
	onStart: ( invoke, clearMessages = () => {}, agentMessage = null ) => {
		if ( agentMessage ) {
			clearMessages();
			invoke.agentSay( agentMessage );
		}
	},
};

export default fontAssistant;
