const createGetBuildDesignsTool = (
	colors,
	fonts,
	additionalProperties = {}
) => {
	return {
		name: 'getBuildDesigns',
		description: 'Sets site color palettes and font typeset.',
		strict: true,
		parameters: {
			type: 'object',
			properties: {
				...{
					// We want to inject enums for colors and fonts to make sure they are not hallucinated.
					colors: {
						type: 'array',
						description:
							'The list of color palette names that best match the site',
						items: {
							type: 'string',
							enum: colors,
						},
					},
					fonts: {
						type: 'array',
						description:
							'The list of font typeset names that best match the site',
						items: {
							type: 'string',
							enum: fonts,
						},
					},
				},
				...additionalProperties,
			},
			required: [
				'colors',
				'fonts',
				...Object.keys( additionalProperties ),
			],
			additionalProperties: false,
		},
	};
};

export default createGetBuildDesignsTool;
