/**
 * External dependencies
 */
import classnames from 'classnames';
import deepmerge from 'deepmerge';
import { isPlainObject } from 'is-plain-object';

/**
 * WordPress dependencies
 */
import { useSelect } from '@wordpress/data';

import { store as coreStore } from '@wordpress/core-data';
import { useContext, useMemo, useState } from '@wordpress/element';
import { ENTER } from '@wordpress/keycodes';
import { __experimentalGrid as Grid } from '@wordpress/components';
import { __, sprintf } from '@wordpress/i18n';
import { privateApis as blockEditorPrivateApis } from '@wordpress/block-editor';
import { recordTracksEvent } from '@utils/tracks';

/**
 * Internal dependencies
 */
import StylesPreview from '@components/styles-preview';
import { unlock } from '@utils/lock-unlock';
import useSiteMetadata from '@hooks/use-site-metadata';
import useStyles from '@hooks/use-styles';

const { GlobalStylesContext } = unlock( blockEditorPrivateApis );

function mergeBaseAndUserConfigs( base = {}, user = {} ) {
	return deepmerge( base, user, {
		// We only pass as arrays the presets,
		// in which case we want the new array of values
		// to override the old array (no merging).
		isMergeableObject: isPlainObject,
	} );
}

export function Variation( { variation, type } ) {
	const { setStyles } = useStyles();
	const [ isFocused, setIsFocused ] = useState( false );
	const { base } = useContext( GlobalStylesContext );
	const context = useMemo( () => {
		const user = {
			settings: {
				...variation?.settings,
				isPreview: true,
			},
			styles: variation?.styles ?? {},
		};

		return {
			user,
			base,
			merged: mergeBaseAndUserConfigs( base, user ),
			setUserConfig: () => {},
		};
	}, [ variation, base ] );
	const { currentColor, currentFont } = useSiteMetadata().getSiteMetadata();

	const handleSelectVariation = () => {
		if ( type === 'color' ) {
			recordTracksEvent( 'color_variation_click', {
				color: variation.title,
			} );
			setStyles( { color: variation.title } );
		} else {
			recordTracksEvent( 'font_variation_click', {
				font: variation.title,
			} );
			setStyles( { font: variation.title } );
		}
	};

	const selectOnEnter = ( event ) => {
		if ( event.keyCode === ENTER ) {
			event.preventDefault();
			handleSelectVariation();
		}
	};

	const isActive = () => {
		if ( type === 'color' ) {
			return variation?.title === currentColor;
		}

		return variation?.title === currentFont;
	};

	let label = variation?.title;
	if ( variation?.description ) {
		label = sprintf(
			/* translators: %1$s: variation title. %2$s variation description. */
			__( '%1$s (%2$s)', 'big-sky' ),
			variation?.title,
			variation?.description
		);
	}

	return (
		<GlobalStylesContext.Provider value={ context }>
			<div
				className={ classnames(
					'edit-site-global-styles-variations_item',
					{
						'is-active': isActive(),
					}
				) }
				role="button"
				onClick={ handleSelectVariation }
				onKeyDown={ selectOnEnter }
				tabIndex="0"
				aria-label={ label }
				aria-current={ isActive() }
				onFocus={ () => setIsFocused( true ) }
				onBlur={ () => setIsFocused( false ) }
			>
				<div className="edit-site-global-styles-variations_item-preview">
					<StylesPreview
						label={ variation?.title }
						isFocused={ isFocused }
						type={ type }
						className="big-sky__styles-preview"
					/>
				</div>
			</div>
		</GlobalStylesContext.Provider>
	);
}

export function StyleVariationsContainer() {
	const variations = useSelect( ( select ) => {
		return select(
			coreStore
		).__experimentalGetCurrentThemeGlobalStylesVariations();
	}, [] );

	const withEmptyVariation = useMemo( () => {
		return [
			{
				title: __( 'Default', 'big-sky' ),
				settings: {},
				styles: {},
			},
			...( variations ?? [] ).map( ( variation ) => ( {
				...variation,
				settings: variation.settings ?? {},
				styles: variation.styles ?? {},
			} ) ),
		];
	}, [ variations ] );

	return (
		<Grid
			columns={ 2 }
			className="edit-site-global-styles-style-variations-container"
		>
			{ withEmptyVariation.map( ( variation, index ) => (
				<Variation key={ index } variation={ variation } />
			) ) }
		</Grid>
	);
}
