/**
 * External dependencies
 */
import { useCallback, useEffect, useState } from '@wordpress/element';
import { parse } from '@wordpress/blocks';
import { useDispatch, useSelect } from '@wordpress/data';
import { store as blockEditorStore } from '@wordpress/block-editor';

/**
 * Internal dependencies
 */
import useBlocks from '@hooks/use-blocks';
import { store as aiStore } from '@store/index';

/**
 * Custom hook for rendering and managing pattern generation in the editor.
 *
 * Handles the lifecycle of pattern generation including processing phases,
 * converting between string and block representations, and rendering in the editor.
 */
export default function usePatternRenderer() {
	const [ isProcessing, setIsProcessing ] = useState( false );
	const { replaceBlock, insertBlock, clearSelectedBlock } =
		useDispatch( blockEditorStore );
	const { getBlock } = useSelect( blockEditorStore );
	const { fixInvalidBlock, getContentBlockClientID } = useBlocks();

	const {
		setGeneratedPatternAsString,
		setGeneratedPatternAsBlocks,
		setGeneratedPatternChunksAsJSON,
		setGeneratedRootPatternClientId,
	} = useDispatch( aiStore );

	const {
		generatedPatternAsString,
		generatedPatternChunksAsJSON,
		generatedPatternDummySectionClientId,
		generatedRootPatternClientId,
	} = useSelect( ( select ) => ( {
		generatedPatternAsString:
			select( aiStore ).getGeneratedPatternAsString(),
		generatedPatternChunksAsJSON:
			select( aiStore ).getGeneratedPatternChunksAsJSON(),
		generatedPatternDummySectionClientId:
			select( aiStore ).getGeneratedPatternDummySectionClientId(),
		generatedRootPatternClientId:
			select( aiStore ).getGeneratedRootPatternClientId(),
	} ) );

	/**
	 * Processes a phase of pattern generation.
	 *
	 * Cleans and validates the content, extracts container references,
	 * and updates the pattern chunks in the store.
	 *
	 * @param {Object} options             Options for processing the phase.
	 * @param {string} options.content     The content to process.
	 * @param {number} options.phaseNumber The phase number being processed.
	 */
	const processPhase = ( { content, phaseNumber } ) => {
		// Clean up any invalid content
		const lines = content.split( '\n' );
		const cleanLines = lines.filter( ( line ) => {
			const trimmed = line.trim();
			return (
				trimmed.startsWith( '<!-- wp:' ) ||
				trimmed.startsWith( '<!-- CONTAINER' ) ||
				trimmed.startsWith( '<!-- /wp:' ) ||
				trimmed.startsWith( '<!-- /CONTAINER' ) ||
				( trimmed.length > 0 &&
					! trimmed.startsWith( '<!--' ) &&
					! trimmed.endsWith( '-->' ) )
			);
		} );

		const cleanedCode = cleanLines.join( '\n' ).trim();

		const containerRefPattern =
			/<!--\s*CONTAINER_REF:([^\s]+)\s*-->((?:(?!<!--\s*(?:\/)?CONTAINER_REF)[\s\S])*?)<!--\s*\/CONTAINER_REF:[^\s]+\s*-->/g;
		const containerRefs = [];
		let match;

		while ( ( match = containerRefPattern.exec( cleanedCode ) ) !== null ) {
			containerRefs.push( {
				refId: match[ 1 ],
				content: match[ 2 ].trim(),
			} );
		}

		if ( containerRefs.length === 0 ) {
			containerRefs.push( {
				refId: 'root',
				content: cleanedCode,
			} );
		}

		const phaseJSON = {
			phase: phaseNumber,
			containers: containerRefs,
		};

		setGeneratedPatternChunksAsJSON( phaseJSON );
	};

	/**
	 * Renders pattern blocks in the editor.
	 *
	 * Either replaces an existing block or inserts a new block in the post content,
	 * depending on whether we have a root pattern ID.
	 *
	 * @param {Array} patternAsBlocks Array of blocks to render.
	 */
	const renderInEditor = useCallback(
		( patternAsBlocks ) => {
			if ( ! patternAsBlocks?.length ) return;

			// Get the post content block as the target location
			const contentBlockClientId = getContentBlockClientID();
			if ( ! contentBlockClientId ) return;

			// If we have a rootPatternId, we're updating an existing block
			if (
				generatedRootPatternClientId &&
				getBlock( generatedRootPatternClientId )
			) {
				replaceBlock(
					generatedRootPatternClientId,
					patternAsBlocks[ 0 ]
				);
			} else {
				// Otherwise insert as a new block in the post content
				insertBlock(
					patternAsBlocks[ 0 ],
					undefined,
					generatedPatternDummySectionClientId
				);
			}

			// Deselect any selected block after rendering
			clearSelectedBlock();
		},
		[
			getContentBlockClientID,
			generatedRootPatternClientId,
			getBlock,
			replaceBlock,
			insertBlock,
			clearSelectedBlock,
			generatedPatternDummySectionClientId,
		]
	);

	/**
	 * Converts a pattern string to blocks.
	 *
	 * Removes container IDs, parses the pattern into blocks,
	 * fixes any invalid blocks, and manages the root pattern ID.
	 *
	 * @param {string} patternAsString Pattern HTML string to convert.
	 * @return {Array|undefined}      Array of blocks if successful, undefined otherwise.
	 */
	const convertToBlocks = useCallback(
		( patternAsString ) => {
			if ( ! patternAsString ) return;

			// Remove <!-- CONTAINER_ID:[ID] --> from the raw patternAsString
			// otherwise it will cause validation errors
			const containerIdPattern =
				/<!--\s*CONTAINER_ID:([^\s]+)(?:\s+type="[^"]*")?\s*-->/g;
			const patternWithoutContainerIds = patternAsString.replace(
				containerIdPattern,
				''
			);

			const blocks = parse( patternWithoutContainerIds );
			const fixedBlocks = blocks.map( ( block ) =>
				fixInvalidBlock( block )
			);

			// If rootPatternId is not null, set the root pattern id to the clientId of the first block
			if ( ! generatedRootPatternClientId ) {
				setGeneratedRootPatternClientId( fixedBlocks[ 0 ].clientId );
			} else {
				// Otherwise make sure the root pattern client ID remains the same
				fixedBlocks[ 0 ].clientId = generatedRootPatternClientId;
			}

			setGeneratedPatternAsBlocks( fixedBlocks );
			return fixedBlocks;
		},
		[
			fixInvalidBlock,
			generatedRootPatternClientId,
			setGeneratedRootPatternClientId,
			setGeneratedPatternAsBlocks,
		]
	);

	/**
	 * Converts pattern JSON to a string representation.
	 *
	 * Processes each phase and container, replacing container references
	 * with their content in the pattern string.
	 *
	 * @param {Array} patternJSON Array of pattern phases to convert.
	 * @return {string|undefined} Updated pattern string if changed, undefined otherwise.
	 */
	const convertToString = useCallback(
		( patternJSON ) => {
			if ( ! patternJSON?.length ) return;

			let updatedPatternAsString = '';
			patternJSON.forEach( ( json ) => {
				const { phase, containers } = json;

				containers.forEach( ( container ) => {
					if ( phase === 1 ) {
						updatedPatternAsString = container.content;
						return;
					}

					const containerRefPattern = new RegExp(
						`<!-- CONTAINER_ID:${ container.refId }(?:\\s+type="[^"]*")?\\s*-->`,
						'g'
					);

					const existingContainer =
						updatedPatternAsString.match( containerRefPattern );

					if ( existingContainer ) {
						updatedPatternAsString = updatedPatternAsString.replace(
							containerRefPattern,
							container.content
						);
					}
				} );
			} );

			if ( updatedPatternAsString !== generatedPatternAsString ) {
				setGeneratedPatternAsString( updatedPatternAsString );
				return updatedPatternAsString;
			}
		},
		[ setGeneratedPatternAsString, generatedPatternAsString ]
	);

	// Process pattern changes when chunks are updated
	useEffect( () => {
		if ( isProcessing || ! generatedPatternChunksAsJSON?.length ) return;

		const processPattern = async () => {
			setIsProcessing( true );
			try {
				const patternString = convertToString(
					generatedPatternChunksAsJSON
				);
				if ( patternString ) {
					const blocks = convertToBlocks( patternString );
					if ( blocks?.length ) {
						renderInEditor( blocks );
					}
				}
			} finally {
				setIsProcessing( false );
			}
		};

		processPattern();
	}, [
		generatedPatternChunksAsJSON,
		convertToString,
		convertToBlocks,
		renderInEditor,
		isProcessing,
	] );

	return {
		processPhase,
	};
}
