export default function generatePatternTool( siteLocationName = null ) {
	return {
		name: 'generate_pattern',
		description: `When a user asks to add a section, pattern, or something new that isn't a full new page,
		use this tool to generate a pattern and add it to the current page.
		When the user provides a list of sections (bulleted, numbered, or otherwise), treat it as a single pattern request and include all sections in one call.
		If the site location is not specified in the user request, default to ${
			siteLocationName ? `"${ siteLocationName }"` : '"null"'
		}`,
		parameters: {
			type: 'object',
			properties: {
				clientId: {
					type: [ 'string', 'null' ],
					description:
						'The clientId of the selected block that the pattern will be inserted before or after. Default to null if not supplied in the context.',
				},
				direction: {
					type: 'string',
					enum: [ 'before', 'after' ],
					description:
						'Where to insert patterns relative to clientId. If user does not indicate "before" or "after", default to "after".',
				},
				siteLocation: {
					type: [ 'string', 'null' ],
					description: 'The site location.',
				},
				userPrompt: {
					type: 'string',
					description:
						'The original prompt from the user. Pass this through, do not edit it. If the prompt contains multiple sections, include them all in this single prompt.',
				},
			},
			required: [ 'clientId', 'direction', 'siteLocation', 'userPrompt' ],
		},
		strict: true,
	};
}
