/**
 * WordPress dependencies
 */
import { useDispatch } from '@wordpress/data';
import { __ } from '@wordpress/i18n';
import { useCallback } from '@wordpress/element';

/**
 * Internal dependencies
 */
import LayoutPicker from '@components/dock/menus/layout-picker';
import useCanvas from '@hooks/use-canvas';
import usePatterns from '@hooks/use-patterns';
import useSiteMetadata from '@hooks/use-site-metadata';
import { store as aiStore } from '@store';
import { dockViews } from '@utils/constants';

export default function useShowLayoutPickerToolFunctions() {
	const { getSiteMetadata, getPagePatterns } = useSiteMetadata();

	const { zoomIn } = useCanvas();
	const { composePatterns, createConceptFromCurrentCanvas, insertPatterns } =
		usePatterns();

	const { setDockView, setLastCancelableAction, setIsToolContainerVisible } =
		useDispatch( aiStore );

	const composeNewConcept = useCallback(
		( concepts ) => {
			const { topic, siteDescription, siteLocation } = getSiteMetadata();
			const categoriesFromPageMeta = getPagePatterns();
			const selectedConcept = createConceptFromCurrentCanvas();
			const newConcepts = composePatterns( {
				categories: categoriesFromPageMeta.filter(
					( category ) =>
						category.slug !== 'header' && category.slug !== 'footer'
				),
				topic,
				siteDescription,
				siteLocation,
				numberOfConcepts: 1,
				selectedConcept,
			} );

			const updatedConcepts = [ ...concepts, ...newConcepts ];
			return updatedConcepts;
		},
		[
			getPagePatterns,
			createConceptFromCurrentCanvas,
			composePatterns,
			getSiteMetadata,
		]
	);

	const showSiteLayouts = useCallback(
		( {
			categories = null,
			numberOfConcepts = 3,
			callback = () => {},
		} ) => {
			const { topic, siteDescription, siteLocation } = getSiteMetadata();
			const categoriesFromPageMeta = getPagePatterns();
			const selectedConcept = createConceptFromCurrentCanvas();
			const categoriesFromPageMetaFiltered =
				categoriesFromPageMeta.filter(
					( category ) =>
						category.slug !== 'header' && category.slug !== 'footer'
				);

			const newConcepts = composePatterns( {
				categories: categories || categoriesFromPageMetaFiltered,
				topic,
				siteDescription,
				siteLocation,
				numberOfConcepts,
				selectedConcept,
			} );

			const updatedConcepts = [ selectedConcept, ...newConcepts ].filter(
				( concept, index, self ) =>
					index === self.findIndex( ( t ) => t.id === concept.id )
			);

			setLastCancelableAction( {
				action: 'show_layout_picker',
				originalLayout: selectedConcept,
			} );

			callback(
				<LayoutPicker
					concepts={ updatedConcepts }
					selectedConcept={ selectedConcept }
					key="site-layout-picker"
					onGenerateNewConcept={ ( concepts ) =>
						composeNewConcept( concepts )
					}
				/>
			);

			return __( 'Which of these layouts would you like?', 'big-sky' );
		},
		[
			getSiteMetadata,
			getPagePatterns,
			createConceptFromCurrentCanvas,
			composePatterns,
			setLastCancelableAction,
			composeNewConcept,
		]
	);

	const cancelShowLayoutPicker = ( layout ) => {
		insertPatterns( layout.patterns, null, 'replace', false );
		setDockView( dockViews.MENUS );
		setIsToolContainerVisible( false );
		zoomIn();
	};

	const doneShowLayoutPicker = () => {
		setDockView( dockViews.MENUS );
		setIsToolContainerVisible( false );
		zoomIn();
	};

	return {
		cancelShowLayoutPicker,
		doneShowLayoutPicker,
		showSiteLayouts,
		composeNewConcept,
	};
}
