import events from './events.js';
import footer from './footer.js';
import menu from './menus.js';
import pricing from './pricing.js';
import services from './services.js';
import testimonials from './testimonials.js';
import contact from './contact.js';
import team from './team.js';

// the name here matches the category name in dotcom patterns.
const preprocessSectionTypes = {
	pricing,
	footer,
	contact,
};

const customSectionTypes = {
	events,
	menu,
	pricing,
	services,
	testimonials,
	contact,
	team,
	footer,
};
const customSectionNames = Object.keys( customSectionTypes );
const preprocessSectionNames = Object.keys( preprocessSectionTypes );

function sectionCachedContentIsValid( sectionCachedContent ) {
	return sectionCachedContent?.length > 0;
}

export {
	customSectionNames,
	customSectionTypes,
	preprocessSectionNames,
	preprocessSectionTypes,
	sectionCachedContentIsValid,
};
