import addPatternTool from './tools/add-pattern/add-pattern-tool.js';
import generatePatternTool from './tools/generate-pattern/generate-pattern-tool.js';
import removeBlockTool from './tools/remove-block/remove-block-tool.js';
import replacePatternTool from './tools/replace-pattern/replace-pattern-tool.js';
import changeAssistantTool from '@ai/assistants/shared/change-assistant/change-assistant-tool';
import patternCategories from '../../../data/pattern-categories.js';
import { ASSISTANTS } from '@ai/assistants/constants.js';
import { isForwardedByRouter } from '@ai/assistants/shared/prompts.js';

const availableCategories = patternCategories.map(
	( category ) => category.slug
);

const patternToolkit = {
	name: 'pattern',
	tools: ( context ) => {
		const tools = [
			typeof window !== 'undefined' &&
			window.localStorage.getItem( 'isGeneratingPatterns' ) === 'true'
				? generatePatternTool( context?.site?.location?.name )
				: addPatternTool(
						availableCategories,
						context?.site?.location?.name
				  ),
			removeBlockTool(),
			replacePatternTool(
				availableCategories,
				context?.selectedBlock?.parentPatternCategory,
				context?.site?.location?.name
			),
			...( ! isForwardedByRouter( context )
				? [ changeAssistantTool( ASSISTANTS.PATTERN ) ]
				: [] ),
		];

		return tools;
	},
};

export default patternToolkit;
