import { useRive, useStateMachineInput } from '@rive-app/react-canvas';
import { useCallback, useEffect } from '@wordpress/element';

export default function useBigSkyIcon( color = 'blue' ) {
	const stateMachineName = 'State Machine A';

	const iconURL =
		'https://s0.wp.com/wp-content/plugins/big-sky-plugin/assets/big-sky-icon';
	const blueIcon = `${ iconURL }/blue.riv`;
	const whiteIcon = `${ iconURL }/white.riv`;

	const { rive, RiveComponent } = useRive( {
		src: color === 'blue' ? blueIcon : whiteIcon,
		stateMachines: stateMachineName,
		autoplay: false,
	} );

	const inactiveInput = useStateMachineInput(
		rive,
		stateMachineName,
		'inactive',
		false
	);
	const thinkingInput = useStateMachineInput(
		rive,
		stateMachineName,
		'thinking',
		false
	);
	const typingInput = useStateMachineInput(
		rive,
		stateMachineName,
		'typing',
		false
	);

	const startStateMachine = useCallback( () => {
		if ( rive ) {
			rive.play( stateMachineName );
		}
	}, [ rive, stateMachineName ] );

	const pauseStateMachine = useCallback( () => {
		if ( rive ) {
			rive.pause( stateMachineName );
		}
	}, [ rive, stateMachineName ] );

	useEffect( () => {
		return () => {
			if ( rive ) {
				rive.cleanup();
			}
		};
	}, [ rive ] );

	useEffect( () => {
		startStateMachine();
	}, [ startStateMachine ] );

	return {
		rive,
		RiveComponent,
		inactiveInput,
		typingInput,
		thinkingInput,
		startStateMachine,
		pauseStateMachine,
	};
}
