/**
 * Internal dependencies
 */
import routerAssistant from './router/index.js';
import specAssistant from './spec/index.js';
import contentAssistant from './content/index.js';
import colorAssistant from './color/index.js';
import patternAssistant from './pattern/index.js';
import fontAssistant from './font/index.js';
import layoutAssistant from './layout/index.js';
import imageAssistant from './image/index.js';
import pageAssistant from './page/index.js';
import helpAssistant from './help/index.js';
import {
	getAssistantById,
	getAssistants,
	registerAssistant,
} from './assistant-registry.js';

// Register all assistants
[
	routerAssistant,
	specAssistant,
	contentAssistant,
	colorAssistant,
	patternAssistant,
	fontAssistant,
	layoutAssistant,
	imageAssistant,
	pageAssistant,
	helpAssistant,
].forEach( registerAssistant );

// Export the assistants array and lookup functions
export const assistants = getAssistants();
export const assistantsById = ( id ) => getAssistantById( id );

// Named exports for specific assistants
export {
	routerAssistant,
	colorAssistant,
	patternAssistant,
	fontAssistant,
	layoutAssistant,
	imageAssistant,
	pageAssistant,
	helpAssistant,
};
