/**
 * WordPress dependencies
 */
import { useDispatch, useSelect } from '@wordpress/data';
import { store as blockEditorStore } from '@wordpress/block-editor';

/**
 * Internal dependencies
 */
import useImages from '@hooks/use-images.js';
import useSiteMetadata from '@hooks/use-site-metadata';
import { store as aiStore } from '@store/index.js';
import useBlocks from '@hooks/use-blocks.js';

export default function useGenerateImageTool() {
	const { generateImageWithAI } = useImages();
	const { setSiteMetadata } = useSiteMetadata();
	const { getBlock } = useSelect( blockEditorStore );
	const { setBlockBeforeChanges } = useDispatch( aiStore );
	const { setBlockIsProcessing } = useBlocks();

	const generateImage = async ( parameters ) => {
		const { clientId, description, style } = parameters;

		const imageBlock = getBlock( clientId );
		setBlockBeforeChanges( imageBlock );
		setSiteMetadata( {
			defaultImageStyle: style,
		} );

		setBlockIsProcessing( imageBlock, true );

		const generatedImage = await generateImageWithAI( {
			description,
			style,
			block: imageBlock,
		} );

		setBlockIsProcessing( imageBlock, false );

		return generatedImage;
	};

	return { generateImage };
}
