/**
 * WordPress dependencies
 */
import {
	Button,
	DropZone,
	__experimentalItem as Item,
	__experimentalItemGroup as ItemGroup,
} from '@wordpress/components';
import { useCallback, useEffect, useRef } from '@wordpress/element';
import { useDispatch, useSelect } from '@wordpress/data';
import { close } from '@wordpress/icons';

import {
	ChatModelService,
	ChatModelType,
	useAgentExecutor,
	useAgents,
	useChat,
	useChatSettings,
	useToolkits,
} from '@automattic/big-sky-agents';

/**
 * Internal dependencies
 */
import './style.scss';
import ChatInput from '@components/chat/input';
import ChatActions from '@components/chat/actions';
import ChatNavigation from '@components/chat/navigation';
import ChatThread from '@components/chat/thread';
import useIsThinking from '@hooks/use-is-thinking';
import useJetpackToken from '@hooks/use-jetpack-token';
import { store as aiStore } from '@store';
import ChatSuggestions from '@ai/assistants/shared/make-suggestions';
import ChatComponents from '@components/chat/components';
import BigSkyIcon from '@components/big-sky-icon';

export default function AgentChat( {
	showNavigation = true,
	navigationTitle,
	onClickBackButton,
	useBlockContext,
	defaultPrompt,
	isOnboarded,
	agentQuestion,
	fileUploadEnabled = true,
	onlyShowLastRequest = false,
} ) {
	const {
		apiKey,
		inputtedFiles,
		isHandoff,
		showThread,
		currentUserMessage,
		sessionId,
		assistant,
	} = useSelect( ( select ) => ( {
		apiKey: select( aiStore ).getApiKey(),
		inputtedFiles: select( aiStore ).getInputtedFiles(),
		isBuildingSite: select( aiStore ).getIsBuildingSite(),
		isHandoff: select( aiStore ).getIsHandoff(),
		showThread: select( aiStore ).getShowThread(),
		sessionId: select( aiStore ).getSessionId(),
		currentUserMessage: select( aiStore ).currentUserMessage(),
		showEditorWelcome: select( aiStore ).getShowEditorWelcome(),
		assistant: select( aiStore ).getAssistant(),
	} ) );
	const { setCurrentUserMessage, setInputtedFiles, setShowThread } =
		useDispatch( aiStore );

	const { withJetpackTokenRequest } = useJetpackToken();

	const isLocalGraph = window.bigSkyInitialState?.isLocalGraph;
	useChatSettings( {
		apiKey,
		feature: 'big-sky',
		service: isLocalGraph
			? ChatModelService.LOCAL_GRAPH
			: ChatModelService.WPCOM_JETPACK_AI, // switch to LOCAL_GRAPH for end-to-end local development
		model: ChatModelType.GPT_4O,
		stream: false,
		sessionId,
	} );

	const inputRef = useRef();
	const { activeAgent, setActiveAgent } = useAgents();
	const { messages, clearMessages, userSay } = useChat();
	const { invoke } = useToolkits();
	const { isThinking } = useIsThinking();
	useAgentExecutor();

	useEffect( () => {
		if ( assistant ) {
			setActiveAgent( assistant );
		}
	}, [ assistant, setActiveAgent ] );

	useEffect( () => {
		if ( activeAgent?.onStart ) {
			activeAgent.onStart( invoke, clearMessages, agentQuestion );
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [ activeAgent, agentQuestion, clearMessages ] );

	const focusInput = () => {
		inputRef.current?.focus();
	};

	const addFiles = useCallback(
		async ( newFiles ) => {
			const fileNamesAndUrls = await Promise.all(
				newFiles.map( async ( file ) => ( {
					file,
					name: file.name,
					url: URL.createObjectURL( file ),
				} ) )
			);

			setInputtedFiles( [ ...inputtedFiles, ...fileNamesAndUrls ] );
		},
		[ inputtedFiles, setInputtedFiles ]
	);

	const removeFile = useCallback(
		( index ) => {
			setInputtedFiles( inputtedFiles.filter( ( _, i ) => i !== index ) );
		},
		[ inputtedFiles, setInputtedFiles ]
	);

	const FilePreview = ( { name, url, onRemove } ) => {
		return (
			<>
				{ url && (
					<img
						width="50"
						height="50"
						style={ { verticalAlign: 'middle' } }
						alt={ name }
						src={ url }
					/>
				) }
				{ name }{ ' ' }
				<Button size="small" icon={ close } onClick={ onRemove } />
			</>
		);
	};

	const inputAnimationProps = {
		initial: {
			opacity: 0,
			filter: 'blur(5px)',
			scale: 0.9,
		},
		animate: {
			opacity: 1,
			filter: 'blur(0px)',
			scale: 1,
			transition: {
				duration: 0.3,
				delay: 0.1,
				ease: [ 0.42, 0.24, 0.02, 1 ],
			},
		},
		exit: {
			filter: 'blur(5px)',
			opacity: 0,
			scale: 0.9,
			transition: {
				duration: 0.2,
				ease: [ 0.42, 0.24, 0.02, 1 ],
			},
		},
	};

	return (
		<>
			{ fileUploadEnabled && <DropZone onFilesDrop={ addFiles } /> }
			<div className="big-sky__dock__chat-view">
				<div className="big-sky__dock__chat-content">
					{ showNavigation && (
						<ChatNavigation
							title={ navigationTitle }
							onClickBackButton={ () => onClickBackButton() }
						/>
					) }

					{ ! messages.length && ! isHandoff && (
						<>
							<ChatNavigation
								title={ ' ' }
								onClickBackButton={ () => onClickBackButton() }
							/>
							<BigSkyIcon width={ 64 } height={ 64 } />
						</>
					) }

					{ ( showThread || ! isOnboarded ) && (
						<ChatThread
							onlyShowLastRequest={ onlyShowLastRequest }
						/>
					) }
					{ ! showThread && isOnboarded && ! currentUserMessage && (
						<ChatActions
							useBlockContext={ useBlockContext }
							focusInput={ focusInput }
							onActionClick={ async ( message ) => {
								setShowThread( true );
								await withJetpackTokenRequest( () =>
									userSay( message )
								);
								setCurrentUserMessage( '' );
							} }
						/>
					) }
				</div>
				<ChatComponents />
				<ChatSuggestions />
				<div className="big-sky__dock__chat-input">
					<ChatInput
						prompt={ defaultPrompt }
						ref={ inputRef }
						animationProps={ inputAnimationProps }
					/>
				</div>
				{ fileUploadEnabled &&
					! isThinking &&
					!! inputtedFiles?.length && (
						<ItemGroup isBordered isSeparated>
							{ inputtedFiles.map( ( { name, url }, index ) => (
								<Item key={ index }>
									<FilePreview
										name={ name }
										url={ url }
										onRemove={ () => removeFile( index ) }
									/>
								</Item>
							) ) }
						</ItemGroup>
					) }
			</div>
		</>
	);
}
