/**
 * Internal dependencies
 */
import { assistants } from '@ai/assistants';

// Component that safely calls a single hook
const HookRunner = ( { hook } ) => {
	hook();
	return null;
};

export default function ToolkitsProvider() {
	// Render a HookRunner for each assistant hook so we can load these
	// without having to name them while still complying with the rules of hooks.

	return (
		<>
			{ assistants
				.filter( ( assistant ) => assistant.toolkitHook )
				.map( ( assistant, index ) => (
					<HookRunner key={ index } hook={ assistant.toolkitHook } />
				) ) }
		</>
	);
}
