import classnames from 'classnames';

/**
 * WordPress dependencies
 */
import { Button, ColorPicker, Dropdown, Icon } from '@wordpress/components';
import { useCallback, useEffect, useRef, useState } from '@wordpress/element';
import { useDispatch, useSelect } from '@wordpress/data';
import { store as blockEditorStore } from '@wordpress/block-editor';

/**
 * Internal dependencies
 */
import './style.scss';
import { chevronDown } from '@wordpress/icons';
import useStyles from '@hooks/use-styles';
import useBlocks from '@hooks/use-blocks';
import useVariations from '@hooks/use-variations';
import useSiteMetadata from '@hooks/use-site-metadata';
import { recordTracksEvent } from '@utils/tracks';
import { sectionStyleRegex } from '@utils/constants';

export default function ColorPickerMenu() {
	const { currentColor } = useSiteMetadata().getSiteMetadata();
	const { getBlockAttributes, selectedBlock } = useSelect( ( select ) => {
		const { getBlockAttributes: _getBlockAttributes, getSelectedBlock } =
			select( blockEditorStore );
		return {
			getBlockAttributes: _getBlockAttributes,
			selectedBlock: getSelectedBlock(),
		};
	} );
	const { updateBlockAttributes } = useDispatch( blockEditorStore );

	const { getParentRootPatternBlock } = useBlocks();
	const { getThemeColors } = useStyles();
	const { getSectionVariationTextColor } = useVariations();

	const themeColors = getThemeColors();
	const [ selectedColor, setSelectedColor ] = useState( () => {
		return (
			selectedBlock.attributes?.textColor ||
			selectedBlock.attributes?.style?.color?.text ||
			null
		);
	} );
	const [ customColor, setCustomColor ] = useState( null );

	const getDefaultTextColor = useCallback( () => {
		const blankStyle =
			'#fff linear-gradient(-45deg,#0000 48%,#ddd 0,#ddd 52%,#0000 0)';

		if ( ! selectedBlock ) {
			return blankStyle;
		}

		const parentRootPatternBlock = getParentRootPatternBlock(
			selectedBlock.clientId
		);

		if (
			! parentRootPatternBlock ||
			! parentRootPatternBlock.attributes?.className
		) {
			return blankStyle;
		}

		// Find the css Classname in parentRootPatternBlock.attributes.className and that starts with is-style-X and return it
		const classNames = [
			...parentRootPatternBlock.attributes.className?.matchAll(
				sectionStyleRegex
			),
		];

		if ( ! classNames.length ) {
			return blankStyle;
		}

		const variationName = classNames[ 0 ][ 1 ];

		if ( ! variationName ) {
			return blankStyle;
		}

		const { hex: defaultTextColor } = getSectionVariationTextColor(
			variationName,
			currentColor
		);

		return defaultTextColor || blankStyle;
	}, [
		selectedBlock,
		getParentRootPatternBlock,
		getSectionVariationTextColor,
		currentColor,
	] );

	const getSelectedBlockTextColor = useCallback( () => {
		// find the theme color corresponding to the slug
		const themeColor = themeColors.find( ( { slug } ) => {
			return slug === selectedBlock.attributes?.textColor;
		} );
		return (
			themeColor?.color ||
			selectedBlock.attributes?.style?.color?.text ||
			getDefaultTextColor()
		);
	}, [ getDefaultTextColor, selectedBlock.attributes, themeColors ] );

	useEffect( () => {
		setSelectedColor( null );

		const selectedBlockTextColor = getSelectedBlockTextColor();

		if ( selectedBlockTextColor ) {
			setSelectedColor( selectedBlockTextColor );
		}

		// Check if the selected color is in the theme colors list.
		// If not then set it as a custom color.
		if (
			themeColors.find( ( { color } ) => {
				return color === selectedBlock.attributes?.textColor;
			} ) === undefined
		) {
			doSetCustomColor( selectedBlock.attributes?.style?.color?.text );
		}
	}, [ getSelectedBlockTextColor, selectedBlock, themeColors ] );

	const colorWheelButton = useRef();

	const doSetSelectedColor = async ( { color, slug } ) => {
		setSelectedColor( color );
		const attributes = getBlockAttributes( selectedBlock.clientId );
		const updatedAttributes = {
			...attributes,
			textColor: slug,
		};
		if ( ! slug ) {
			updatedAttributes.textColor = null;
			updatedAttributes.style = {
				...attributes.style,
				color: {
					...attributes.style.color,
					text: color,
				},
			};
		}
		await updateBlockAttributes(
			selectedBlock.clientId,
			updatedAttributes
		);
	};

	const doSetCustomColor = ( newColor ) => {
		if ( colorWheelButton.current ) {
			colorWheelButton.current.style.background = newColor;
		}
		setCustomColor( newColor );
	};

	const renderColorPalette = () => {
		return themeColors.map( ( { color, slug } ) => {
			return (
				<Button
					key={ color }
					style={ { background: color } }
					onClick={ () => {
						recordTracksEvent( 'dock_menu_item_click', {
							selected_block: selectedBlock?.name || null,
							menu_slug: 'color-picker-choice',
							color_name: color,
						} );
						doSetSelectedColor( { color, slug } );
					} }
					className={ `big-sky__assistant-ai-actions__color-picker__button${
						selectedColor === color ? ' is-selected' : ''
					}` }
				/>
			);
		} );
	};

	const renderColorWheel = () => {
		return (
			<Dropdown
				className="big-sky__assistant-ai-actions__color-picker__wheel"
				contentClassName="big-sky__assistant-ai-actions__popover-content big-sky__assistant-ai-actions__popover-colorpicker"
				popoverProps={ { placement: 'top-center', offset: 16 } }
				renderToggle={ ( { isOpen, onToggle } ) => (
					<Button
						className={ classnames(
							'big-sky__assistant-ai-actions__color-picker__button',
							'big-sky__assistant-ai-actions__color-picker__wheel-button',
							{
								'is-selected': selectedColor === customColor,
								'has-custom-color': customColor,
							}
						) }
						style={ { background: customColor } }
						ref={ colorWheelButton }
						onClick={ () => {
							recordTracksEvent( 'dock_menu_item_click', {
								selected_block: selectedBlock?.name || null,
								menu_slug: 'color-picker-choice',
								color_name: 'color-wheel',
							} );
							if ( customColor ) {
								doSetCustomColor( customColor );
								doSetSelectedColor( {
									color: customColor,
									slug: null,
								} );
							}
							onToggle();
						} }
						aria-expanded={ isOpen }
					>
						{ customColor && (
							<span className="big-sky__assistant-ai-actions__color-picker__custom-indicator"></span>
						) }
					</Button>
				) }
				renderContent={ () => {
					return (
						<ColorPicker
							onChange={ ( color ) => {
								doSetCustomColor( color );
								doSetSelectedColor( {
									color,
									slug: null,
								} );
							} }
							color={ customColor }
						/>
					);
				} }
			/>
		);
	};

	const menuBackgroundColor =
		selectedColor || customColor || getDefaultTextColor();

	return (
		<Dropdown
			contentClassName="big-sky__assistant-ai-actions__popover-content"
			popoverProps={ { placement: 'top-center', offset: 14 } }
			renderToggle={ ( { isOpen, onToggle } ) => (
				<div
					onClick={ () => {
						recordTracksEvent( 'dock_menu_item_click', {
							selected_block: selectedBlock?.name || null,
							menu_slug: 'color-picker-toggle',
						} );
						onToggle();
					} }
					role="button"
					tabIndex={ 0 }
					onKeyDown={ ( e ) => {
						if ( e.key === 'Enter' || e.key === ' ' ) {
							onToggle();
						}
					} }
				>
					<Button
						className="has-chevron has-color-picker"
						aria-expanded={ isOpen }
					>
						<span
							style={ { background: menuBackgroundColor } }
							className="big-sky__assistant-ai-actions__color-picker__button is-small"
						></span>
						<Icon icon={ chevronDown } />
					</Button>
				</div>
			) }
			renderContent={ () => {
				return (
					<div className="big-sky__assistant-ai-actions__color-picker">
						{ renderColorPalette() }
						{ renderColorWheel() }
					</div>
				);
			} }
		/>
	);
}
