/**
 * WordPress dependencies
 */
import { useDispatch } from '@wordpress/data';
import { useCallback } from '@wordpress/element';

/**
 * Internal dependencies
 */

import useHandleReset from '@hooks/use-handle-reset.js';
import { store as aiStore } from '@store';
import setDelay from '@utils/set-delay';
import log from '@utils/log-debug';

const useCreateToolCallback = () => {
	const handleReset = useHandleReset();

	const {
		decrementPendingToolCallbacks,
		incrementPendingToolCallbacks,
		setCurrentToolCall,
	} = useDispatch( aiStore );

	const withPendingToolCallback = useCallback(
		( callback ) => async ( args ) => {
			incrementPendingToolCallbacks();
			// Because replaceContentAgent is sync a delay is needed here for the pending increment to be registered by the dock component.
			await setDelay( 200 );
			await callback( args );
			decrementPendingToolCallbacks();
		},
		[ decrementPendingToolCallbacks, incrementPendingToolCallbacks ]
	);

	const createToolCallback =
		( {
			name,
			action,
			closeChatWhenDone = () => true, // Set to false to keep the chat open after the tool runs.
			validator = null,
			failValidatorAction = null,
		} ) =>
		async ( args ) => {
			setCurrentToolCall( name );

			if ( validator ) {
				try {
					const isValid = await validator( args );
					if ( ! isValid ) {
						if ( typeof failValidatorAction === 'function' ) {
							await failValidatorAction( args, {
								message: 'Failed to validate arguments',
							} );
						}
						return;
					}
				} catch ( error ) {
					if ( typeof failValidatorAction === 'function' ) {
						await failValidatorAction( args, error );
					}
					return;
				}
			}

			const executeAction = async ( _args ) => {
				try {
					await action( _args );
				} catch ( error ) {
					log.error( 'Tool error:', error );
					handleReset();
				}
			};

			// Some tools keep the chat open, some close afterward.
			// closeChatWhenDone() is a function because it needs to be called when the callback runs, not when the tool is created.
			// Otherwise, getIsHandoff() will not return the updated value.
			if ( closeChatWhenDone() ) {
				withPendingToolCallback( executeAction )( args );
			} else {
				await executeAction( args );
			}
		};

	return {
		withPendingToolCallback,
		createToolCallback,
	};
};

export default useCreateToolCallback;
