/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { useCallback, useEffect, useState } from '@wordpress/element';
import { useDispatch, useSelect } from '@wordpress/data';
import { store as blockEditorStore } from '@wordpress/block-editor';
import { useToolkits } from '@automattic/big-sky-agents';
import { store as aiStore } from '@store';

/**
 * Internal dependencies
 */
import './style.scss';
import { customSectionNames } from '@ai/tasks/replace-content/section-types';
import usePatterns from '@hooks/use-patterns';
import useSiteMetadata from '@hooks/use-site-metadata';
import ActionCarousel from './action-carousel.js';
import useLog from '@hooks/use-log';
import { getAssistantById } from '@ai/assistants/assistant-registry';
import { ASSISTANTS } from '@ai/assistants/constants.js';

export default function ChatActions( {
	useBlockContext = true,
	onActionClick,
	actions: propActions = [],
	animationVariants = {},
} ) {
	const [ actions, setActions ] = useState( [] );
	const { getParentPatternCategory, getItems, getSections } = usePatterns();
	const { isOnboarded } = useSiteMetadata().getSiteMetadata();
	const { selectedBlock } = useSelect( ( select ) => {
		const { getSelectedBlock } = select( blockEditorStore );
		return {
			selectedBlock: getSelectedBlock(),
		};
	} );
	const { callbacks } = useToolkits();
	const { setCurrentUserMessage, setShowThread } = useDispatch( aiStore );
	const { logUserChoice } = useLog();

	const assistant = getAssistantById( ASSISTANTS.CONTENT );
	const toolkitHook = assistant?.toolkitHook;
	const { callbacks: contentCallbacks } = toolkitHook ? toolkitHook() : {};

	const handleActionClick = useCallback(
		( message ) => {
			setShowThread( true );
			logUserChoice( message );
			setCurrentUserMessage( message );
			onActionClick( message );
		},
		[ logUserChoice, onActionClick, setCurrentUserMessage, setShowThread ] // Ensure the callback is updated when callbacks change
	);

	const getEventsActions = useCallback( () => {
		const eventsActions = [];
		const eventsItems = getItems( 'events' );
		if ( eventsItems ) {
			eventsActions.push( {
				key: 'events-add-item',
				actionName: __( 'Add a new item', 'big-sky' ),
				description: __( 'to the events', 'big-sky' ),
				onClick: () => {
					if ( contentCallbacks?.add_item_to_events_container ) {
						contentCallbacks.add_item_to_events_container( {
							clientId: selectedBlock.clientId,
							count: 1,
						} );
					} else {
						handleActionClick(
							__( 'Add a new item to the events.', 'big-sky' )
						);
					}
				},
			} );
		}
		const eventsSections = getSections( 'events' );
		if ( eventsSections ) {
			eventsActions.unshift( {
				key: 'events-add-section',
				actionName: __( 'Add a new row', 'big-sky' ),
				description: __( 'to the events', 'big-sky' ),
				onClick: () => {
					if ( contentCallbacks?.add_container_to_events_section ) {
						contentCallbacks.add_container_to_events_section();
					} else {
						handleActionClick(
							__( 'Add a new row to the events.', 'big-sky' )
						);
					}
				},
			} );
		}
		return eventsActions;
	}, [
		contentCallbacks,
		getItems,
		getSections,
		handleActionClick,
		selectedBlock,
	] );

	const getMenuActions = useCallback( () => {
		const menuActions = [
			{
				key: 'menu-add-item',
				actionName: __( 'Add a new item', 'big-sky' ),
				description: __( 'to the menu', 'big-sky' ),
				onClick: () => {
					if ( contentCallbacks?.add_item_to_menu_container ) {
						contentCallbacks.add_item_to_menu_container( {
							clientId: selectedBlock.clientId,
							count: 1,
						} );
					} else {
						handleActionClick(
							__( 'Add a new item to the menu.', 'big-sky' )
						);
					}
				},
			},
		];
		const menuSections = getSections( 'menu' );
		if ( menuSections ) {
			menuActions.unshift( {
				key: 'menu-add-section',
				actionName: __( 'Add a new section', 'big-sky' ),
				description: __( 'to the menu', 'big-sky' ),
				onClick: () => {
					if ( contentCallbacks?.add_container_to_menu_section ) {
						contentCallbacks.add_container_to_menu_section( {
							title: 'New Section',
							items: [
								{
									name: 'New Item',
									description: 'New Item Description',
									price: '10',
								},
							],
						} );
					} else {
						handleActionClick(
							__( 'Add a new section to the menu.', 'big-sky' )
						);
					}
				},
			} );
		}
		return menuActions;
	}, [ contentCallbacks, getSections, handleActionClick, selectedBlock ] );

	const getPricingActions = useCallback( () => {
		return [
			{
				key: 'pricing-add-section',
				actionName: __( 'Add a row', 'big-sky' ),
				description: __( 'to the pricing section', 'big-sky' ),
				onClick: () => {
					if ( contentCallbacks?.add_container_to_pricing_section ) {
						contentCallbacks.add_container_to_pricing_section( {
							items: [],
						} );
					} else {
						handleActionClick(
							__(
								'Add a new row to the pricing section.',
								'big-sky'
							)
						);
					}
				},
			},
		];
	}, [ contentCallbacks, handleActionClick ] );

	const getServicesActions = useCallback( () => {
		const servicesSections = getSections( 'services' );
		if ( servicesSections ) {
			return [
				{
					key: 'services-add-service',
					actionName: __( 'Add a row', 'big-sky' ),
					description: __( 'to the services', 'big-sky' ),
					onClick: () => {
						if (
							contentCallbacks?.add_container_to_services_section
						) {
							contentCallbacks.add_container_to_services_section();
						} else {
							handleActionClick(
								__(
									'Add a new row to the services.',
									'big-sky'
								)
							);
						}
					},
				},
			];
		}
		return [];
	}, [ getSections, contentCallbacks, handleActionClick ] );

	const getTestimonialsActions = useCallback( () => {
		return [
			{
				key: 'testimonials-add-testimonial',
				actionName: __( 'Add a row', 'big-sky' ),
				description: __( 'to the testimonials', 'big-sky' ),
				onClick: () => {
					if ( contentCallbacks?.add_item_to_testimonials_section ) {
						contentCallbacks.add_item_to_testimonials_section();
					} else {
						handleActionClick(
							__(
								'Add a new row to the testimonials.',
								'big-sky'
							)
						);
					}
				},
			},
		];
	}, [ contentCallbacks, handleActionClick ] );

	const getTeamActions = useCallback( () => {
		const teamActions = [];
		const teamItems = getItems( 'team' );
		if ( teamItems ) {
			teamActions.push( {
				key: 'team-add-member',
				actionName: __( 'Add a new member', 'big-sky' ),
				description: __( 'to the team', 'big-sky' ),
				onClick: () => {
					if ( contentCallbacks?.add_member_to_team_container ) {
						contentCallbacks.add_member_to_team_container( {
							clientId: selectedBlock.clientId,
							count: 1,
						} );
					} else {
						handleActionClick(
							__( 'Add a new member to the team.', 'big-sky' )
						);
					}
				},
			} );
		}
		const teamSections = getSections( 'team' );
		if ( teamSections ) {
			teamActions.unshift( {
				key: 'team-add-section',
				actionName: __( 'Add a new section', 'big-sky' ),
				description: __( 'to the team', 'big-sky' ),
				onClick: () => {
					if ( contentCallbacks?.add_container_to_team_section ) {
						contentCallbacks.add_container_to_team_section();
					} else {
						handleActionClick(
							__( 'Add a new section to the team.', 'big-sky' )
						);
					}
				},
			} );
		}
		return teamActions;
	}, [
		contentCallbacks,
		getItems,
		getSections,
		handleActionClick,
		selectedBlock,
	] );

	const getBigSkyPatternActions = useCallback(
		( category ) => {
			switch ( category ) {
				case 'events':
					return getEventsActions();
				case 'menu':
					return getMenuActions();
				case 'pricing':
					return getPricingActions();
				case 'services':
					return getServicesActions();
				case 'testimonials':
					return getTestimonialsActions();
				case 'team':
					return getTeamActions();
				default:
					return [];
			}
		},
		[
			getEventsActions,
			getMenuActions,
			getPricingActions,
			getServicesActions,
			getTestimonialsActions,
			getTeamActions,
		]
	);

	const getBaseActions = useCallback( () => {
		const bigSkyPatternActions = [];
		// check if the selected block is a custom section type
		if ( selectedBlock ) {
			const parentPatternCategory =
				getParentPatternCategory( selectedBlock );
			if ( customSectionNames.includes( parentPatternCategory ) ) {
				bigSkyPatternActions.push(
					...getBigSkyPatternActions( parentPatternCategory )
				);
			}
		}

		const siteActions = [
			{
				key: 'site-add-page',
				actionName: __( 'Add a page', 'big-sky' ),
				description: __( 'to my website', 'big-sky' ),
				onClick: () => {
					handleActionClick(
						__( 'Add a new page to my website.', 'big-sky' )
					);
				},
			},
			{
				key: 'site-change-colors',
				actionName: __( 'Change the colors', 'big-sky' ),
				description: __( 'of my website', 'big-sky' ),
				onClick: () => {
					handleActionClick(
						__( 'Change the colors of my website.', 'big-sky' )
					);
				},
			},
			{
				key: 'site-change-fonts',
				actionName: __( 'Change the fonts', 'big-sky' ),
				description: __( 'of my website', 'big-sky' ),
				onClick: () => {
					handleActionClick(
						__( 'Change the fonts of my website.', 'big-sky' )
					);
				},
			},
			{
				key: 'site-change-layout',
				actionName: __( 'Change layout', 'big-sky' ),
				description: __( 'try a different layout', 'big-sky' ),
				onClick: () => {
					// Only the site-design agent has the show_layout_picker tool, need a user message to trigger the layout if on another agent.
					if ( callbacks?.show_layout_picker ) {
						callbacks.show_layout_picker();
					} else {
						handleActionClick(
							__( 'Change the layout of my website.', 'big-sky' )
						);
					}
				},
			},
		];

		return [ ...bigSkyPatternActions, ...siteActions ];
	}, [
		callbacks,
		getBigSkyPatternActions,
		getParentPatternCategory,
		handleActionClick,
		selectedBlock,
	] );

	const getImageActions = useCallback( () => {
		return [
			{
				key: 'image-ai-image',
				actionName: __( 'Generate an image', 'big-sky' ),
				description: __( 'using AI', 'big-sky' ),
				onClick: () =>
					handleActionClick(
						__(
							'Make a new AI image to replace the one I have selected.',
							'big-sky'
						)
					),
			},
			{
				key: 'image-find-stock',
				actionName: __( 'Find an image', 'big-sky' ),
				description: __( 'from stock', 'big-sky' ),
				onClick: () =>
					handleActionClick(
						__(
							'Find me a new stock image to replace the one I have selected.',
							'big-sky'
						)
					),
			},
		];
	}, [ handleActionClick ] );

	const getLogoActions = useCallback( () => {
		return [
			{
				key: 'logo-ai-image',
				actionName: __( 'Make a logo', 'big-sky' ),
				description: __( 'using AI', 'big-sky' ),
				onClick: () =>
					handleActionClick(
						__(
							'Make a new logo to replace the one I have selected.',
							'big-sky'
						)
					),
			},
		];
	}, [ handleActionClick ] );

	const getTextActions = useCallback( () => {
		return [
			{
				key: 'text-change-tone',
				actionName: __( 'Change tone', 'big-sky' ),
				description: __( 'to something different', 'big-sky' ),
				onClick: () =>
					handleActionClick(
						__(
							'Change the tone of the text I have selected.',
							'big-sky'
						)
					),
			},
			{
				key: 'text-fix-spelling-grammar',
				actionName: __( 'Fix spelling and grammar', 'big-sky' ),
				description: __( 'of this text', 'big-sky' ),
				onClick: () =>
					handleActionClick(
						__(
							'Fix the spelling and grammar of the text I have selected.',
							'big-sky'
						)
					),
			},
			{
				key: 'text-make-shorter',
				actionName: __( 'Reduce length', 'big-sky' ),
				description: __( 'of this text', 'big-sky' ),
				onClick: () =>
					handleActionClick(
						__(
							'Make the text I have selected, shorter.',
							'big-sky'
						)
					),
			},
		];
	}, [ handleActionClick ] );

	const getButtonActions = useCallback( () => {
		return [
			{
				key: 'button-update-link',
				actionName: __( 'Update link', 'big-sky' ),
				description: __( 'to something else', 'big-sky' ),
				onClick: () =>
					handleActionClick(
						__(
							'Update the button link to something else.',
							'big-sky'
						)
					),
			},
		];
	}, [ handleActionClick ] );

	const getActions = useCallback( () => {
		const currentActionType = actions[ 0 ]?.key;

		// These are passed in from the parent component.
		if ( propActions.length > 0 ) {
			return propActions.map( ( action ) => ( {
				...action,
				action_type: 'suggested',
			} ) );
		}

		// If the user has clicked on the Assistant icon while a block is selected
		// we should show the site context actions, not the block context actions.
		// This is what the useBlockContext prop is for.
		if ( ( ! selectedBlock || ! useBlockContext ) && isOnboarded ) {
			return getBaseActions();
		}

		const blockName = selectedBlock?.name;

		switch ( blockName ) {
			case 'core/paragraph':
			case 'core/heading':
				return getTextActions();
			case 'core/image':
			case 'core/cover':
				return getImageActions();
			case 'core/site-logo':
				return getLogoActions();
			case 'core/button':
			case 'core/social-link':
				return getButtonActions();
			case 'core/group':
			default:
				if (
					useBlockContext &&
					! currentActionType?.includes( 'site-' ) &&
					isOnboarded
				) {
					return getBaseActions();
				}
		}
	}, [
		actions,
		getImageActions,
		getLogoActions,
		getBaseActions,
		getTextActions,
		getButtonActions,
		isOnboarded,
		selectedBlock,
		useBlockContext,
		propActions,
	] );

	useEffect( () => {
		setActions( getActions() );
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [ selectedBlock ] );

	return actions?.length > 0 ? (
		<ActionCarousel
			key={ actions[ 0 ]?.key }
			actions={ actions }
			animationVariants={ animationVariants }
		/>
	) : null;
}
