import specAssistantPrompt from './prompt.js';
import specToolkit from './toolkit.js';
import specContext from './context.js';
import { ASSISTANTS } from '../constants.js';
import useSpecToolkit from './use-spec-toolkit.js';

const specAssistant = {
	id: ASSISTANTS.SPEC,
	description:
		'An assistant that gathers and confirms information about the site specs.',
	instructions: specAssistantPrompt,
	tags: [ ASSISTANTS.SPEC ],
	additionalInstructions: ( context ) => {
		return specContext.format( context );
	},
	toolkits: [ specToolkit ],
	toolkitHook: useSpecToolkit,
	onStart: ( invoke, clearMessages = () => {}, agentMessage = null ) => {
		if ( agentMessage ) {
			clearMessages();
			invoke.agentSay( agentMessage );
		}
	},
	onToolResult: ( _, value, invoke ) => {
		invoke.userSay( value );
	},
};

export default specAssistant;
