/**
 * WordPress dependencies
 */
import { useDispatch } from '@wordpress/data';
import { __ } from '@wordpress/i18n';
import { useRef } from '@wordpress/element';

/**
 * Internal dependencies
 */
import { useChat, useToolkits } from '@automattic/big-sky-agents';
import log from '@utils/log-debug';
import { store as aiStore } from '@store/index';
import { ASSISTANTS } from '@ai/assistants/constants.js';
import { messageRoles } from '@utils/constants';
import { getAssistantToolkitName } from '@ai/assistants/assistant-registry.js';

export default function useChangeAssistantTool() {
	const MAX_CONSECUTIVE_CHANGES = 3;
	const MAX_HISTORY_LENGTH = 4; // Track last 4 changes to detect loops
	const { setAssistant } = useDispatch( aiStore );
	const {
		agentSay,
		userSayRepeat,
		getChangeAgentCount,
		incrementChangeAgentCount,
		resetChangeAgentCount,
		clearMessages,
	} = useChat();
	const { setContext } = useToolkits();

	// Track history of changes
	const changesHistory = useRef( [] );

	const changeAssistant = ( parameters ) => {
		if (
			hasReachedMaxChanges( parameters.userRequest ) ||
			isLoopDetected( parameters )
		) {
			return;
		}

		log.info(
			` Changing the assistant:
	-- To: ${ parameters.toAssistantId }
	-- From: ${ parameters.fromAssistantId }
	-- Reason: ${ parameters.reason }`
		);

		// Add this change to history
		changesHistory.current = [
			{
				toAssistantId: parameters.toAssistantId,
				fromAssistantId: parameters.fromAssistantId,
			},
			...changesHistory.current,
		].slice( 0, MAX_HISTORY_LENGTH );

		setAssistant( parameters.toAssistantId );
		incrementChangeAgentCount();
		clearMessages( messageRoles.TOOL );
		clearMessages( messageRoles.SYSTEM );

		if (
			parameters.fromAssistantId === ASSISTANTS.ROUTER &&
			parameters.toAssistantId !== ASSISTANTS.HELP
		) {
			const toolkitName = getAssistantToolkitName(
				parameters.toAssistantId
			);
			if ( toolkitName ) {
				setContext( toolkitName, {
					fromAssistantId: parameters.fromAssistantId,
				} );
			}
		}

		if ( parameters.toAssistantId === ASSISTANTS.HELP ) {
			const toolkitName = getAssistantToolkitName(
				parameters.toAssistantId
			);
			if ( toolkitName ) {
				setContext( toolkitName, {
					fromAssistantId: parameters.fromAssistantId,
				} );
			}
		}

		userSayRepeat();

		return 'Assistant changed';
	};

	const hasReachedMaxChanges = ( userRequest ) => {
		const changeAgentCount = getChangeAgentCount();

		if ( changeAgentCount > MAX_CONSECUTIVE_CHANGES ) {
			logRoutingError( 'maxRetriesReached', userRequest );
			resetChangeAgentCount();
			return true;
		}
		return false;
	};

	const isLoopDetected = ( parameters ) => {
		if ( changesHistory.current.length < 2 ) {
			return false;
		}

		// Check for immediate back-and-forth
		const isImmediateLoop =
			changesHistory.current[ 0 ]?.toAssistantId ===
				parameters.fromAssistantId &&
			changesHistory.current[ 0 ]?.fromAssistantId ===
				parameters.toAssistantId;

		// Check for pattern of repeated switching (e.g. router->color->router->color)
		const isPatternLoop =
			changesHistory.current.length >= 3 &&
			changesHistory.current[ 2 ]?.toAssistantId ===
				parameters.toAssistantId &&
			changesHistory.current[ 2 ]?.fromAssistantId ===
				parameters.fromAssistantId &&
			changesHistory.current[ 1 ]?.toAssistantId ===
				changesHistory.current[ 0 ]?.fromAssistantId &&
			changesHistory.current[ 1 ]?.fromAssistantId ===
				changesHistory.current[ 0 ]?.toAssistantId;

		if ( isImmediateLoop || isPatternLoop ) {
			logRoutingError( 'loopDetected', parameters.userRequest );
			resetChangeAgentCount();
			return true;
		}

		return false;
	};

	const logRoutingError = ( error, userRequest ) => {
		log.error( 'Router error:', error, userRequest );
		agentSay(
			__(
				'I am sorry, I had trouble processing that request. Please try again.',
				'big-sky'
			)
		);
	};

	return {
		changeAssistant,
	};
}
