/**
 * WordPress dependencies
 */
import { useSelect } from '@wordpress/data';
import { useChat } from '@automattic/big-sky-agents';

/**
 * Internal dependencies
 */
import { store as aiStore } from '@store';

export default function useIsThinking() {
	const { pendingToolCallbacks } = useSelect( ( select ) => ( {
		pendingToolCallbacks: select( aiStore ).getPendingToolCallbacks(),
	} ) );

	const { running, isAwaitingUserInput } = useChat();

	return {
		isThinking:
			isAwaitingUserInput === false ||
			pendingToolCallbacks > 0 ||
			running,
	};
}
