/**
 * WordPress dependencies
 */
import { createBlock } from '@wordpress/blocks';
import { __ } from '@wordpress/i18n';
import { useDispatch, useSelect } from '@wordpress/data';
import { store as blockEditorStore } from '@wordpress/block-editor';

/**
 * Internal dependencies
 */
import useBlocks from '@hooks/use-blocks';
import { store as aiStore } from '@store';
import setDelay from '@utils/set-delay';
import { animationTiming } from '@utils/constants';
import { availableContactFormFields } from './constants.js';

const useContactFormToolFunctions = () => {
	const { canRemoveBlock, getBlock, getBlocksByName } =
		useSelect( blockEditorStore );
	const {
		insertBlock,
		removeBlock,
		replaceInnerBlocks,
		updateBlockAttributes,
	} = useDispatch( blockEditorStore );
	const { setBlockBeforeChanges } = useDispatch( aiStore );
	const { setBlockIsProcessing } = useBlocks();

	const modifyContactForm = async ( { action, fields } ) => {
		const contactFormBlock = getContactFormBlock();

		const mapActionsToCallbackFunctions = {
			insert: addContactFormFields,
			update: updateContactFormFields,
			delete: deleteContactFormFields,
		};

		if ( ! mapActionsToCallbackFunctions[ action ] ) {
			throw new Error( __( 'Invalid action specified.', 'big-sky' ) );
		}

		setBlockBeforeChanges( contactFormBlock );
		setBlockIsProcessing( contactFormBlock, true );
		await setDelay( animationTiming.visualAdjustmentWaitTime );
		const result = await mapActionsToCallbackFunctions[ action ](
			fields,
			contactFormBlock
		);
		setBlockIsProcessing( contactFormBlock, false );

		return result;
	};

	const isAJetpackFormField = ( block ) => {
		return (
			block.name.startsWith( 'jetpack/field-' ) ||
			block.name === 'jetpack/button'
		);
	};

	const getContactFormBlock = () => {
		const clientIds = getBlocksByName( 'jetpack/contact-form' );
		return clientIds.length ? getBlock( clientIds[ 0 ] ) : null;
	};

	const getContactFormFields = ( contactFormBlock ) => {
		return contactFormBlock.innerBlocks.filter( ( block ) =>
			isAJetpackFormField( block )
		);
	};
	const isContactFormOnPage = () => !! getContactFormBlock();

	const addContactFormFields = ( fields, contactFormBlock ) => {
		const validFields = fields
			.filter( ( field ) =>
				availableContactFormFields.includes( field.type )
			)
			.map( ( field ) => ( {
				...field,
				type: field.type.startsWith( 'jetpack/field-' )
					? field.type
					: `jetpack/field-${ field.type }`,
			} ) );

		validFields.forEach( ( field ) => {
			const blockAttributes =
				field.label && field.label.trim() !== ''
					? { label: field.label }
					: {};
			const newBlock = createBlock( field.type, blockAttributes );
			insertBlock(
				newBlock,
				Math.max( contactFormBlock.innerBlocks.length - 1, 0 ),
				contactFormBlock.clientId,
				false
			);
		} );

		return __(
			'I have added the requested fields to the contact form.',
			'big-sky'
		);
	};

	const updateContactFormFields = async ( fields, contactFormBlock ) => {
		const updatedFields = [];
		const notUpdatedFields = [];

		// Get all form field blocks
		const formFieldBlocks = getContactFormFields( contactFormBlock );

		fields.forEach( ( field ) => {
			// Find blocks matching the field type
			const matchingTypeBlocks = formFieldBlocks.filter(
				( block ) =>
					block.name === `jetpack/field-${ field.type }` ||
					( block.name === 'jetpack/button' &&
						field.type === 'button' )
			);

			let blockToUpdate;
			const fieldLabelKey = field.type === 'button' ? 'text' : 'label';

			if ( matchingTypeBlocks.length === 1 ) {
				// If there's only one block of this type, update it regardless of label
				blockToUpdate = matchingTypeBlocks[ 0 ];
			} else if ( matchingTypeBlocks.length > 1 ) {
				// If there are multiple blocks of this type, try to match by label
				blockToUpdate = matchingTypeBlocks.find(
					( block ) =>
						block.attributes[ fieldLabelKey ]?.toLowerCase() ===
						field.label.toLowerCase()
				);
			} else {
				// The LLM might hallucinate the type but not the label. If there are no blocks of this type, try to match by label.
				blockToUpdate = formFieldBlocks.find(
					( block ) =>
						block.attributes[ fieldLabelKey ]?.toLowerCase() ===
						field.label.toLowerCase()
				);
			}

			if ( blockToUpdate ) {
				const updates = {};
				if ( field.blockAttributes.required !== undefined ) {
					updates.required = field.blockAttributes.required;
				}
				if ( field.blockAttributes.newLabel ) {
					if ( field.type === 'button' ) {
						updates.text = field.blockAttributes.newLabel;
					} else {
						updates.label = field.blockAttributes.newLabel;
					}
				}

				updateBlockAttributes( blockToUpdate.clientId, updates );
				updatedFields.push( field.type );

				// Remove the block from our local array to avoid double-updating
				const index = formFieldBlocks.findIndex(
					( b ) => b.clientId === blockToUpdate.clientId
				);
				if ( index > -1 ) formFieldBlocks.splice( index, 1 );
			} else {
				notUpdatedFields.push( field.type );
			}
		} );

		let message = '';

		if ( updatedFields.length > 0 ) {
			message +=
				updatedFields.length === 1
					? __( "I've updated a field for you.", 'big-sky' )
					: __( "I've updated some fields for you.", 'big-sky' );
		} else {
			message += __( 'I was not able to update any fields.', 'big-sky' );
		}

		if ( notUpdatedFields.length > 0 ) {
			message +=
				'\n' +
				__( 'I could not update the following fields:', 'big-sky' ) +
				'\n';
			message += notUpdatedFields.join( '\n' );
		}
		return message.trim();
	};

	const deleteContactFormFields = async ( fields, contactFormBlock ) => {
		const deletedFields = [];
		const notDeletedFields = [];

		// Get all form field blocks
		const formFieldBlocks = getContactFormFields( contactFormBlock );

		fields.forEach( ( field ) => {
			// Find blocks matching the field type
			const matchingTypeBlocks = formFieldBlocks.filter(
				( block ) => block.name === `jetpack/field-${ field.type }`
			);

			let blockToRemove;

			if ( matchingTypeBlocks.length === 1 ) {
				// If there's only one block of this type, remove it regardless of label
				blockToRemove = matchingTypeBlocks[ 0 ];
			} else if ( matchingTypeBlocks.length > 1 ) {
				// If there are multiple blocks of this type, try to match by label
				blockToRemove = matchingTypeBlocks.find(
					( block ) =>
						block.attributes.label.toLowerCase() ===
						field.label.toLowerCase()
				);
			} else {
				// The LLM might hallucinate the type but not the label. If there are no blocks of this type, try to match by label.
				blockToRemove = formFieldBlocks.find(
					( block ) =>
						block.attributes.label.toLowerCase() ===
						field.label.toLowerCase()
				);
			}

			if ( blockToRemove && canRemoveBlock( blockToRemove.clientId ) ) {
				removeBlock( blockToRemove.clientId, false );
				deletedFields.push( {
					type: field.type,
					label: blockToRemove.attributes.label,
				} );
				// Remove the block from our local array to avoid double-deletion
				const index = formFieldBlocks.findIndex(
					( b ) => b.clientId === blockToRemove.clientId
				);
				if ( index > -1 ) formFieldBlocks.splice( index, 1 );
			} else {
				notDeletedFields.push( field );
			}
		} );

		let message = '';

		if ( deletedFields.length > 0 ) {
			message +=
				deletedFields.length === 1
					? __( 'I deleted a field for you.', 'big-sky' )
					: __( 'I deleted some fields for you.', 'big-sky' );
		}

		if ( notDeletedFields.length > 0 ) {
			message +=
				'\n' +
				__( 'I could not delete the following fields:', 'big-sky' ) +
				'\n' +
				notDeletedFields.map( ( f ) => `${ f.label }` ).join( '\n' ) +
				'. ';
		}

		if ( deletedFields.length === 0 && notDeletedFields.length === 0 ) {
			message = __( 'No fields were deleted.', 'big-sky' );
		}

		return message.trim();
	};

	const validateContactFormArgs = ( { action, fields } ) => {
		if ( ! getContactFormBlock() ) {
			throw new Error(
				__(
					'No contact form block found on this page. Have you added one yet?',
					'big-sky'
				)
			);
		}
		if ( ! Array.isArray( fields ) ) {
			throw new Error(
				__(
					'Please specify the field(s) to modify in the contact form.',
					'big-sky'
				)
			);
		}
		if (
			fields.some( ( field ) => field.type === 'button' ) &&
			action === 'delete'
		) {
			throw new Error(
				__(
					'I cannot delete the button field, as it is required to submit the form. Instead, I can change the label for you.',
					'big-sky'
				)
			);
		}
		if (
			fields.some( ( field ) => field.type === 'button' ) &&
			action === 'insert'
		) {
			throw new Error(
				__(
					"The contact form can only have one button. I can change the button's label for you, if you like.",
					'big-sky'
				)
			);
		}
		if (
			! fields.some( ( field ) =>
				availableContactFormFields.includes( field.type )
			)
		) {
			throw new Error(
				__(
					'It seems that a field type you specified is not valid. Please specify a valid field type in the contact form.',
					'big-sky'
				)
			);
		}
		if ( ! [ 'insert', 'update', 'delete' ].includes( action ) ) {
			throw new Error(
				__(
					'Something went wrong when trying to modify the contact form. Please try again.',
					'big-sky'
				)
			);
		}
		return true;
	};

	const cancelModifyContactForm = ( clientId, originalInnerBlocks ) => {
		replaceInnerBlocks( clientId, originalInnerBlocks, false );
	};

	return {
		cancelModifyContactForm,
		isContactFormOnPage,
		modifyContactForm,
		validateContactFormArgs,
	};
};

export default useContactFormToolFunctions;
