// Initialize empty registries
const assistantsRegistry = new Map();
const assistantsList = [];

/**
 * Registers an assistant in the registry
 * @param {Object} assistant The assistant to register
 */
export function registerAssistant( assistant ) {
	assistantsRegistry.set( assistant.id, assistant );
	assistantsList.push( assistant );
}

/**
 * Gets an assistant by ID
 * @param {string} id The assistant ID
 * @return {Object|undefined} The assistant object or undefined
 */
export function getAssistantById( id ) {
	return assistantsRegistry.get( id );
}

/**
 * Gets all registered assistants
 * @return {Array} Array of assistant objects
 */
export function getAssistants() {
	return assistantsList;
}

/**
 * Gets the toolkit name for an assistant
 * @param {string} assistantId The assistant ID
 * @return {string|undefined} The toolkit name or undefined
 */
export function getAssistantToolkitName( assistantId ) {
	const assistant = getAssistantById( assistantId );
	return assistant?.toolkits?.[ 0 ]?.name;
}
