import patternAssistantPrompt from './prompt.js';
import patternToolkit from './toolkit.js';
import { ASSISTANTS } from '../constants.js';
import usePatternToolkit from './use-pattern-toolkit.js';

const patternAssistant = {
	id: ASSISTANTS.PATTERN,
	description:
		'An assistant that handles adding, removing, and replacing patterns and sections.',
	instructions: ( context ) => {
		return patternAssistantPrompt( context );
	},
	tags: [ 'big-sky-pattern' ],
	toolkits: [ patternToolkit ],
	toolkitHook: usePatternToolkit,
	onStart: ( invoke, clearMessages = () => {}, agentMessage = null ) => {
		if ( agentMessage ) {
			clearMessages();
			invoke.agentSay( agentMessage );
		}
	},
};

export default patternAssistant;
