/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { Button } from '@wordpress/components';
import { useCallback, useMemo } from '@wordpress/element';
import { useDispatch, useSelect } from '@wordpress/data';
import { store as blockEditorStore } from '@wordpress/block-editor';
import { store as blocksStore } from '@wordpress/blocks';

/**
 * Internal dependencies
 */
import './style.scss';
import useVariations from '@hooks/use-variations';
import useSiteMetadata from '@hooks/use-site-metadata';
import useStyles from '@hooks/use-styles';
import { recordTracksEvent } from '@utils/tracks';
import { sectionStyleRegex } from '@utils/constants';

export default function SectionColorVariationPickerMenu() {
	const { selectedBlock } = useSelect( ( select ) => {
		const { getSelectedBlock } = select( blockEditorStore );
		return {
			selectedBlock: getSelectedBlock(),
		};
	} );
	const { getBlockStyles } = useSelect( blocksStore );
	const { updateBlockAttributes } = useDispatch( blockEditorStore );

	const { resetBlockTextColor } = useStyles();
	const { getSectionVariationBackgroundColor, getSectionVariationTextColor } =
		useVariations();
	const { currentColor: currentColorVariationName } =
		useSiteMetadata().getSiteMetadata();

	const getVariations = useCallback( () => {
		if ( ! selectedBlock ) {
			return null;
		}

		const variations = getBlockStyles( selectedBlock.name );

		if ( ! variations ) {
			return null;
		}

		// Add in the default theme variation if default doesn't exist already
		if ( ! variations.find( ( v ) => v.name === 'section-default' ) ) {
			variations.unshift( {
				name: 'section-default',
				label: 'Default',
			} );
		}

		return variations;
	}, [ getBlockStyles, selectedBlock ] );

	const getCurrentVariation = useCallback( () => {
		if ( ! selectedBlock || ! selectedBlock?.attributes?.className ) {
			return null;
		}

		const styleClasses = [
			...selectedBlock.attributes.className?.matchAll(
				sectionStyleRegex
			),
		];

		if ( ! styleClasses || styleClasses.length < 1 ) {
			return false;
		}

		return styleClasses[ 0 ][ 1 ]?.trim();
	}, [ selectedBlock ] );

	const resetInnerBlocksTextColor = useCallback(
		( block ) => {
			resetBlockTextColor( block );
			if ( block.innerBlocks.length ) {
				block.innerBlocks.forEach( ( innerBlock ) => {
					resetInnerBlocksTextColor( innerBlock );
				} );
			}
		},
		[ resetBlockTextColor ]
	);

	const setNextVariation = useCallback(
		( currentVariationName ) => {
			if ( ! selectedBlock ) {
				return;
			}

			const variations = getVariations();

			recordTracksEvent( 'dock_menu_item_click', {
				menu_slug: 'section-color-variation-picker',
			} );

			if ( ! variations ) {
				return;
			}

			const currentVariationIndex = variations.findIndex(
				( v ) => v.name === currentVariationName
			);

			const nextVariationIndex =
				currentVariationIndex + 1 >= variations.length
					? 0
					: currentVariationIndex + 1;

			const sectionVariationName = variations[ nextVariationIndex ].name;

			// remove is-style-X from the className of the selectedBlock
			const newClassNames = selectedBlock.attributes.className.replaceAll(
				sectionStyleRegex,
				''
			);

			updateBlockAttributes( selectedBlock.clientId, {
				className: `${ newClassNames.trim() } is-style-${ sectionVariationName }`,
			} );

			// reset color of all inner blocks
			resetInnerBlocksTextColor( selectedBlock );
		},
		[
			getVariations,
			resetInnerBlocksTextColor,
			selectedBlock,
			updateBlockAttributes,
		]
	);

	return useMemo( () => {
		const activeVariation = getCurrentVariation();

		if ( ! activeVariation ) {
			return null;
		}

		const { hex: backgroundColor } = getSectionVariationBackgroundColor(
			activeVariation,
			currentColorVariationName
		);
		const { hex: textColor } = getSectionVariationTextColor(
			activeVariation,
			currentColorVariationName
		);

		return (
			backgroundColor &&
			textColor && (
				<Button
					key={ `variation-${ activeVariation }` }
					label={ __( 'Pattern styles', 'big-sky' ) }
					onClick={ () => setNextVariation( activeVariation ) }
					style={ {
						background: `linear-gradient(90deg, ${ backgroundColor } 50%, ${ textColor } 50%, ${ textColor } 100%)`,
					} }
					className={ `big-sky__pattern-variations-menu__button` }
				/>
			)
		);
	}, [
		getCurrentVariation,
		getSectionVariationBackgroundColor,
		currentColorVariationName,
		getSectionVariationTextColor,
		setNextVariation,
	] );
}
