/**
 * WordPress dependencies
 */
import { store as blockEditorStore } from '@wordpress/block-editor';
import { getBlockSupport } from '@wordpress/blocks';
import { useDispatch, useRegistry, useSelect } from '@wordpress/data';
import { __, sprintf } from '@wordpress/i18n';

/**
 * External dependencies
 */
import { colord, extend } from 'colord';
import namesPlugin from 'colord/plugins/names';
import labPlugin from 'colord/plugins/lab';
extend( [ namesPlugin, labPlugin ] );

/**
 * Internal dependencies
 */
import ColorVariationPicker from '@components/color-variation-picker';
import { store as aiStore } from '@store';
import useBlocks from '@hooks/use-blocks';
import useCanvas from '@hooks/use-canvas';
import usePatterns from '@hooks/use-patterns';
import useStyles from '@hooks/use-styles';
import useVariations from '@hooks/use-variations';
import { dockViews } from '@utils/constants';
import useSiteMetadata from '@hooks/use-site-metadata';
import { BIG_SKY_CUSTOM_THEME_PREFIX } from '@utils/custom-color-variations/constants';
import { addColorToTheme } from '@utils/colors';

const cssKeywordToColorMapping = {
	beige: 'white',
	black: 'black',
	bisque: 'white',
	blanchedalmond: 'white',
	crimson: 'red',
	darkslategray: 'black',
	darkgoldenrod: 'brown',
	darkolivegreen: 'green',
	firebrick: 'red',
	gainsboro: 'gray',
	gold: 'yellow',
	honeydew: 'white',
	indigo: 'blue',
	ivory: 'white',
	khaki: 'brown',
	lavender: 'purple',
	lavenderblush: 'white',
	lightgrey: 'gray',
	lightsalmon: 'orange',
	lime: 'green',
	linen: 'white',
	maroon: 'red',
	mintcream: 'white',
	peru: 'brown',
	salmon: 'orange',
	seashell: 'white',
	sienna: 'brown',
	silver: 'gray',
	snow: 'white',
	tan: 'brown',
	teal: 'blue',
	tomato: 'red',
	violet: 'purple',
	wheat: 'brown',
	white: 'white',
};

const getColorSlug = ( backgroundVar ) => {
	const parts = backgroundVar.split( '--' );
	return parts.pop().replace( ')', '' );
};

export default function useColorToolFunctions() {
	const {
		pushGeneratedVariations,
		setDockView,
		setLastCancelableAction,
		setIsToolContainerVisible,
	} = useDispatch( aiStore );
	const { zoomIn } = useCanvas();
	const { getColorVariations, getColorVariationByTitle } = useVariations();
	const { currentColor: currentColorMeta } =
		useSiteMetadata().getSiteMetadata();
	const { getCurrentColor } = useSelect( aiStore );
	const {
		paletteColors,
		getCurrentTheme,
		saveStylesEntityRecord,
		setStyles,
		createCustomSchemeVariations,
		updateRootBlocksStyles,
	} = useStyles();
	const { setSiteMetadata } = useSiteMetadata();
	const { getBlocks, getBlock, getBlockAttributes } =
		useSelect( blockEditorStore );
	const { updateBlockAttributes } = useDispatch( blockEditorStore );
	const { getTemplatePartClientID } = useBlocks();

	const { getParentRootPatternBlock } = useBlocks();
	const { getRootBlocksOfPostContent } = usePatterns();
	const registry = useRegistry();

	const validateGenerateColorPaletteArgs = ( args ) => {
		if ( ! args ) {
			return false;
		}

		const { colors, keyword } = args;

		if (
			( ! colors || ! Array.isArray( colors ) || ! colors.length ) &&
			! keyword
		) {
			return false;
		}

		return true;
	};

	const generateColorPalette = ( {
		colors: requestedColors = [],
		keyword = '',
		custom_colors,
		callback = () => {},
	} ) => {
		const currentColor = getCurrentColor() || currentColorMeta;
		const colorVariations = getColorVariations();

		// get the current theme to use for the cancel action
		const currentTheme =
			getColorVariationByTitle( currentColor ) || getCurrentTheme();

		// eslint-disable-next-line no-restricted-syntax
		colorVariations.sort( () => Math.random() - 0.5 );

		// get color variations with a background that match the requested color
		let suggestedVariations = [];
		if ( keyword || requestedColors?.length > 0 ) {
			suggestedVariations = colorVariations.filter(
				( colorVariation ) => {
					// get a list of color slugs that are used as a background
					const backgroundColorSlugs = [];
					const defaultBackground =
						colorVariation.styles?.color?.background;
					if ( defaultBackground ) {
						backgroundColorSlugs.push(
							getColorSlug( defaultBackground )
						);
					}
					const variationBackgrounds =
						colorVariation.styles?.blocks?.[ 'core/group' ]
							?.variations;
					if ( ! variationBackgrounds ) {
						return false;
					}
					for ( const styles of Object.values(
						variationBackgrounds
					) ) {
						const background = styles.color?.background;
						if ( background )
							backgroundColorSlugs.push(
								getColorSlug( background )
							);
					}
					// remove null values and duplicates
					const uniqueBackgroundColorSlugs = [
						...new Set( backgroundColorSlugs.filter( Boolean ) ),
					];

					if ( requestedColors?.length ) {
						// Calculate the color relevance
						colorVariation.relevance =
							colorVariation.settings?.color?.palette?.theme
								?.filter( ( color ) =>
									uniqueBackgroundColorSlugs.includes(
										color.slug
									)
								)
								.reduce( ( maxRelevance, color ) => {
									return Math.max(
										maxRelevance,
										...requestedColors.map(
											( requestedColor ) =>
												1 -
												colord( requestedColor ).delta(
													color.color
												)
										)
									);
								}, 0 );
					}

					// check if the requested color is in the palette background colors
					const hasRequestedColor =
						!! requestedColors &&
						colorVariation.settings?.color?.palette?.theme?.some(
							( color ) => {
								if (
									! uniqueBackgroundColorSlugs.includes(
										color.slug
									)
								) {
									return false;
								}

								const cssKeyword = colord( color.color ).toName(
									{
										closest: true,
									}
								);
								const colorName =
									cssKeywordToColorMapping[ cssKeyword ] ||
									'';
								return requestedColors.some(
									( requestedColor ) =>
										colorName.includes( requestedColor ) ||
										cssKeyword.includes( requestedColor ) ||
										colorVariation.title === requestedColor
								);
							}
						);
					// check if the requested keyword matches
					const hasRequestedKeyword =
						colorVariation[ 'x-big-sky-meta' ]?.keywords?.includes(
							keyword
						);

					return hasRequestedColor || hasRequestedKeyword;
				}
			);
		}
		// check if the current color is one of the suggested variations
		const currentColorIsSuggested = suggestedVariations.find(
			( colorVariation ) => colorVariation.title === currentColor
		);

		// order suggested variations by relevance descending
		let suggestedVariationsSorted = suggestedVariations.sort( ( a, b ) => {
			if ( a.relevance > b.relevance ) {
				return -1;
			}
			if ( a.relevance < b.relevance ) {
				return 1;
			}
			return 0;
		} );

		// put suggested color palettes first
		let colorVariationsSorted = suggestedVariationsSorted.concat(
			colorVariations.filter(
				( colorVariation ) =>
					! suggestedVariationsSorted.includes( colorVariation )
			)
		);

		if ( colorVariationsSorted.length === 0 ) {
			colorVariationsSorted = colorVariations;
		}

		let customPalettes = [];
		if ( custom_colors ) {
			customPalettes = createCustomSchemeVariations( {
				custom_colors,
			} );
			colorVariationsSorted = [
				...customPalettes,
				...colorVariationsSorted,
			];
			suggestedVariationsSorted = [
				...customPalettes,
				...suggestedVariationsSorted,
			];
			// We have to update the global styles with the custom color scheme.
		}

		const styleChanges = collectBlockColorAttributes();
		// Set the last cancelable action to allow the user to undo the color selection
		setLastCancelableAction( {
			action: 'color_palette_generator',
			originalVariationName:
				currentTheme.title.raw ||
				currentTheme.title ||
				BIG_SKY_CUSTOM_THEME_PREFIX,
			paletteColors,
			customColors: styleChanges,
		} );

		callback(
			<ColorVariationPicker
				colorVariations={ colorVariationsSorted }
				numSuggestions={ suggestedVariationsSorted.length }
				currentVariationIsSuggested={ currentColorIsSuggested }
				maxColorsToShow={ 6 }
				key="color-picker"
			/>,
			__( 'Which of these palettes would you like?', 'big-sky' )
		);
	};

	const cancelGenerateColorPalette = ( lastCancelableAction ) => {
		setStyles( {
			color: lastCancelableAction.originalVariationName,
		} );
		reapplyBlockColors( lastCancelableAction.customColors );
		setDockView( dockViews.MENUS );
		setIsToolContainerVisible( false );
		zoomIn();
	};

	const doneGenerateColorPalette = () => {
		saveStylesEntityRecord();
		setSiteMetadata( {}, true ); // persist the current site metadata
		setDockView( dockViews.MENUS );
		setIsToolContainerVisible( false );
		zoomIn();
	};

	const validateUpdateComponentColorArgs = ( {
		background_color,
		text_color,
		button_color,
		button_text_color,
		scope,
		clientId,
	} ) => {
		if ( ! clientId && scope !== 'global' ) {
			throw new Error(
				__(
					'Select the block you wish to update and try again.',
					'big-sky'
				)
			);
		} else if ( clientId && scope !== 'global' ) {
			// check if the selected block is supported
			const selectedBlock = getBlock( clientId );

			if (
				! [ 'jetpack/button', 'jetpack/subscriptions' ].includes(
					selectedBlock.name
				)
			) {
				const supports = getBlockSupport( selectedBlock.name, 'color' );
				const supportsTextColor =
					supports?.text ||
					supports?.__experimentalDefaultControls?.text ||
					supports?.defaultControls?.text;
				const supportsBackgroundColor =
					supports?.background ||
					supports?.__experimentalDefaultControls?.background ||
					supports?.defaultControls?.background;
				if (
					( text_color || button_text_color ) &&
					! supportsTextColor
				) {
					throw new Error(
						__(
							'The selected block does not support text color. Please select a different block and try again.',
							'big-sky'
						)
					);
				}
				if (
					( background_color || button_color ) &&
					! supportsBackgroundColor
				) {
					throw new Error(
						__(
							'The selected block does not support background color. Please select a different block and try again.',
							'big-sky'
						)
					);
				}
			}
		}
		return true;
	};

	const updateComponentColor = async ( {
		background_color,
		text_color,
		button_color,
		button_text_color,
		scope,
		clientId,
	} ) => {
		const updatedComponents = [];
		const currentColor = getCurrentColor() || currentColorMeta;

		// get a copy of the current theme to avoid mutating the original theme
		const currentTheme = JSON.parse(
			JSON.stringify(
				getColorVariationByTitle( currentColor ) || getCurrentTheme()
			)
		);

		// if the colors don't already exist in the current palette, add them
		if ( background_color ) {
			background_color = addColorToTheme( {
				theme: currentTheme,
				color: background_color,
			} );
			updatedComponents.push( 'background color' );
		}
		if ( text_color ) {
			text_color = addColorToTheme( {
				theme: currentTheme,
				color: text_color,
			} );
			updatedComponents.push( 'text color' );
		}
		if ( button_color ) {
			button_color = addColorToTheme( {
				theme: currentTheme,
				color: button_color,
			} );
			updatedComponents.push( 'button color' );
		}
		if ( button_text_color ) {
			button_text_color = addColorToTheme( {
				theme: currentTheme,
				color: button_text_color,
			} );
			updatedComponents.push( 'button text color' );
		}

		// make sure the variations are copied over from the current theme
		currentTheme.styles.variations =
			currentTheme.styles.blocks[ 'core/group' ].variations;

		registry.batch( () => {
			switch ( scope ) {
				case 'global':
					// apply colors to all root blocks
					const rootBlocks = [
						...getRootBlocksOfPostContent(),
						...getBlocks( getTemplatePartClientID( 'header' ) ),
						...getBlocks( getTemplatePartClientID( 'footer' ) ),
					];
					updateRootBlocksStyles( {
						rootBlocks,
						background_color,
						text_color,
						button_color,
						button_text_color,
					} );
					break;
				case 'section':
					// apply background color and text color to the root block of the selected block
					const rootBlock = getParentRootPatternBlock( clientId );
					if ( rootBlock ) {
						updateRootBlocksStyles( {
							rootBlocks: [ rootBlock ],
							background_color,
							text_color,
							button_color,
							button_text_color,
						} );
					}
					break;
				case 'block':
					// apply changes to the selected block and its children (thoeretically not much)
					const selectedBlock = getBlock( clientId );
					if ( selectedBlock ) {
						updateRootBlocksStyles( {
							rootBlocks: [ selectedBlock ],
							background_color,
							text_color,
							button_color,
							button_text_color,
						} );
					}
					break;
			}

			currentTheme.title = BIG_SKY_CUSTOM_THEME_PREFIX;
			pushGeneratedVariations( [ currentTheme ] );
			setStyles( {
				color: currentTheme.title,
				updateBlocks: false,
			} );
		} );

		const closingMessages = [
			__( 'Let me know if you need me to do anything else!', 'big-sky' ),
			__( 'Do you wish to change any other colors?', 'big-sky' ),
			__( "Is there anything else you'd like me to adjust?", 'big-sky' ),
		];
		let contextSuffix = '';
		if ( scope === 'global' ) {
			contextSuffix = __( 'for your site', 'big-sky' );
		} else if ( scope === 'section' ) {
			contextSuffix = __( 'in this section', 'big-sky' );
		} else if ( scope === 'block' ) {
			contextSuffix = __( 'of the selected block', 'big-sky' );
		}
		const message = sprintf(
			/* translators: 1: list of updated color components, 2: context (for your site/in this section) */
			__( "I've updated the %1$s %2$s.", 'big-sky' ),
			updatedComponents.length > 1
				? updatedComponents.slice( 0, -1 ).join( ', ' ) +
						__( ' and ', 'big-sky' ) +
						updatedComponents.slice( -1 )
				: updatedComponents[ 0 ],
			contextSuffix
		);
		/* eslint-disable no-restricted-syntax */
		const randomClosing =
			closingMessages[
				Math.floor( Math.random() * closingMessages.length )
			];
		/* eslint-enable no-restricted-syntax */

		return `${ message } ${ randomClosing }`;
	};

	const collectBlockColorAttributes = () => {
		const blocks = [
			...getRootBlocksOfPostContent(),
			...getBlocks( getTemplatePartClientID( 'header' ) ),
			...getBlocks( getTemplatePartClientID( 'footer' ) ),
		];
		const colorAttributes = [];

		const processBlock = ( block ) => {
			const attributes = getBlockAttributes( block.clientId );

			// Check for direct color attributes
			if ( attributes?.backgroundColor ) {
				colorAttributes.push( {
					clientId: block.clientId,
					key: 'backgroundColor',
					value: attributes.backgroundColor,
				} );
			}

			if ( attributes?.textColor ) {
				colorAttributes.push( {
					clientId: block.clientId,
					key: 'textColor',
					value: attributes.textColor,
				} );
			}

			// Check for style color attributes
			if ( attributes?.style?.color?.text ) {
				colorAttributes.push( {
					clientId: block.clientId,
					key: 'styleText',
					value: attributes.style.color.text,
				} );
			}

			if ( attributes?.style?.color?.background ) {
				colorAttributes.push( {
					clientId: block.clientId,
					key: 'styleBackground',
					value: attributes.style.color.background,
				} );
			}

			// Recursively check inner blocks
			if ( block.innerBlocks?.length > 0 ) {
				block.innerBlocks.forEach( ( innerBlock ) => {
					processBlock( innerBlock );
				} );
			}
		};

		// Process each block in the input array
		blocks.forEach( ( block ) => {
			processBlock( block );
		} );

		return colorAttributes;
	};

	const reapplyBlockColors = ( colorAttributes ) => {
		registry.batch( () => {
			colorAttributes.forEach( ( { clientId, key, value } ) => {
				const attributes = getBlockAttributes( clientId );
				const updatedAttributes = { ...attributes };

				switch ( key ) {
					case 'backgroundColor':
					case 'textColor':
						updatedAttributes[ key ] = value;
						break;
					case 'styleText':
					case 'styleBackground':
						if ( ! updatedAttributes.style )
							updatedAttributes.style = {};
						if ( ! updatedAttributes.style.color )
							updatedAttributes.style.color = {};

						if ( key === 'styleText' ) {
							updatedAttributes.style.color.text = value;
						} else {
							updatedAttributes.style.color.background = value;
						}
						break;
				}

				updateBlockAttributes( clientId, updatedAttributes );
			} );
		} );
	};

	return {
		validateGenerateColorPaletteArgs,
		generateColorPalette,
		cancelGenerateColorPalette,
		doneGenerateColorPalette,
		validateUpdateComponentColorArgs,
		updateComponentColor,
		collectBlockColorAttributes,
		reapplyBlockColors,
	};
}
