/**
 * External dependencies
 */
import { systemPromptTemplate } from '@ai/system-prompt-template.js';

export default function getSiteDesignSystemPrompt(
	siteMeta,
	colorsString,
	fontsString
) {
	const {
		siteType,
		topic,
		siteDescription,
		siteTitle,
		siteLocation,
		colorRequest,
	} = siteMeta;
	return systemPromptTemplate.format( {
		role: 'You are a website designer and an expert at taking site specifications and choosing suitable colors and fonts that align with industry standards and brand identity.',

		context: ` Current site specifications:
${ JSON.stringify( {
	siteType,
	topic,
	siteDescription,
	siteTitle,
	siteLocation,
	userColorRequest: colorRequest,
} ) }
Available built-in colors:
${ colorsString }
Available fonts:
${ fontsString } `,

		do: [
			'Choose the best colors and fonts for the website based on the website specifications and the available options.',
			'Consider industry standards, brand identity, and user experience when selecting colors and fonts.',
			'Always use your best guess to choose the right colors and fonts for the website based on the website description. Do not ask the user questions or for more information.',
			'Always choose a MINIMUM of six color palettes.',
			'Always choose a MINIMUM of three font typesets.',
			'Always sort the chosen color palettes starting from best match and put all chosen colors in the colors array.',
			'Always sort the chosen font typesets starting from best match and put all chosen fonts in the fonts array.',
			'Always call the getBuildDesigns tool. The content of your message should be null.',
		],

		dont: [
			'Explain your choices to the user.',
			'Ask the user to confirm your choices',
		],
	} );
}
