/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { useEffect, useRef } from '@wordpress/element';
import {
	__unstableAnimatePresence as AnimatePresence,
	__unstableMotion as motion,
} from '@wordpress/components';

/**
 * Internal dependencies
 */
import ChatInput from '@components/chat/input';
import BigSkyIcon from '@components/big-sky-icon';
import ChatThread from '@components/chat/thread';
import ChatSuggestions from '@ai/assistants/shared/make-suggestions';
import ChatComponents from '@components/chat/components';

export default function OnboardingChatView( { onSendMessage } ) {
	const inputRef = useRef( null );
	const chatInputRef = useRef( null );

	useEffect( () => {
		// eslint-disable-next-line no-undef
		const resizeObserver = new ResizeObserver( ( entries ) => {
			for ( const entry of entries ) {
				if ( entry.target === chatInputRef.current ) {
					document.documentElement.style.setProperty(
						'--chat-input-height',
						`${ entry.contentRect.height }px`
					);
				}
			}
		} );

		if ( chatInputRef.current ) {
			resizeObserver.observe( chatInputRef.current );
		}

		return () => {
			resizeObserver.disconnect();
		};
	}, [] );

	return (
		<div className="big-sky-onboarding__chat-view">
			<motion.div className="big-sky-onboarding__assistant-button">
				<BigSkyIcon width={ 100 } height={ 100 } color="white" />
			</motion.div>

			<motion.div className="big-sky-onboarding__chat-thread">
				<ChatThread />
			</motion.div>

			<motion.div
				className="big-sky-onboarding__chat-input"
				ref={ chatInputRef }
			>
				<motion.div className="big-sky-onboarding__actions">
					<AnimatePresence mode="wait">
						<div>
							<ChatComponents key="chat-components-actions" />
							<ChatSuggestions key="chat-suggestions-actions" />
						</div>
					</AnimatePresence>
				</motion.div>

				<ChatInput
					ref={ inputRef }
					prompt={ __( 'Send a message', 'big-sky' ) }
					onSendMessage={ onSendMessage }
				/>
			</motion.div>
		</div>
	);
}
