/**
 * WordPress dependencies
 */
import {
	__experimentalGrid as Grid,
	__unstableMotion as motion,
	__experimentalVStack as VStack,
} from '@wordpress/components';
import { memo, useEffect, useMemo, useRef, useState } from '@wordpress/element';
import useScrollButtons from '@hooks/use-scroll-buttons';
/**
 * Internal dependencies
 */
import ItemPreview from '@components/item-preview';
import { getCanvasIframeElements } from '@utils/canvas-iframe-elements';
import { AskAI } from '@components/icons';
import { recordTracksEvent } from '@utils/tracks';

const ItemPicker = ( {
	items,
	clientId,
	generateNewItem,
	initialRenderedItems = {},
} ) => {
	const pickerRef = useRef();
	const [ allItemsRendered, setAllItemsRendered ] = useState( false );
	const [ renderedItems, setRenderedItems ] =
		useState( initialRenderedItems );

	const getAdditionalStyles = useMemo( () => {
		const { canvasiFrameRoot } = getCanvasIframeElements();
		const canvasiFrameStyle = window.getComputedStyle( canvasiFrameRoot );

		const stylePropertiesToCopy = [
			'--site-logo-url',
			'--site-logo-object-position',
		];

		const styleProperties = stylePropertiesToCopy.map(
			( styleProperty ) =>
				`${ styleProperty }: ${
					canvasiFrameStyle.getPropertyValue( styleProperty ) || ' '
				};`
		);

		return [
			{
				css: `html { ${ styleProperties.join( ' ' ) } }`,
			},
		];
	}, [] );

	const categories = new Set(
		items.map( ( item ) => item?.patterns?.[ 0 ]?.category )
	);
	let columns = 3;
	let className = 'big-sky__layout-variations';
	let viewportWidthMax = 1440;
	if ( categories.size === 1 ) {
		const category = [ ...categories.values() ][ 0 ];
		switch ( category ) {
			case 'header':
			case 'footer':
				// Widen the preview by switching from 3 to two columns.
				columns = 2;
				className += ' big-sky__layout-variations-wider';
				// "Zoom in" more by making the iframe's viewport narrower.
				viewportWidthMax = 'header' === category ? 480 : 1000;
				break;
		}
	}

	const { leftScrollButton, rightScrollButton, checkScrollButtons } =
		useScrollButtons( pickerRef, '.components-grid' );

	useEffect( () => {
		checkScrollButtons();
	}, [ items, checkScrollButtons ] );

	useEffect( () => {
		const allRendered = items.every( ( item ) => renderedItems[ item.id ] );
		setAllItemsRendered( allRendered );
	}, [ items, renderedItems ] );

	const handleItemRendered = ( itemId ) => {
		setRenderedItems( ( prev ) => ( { ...prev, [ itemId ]: true } ) );
	};

	const generateNewItemClickHandler = () => {
		recordTracksEvent( 'item_picker_generate_new_item_click' );
		generateNewItem();
	};

	return (
		<div ref={ pickerRef } className="big-sky__item-picker">
			{ leftScrollButton }

			<VStack spacing={ 3 }>
				<Grid columns={ columns } className={ className }>
					{ items && (
						<>
							{ items
								.filter(
									( item ) =>
										! item.isPlaceholder &&
										item.patterns?.length > 0
								)
								.map( ( item, index ) => {
									return (
										<ItemPreview
											key={ item.id }
											index={ index }
											item={ item }
											additionalStyles={
												getAdditionalStyles
											}
											viewportWidthMax={
												viewportWidthMax
											}
											clientId={ clientId }
											onRendered={ () =>
												handleItemRendered( item.id )
											}
										/>
									);
								} ) }
						</>
					) }
					{ generateNewItem && allItemsRendered && (
						<motion.button
							initial={ {
								opacity: 0.8,
							} }
							whileHover={ {
								opacity: 1,
								scale: 1.05,
								boxShadow: '0px 2px 4px rgba(0,0,0,.05)',
							} }
							className={ 'big-sky__item-preview' }
							onClick={ generateNewItemClickHandler }
						>
							<AskAI />
						</motion.button>
					) }
				</Grid>
			</VStack>
			{ rightScrollButton }
		</div>
	);
};

export default memo( ItemPicker );
