/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { Button } from '@wordpress/components';
import { chevronDown, Icon } from '@wordpress/icons';

export default function TextDockMenus( { onAssistantMenuClick } ) {
	const assistantMenuTitle = __( 'Rewrite', 'big-sky' );
	const assistantMenuSlug = 'rewrite';

	return (
		<>
			<Button
				className="has-chevron"
				onClick={ () =>
					onAssistantMenuClick(
						assistantMenuTitle,
						assistantMenuSlug
					)
				}
			>
				{ assistantMenuTitle }
				<Icon icon={ chevronDown } />
			</Button>
		</>
	);
}
