/**
 * Internal dependencies
 */
import usePatternRenderer from '@ai/assistants/pattern/tools/generate-pattern/hooks/use-pattern-renderer';

/**
 * Custom hook for parsing streaming pattern generation responses.
 *
 * Provides functions for extracting and processing phases from content streams
 * and parsing Server-Sent Events chunks.
 */
export default function useStreamParser() {
	const { processPhase } = usePatternRenderer();

	/**
	 * Extracts and processes a phase from the content stream.
	 *
	 * Validates the phase content structure and processes it if valid.
	 * Logs warnings for any validation issues encountered.
	 *
	 * @param {string} content     The full content stream.
	 * @param {number} phaseNumber The phase number to extract.
	 */
	const extractPhase = ( content, phaseNumber ) => {
		try {
			// Extract phase content
			const extraction = extractPhaseContent( content, phaseNumber );
			if ( ! extraction.success ) return null;

			// Validate block structure
			const blockValidation = validateBlockStructure(
				extraction.content
			);
			if ( ! blockValidation.isValid ) {
				console.warn(
					`Phase ${ phaseNumber } ${ blockValidation.message }`
				);
				return null;
			}

			// Validate container structure
			const containerValidation = validateContainerStructure(
				extraction.content,
				content
			);
			if ( ! containerValidation.isValid ) {
				containerValidation.warnings.forEach( ( warning ) =>
					console.warn( `Phase ${ phaseNumber } ${ warning }` )
				);
			}

			// Process the phase content
			processPhase( {
				content: extraction.content,
				phaseNumber,
			} );
		} catch ( error ) {
			console.error( `Error extracting phase ${ phaseNumber }:`, error );
			return null;
		}
	};

	/**
	 * Parses a Server-Sent Events chunk into text content.
	 *
	 * Processes SSE chunks containing content block deltas and extracts
	 * the text content. Handles JSON parsing errors gracefully.
	 *
	 * @param {string} chunk The SSE chunk to parse.
	 * @return {string}     The extracted text content.
	 */
	const parseSSEChunk = ( chunk ) => {
		try {
			const lines = chunk.split( '\n' );
			let text = '';

			for ( const line of lines ) {
				if ( line.startsWith( 'data: ' ) ) {
					try {
						const data = JSON.parse( line.slice( 6 ) );
						if (
							data.type === 'content_block_delta' &&
							data.delta?.type === 'text_delta'
						) {
							text += data.delta.text;
						}
					} catch ( e ) {
						console.warn( 'Invalid JSON in SSE chunk:', e );
					}
				}
			}

			return text;
		} catch ( error ) {
			console.error( 'Error parsing SSE chunk:', error );
			return '';
		}
	};

	/**
	 * Validates WordPress block structure in content.
	 *
	 * Checks if the content is not empty and contains valid WordPress block markers.
	 *
	 * @param {string} content The content to validate.
	 * @return {Object}       Validation result with status and message.
	 */
	const validateBlockStructure = ( content ) => {
		if ( ! content ) {
			return { isValid: false, message: 'Content is empty' };
		}

		if ( ! content.includes( '<!-- wp:' ) ) {
			return {
				isValid: false,
				message: 'Content may be malformed: no block markers found',
			};
		}

		return { isValid: true };
	};

	/**
	 * Validates container references and IDs.
	 *
	 * Ensures all container references have corresponding container IDs
	 * either in the phase content or the full content stream.
	 *
	 * @param {string} phaseContent The phase content to validate.
	 * @param {string} fullContent  The full content for cross-referencing.
	 * @return {Object}            Validation result with status and warnings.
	 */
	const validateContainerStructure = ( phaseContent, fullContent ) => {
		const containerIds = [
			...phaseContent.matchAll( /<!-- CONTAINER_ID:([^\s]+)/g ),
		].map( ( match ) => match[ 1 ] );
		const containerRefs = [
			...phaseContent.matchAll( /<!-- CONTAINER_REF:([^\s]+)/g ),
		].map( ( match ) => match[ 1 ] );

		const warnings = [];
		for ( const ref of containerRefs ) {
			if (
				! containerIds.includes( ref ) &&
				! fullContent.includes( `<!-- CONTAINER_ID:${ ref }` )
			) {
				warnings.push( `References undefined container: ${ ref }` );
			}
		}

		return { isValid: warnings.length === 0, warnings };
	};

	/**
	 * Extracts phase content from markers.
	 *
	 * Finds and extracts content between phase markers in the stream.
	 * Returns a success status and the extracted content if found.
	 *
	 * @param {string} content     The content to extract from.
	 * @param {number} phaseNumber The phase number to extract.
	 * @return {Object}           Extraction result with success status and content.
	 */
	const extractPhaseContent = ( content, phaseNumber ) => {
		const startMarker = `/begin-phase-${ phaseNumber }`;
		const endMarker = `/end-phase-${ phaseNumber }`;

		const startIndex = content.indexOf( startMarker );
		if ( startIndex === -1 ) return { success: false };

		const endIndex = content.indexOf( endMarker, startIndex );
		if ( endIndex === -1 ) return { success: false };

		return {
			success: true,
			content: content
				.substring( startIndex + startMarker.length, endIndex )
				.trim(),
		};
	};

	return {
		extractPhase,
		parseSSEChunk,
	};
}
