/**
 * Internal dependencies
 */
import { ASSISTANTS } from '@ai/assistants/constants.js';

export const languagePrompt =
	typeof window !== 'undefined' && window.bigSkyInitialState?.userLanguage
		? window.bigSkyInitialState.userLanguage
		: 'English (United States)';

export const analysisPrompt = `IMPORTANT: The content between <request_analysis> tags is strictly for internal model reasoning only. This is a hard requirement: Never include or reference any content from inside <request_analysis> tags in your response to the user.
Conduct your analysis inside <request_analysis> tags.`;

export const getSiteContextPrompt = ( context, fields ) => {
	const metadata = context?.site || {};
	const prompt = [];

	const shouldIncludeField = ( field ) =>
		! fields || fields.includes( field );

	if ( metadata.siteTitle && shouldIncludeField( 'siteTitle' ) ) {
		prompt.push( ` - Site Title: ${ metadata.siteTitle }` );
	}

	if ( metadata.siteDescription && shouldIncludeField( 'siteDescription' ) ) {
		prompt.push( ` - Site Description: ${ metadata.siteDescription }` );
	}

	if ( metadata.siteLocation?.name && shouldIncludeField( 'siteLocation' ) ) {
		prompt.push( ` - Site Location: ${ metadata.siteLocation.name }` );
	}

	if ( metadata.topic && shouldIncludeField( 'topic' ) ) {
		prompt.push( ` - Site Topic: ${ metadata.topic }` );
	}

	return prompt.join( '\n' );
};

export const getSelectedBlockContextPrompt = ( context, fields ) => {
	const selectedBlock = context?.selectedBlock;

	if ( ! selectedBlock?.clientId ) {
		return 'No selected block';
	}

	const prompt = [];

	const shouldIncludeField = ( field ) =>
		! fields || fields.includes( field );

	if ( selectedBlock?.clientId && shouldIncludeField( 'clientId' ) ) {
		prompt.push( ` - clientId: ${ selectedBlock.clientId }` );
	}

	if ( shouldIncludeField( 'type' ) ) {
		// Some blocks use name, some use type. If the user is asking for type and the block
		// only uses name, show the name instead.
		if ( selectedBlock?.type ) {
			prompt.push( ` - type: ${ selectedBlock.type }` );
		} else if ( selectedBlock?.name ) {
			prompt.push( ` - name: ${ selectedBlock.name }` );
		}
	}

	// Get the selected block content
	if ( selectedBlock?.content && shouldIncludeField( 'content' ) ) {
		prompt.push( ` - content: ${ selectedBlock.content }` );
	}

	return prompt.join( '\n' );
};

export const getReturnToRouterPrompt = ( context ) => {
	if ( ! isForwardedByRouter( context ) ) {
		return ` - For any other requests, immediately call 'change_assistant' with the toAssistantId '${ ASSISTANTS.ROUTER }'.`;
	}
	return '';
};

export const isForwardedByRouter = ( context ) => {
	const fromAssistantId = context?.fromAssistantId;
	if ( fromAssistantId === ASSISTANTS.ROUTER ) {
		return true;
	}
	return false;
};

export const helpAssistantPrompt = `If the input contains a question mark or starts with question words (who, what, where, when, why, how, can, could, should, would, will, do, does, did, is, are, was, were), immediately call 'change_assistant' with the toAssistantId '${ ASSISTANTS.HELP }'.`;
export const helpActionPrompt = `Call change_assistant tool with the toAssistantId '${ ASSISTANTS.HELP }'`;

export const routerAssistantPrompt = `For any other requests, immediately call 'change_assistant' with the toAssistantId '${ ASSISTANTS.ROUTER }'.`;
export const routerActionPrompt = `Call change_assistant tool with the toAssistantId '${ ASSISTANTS.ROUTER }'`;

export const invalidRequestPrompt = `If the user input is not related to WordPress, WordPress.com, WordPress hosting, website building or website design changes, let the user know that you are not able to help with that, remind them what you can help with and wait for further input`;

export const dontReturnToRouterPrompt = `If the request has been forwarded by '${ ASSISTANTS.ROUTER }', never return to '${ ASSISTANTS.ROUTER }'. In this case if you are not able to handle the request always call 'change_assistant' with the toAssistantId '${ ASSISTANTS.HELP }'`;
