/**
 * WordPress dependencies
 */
import { store as blockEditorStore } from '@wordpress/block-editor';
import { createBlock } from '@wordpress/blocks';
import { subscribe, useDispatch, useSelect } from '@wordpress/data';
import { useCallback } from '@wordpress/element';
import { privateApis as routerPrivateApis } from '@wordpress/router';

/**
 * Internal dependencies
 */
import useBlocks from '@hooks/use-blocks';
import { unlock } from '@utils/lock-unlock';
import { store as aiStore } from '@store';
import setDelay from '@utils/set-delay';
import { getCanvasIframeElements } from '@utils/canvas-iframe-elements';

export default function useCanvas() {
	const DUMMY_SECTION_CLASSNAMES = 'big-sky__dummy-section alignfull';

	const { getBlocks, getBlocksByName } = useSelect( blockEditorStore );
	const { setZoomLevel, resetZoomLevel } = unlock(
		useDispatch( blockEditorStore )
	);

	const {
		__unstableSetEditorMode,
		replaceInnerBlocks,
		updateBlockAttributes,
		replaceBlock,
		__unstableMarkNextChangeAsNotPersistent,
	} = useDispatch( blockEditorStore );

	const { delaySettings, isEditorIframeLoaded } = useSelect( ( select ) => ( {
		delaySettings: select( aiStore ).getDelaySettings(),
		isEditorIframeLoaded: select( aiStore ).getIsEditorIframeLoaded(),
	} ) );

	const { getTemplatePartClientID, getContentBlockClientID, scrollToBlock } =
		useBlocks();

	const { useLocation } = unlock( routerPrivateApis );
	const { params, query } = useLocation();
	let canvas = 'view';
	if ( query?.canvas ) {
		canvas = query.canvas;
	} else if ( params?.canvas ) {
		canvas = params.canvas;
	}

	const addPatternsToCanvas = async (
		patterns,
		replaceTemplateParts = true,
		isQuickOnboarding = false
	) => {
		// Adjust the number of dummy sections to match the number of patterns incoming.
		await insertDummySections( {
			count: patterns.length,
			replaceTemplateParts,
		} );
		const { headerPattern, bodyPatterns, footerPattern } =
			splitPatterns( patterns );

		// Wait on each pattern resolver in order, then display the
		// final patterns one by on as the resolve.
		await setDelay( delaySettings.onboarding.afterBlockProcessed );

		// Use each of the dummy sections on the canvas as a base for creating
		// a 'skeleton' of the incoming pattern. The skeleton is the structure
		// of the pattern minus images/colors and readable text.
		if ( headerPattern ) addPatternSkeleton( headerPattern.pattern, 0 );
		if ( footerPattern ) addPatternSkeleton( footerPattern.pattern, 0 );

		for ( let i = 0; i < bodyPatterns.length; i++ ) {
			addPatternSkeleton( bodyPatterns[ i ].pattern, i );
		}

		if ( headerPattern ) {
			await headerPattern.resolver;
			await addPatternToCanvas(
				headerPattern.pattern,
				0,
				isQuickOnboarding
			);
		}

		for ( let i = 0; i < bodyPatterns.length; i++ ) {
			const { pattern, resolver } = bodyPatterns[ i ];

			await resolver;

			// This delay is here to make sure there is always space
			// between each section sliding in. We don't want more than
			// one section sliding in at a time.
			if ( ! isQuickOnboarding ) {
				await setDelay( delaySettings.onboarding.afterBlockProcessed );
			}

			await addPatternToCanvas( pattern, i, isQuickOnboarding );
		}

		if ( footerPattern ) {
			await footerPattern.resolver;

			if ( ! isQuickOnboarding ) {
				await setDelay( delaySettings.onboarding.afterBlockProcessed );
			}

			await addPatternToCanvas(
				footerPattern.pattern,
				0,
				isQuickOnboarding
			);
		}

		await setDelay( delaySettings.onboarding.afterBlockProcessed );
		scrollToTop();
	};

	const splitPatterns = ( patterns ) => {
		const headerPattern = patterns.find(
			( p ) => p.pattern.category === 'header'
		);
		const footerPattern = patterns.find(
			( p ) => p.pattern.category === 'footer'
		);
		const bodyPatterns = patterns.filter(
			( p ) =>
				p.pattern.category !== 'header' &&
				p.pattern.category !== 'footer'
		);

		return { headerPattern, bodyPatterns, footerPattern };
	};

	const addPatternSkeleton = ( pattern, index ) => {
		const rootPatternBlock = pattern.blocks[ 0 ];
		const containerClientID = getContainerClientID( pattern );

		const dummySectionClientIds = getBlocks( containerClientID ).map(
			( block ) => block.clientId
		);

		if ( dummySectionClientIds[ index ] ) {
			addBlocksToDummySection(
				rootPatternBlock,
				dummySectionClientIds[ index ]
			);
		}
	};

	const addPatternToCanvas = async ( pattern, index, isQuickOnboarding ) => {
		const rootPatternBlock = pattern.blocks[ 0 ];
		const containerClientID = getContainerClientID( pattern );

		const dummySectionClientIds = getBlocks( containerClientID ).map(
			( block ) => block.clientId
		);

		// Update classname and support for background color.
		rootPatternBlock.attributes.className += ' big-sky__slide-in';
		rootPatternBlock.supports = {
			color: { background: true },
			...rootPatternBlock.supports,
		};

		if ( ! isQuickOnboarding ) {
			// Start scrolling to the right location before we insert the block
			scrollToBlock( dummySectionClientIds[ index ], 'center' );

			// Let the scroll get there before inserting.
			await setDelay( 500 );
		}

		// Replace the dummy pattern with the final pattern
		if ( pattern.category === 'header' || pattern.category === 'footer' ) {
			__unstableMarkNextChangeAsNotPersistent();
			replaceInnerBlocks( containerClientID, [ rootPatternBlock ] );
		} else {
			__unstableMarkNextChangeAsNotPersistent();
			replaceBlock( dummySectionClientIds[ index ], rootPatternBlock );
		}

		if ( ! isQuickOnboarding ) {
			await setDelay( 750 );
		}

		updateBlockAttributes( rootPatternBlock.clientId, {
			className: rootPatternBlock.attributes.className.replace(
				' big-sky__slide-in',
				''
			),
		} );
	};

	const setIframeScale = useCallback( () => {
		if ( ! isEditorIframeLoaded || canvas === 'view' ) return;

		const { canvasiFrameRoot } = getCanvasIframeElements();
		let windowWidth = window.innerWidth;

		if ( windowWidth > 1850 ) {
			windowWidth = 1850;
		} else if ( windowWidth < 980 ) {
			windowWidth = 980;
		}

		// set css vars needed
		if ( canvasiFrameRoot ) {
			const selectedBlockScale = -0.00001 * windowWidth + 1.035;
			canvasiFrameRoot.style.setProperty(
				'--block-editor-selected-block-scale',
				selectedBlockScale
			);
		}
	}, [ canvas, isEditorIframeLoaded ] );

	const clearBlocks = () => {
		return new Promise( ( resolve ) => {
			let unsubscribe = () => {}; // Initialize with a no-op function
			const checkAndClearBlocks = () => {
				const contentBlocks = getBlocks( getContentBlockClientID() );
				if ( contentBlocks.length > 0 ) {
					unsubscribe(); // Now safe to call
					__unstableMarkNextChangeAsNotPersistent();
					replaceInnerBlocks( getContentBlockClientID(), [
						createBlock( 'core/paragraph', {
							placeholder: ' ',
						} ),
					] );
					resolve();
				}
			};
			unsubscribe = subscribe( checkAndClearBlocks );
			checkAndClearBlocks(); // Check immediately in case blocks already exist
		} );
	};

	const clearTemplateParts = () => {
		return new Promise( ( resolve ) => {
			let unsubscribe = () => {}; // Initialize with a no-op function
			const checkAndClearTemplateParts = () => {
				const templatePartClientIds =
					getBlocksByName( 'core/template-part' );
				if ( templatePartClientIds.length > 0 ) {
					unsubscribe(); // Now safe to call
					for ( const clientId of templatePartClientIds ) {
						__unstableMarkNextChangeAsNotPersistent();
						replaceInnerBlocks( clientId, [
							createBlock( 'core/paragraph', {
								placeholder: ' ',
							} ),
						] );
					}
					resolve();
				}
			};
			unsubscribe = subscribe( checkAndClearTemplateParts );
			checkAndClearTemplateParts(); // Check immediately in case blocks already exist
		} );
	};

	const createDummySection = ( { isEmpty = true, className = '' } = {} ) => {
		return createBlock(
			'core/group',
			{
				className: `${ DUMMY_SECTION_CLASSNAMES } ${
					isEmpty ? 'big-sky__is-empty-dummy' : ''
				} ${ className }`,
			},
			[
				createBlock( 'core/paragraph', {
					placeholder: ' ',
				} ),
			]
		);
	};

	const insertDummySections = async ( {
		count,
		replaceTemplateParts = true,
	} ) => {
		if ( count === 0 ) return;

		// Create dummy sections based on the wireframe section count
		const dummySections = Array.from( {
			length: count,
		} ).map( createDummySection );

		const contentBlockClientID = getContentBlockClientID();
		const headerTemplatePartClientID = getTemplatePartClientID( 'header' );
		const footerTemplatePartClientID = getTemplatePartClientID( 'footer' );

		if ( replaceTemplateParts && headerTemplatePartClientID ) {
			const currentHeaderBlocks = getBlocks( headerTemplatePartClientID );
			if (
				! currentHeaderBlocks?.[ 0 ]?.innerBlocks?.[ 0 ]?.attributes
					?.metadata?.remotePatternId
			) {
				__unstableMarkNextChangeAsNotPersistent();
				replaceInnerBlocks( headerTemplatePartClientID, [
					dummySections.shift(),
				] );
			}
		}

		if ( replaceTemplateParts && footerTemplatePartClientID ) {
			const currentFooterBlocks = getBlocks( footerTemplatePartClientID );
			if (
				! currentFooterBlocks?.[ 0 ]?.innerBlocks?.[ 0 ]?.attributes
					?.metadata?.remotePatternId
			) {
				__unstableMarkNextChangeAsNotPersistent();
				replaceInnerBlocks( footerTemplatePartClientID, [
					dummySections.shift(),
				] );
			}
		}

		const currentContentBlocks = getBlocks( contentBlockClientID );
		if (
			! currentContentBlocks?.[ 0 ]?.innerBlocks?.[ 0 ]?.attributes
				?.metadata?.remotePatternId
		) {
			__unstableMarkNextChangeAsNotPersistent();
			replaceInnerBlocks( contentBlockClientID, dummySections );
		}
	};

	const addBlocksToDummySection = (
		rootPatternBlock,
		dummySectionClientId
	) => {
		__unstableMarkNextChangeAsNotPersistent();
		replaceInnerBlocks( dummySectionClientId, [ rootPatternBlock ] );

		// Remove the background color style form the rootPattern block in the skeleton
		__unstableMarkNextChangeAsNotPersistent();
		updateBlockAttributes( rootPatternBlock.clientId, {
			style: {
				...rootPatternBlock.attributes.style,
				color: {
					background: undefined,
				},
			},
		} );
		__unstableMarkNextChangeAsNotPersistent();
		updateBlockAttributes( dummySectionClientId, {
			className: `${ DUMMY_SECTION_CLASSNAMES } big-sky__has-dummy-blocks `,
		} );
	};

	const getContainerClientID = ( pattern ) => {
		if ( pattern?.category === 'header' ) {
			return getTemplatePartClientID( 'header' );
		}

		if ( pattern?.category === 'footer' ) {
			return getTemplatePartClientID( 'footer' );
		}

		return getContentBlockClientID();
	};

	const scrollToTop = () => {
		const { canvasiFrameWindow } = getCanvasIframeElements();
		canvasiFrameWindow?.scrollTo( { top: 0, behavior: 'smooth' } );
	};

	const setStyleProperties = useCallback( ( properties ) => {
		const { canvasiFrameRoot } = getCanvasIframeElements();
		if ( canvasiFrameRoot ) {
			for ( const [ name, value ] of Object.entries( properties ) ) {
				canvasiFrameRoot.style.setProperty( name, value );
			}
		}
	}, [] );

	const blockDblclick = ( e ) => {
		e.preventDefault();
		e.stopImmediatePropagation();
	};

	const zoomOut = async () => {
		const { canvasiFrameRoot } = getCanvasIframeElements();
		if ( setZoomLevel ) setZoomLevel( 0.5 );
		__unstableSetEditorMode( 'zoom-out' );

		// Add double-click event listener to prevent default behavior
		const { canvasiFrameBody } = getCanvasIframeElements();
		if ( canvasiFrameBody ) {
			canvasiFrameBody.addEventListener(
				'dblclick',
				blockDblclick,
				true
			);
		}

		// Wait for the next microtask to ensure the animations are registered
		await Promise.resolve();

		if ( canvasiFrameRoot ) {
			await Promise.all(
				canvasiFrameRoot
					.getAnimations()
					.map( ( animation ) => animation.finished )
			);
		}

		window.dispatchEvent( new Event( 'resize' ) );
		// Wait for the next event loop to make sure the `.zoom-out-animation` class is removed
		// (That Gutenberg class prevents us from scrolling to the new block)
		return await setDelay( 0 );
	};
	const zoomIn = async () => {
		const { canvasiFrameRoot } = getCanvasIframeElements();
		if ( resetZoomLevel ) resetZoomLevel();
		__unstableSetEditorMode( 'edit' );

		await Promise.resolve();

		if ( canvasiFrameRoot ) {
			await Promise.all(
				canvasiFrameRoot
					.getAnimations()
					.map( ( animation ) => animation.finished )
			);
		}

		window.dispatchEvent( new Event( 'resize' ) );
		// Remove double-click event listener
		const { canvasiFrameBody } = getCanvasIframeElements();
		if ( canvasiFrameBody ) {
			canvasiFrameBody.removeEventListener( 'dblclick', blockDblclick );
		}
		return await setDelay( 0 );
	};
	return {
		canvasMode: canvas,
		addPatternsToCanvas,
		setStyleProperties,
		clearTemplateParts,
		clearBlocks,
		createDummySection,
		insertDummySections,
		setIframeScale,
		zoomOut,
		zoomIn,
	};
}
