/**
 * External dependencies
 */
import {
	LocalGraphChatModel,
	WPCOMJetpackAIChatModel,
} from '@automattic/big-sky-agents';

/**
 * Internal dependencies
 */
import { requestJetpackToken } from '@utils/request-jetpack-token.ts';
import log from '@utils/log-debug.js';

let currentChatMessageModel = null;

export const runChatCompletion =
	(
		model,
		messages = [],
		tools,
		temperature = 0.1,
		response_format = 'text',
		retry = false
	) =>
	async ( { select, dispatch } ) => {
		const jetpackToken = await requestJetpackToken( ! retry );
		if ( ! jetpackToken ) {
			return;
		}
		const params = {
			model,
			messages,
			stream: false,
			temperature,
			session_id: select.getSessionId(),
		};

		if ( tools ) {
			params.tools = tools;
		}

		if ( response_format ) {
			params.response_format = response_format;
		}

		const isLocalGraph = window.bigSkyInitialState?.isLocalGraph;

		const ChatModelClass = isLocalGraph
			? LocalGraphChatModel
			: WPCOMJetpackAIChatModel;
		const chatModel = new ChatModelClass( {
			apiKey: jetpackToken.token,
			feature: 'big-sky',
			sessionId: select.getSessionId(),
		} );

		currentChatMessageModel = chatModel;

		try {
			const response = await chatModel.call( params );
			dispatch.logOpenAIUsage( model, response.usage );

			return response;
		} catch ( e ) {
			if ( e.name === 'AbortError' ) {
				throw e;
			}
			// retry once with a new token
			if ( ! retry ) {
				return dispatch.runChatCompletion(
					model,
					messages,
					tools,
					temperature,
					response_format,
					true
				);
			}

			log.error( 'OpenAI error', e );
			return null;
		}
	};

export const generateImage =
	( {
		model,
		prompt,
		n = 1,
		size,
		response_format = 'url',
		image_format = 'PNG',
		retry = false,
	} ) =>
	async ( { select, dispatch } ) => {
		try {
			const jetpackToken = await requestJetpackToken( ! retry );
			if ( ! jetpackToken ) {
				return;
			}
			const imageGenerationUrl =
				'https://public-api.wordpress.com/wpcom/v2/jetpack-ai-image';

			const params = {
				model,
				prompt,
				n,
				size,
				response_format,
				remove_background: true,
				image_format,
				feature: 'big-sky',
				session_id: select.getSessionId(),
			};
			const request = await fetch( imageGenerationUrl, {
				method: 'POST',
				headers: {
					Authorization: `Bearer ${ jetpackToken.token }`,
					'Content-Type': 'application/json',
				},
				body: JSON.stringify( params ),
			} );

			if ( request.status >= 300 ) {
				// retry once with a new token
				if ( ! retry ) {
					return dispatch.generateImage( {
						model,
						prompt,
						n,
						size,
						response_format,
						image_format,
						retry: true,
					} );
				}

				log.error( 'Error calling Image Generation API: ', response );
				return null;
			}
			const response = await request.json();

			dispatch.logOpenAIUsage( model, response.usage );
			return response;
		} catch ( e ) {
			// retry once with a new token
			if ( ! retry ) {
				return dispatch.generateImage( {
					model,
					prompt,
					n,
					size,
					response_format,
					image_format,
					retry: true,
				} );
			}

			log.error( 'OpenAI error', e );
			return null;
		}
	};

export const getCurrentChatMessageModel = () => currentChatMessageModel;

export function logOpenAIUsage( model, usage ) {
	return {
		type: 'LOG_OPENAI_USAGE',
		payload: {
			model,
			usage,
		},
	};
}
