/**
 * WordPress dependencies
 */
import { useDispatch, useSelect } from '@wordpress/data';
import { __, sprintf } from '@wordpress/i18n';
import { Button } from '@wordpress/components';
import { plus } from '@wordpress/icons';
import { useChat } from '@automattic/big-sky-agents';
import { store as editorStore } from '@wordpress/editor';

/**
 * Internal dependencies
 */
import { ChatSuggestions } from '@ai/assistants/shared/make-suggestions';
import useChatThread from '@components/chat/thread/use-thread.js';
import { store as aiStore } from '@store';
import useJetpackToken from '@hooks/use-jetpack-token';
import usePatterns from '@hooks/use-patterns';
import useSiteMetadata from '@hooks/use-site-metadata';

export default function GroupDockMenus( { onAssistantMenuClick } ) {
	const assistantMenuTitle = __( 'Add Pattern', 'big-sky' );
	const assistantMenuSlug = 'add-pattern';
	const assistantPrompt = __( 'Describe the pattern to add', 'big-sky' );
	const { userSay } = useChat();
	const { initializeThread } = useChatThread();
	const { setChatComponents, setCurrentUserMessage } = useDispatch( aiStore );
	const { withJetpackTokenRequest } = useJetpackToken();
	const { getAvailablePatternCategories } = usePatterns();

	const { patterns } = useSiteMetadata().getSiteMetadata();

	const { currentPostId } = useSelect( ( select ) => ( {
		currentPostId: select( editorStore ).getCurrentPostId(),
	} ) );

	const onClickAddPattern = async () => {
		onAssistantMenuClick(
			assistantMenuTitle,
			assistantMenuSlug,
			assistantPrompt
		);
		initializeThread(
			__(
				'What kind of pattern would you like to add to your page?',
				'big-sky'
			)
		);

		const availableCategories = getAvailablePatternCategories()?.filter(
			( category ) => category !== 'header' && category !== 'footer'
		);

		let currentPatternCategories = [];
		if ( Array.isArray( patterns ) ) {
			currentPatternCategories = patterns.map(
				( pattern ) => pattern.category
			);
		} else {
			currentPatternCategories = patterns[ currentPostId ]?.map(
				( pattern ) => pattern.category
			);
		}

		// Randomize the order of the suggested categories
		// @todo: Prioritize the list based on something relevant.
		const suggestedCategories = availableCategories
			.filter(
				( category ) => ! currentPatternCategories.includes( category )
			)
			/* eslint-disable-next-line no-restricted-syntax */
			.sort( () => Math.random() - 0.5 );

		const suggestionArgs = ( category ) => {
			switch ( category ) {
				case 'about':
					return {
						key: category,
						actionName: __( 'About Section', 'big-sky' ),
						description: __( 'A section about my site', 'big-sky' ),
						message: __(
							'Insert a section all about my site',
							'big-sky'
						),
					};
				case 'contact':
					return {
						key: category,
						actionName: __( 'Contact', 'big-sky' ),
						description: __( 'A contact section', 'big-sky' ),
						message: __(
							'Insert a section with contact information',
							'big-sky'
						),
					};
				case 'events':
					return {
						key: category,
						actionName: __( 'Events List', 'big-sky' ),
						description: __(
							'A list of upcoming events',
							'big-sky'
						),
						message: __(
							'Insert a section with a list of upcoming events',
							'big-sky'
						),
					};
				case 'gallery':
					return {
						key: category,
						actionName: __( 'Image Gallery', 'big-sky' ),
						description: __( 'A gallery of images', 'big-sky' ),
						message: __(
							'Insert a section with a gallery of images',
							'big-sky'
						),
					};
				case 'intro':
					return {
						key: category,
						actionName: __( 'Intro Section', 'big-sky' ),
						description: __(
							'A section introducing my site',
							'big-sky'
						),
						message: __(
							'Insert a section introducing my site',
							'big-sky'
						),
					};
				case 'menu':
					return {
						key: category,
						actionName: __( 'Menu', 'big-sky' ),
						description: __( 'A food menu', 'big-sky' ),
						message: __(
							'Insert a section for my food menu',
							'big-sky'
						),
					};
				case 'newsletter':
					return {
						key: category,
						actionName: __( 'Newsletter', 'big-sky' ),
						description: __(
							'A newsletter subscribe form',
							'big-sky'
						),
						message: __(
							'Insert a section with a newsletter subscribe form',
							'big-sky'
						),
					};
				case 'posts':
					return {
						key: category,
						actionName: __( 'Posts List', 'big-sky' ),
						description: __( 'A list of my posts', 'big-sky' ),
						message: __(
							'Insert a section with a list of my posts',
							'big-sky'
						),
					};
				case 'pricing':
					return {
						key: category,
						actionName: __( 'Pricing Table', 'big-sky' ),
						description: __(
							'A pricing table for my services',
							'big-sky'
						),
						message: __(
							'Insert a section with a pricing table for my services',
							'big-sky'
						),
					};
				case 'services':
					return {
						key: category,
						actionName: __( 'Services List', 'big-sky' ),
						description: __( 'A list of my services', 'big-sky' ),
						message: __(
							'Insert a section with a list of my services',
							'big-sky'
						),
					};
				case 'testimonials':
					return {
						key: category,
						actionName: __( 'Testimonials', 'big-sky' ),
						description: __( 'A list of testimonials', 'big-sky' ),
						message: __(
							'Insert a section with some testimonials',
							'big-sky'
						),
					};
				case 'team':
					return {
						key: category,
						actionName: __( 'Team', 'big-sky' ),
						description: __( 'A list of team members', 'big-sky' ),
						message: __(
							'Insert a section with a list of team members',
							'big-sky'
						),
					};
				default:
					const uppercaseCategory =
						category.charAt( 0 ).toUpperCase() +
						category.slice( 1 );
					return {
						key: category,
						actionName: uppercaseCategory,
						description: sprintf(
							// translators: %s is the pattern category name
							__( 'A %s section', 'big-sky' ),
							uppercaseCategory
						),
					};
			}
		};

		// Build the args for the chat suggestions
		// If there are no suggested patterns, show a generic "add a pattern" suggestion
		const args = {
			actions: suggestedCategories.map( ( category ) =>
				suggestionArgs( category )
			),
		};

		const respond = async ( message ) => {
			await withJetpackTokenRequest( () => userSay( message ) );
			setCurrentUserMessage( '' );
			setChatComponents( [] );
		};

		setChatComponents( [
			<ChatSuggestions
				key="suggestions"
				args={ args }
				respond={ respond }
			/>,
		] );
	};

	return (
		<>
			<Button
				onClick={ () => onClickAddPattern() }
				icon={ plus }
				iconPosition="left"
			>
				{ assistantMenuTitle }
			</Button>
		</>
	);
}
