/**
 * Internal dependencies
 */
import { getMenuContentSystemPrompt } from './system-prompt.js';
import { patternKeys } from '@utils/constants';

let menuDataIndex = false;

function getNextAvailableMenuSectionContentValue( {
	data,
	type,
	sectionIndex,
	sectionHeading,
	offset = 0,
} ) {
	let index = sectionIndex || 0;
	// get index by name if sectionHeading is provided
	if ( sectionHeading ) {
		index = Object.values( data ).findIndex(
			( section ) => section.name === sectionHeading
		);
		if ( index === -1 ) {
			index = 0;
		}
	}

	const section = data[ index ];
	let contentValue = '';

	if ( type === 'section-name' ) {
		contentValue = section.name;
		return { data, contentValue };
	}

	const newType = type.replace( 'item-', '' );

	offset = offset % section.items.length;

	for ( let j = offset; j < section.items.length; j++ ) {
		if ( section.items[ j ][ newType ] ) {
			contentValue = section.items[ j ][ newType ];

			return { data, contentValue };
		}
	}

	return { data, contentValue };
}

function replaceContent( {
	rootBlock,
	data: menuData,
	updateBlockAttributes,
	sectionHeading,
	replaceHeading,
	startIndex = -1,
} ) {
	const { ITEMS_KEY, ITEM_KEY } = patternKeys;
	let newMenuData = null;

	if ( ! menuData ) {
		return;
	}

	if ( rootBlock?.attributes?.metadata?.patternCategory ) {
		// If this is the root pattern block, make a deep copy of the menu item data to avoid mutation for other patterns
		newMenuData = JSON.parse( JSON.stringify( menuData ) );
		menuDataIndex = false;
	} else {
		newMenuData = menuData;
	}

	let newStartIndex = startIndex;
	// reset the start index if we're changing menu items
	if ( rootBlock?.attributes?.metadata?.name === ITEMS_KEY ) {
		newStartIndex = startIndex;
	} else if ( rootBlock?.attributes?.metadata?.name === ITEM_KEY ) {
		// increment the start index if we're going to a new menu item
		newStartIndex++;
	}

	const validClassNames = [
		'is-menu-section-name',
		'is-menu-item-name',
		'is-menu-item-description',
		'is-menu-item-currency',
		'is-menu-item-price',
	];

	if ( rootBlock?.attributes?.className ) {
		const rootBlockClassNames = rootBlock.attributes.className.split( ' ' );

		const matchingClassName = rootBlockClassNames.find( ( className ) =>
			validClassNames.includes( className )
		);

		if ( matchingClassName ) {
			if ( matchingClassName === 'is-menu-section-name' ) {
				if ( ! replaceHeading ) return;

				if ( menuDataIndex === false ) {
					menuDataIndex = 0;
				} else {
					menuDataIndex = 1;
				}
				startIndex++;
			}

			const { data, contentValue } =
				getNextAvailableMenuSectionContentValue( {
					data: newMenuData,
					type: matchingClassName.replace( 'is-menu-', '' ),
					sectionIndex: menuDataIndex,
					sectionHeading,
					offset: newStartIndex,
				} );

			newMenuData = data;
			if ( updateBlockAttributes ) {
				updateBlockAttributes( rootBlock.clientId, {
					content: contentValue,
				} );
			} else {
				rootBlock.attributes.content = contentValue;
			}
		}
	}

	for ( const block of rootBlock.innerBlocks ) {
		newStartIndex = replaceContent( {
			rootBlock: block,
			data: newMenuData,
			updateBlockAttributes,
			sectionHeading,
			replaceHeading,
			startIndex: newStartIndex,
		} );
	}

	return newStartIndex;
}

function getPrompts( { siteLocation, numOfSections = 2 } ) {
	const prompts = {
		system: getMenuContentSystemPrompt( siteLocation?.name ),
		user: `Generate ${ numOfSections } menu sections, each with 8 menu items relevant for this website based on the description and reason the section exists.`,
	};
	return prompts;
}

export default {
	name: 'menu',
	getPrompts,
	replaceContent,
};
