/**
 * External dependencies
 */
import analytics from '@automattic/jetpack-analytics';

/**
 * Internal dependencies
 */
import { store as aiStore } from '@store';

/**
 * WordPress dependencies
 */
import { select } from '@wordpress/data';
import { store as editorStore } from '@wordpress/editor';
import { store as coreStore } from '@wordpress/core-data';

const tracks_prefix = 'jetpack_big_sky';
const validNameRegex = /^[a-z_][a-z0-9_]*$/;

/**
 * Validates that a name matches the required pattern
 * @param {string} name - The name to validate
 * @param {string} type - The type of name being validated ('Event' or 'Property')
 */
function validateName( name, type ) {
	if ( ! validNameRegex.test( name ) ) {
		console.error(
			`${ type } names must match pattern: ^[a-z_][a-z0-9_]*$`
		);
	}
}

/**
 * Validates that all property names match the required pattern
 * @param {Object} properties - The properties object to validate
 */
function validateProperties( properties ) {
	Object.keys( properties ).forEach( ( key ) => {
		validateName( key, 'Property' );
	} );
}

/**
 * Get the current session ID from the Redux store.
 * We load this directly to avoid needing to pass it every time when recording an event.
 * Since we have a stable session ID per page load, we can just grab it from the store.
 * Additionally, there is no reactivity needed for this value in this context.
 */
function getCurrentSessionId() {
	const store = aiStore.instantiate().store;
	const state = store.getState();
	return state.sessionId;
}

/**
 * Get the current phase of Big Sky based on store state
 * @return {string} The current phase ('handoff', 'onboarding', or 'editor')
 */
function getPhase() {
	const state = select( aiStore );

	if ( state.getIsHandoff() ) {
		return 'handoff';
	} else if ( ! state.getSiteMetadata()?.isOnboarded ) {
		return 'onboarding';
	}
	return 'editor';
}

export function getIsDevMode() {
	const aiState = select( aiStore );
	return aiState?.getIsDevMode() || false;
}

/**
 * Record an action a user takes and send to Tracks.
 *
 * @param {string} action     - The action name to record via Tracks.
 * @param {Object} properties - Additional optional properties to send with the action.
 */
export function recordUserActionTracks( action, properties = {} ) {
	validateName( action, 'Action' );

	const combinedProperties = {
		...properties,
		action,
		sessionid: getCurrentSessionId(),
		phase: getPhase(),
		is_test: getIsDevMode(),
	};

	validateProperties( properties );

	analytics.tracks.recordEvent( `${ tracks_prefix }_action`, {
		...combinedProperties,
	} );
}

/**
 * Get current page properties
 */
function getCurrentPageProperties() {
	const homePageId = select( coreStore ).getEntityRecord(
		'root',
		'site'
	)?.page_on_front;
	const currentPostId = select( editorStore ).getCurrentPostId();
	const isHomePage = currentPostId === homePageId;

	return {
		post_id: currentPostId,
		title: select( editorStore ).getEditedPostAttribute( 'title' ),
		is_home_page: isHomePage,
	};
}

/**
 * Record a generic Big Sky Event and send to Tracks(funnel tracking, etc.)
 *
 * @param {*} event      - The event name to record via Tracks.
 * @param {*} properties - Additional optional properties to send with the event.
 */
export function recordTracksEvent( event, properties = {} ) {
	validateName( event, 'Event' );

	const combinedProperties = {
		...properties,
		...getCurrentPageProperties(),
		sessionid: getCurrentSessionId(),
		phase: getPhase(),
		is_test: getIsDevMode(),
	};

	validateProperties( combinedProperties );

	analytics.tracks.recordEvent( `${ tracks_prefix }_${ event }`, {
		...combinedProperties,
	} );
}
