/**
 * Internal dependencies
 */
import { removeBlockFromHtml } from '../utils.js';
import { getPricingContentSystemPrompt } from './system-prompt.js';
import { patternKeys } from '@utils/constants';

let servicesDataIndex = -1;

function getNextAvailablePricingTierSectionContentValue( {
	data,
	type,
	tierIndex,
	serviceIndex = 0,
} ) {
	const section = Array.isArray( data ) ? data[ 0 ] : data;
	let contentValue = '';

	if ( [ 'title', 'description' ].includes( type ) ) {
		contentValue = section[ type ];
		return { data, contentValue };
	}

	const newType = type.replace( 'tier-', '' );
	const index =
		( tierIndex && tierIndex !== -1 ? tierIndex : 0 ) %
		section.tiers.length;

	if ( [ 'name', 'price' ].includes( newType ) ) {
		contentValue = section.tiers[ index ][ newType ];
		return { data, contentValue };
	}

	const services = section.tiers[ index ].services;
	if ( [ 'service' ].includes( newType ) ) {
		contentValue = services[ serviceIndex ];
		return { data, contentValue };
	}
	if ( [ 'service-checked' ].includes( newType ) ) {
		const nextService = services[ serviceIndex ];
		contentValue = `<b></b> ${ nextService }`;
		return { data, contentValue };
	}

	return { data, contentValue };
}

function replaceContent( {
	rootBlock,
	data: pricingData,
	updateBlockAttributes,
	startIndex = -1,
} ) {
	const { ITEMS_KEY, ITEM_KEY } = patternKeys;
	let newPricingData = null;

	if ( ! pricingData ) {
		return;
	}

	if ( rootBlock?.attributes?.metadata?.patternCategory ) {
		// If this is the root pattern block, make a deep copy of the pricing item data to avoid mutation for other patterns
		newPricingData = JSON.parse( JSON.stringify( pricingData ) );
	} else {
		newPricingData = pricingData;
	}

	let newStartIndex = startIndex;
	// reset the start index if we're changing pricing items (tiers)
	if ( rootBlock?.attributes?.metadata?.name === ITEMS_KEY ) {
		newStartIndex = startIndex;
	} else if ( rootBlock?.attributes?.metadata?.name === ITEM_KEY ) {
		// increment the start index if we're going to a new tier item
		newStartIndex++;
	}

	const validClassNames = [
		'is-pricing-title',
		'is-pricing-description',
		'is-pricing-tier-name',
		'is-pricing-tier-price',
		'is-pricing-tier-service',
		'is-pricing-tier-service-checked',
	];

	if ( rootBlock?.attributes?.className ) {
		const rootBlockClassNames = rootBlock.attributes.className.split( ' ' );

		const matchingClassName = rootBlockClassNames.find( ( className ) =>
			validClassNames.includes( className )
		);

		if ( matchingClassName ) {
			if ( matchingClassName === 'is-pricing-tier-name' ) {
				startIndex++;
				servicesDataIndex = -1;
			}

			if ( matchingClassName.startsWith( 'is-pricing-tier-service' ) ) {
				servicesDataIndex++;
			}

			const { data, contentValue } =
				getNextAvailablePricingTierSectionContentValue( {
					data: newPricingData,
					type: matchingClassName.replace( 'is-pricing-', '' ),
					tierIndex: newStartIndex,
					serviceIndex: servicesDataIndex,
				} );

			newPricingData = data;
			if ( updateBlockAttributes ) {
				updateBlockAttributes( rootBlock.clientId, {
					content: contentValue,
				} );
			} else {
				rootBlock.attributes.content = contentValue;
			}
		}
	}

	for ( const block of rootBlock.innerBlocks ) {
		newStartIndex = replaceContent( {
			rootBlock: block,
			data: newPricingData,
			updateBlockAttributes,
			startIndex: newStartIndex,
		} );
	}

	return newStartIndex;
}

function preprocessSection( pattern ) {
	pattern.html = removeBlockFromHtml( pattern.html, 'buttons' );
}

function getPrompts( { siteLocation } ) {
	const prompts = {
		system: getPricingContentSystemPrompt( siteLocation?.name ),

		user: 'Generate 3 pricing tiers with 4 features in each tier. The first feature of the second tier should include all the features of the first tier, and so on.',
	};

	return prompts;
}

function isCachedContentValid( cachedContent ) {
	return cachedContent?.length > 0 && cachedContent[ 0 ]?.tiers;
}

export default {
	name: 'pricing',
	getPrompts,
	replaceContent,
	preprocessSection,
	isCachedContentValid,
};
