/**
 * Internal dependencies
 */
import logAction from './log-action.js';
import { messageRoles } from './constants.js';

export default async function logMessage( sessionId, message ) {
	if ( ! sessionId ) {
		return;
	}

	let content = message.content;

	// if the content is a react component,

	if ( message.role === messageRoles.COMPONENT ) {
		const componentTitle = content.type.name;
		content = `Component: &lt;${ componentTitle }/&gt;`;
	} else if ( typeof content !== 'string' ) {
		content = JSON.stringify( content );
	}

	return await logAction(
		sessionId,
		content,
		message.role,
		message.id,
		message.parentId
	);
}
