export default function addPageTool( categories = [] ) {
	const pageGuidance = {
		about: [ 'intro', 'about', 'contact' ],
		contact: [ 'contact', 'newsletter' ],
		events: [ 'intro', 'events', 'newsletter' ],
		menu: [ 'about', 'menu' ],
		pricing: [ 'intro', 'pricing', 'testimonials' ],
		gallery: [ 'about', 'gallery' ],
		blog: [ 'intro', 'posts', 'about' ],
		services: [ 'about', 'services' ],
		other: [ 'intro', 'about' ],
	};

	return {
		name: 'add_page',
		description: `Add a new page based on the user's description.
			Categories define the type of content blocks that will be used for page layout.
			Do not use this tool if the user requests a new section or pattern.
			Please refer to the following guidance for the categories to choose for the type of page:
			${ JSON.stringify( pageGuidance ) }`,
		parameters: {
			type: 'object',
			properties: {
				title: {
					type: 'string',
					description:
						'The title of the page that will be displayed to the users.',
				},
				categories: {
					type: 'array',
					description: `An ordered list of page sections. The tool will use these categories to generate the structure of the page.`,
					items: {
						type: 'object',
						additionalProperties: false,
						required: [ 'slug', 'reason' ],
						properties: {
							slug: {
								type: 'string',
								description:
									'The slug identifying the specific category of the page section.',
								enum: categories,
							},
							reason: {
								type: 'string',
								description:
									'A brief explanation for choosing this category. This helps guide content creation by clarifying what type of content or information would fit well in this section based on its purpose.',
							},
						},
					},
				},
			},
			required: [ 'title', 'categories' ],
		},
		strict: true,
	};
}
