const addContainerToEventsSectionTool = ( {
	description,
	itemCount = 5,
	dateFormatExample,
	timeFormatExample,
} ) => {
	return {
		name: 'add_container_to_events_section',
		description: `Adds a new events container (group/row of event items) to the events pattern.
		The description of the site is: ${ description }.
		If not specified in the user request, make up ${ itemCount } events items according to the description of the site.
		The event dates should be later than and in the same format as ${ dateFormatExample }.
		The event times should be in the same format as ${ timeFormatExample }.
		`,
		parameters: {
			type: 'object',
			properties: {
				items: {
					type: 'array',
					description:
						'A list of events items to add to the events section',
					items: {
						type: 'object',
						description:
							'The events item to add to the events section.',
						properties: {
							date: {
								type: 'string',
								description: 'The date of the event.',
							},
							time: {
								type: 'string',
								description: 'The time of the event.',
							},
							name: {
								type: 'string',
								description: 'The name of the event.',
							},
							description: {
								type: 'string',
								description:
									'The description of the menu item. If not provided, make something up.',
							},
							location: {
								type: 'string',
								description:
									'The location of the event. If not provided, make something up.',
							},
						},
						additionalProperties: false,
						required: [
							'date',
							'time',
							'name',
							'description',
							'location',
						],
					},
				},
			},
			required: [ 'items' ],
		},
		strict: true,
	};
};

export default addContainerToEventsSectionTool;
